import { defaultGetInit, defaultPostInit } from './helpers';

import { loading, finished } from '../../actions/loading';
import { setCSRFHeader } from '../../actions/user';

class Base {
  constructor(protected dispatch: any) {

  }

  private status = (resp: Response): Promise<Response> => (
    new Promise((resolve, reject) => {
      if (resp.status >= 200 && resp.status < 300) {
        return resolve(resp);
      }
      return reject(resp);
    })
  )

  api = (url: string, init?: any): Promise<any> => {

    let shouldCallFinishDispatch = false;
    if (url !== '/api/user' && url !== '/api/user/ping' && url !== '/api/user/image-upload' && url !== '/api/user/check-on-idle') {
      this.dispatch(loading());
      shouldCallFinishDispatch = true;
    }

    if (!url.includes('en?isInCache=false')) {

      let csrf_inner_html = 'CSRF_NOT_FOUND';
      if (document && (document as any).getElementById('csrf_token')) {
        csrf_inner_html = document && (document as any).getElementById('csrf_token').innerHTML;
        if (init && init.headers) {
          init.headers['CCSTK'] = csrf_inner_html.replace(/"/g, '');
        }
      }

    }

    let baseURL = '';
    if (global && (global as any).baseURL) {
      baseURL = (global as any).baseURL;
    }

    return fetch(baseURL + url, init)
      .then(this.status)
      .then(r => {
        let possibleHeader = r.headers.get('ccstk');
        if (possibleHeader) {
          this.dispatch(setCSRFHeader(possibleHeader));
        }
        return r.json();
      })
      .then(d => {
        if (shouldCallFinishDispatch)
          this.dispatch(finished());
        return d;
      })
      .catch(err => {
        if (shouldCallFinishDispatch)
          this.dispatch(finished());
        throw err;
      });
  }
}

export default class User extends Base {

  makeSession = () => this.api(`/api/user`, defaultGetInit());
  killSession = () => this.api(`/api/user/kill-session`, defaultGetInit());
  checkOnIdleSession = (ts: any, account: any) => this.api(`/api/user/check-on-idle`, defaultPostInit({ts, account}));
  legacyRedirect = () => this.api(`/api/user/legacy-redirect`, defaultGetInit());
  confirmBets = (odds: any) => this.api(`/api/user/place-bets`, defaultPostInit(odds));
  getTickets = (date: any) => this.api(`/api/user/tickets/${date}`, defaultGetInit());
  getAnimations = (sport: any) => this.api(`/api/user/animations/${sport}`, defaultGetInit());
  sessionPing = (user: any) => this.api(`/api/user/ping`, defaultPostInit({user}));
  imageUpload = (filePurpose: any, ticketId: any, b64: any) => this.api(`/api/user/image-upload`, defaultPostInit({filePurpose, ticketId, b64}));
}
