export const GAMECAST_ON = 'GAMECAST_ON';
export const GAMECAST_OFF = 'GAMECAST_OFF';

export const SHOW_SPORTS_LIST_ON = 'SHOW_SPORTS_LIST_ON';
export const SHOW_SPORTS_LIST_OFF = 'SHOW_SPORTS_LIST_OFF';

export const IS_MOBILE_ON = 'IS_MOBILE_ON';
export const IS_MOBILE_OFF = 'IS_MOBILE_OFF';

export const gameCastOn = () => ({
  type: GAMECAST_ON
});

export const gameCastOff = () => ({
  type: GAMECAST_OFF
});

export const showSportsListOn = () => ({
  type: SHOW_SPORTS_LIST_ON
});

export const showSportsListOff = () => ({
  type: SHOW_SPORTS_LIST_OFF
});

export const isMobileOn = () => ({
  type: IS_MOBILE_ON
});

export const isMobileOff = () => ({
  type: IS_MOBILE_OFF
});
