import { push } from 'connected-react-router';
import Settings from '../middleware/api/settings';

import { normalizePath, createRoute } from '../helpers';
import * as helpers from '../helpers';
import config from '../../config';

// --- Pop-up Settings
export const openSettings = (path: any) => {
  return config.host.trail !== '' ? push(`${normalizePath(path)}?settings`) :
  push(createRoute(`${normalizePath(path)}?settings`));
};

export const closeSettings = (path: any) => {
  return config.host.trail !== '' ? push(normalizePath(path)) :
  push(createRoute(normalizePath(path)));
};

export const REDIRECT_TO_SETTINGS = 'REDIRECT_TO_SETTINGS';

export const redirectSettings = (payload: any) => ({
  type: REDIRECT_TO_SETTINGS,
  payload
});

// --- Language
const languageCache: any = {};

export const CHANGE_LANGUAGE = 'CHANGE_LANGUAGE';

const changeLanguageSuccess = (language: any, data: any) => ({
  type: CHANGE_LANGUAGE,
  payload: {
    language,
    data
  }
});

export const changeLanguage = (language: any, cookies?: any) => (dispatch: any, getState: any) => {
  const { languageData, settings }: any = getState();
  languageCache[settings.language] = languageData;

  const setting = new Settings(dispatch);

  return setting
    .getLanguage(language, !!languageCache[language], cookies)
    .then((data: any) => {
      document.documentElement.lang = language;
      dispatch(changeLanguageSuccess(language, languageCache[language] || data));
    });
};

/* The language data for the Rules page is lazy loaded */
export const getRules = (cookies?: any) => (dispatch: any, getState: any): (Promise<any> | void) => {
  const { languageData, settings } = getState();

  if (languageData.rules) {
    languageCache[settings.language] = languageData;
    return;
  }

  const setting = new Settings(dispatch);

  return setting
    .getLanguage(settings.language, false, cookies)
    .then((data: any) => dispatch(changeLanguageSuccess(settings.language, data)));
};

// --- Time Zone
export const GET_TIMEZONES = 'GET_TIMEZONES';
export const CHANGE_TIMEZONE = 'CHANGE_TIMEZONE';

const changeTimeZoneSuccess = (timeZone: any) => ({
  type: CHANGE_TIMEZONE,
  payload: timeZone
});

const getTimeZonesSuccess = (timeZones: any) => ({
  type: GET_TIMEZONES,
  payload: timeZones
});

export const getTimeZones = (cookies?: any) => (dispatch: any) => {
  const setting = new Settings(dispatch);

  return setting
    .getTimeZones(cookies)
    .then((data: any) => dispatch(getTimeZonesSuccess(data)));
};

export const changeTimeZone = (param: any, cookies?: any) => (dispatch: any) => {
  const setting = new Settings(dispatch);

  return setting
    .changeTimeZone({ tz: param }, cookies)
    .then((data: any) => dispatch(changeTimeZoneSuccess(data)));
};

// --- Odds Format
export const CHANGE_ODDSFORMAT = 'CHANGE_ODDSFORMAT';

export const changeOddsFormatSuccess = (type: any) => ({
  type: CHANGE_ODDSFORMAT,
  payload: type
});

export const changeOddsFormat = (param: any, cookies?: any) => (dispatch: any) => {
  const setting = new Settings(dispatch);

  return setting
    .changeOddsFormat({ oddsFormat: param }, cookies)
    .then(() => dispatch(changeOddsFormatSuccess(param)));
};

// --- Theme
export const CHANGE_THEME = 'CHANGE_THEME';

export const changeThemeSuccess = (theme: any) => ({
  type: CHANGE_THEME,
  payload: theme
});

export const changeTheme = (theme: any, cookies?: any) => (dispatch: any) => {
  const setting = new Settings(dispatch);

  return setting
    .changeTheme({ theme }, cookies)
    .then(() => {
      const head = document.querySelector('head');

      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = config.host.trail === '' ? `/css/${theme}.css` : `${config.host.trail}/css/${theme}.css`;
      link.addEventListener('load', () => head.removeChild(document.querySelector('link[href$=".css"]')));
      head.appendChild(link);

      return dispatch(changeThemeSuccess(theme));
    });
};

// --- View
export const CHANGE_VIEW = 'CHANGE_VIEW';

export const changeViewSuccess = (type: any) => ({
  type: CHANGE_VIEW,
  payload: type
});

export const changeView = (param: any, cookies?: any) => (dispatch: any) => {
  const setting = new Settings(dispatch);

  return setting
    .changeView({ view: param }, cookies)
    .then(() => {
      dispatch(changeViewSuccess(param));
      return window.location.reload();
    });
};

export const CHANGE_VIEW_PREFERENCES = 'CHANGE_VIEW_PREFERENCES';

export const changeViewPreferencesSuccess = (viewPreferences: any) => ({
  type: CHANGE_VIEW_PREFERENCES,
  payload: viewPreferences
});

export const changeViewPreferences = (viewPreferences: any) => (dispatch: any) => {

  const settings = new Settings(dispatch);

  return settings
    .changeViewPreferences(viewPreferences)
    .then(() => dispatch(changeViewPreferencesSuccess(viewPreferences)));

};
