import * as React from 'react';
import { connect } from 'react-redux';

import { prematchTimeConverter } from './../live/live-helpers';
import { removeFromBetSlip } from '../../../actions/bet-slip';
import { removeBet } from '../../../actions/bet-confirmation';

import { toggleBetSlip } from '../../../actions/bet-slip';

import Price from '../../form/price';
import Form from './form';

import { mapOutcomeTitle } from './helpers';

import { reqLiveEvents } from '../../../actions/live';

import { detectmob } from './../live/live-helpers';

class Odd extends React.Component<any, any> {

  state = {};

  componentWillReceiveProps(newProps: any) {

    if ((newProps.odd.display !== this.props.odd.display) || newProps.odd.status) {
      newProps.odd.display = this.props.odd.display;
    }

  }

  removeMe = () => {
    const { view, dispatch, odd, events, betSlipOdds } = this.props;
    dispatch(removeFromBetSlip(odd));

    if (!events && detectmob()) {
      if (betSlipOdds.length === 1) {
        dispatch(toggleBetSlip(false));

        // let isAsian = view === 1;
        // dispatch(reqLiveEvents(isAsian));
      }
    }

  }

  render() {
    const { odd, event, betType, outcomeTitle, language, view, dispatch, betSlipOdds, betConfirmationOdds } = this.props;
    // console.log(odd);
    if (!event) {
      console.log(`event away`);

      if (odd) {
        dispatch(removeFromBetSlip(odd));
        dispatch(removeBet(odd));
        const idxBetslip = betSlipOdds && betSlipOdds.indexOf(odd);
        const idxOdd = betConfirmationOdds && betConfirmationOdds.indexOf(odd);

        if (betSlipOdds) {
          betSlipOdds.splice(idxBetslip, 1);
        }

        if (idxOdd >= 0) {
          betConfirmationOdds.splice(idxOdd, 1);
        }
      }

      dispatch(removeFromBetSlip(odd));
      dispatch(removeBet(odd));
      return <section></section>;
    }

    const className = view !== 3 ? 'bet-slip__bet' : 'bet-slip-new__bet';
    const { home, sep, away, inverted } = event;
    // console.log(event);
    return (
      <section className={className}>
        <h6 style={{padding: '0px', margin: '0px', fontSize: '12px', fontFamily: 'Ubuntu-Light'}}>
          <span> { prematchTimeConverter(event.time) }</span>
        </h6>
        <h4 className={`${className}-title`}>
          <span>
            { !inverted ? home : away } { sep } { !inverted ? away : home }
          </span>
          <span className={`${className}-close`} onClick={this.removeMe}>
            <i className="i i-close" />
          </span>
        </h4>
        <h5 className={`${className}-subtitle`}>
          <span className={`${className}-outcome`}>
            { outcomeTitle }: {odd.display}
          </span>
          <Price className={`${className}-price`}>{ odd }</Price>
        </h5>
        <span className={`${className}__error`}>
          { odd.limit && odd.limit.perLineBlock && language.errors.limitPerLine }
          { odd.limit && odd.limit.priceBlock && language.errors.limitPrice }
          { odd.status || (odd.replacementBet && odd.replacementBet.lastupdate > odd.lastupdate) && (language.errors.lineOutOfDate || language.errors.lineClosed)}
        </span>
        { !betType && <Form odd={odd} /> }
        {odd.limit === undefined && betType === 0 &&
          <p> We are setting your bet limits. Please wait... </p>
        }
      </section>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => {
    const { odd } = ownProps;

    const isPrematch = odd.oddIsLive === 0;
    const { betType } = state.live;
    const { outcomeList } = isPrematch ? state.prematch : state.live;
    const events = isPrematch ? state.prematch.leagues : state.eventsFlatten.byId;

    const event = isPrematch ? events.filter((ev: any) => ev.id === odd.event)[0] : events[odd.event];

    if (event) {
      const { home, away, inverted, sport } = event;

      return {
        odd,
        event,
        betType,
        isPrematch,
        view: state.settings.view,
        language: state.languageData.betSlip,
        betSlipOdds: state.betSlip.odds,
        betConfirmationOdds: state.betConfirmation.odds,
        updatedBetslipOdds: state.live.betslipOutcomes,
        outcomeTitle: mapOutcomeTitle(home, away, inverted, null, outcomeList[sport][isPrematch ? 0 : 1][odd.outcome], odd)
      };
    }

    return {};
  }
)(Odd);
