import * as React from 'react';

const Table = ({ columns, children }: { columns?: string[], children?: any }) => (
  <table className="md-table" cellSpacing="0" cellPadding="0">
    { columns &&
      <thead>
        <tr>
          { columns.map(c => <th key={c}>{c}</th>) }
        </tr>
      </thead>
    }
    { children }
  </table>
);

export default Table;
