import { ISoccerFieldAnimationState, SoccerTeam, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1GateStart: ISoccerFieldAnimationState = t1;
const t1GateEnd: ISoccerFieldAnimationState = deepAssign({}, t1, {
  gate: {
    translateY: 25
  }
});

const t2GateStart: ISoccerFieldAnimationState = t2;
const t2GateEnd: ISoccerFieldAnimationState = deepAssign({}, t2, {
  gate: {
    translateY: 25
  }
});

export function gate(team: SoccerTeam) {
  return {
    start: team === SoccerTeam.Team1 ? t1GateStart : t2GateStart,
    end: team === SoccerTeam.Team1 ? t1GateEnd : t2GateEnd
  };
}
