import * as React from 'react';
import { connect } from 'react-redux';

import { updateBet } from '../../../actions/bet-slip';

import Button from '../../form/button';
import TextField from '../../form/text-field';

import { BetType } from '../../../constants';

import { currencySymbol } from './../../../currencySymbols/currencySymbol';

const Form = ({ dispatch, language, odd, odds, betType, betSlipOpen, user, view }: any) => {
  const className = view !== 3 ? 'bet-slip__bet' : 'bet-slip-new__bet';
  const factor = betType === BetType.straight ? odd.price : odds.map((o: any) => o.price).reduce((a: any, b: any) => a *= b, 1);

  const handleRiskChange = (value: any) => {
    dispatch(updateBet({
      id: odd.id,
      risk: value,
      toWin: (value * (factor - 1)).toFixed(2),
      intent: 'risk'
    }));
  };

  const handleToWinChange = (value: any) => {
    dispatch(updateBet({
      id: odd.id,
      risk: (value / (factor - 1)).toFixed(2),
      toWin: value,
      intent: 'toWin'
    }));
  };

  const generateFastButtons = (ev: any) => (
    [25, 50, 100].map(num => (
      <Button key={num} onClick={() => ev(num)} flat>{currencySymbol(user.currency, num)}</Button>
    ))
  );

  const riskProps: any = {
    autoFocus: !betSlipOpen ? false : true,
    required: true,
    type: 'number',
    step: '0.01',
    label: language.risk,
    placeholder: '',
    value: odd.risk,
    onChange: handleRiskChange,
    min: undefined,
    max: undefined
  };

  const toWinProps = Object.assign({}, riskProps, {
    autoFocus: false,
    label: language.toWin,
    value: odd.toWin,
    onChange: handleToWinChange
  });

  if (odd.limit) {
    riskProps.placeholder = `(${language.min}: ${odd.limit.min.toFixed(2)} ${language.max}: ${odd.limit.max.toFixed(2)})`;
    riskProps.min = odd.limit.min;
    riskProps.max = odd.limit.max;

    const [min, max] = [odd.limit.min * factor - odd.limit.min, odd.limit.max * factor - odd.limit.max];
    toWinProps.placeholder = `(${language.min}: ${min.toFixed(2)} ${language.max}: ${max.toFixed(2)})`;
    toWinProps.min = min.toFixed(2);
    toWinProps.max = max.toFixed(2);
  }

  return (
    <fieldset disabled={odd === undefined}>
      { odd.limit === undefined && betType === 1 &&
        <p> We are setting your bet limits. Please wait... </p>
      }
      <div className={`${className}-risk`}>
        <div className={`${className}-risk__txt`}>
          <TextField {...riskProps} />
        </div>
        { generateFastButtons(handleRiskChange) }
      </div>
      <div className={`${className}-risk`}>
        <div className={`${className}-risk__txt`}>
          <TextField {...toWinProps} />
        </div>
        { generateFastButtons(handleToWinChange) }
      </div>
    </fieldset>
  );
};

export default connect(
  (state: any, ownProps: any) => ({
    language: state.languageData.betSlip,
    odds: state.betSlip.odds,
    betType: state.live.betType,
    odd: ownProps.odd,
    user: state.user,
    view: state.settings.view,
    betSlipOpen: state.betSlip.open
  })
)(Form);
