import { ISoccerFieldAnimationState, SoccerAction, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextTeam: SoccerTeam, nextAction: SoccerAction) {
  return {
    actionText: {
      x: nextTeam === SoccerTeam.Team1 ? 95 : 10,
      y: nextAction === SoccerAction.CornerTop ? 15 : 65
    },
    ball: {
      x: nextTeam === SoccerTeam.Team1 ? 104 : 1,
      y: nextAction === SoccerAction.CornerTop ? 1 : 67,
      direction: BallDirection.PenaltyMark
    }
  };
}

const t1CornerTop: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.CornerTop));
const t2CornerTop: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.CornerTop));
const t1CornerBottom: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.CornerBottom));
const t2CornerBottom: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.CornerBottom));

export function corner(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  let start: ISoccerFieldAnimationState;
  let end: ISoccerFieldAnimationState;

  if (nextAction === SoccerAction.CornerTop) {
    end = nextTeam === SoccerTeam.Team1 ? t1CornerTop : t2CornerTop;
  }
  else {
    end = nextTeam === SoccerTeam.Team1 ? t1CornerBottom : t2CornerBottom;
  }

  start = deepAssign({}, end, {
    actionText: state.actionText,
    ball: {
      x: state.ball.x,
      y: state.ball.y,
      direction: BallDirection.None
    }
  });

  return [{
    start,
    end
  }];
}
