import * as React from 'react';
import { connect } from 'react-redux';
import Odd from './../../views/live/odd';
import { addToBetSlip } from './../../../actions/bet-slip';

function ThreeColumnOdd({
  key,
  odd,
  dispatch,
  rowsLength,
  isInBetSlip
}: any) {

  const oddClassList = ['box-odd'];

  if (!odd || !odd.hasOwnProperty('suspended') || odd.suspended) {
    oddClassList.push('suspended');
  }

  if (!odd) {
    return (
      <td key={key}>
        <span>
          <p className={oddClassList.join(' ')} />
        </span>
      </td>
    );
  }

  const addToBetSlip_ = (e: any, odd: any) => {
    if (odd && odd.suspended === false) {
      dispatch(addToBetSlip(odd));
      e.nativeEvent.stopImmediatePropagation();
    }
  };

  const classList = ['three-column-market'];
  if (classList.indexOf(`box-column-${rowsLength}`) < 0) {
    classList.push(`box-column-${rowsLength}`);
  }

  if (isInBetSlip.filter((bs: any) => bs.id === odd.id)[0] && classList.indexOf(`three-column-market--added`) < 0) {
    classList.push(`three-column-market--added`);
  }

  return (
    <td
      key={key}
      className={classList.join(' ')}
      style={{ cursor: 'pointer', fontFamily: 'Ubuntu-Medium' }}
      onClick={(e) => addToBetSlip_(e, odd)}
    >
      <span>
        <p>
          <span>
            <Odd
              odd={odd}
              event={event}
              isThreeColumns={true}
            />
          </span>
        </p>
      </span>
    </td>
  );
}

export default connect(
  (state: any, ownProps: any) => {
    // console.log(ownProps);
    return {
      isInBetSlip: state.betSlip.odds
    };
  })(ThreeColumnOdd);
