import * as React from 'react';
import { connect } from 'react-redux';

import EventsDesktopOdd from './events-desktop-odd';
import PrematchMobileOdd from './prematch-mobile-odd';
import Price from '../../../../../components/form/price';

import { addToBetSlip } from '../../../../../actions/bet-slip';

class EventsOdd extends React.Component<any, any> {
  state = {
    last: 0,
    update: 0
  };

  timeOut: any;

  componentWillReceiveProps(nextProps: any) {
    const { odd } = this.props;
    const newOdd = nextProps.odd;

    if (odd && newOdd && odd.price && newOdd.price && odd.price !== newOdd.price) {
      this.setState({
        update: newOdd.price - odd.price
      });

      clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => this.setState({
        last: this.state.update,
        update: 0
      }), 3500);
    }
  }

  componentWillUnmount() {
    clearTimeout(this.timeOut);

  }

  addToBetSlip = (e: any) => {
    const { dispatch, odd } = this.props;

    if (odd && odd.suspended === false) {
      dispatch(addToBetSlip(odd));
      e.nativeEvent.stopImmediatePropagation();
    }
  }

  render() {
    const { className = '', odd, sport, /*asian = false,*/ isInBetSlip, sportsList, event, isDisplaySmall } = this.props;
    const { last, update } = this.state;
    const classList = className.split(' ');
    const updClassList = [`i`];

    let oddDisplay = odd.display;

    // Desktop view requires us to remove the team name from
    // some odd.display values e.g. odd.display = Team1 +8.5

    if (oddDisplay) {
      if (!oddDisplay.includes('Over') && !oddDisplay.includes('Under')) {
        if (oddDisplay.indexOf('+') !== 0 || oddDisplay.indexOf('-') !== 0) {
          if (odd.line !== '') {
            oddDisplay = odd.line;
          }
        }
      }
    }

    if (update > 0) {
      updClassList.push('i-bet-up');
      if (!isInBetSlip) {
        classList.push('green');
      }
    }
    else if (update < 0) {
      updClassList.push('i-bet-down');
      if (!isInBetSlip) {
        classList.push('red');
      }
    }
    else {
      updClassList.push('i-bet-none');
      updClassList.push(`i-bet-${last >= 0 ? 'up' : 'down'}`);
    }

    if (isInBetSlip) {
      classList.push(`${className}--added`);
    }

    let isHandicap = false;
    if (oddDisplay !== undefined) {
      if (sport === 7) {
        isHandicap = odd.outcome === 2 || odd.outcome === 260 || odd.outcome === 259;
      } else {
        isHandicap = (odd.outcome === 2 || odd.outcome === 3 || odd.outcome === 260);
      }
    }

    // It is here !!!
    if (odd.outcome === 3 || odd.outcome === 260) {

      if (oddDisplay !== undefined) {

        const { home, away } = event;
        const teams = [home, away];
        let str = oddDisplay;

        // TODO: replace longest team first

        teams.map(team => {

          if (str.match(team)) {
            oddDisplay = `${str.replace(team, '')}`;
          }
        });

      }
    }

    if (event === 1 || !isDisplaySmall) {
      return (
        <EventsDesktopOdd
          odd={odd}
          classList={classList}
          oddDisplay={oddDisplay}
          isHandicap={isHandicap}
          sportsList={sportsList}
          updClassList={updClassList}
          addToBetSlip={this.addToBetSlip}
        />
      );
    } else {
      return (
        <PrematchMobileOdd
          odd={odd}
          event={event}
          classList={classList}
          sportsList={sportsList}
          oddDisplay={oddDisplay}
          addToBetSlip={this.addToBetSlip}
        />
      );
    }

  }

}

export default connect(
  (state: any, ownProps: any) => ({
    isInBetSlip: state.betSlip.odds.some((odd: any) => odd.id === ownProps.odd.id),
    sportsList: state.uiToolbar.show_sports_list
  })
)(EventsOdd);
