import * as React from 'react';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';

import routes from './routes';
import CSRF from './misc/csrf';
import Spinner from './misc/spinner';

import Header from './layout/header';
import Sports from './layout/sports';
import Content from './layout/content';
import Settings from './views/settings';
import MiniModal from './misc/mini-modal';
import BetConfirmation from './views/bet-confirmation';

const Root = ({ store, history, main, sub }: any) => {

    const subPage = !!sub;
    const page = subPage ? sub : main;

    let sports;
    if (!subPage) {
      sports = <Sports />;
    }

    return (
        <Provider store={store}>
            <div className="container">
                <ConnectedRouter history={history}>
                    <div>
                        <Header subPage={true} />
                            { sports }
                        <Content subPage={true}>
                            { routes }
                        </Content>
                        <Settings />
                        <BetConfirmation />
                        <MiniModal />
                    </div>
                </ConnectedRouter>
                <Spinner />
                <CSRF />
            </div>
        </Provider>
    );
};

export default Root;
