import * as React from 'react';
import { connect } from 'react-redux';

import Title from '../title';
import Scroller from '../../components/misc/scroller';
import Select from '../../components/form/select';
import Table from '../../components/form/table';
import Time, { TimeFormat } from '../../components/form/time';

import SessionExpired from './session-expired';

import { changeTicketsSelectedDate, changeTicketsSelectedType, getTickets } from '../../actions/tickets';

import { currencySymbol } from '../../currencySymbols/currencySymbol';

const transactionDates: any = [];
const wagerTypes: any = [];

class Tickets extends React.Component<any, any> {

  state = {
    wait: true
  };

  componentDidMount() {
    const that = this;
    setTimeout(() => {
      this.props.dispatch(getTickets(7));
      that.setState({ wait: false });
    }, 1500);
  }

  componentWillMount() {
    const { languageData } = this.props;

    if (!transactionDates.length) {
      transactionDates.push(...[7, 30, 60, 90, 180]
        .map(v => ({
          value: v,
          text: `${languageData.last} ${v} ${languageData.days}`
        }))
        .concat([{
          value: 5475,
          text: languageData.allTransactions
        }]));
    }

    if (!wagerTypes.length) {
      wagerTypes.push(
        ...[languageData.allWagers, languageData.onlyPendingWagers, languageData.onlyGradedWagers]
          .map((text, i) => ({
            value: i,
            text
          }))
      );
    }
  }

  handleDateChange = (item: any) => {
    this.props.dispatch(changeTicketsSelectedDate(item.value));
    this.props.dispatch(getTickets(item.value));
  }

  handleTypeChange = (item: any) => {
    this.props.dispatch(changeTicketsSelectedType(item.value));
  }

  ticketsContent = (descriptions: any) => {

    const { languageData, tickets, selectedDate, selectedType, user } = this.props;

    return (
      tickets
      .filter((t: any) => t.dateAccepted >= (new Date().getTime() - selectedDate * 1000 * 60 * 60 * 24))
      .filter((t: any) => !selectedType || (selectedType === 1 ? !t.dateSettled : !!t.dateSettled))
      .sort((a: any, b: any) => b.dateAccepted - a.dateAccepted)
      .map((t: any) => (
        <tr key={t.wagerId}>
          <td><Time format={TimeFormat.DateTime}>{ t.dateAccepted }</Time></td>
          <td className="desktop">{ t.wagerId }</td>
          <td className="desktop">{ currencySymbol(user.currency, t.riskAmount.toFixed(2)) }</td>
          <td className="desktop">{ currencySymbol(user.currency, t.toWin.toFixed(2)) }</td>
          <td className="desktop">{ descriptions(t) }</td>
          <td className="mobile-tickets">
            <div>{ languageData.wagerId }: { t.wagerId }</div>
            { descriptions(t) }
            <div>{ languageData.riskAmount }: { currencySymbol(user.currency, t.riskAmount.toFixed(2)) }</div>
            <div>{ languageData.toWin }: { currencySymbol(user.currency, t.toWin.toFixed(2)) }</div>
            <div>{ languageData.status }: { status(t.status, t.dateSettled) }</div>
          </td>
          <td className="desktop">{ t.dateSettled ? currencySymbol(user.currency, t.totalSettlement.toFixed(2)) : '-' }</td>
          <td className="desktop">{ t.dateSettled ? <Time format={TimeFormat.DateTime}>{ t.dateSettled }</Time> : '' }</td>
          <td className="desktop">{ status(t.status, t.dateSettled) }</td>
        </tr>
      ))
    );
  }

  render() {
    const { title, languageData, tickets, view, selectedDate, selectedType, user } = this.props;

    const descriptions = (t: any) => t.descriptions.map((d: any, i: any) => {
      if (d.indexOf('EST') >= 0) {
        const [, prefix, date, rest] = /^(.*?)([a-z]{3} [a-z]{3} [0-9]{2} [0-9]{4} [0-9]{2}:[0-9]{2})EST:(.*?)$/gi.exec(d);
        return (
          <div key={i}>
            <div>{prefix} <Time format={TimeFormat.DateTime}>{new Date(date + ' EST').getTime()}</Time></div>
            <div>{rest}</div>
          </div>
        );
      }
      else {
        return (
          <div key={i}>{d}</div>
        );
      }
    });

    if (tickets.status) {
      return (
        <SessionExpired />
      );
    }

    return (
      <Title title={title}>
        <div>
          <div className="schedule-filter">
            <div className="schedule-filter__item">
              <Select label={languageData.view} dataSource={transactionDates} pk="value" model="text" onChange={this.handleDateChange} />
            </div>
            <div className="schedule-filter__item">
              <Select label={languageData.type} dataSource={wagerTypes} pk="value" model="text" onChange={this.handleTypeChange} />
            </div>
          </div>
          <div>
            <Table>
              <thead>
                <tr>
                  <th>{ languageData.dateAccepted }</th>
                  <th className="desktop">{ languageData.wagerId }</th>
                  <th className="desktop">{ languageData.riskAmount }</th>
                  <th className="desktop">{ languageData.toWin }</th>
                  <th className="desktop">{ languageData.betDescription }</th>
                  <th className="mobile-tickets">{ languageData.betDescription }</th>
                  <th className="desktop">{ languageData.totalSettlement }</th>
                  <th className="desktop">{ languageData.dateSettled }</th>
                  <th className="desktop">{ languageData.status }</th>
                </tr>
              </thead>
              {
                !this.state.wait && tickets &&
                <Scroller content={this.ticketsContent(descriptions)} />
              }
            </Table>
          </div>
        </div>
      </Title>
    );
  }
}

export default connect(
  (state: any) => ({
    title: state.languageData.menu.tickets,
    languageData: state.languageData.tickets,
    tickets: state.tickets.data,
    view: state.settings.view,
    selectedDate: state.tickets.selectedDate,
    selectedType: state.tickets.selectedType,
    user: state.user
  })
)(Tickets);

/// ---
function status(s: any, d: any) {
  switch (s) {
    case '0':
      return d ? 'Lose' : 'Pending';
  }
  return 'Win';
}
