import * as React from 'react';
import { Animation } from '../helpers';
import Fire from './fire';
import Puck from './puck';

export default class Goal extends React.Component<any, any> {
  animation = new Animation(this);

  state = {
    animation: {
      fire: {
        rot: 0
      },
      circle: {
        rad: 0
      }
    }
  };

  animate = () => {
    this.animation.start('fire', { rot: 0 }, { rot: 360 }, 550, true);
    this.animation.start('circle', { rad: 0 }, { rad: 8 }, 750, true, 1200);

  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    this.animation.cancel();
  }

  render() {
    let { x, y } = this.props;
    [x, y] = [x, y].map(Number);

    const fire = this.state.animation.fire.rot;
    const circle = this.state.animation.circle.rad;

    return (
      <svg>
        <g fill="red" >
          <circle cx={x} cy={y} r={circle} opacity="0.35" strokeWidth="0" />
          <circle cx={x} cy={y} r="3" />
          <g  transform={`rotate(${fire} ${x} ${y})`}>
            <Fire x={x - 8} y={y - 8} width="17" height="17" />
          </g>
          <Puck x={x - 7 } y={y - 7} width="15" height="15" />
        </g>
      </svg>
    );
  }
}
