import User from '../middleware/api/user';

export const GET_CRICKET_ANIMATIONS_SUCCESS = 'GET_CRICKET_ANIMATIONS_SUCCESS';
export const GET_FOOTBALL_ANIMATIONS_SUCCESS = 'GET_FOOTBALL_ANIMATIONS_SUCCESS';
export const GET_BASKETBALL_ANIMATIONS_SUCCESS = 'GET_BASKETBALL_ANIMATIONS_SUCCESS';

export const getAnimations = (sport: any) => (dispatch: any) => {

  const user = new User(dispatch);

  user
    .getAnimations(sport)
    .then((data: any) => dispatch(getAnimationsSuccess(sport.toUpperCase(), data)))
    .catch((err: any) => {
      console.log(err);
    });
};

const getAnimationsSuccess = (sport: any, data: any) => ({
  type: `GET_${sport}_ANIMATIONS_SUCCESS`,
  payload: data
});
