import Base from './base';
import { defaultGetInit, defaultPostInit } from './helpers';

export default class Settings extends Base {
  getLanguage = (id: string, isInCache: boolean, cookies?: any) => this.api(`/api/settings/language/${id}?isInCache=${isInCache}`, defaultGetInit(cookies));
  getTimeZones = (cookies?: any) => this.api(`/api/settings/timezone`, defaultGetInit(cookies));
  changeTimeZone = (param: any, cookies?: any) => this.api(`/api/settings/timezone`, defaultPostInit(param, cookies));
  changeOddsFormat = (param: any, cookies?: any) => this.api(`/api/settings/odds-format`, defaultPostInit(param, cookies));
  changeTheme = (theme: any, cookies?: any) => this.api(`/api/settings/theme`, defaultPostInit(theme, cookies));
  changeView = (view: any, cookies?: any) => this.api(`/api/settings/view`, defaultPostInit(view, cookies));
  changeViewPreferences = (viewPreferences: any) => this.api(`/api/settings/view-preferences`, defaultPostInit({viewPreferences}));
}
