import { ISoccerFieldAnimationState, SoccerAction, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextTeam: SoccerTeam, nextAction: SoccerAction) {
  let actionX, ballX;

  switch (nextAction) {
    case SoccerAction.SafeFreeKick:
      actionX = nextTeam === SoccerTeam.Team1 ? 43 : 62;
      ballX = nextTeam === SoccerTeam.Team1 ? 47 : 58;
      break;
    case SoccerAction.HalfWayFreeKick:
      actionX = nextTeam === SoccerTeam.Team1 ? 59 : 46;
      ballX = nextTeam === SoccerTeam.Team1 ? 63 : 42;
      break;
    case SoccerAction.DangerousFreeKick:
      actionX = nextTeam === SoccerTeam.Team1 ? 78 : 27;
      ballX = nextTeam === SoccerTeam.Team1 ? 82 : 23;
      break;
  }

  return {
    actionText: {
      x: actionX
    },
    ball: {
      x: ballX,
      y: 37,
      direction: BallDirection.Goal
    }
  };
}

const t1SafeFreeKick: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.SafeFreeKick));
const t2SafeFreeKick: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.SafeFreeKick));
const t1HalfwayFreeKick: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.HalfWayFreeKick));
const t2HalfwayFreeKick: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.HalfWayFreeKick));
const t1DangerousFreeKick: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.DangerousFreeKick));
const t2DangerousFreeKick: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.DangerousFreeKick));

export function freeKick(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  let start: ISoccerFieldAnimationState;
  let end: ISoccerFieldAnimationState;

  if (nextAction === SoccerAction.SafeFreeKick) {
    end = nextTeam === SoccerTeam.Team1 ? t1SafeFreeKick : t2SafeFreeKick;
  }
  else if (nextAction === SoccerAction.HalfWayFreeKick) {
    end = nextTeam === SoccerTeam.Team1 ? t1HalfwayFreeKick : t2HalfwayFreeKick;
  }
  else {
    end = nextTeam === SoccerTeam.Team1 ? t1DangerousFreeKick : t2DangerousFreeKick;
  }

  start = deepAssign({}, end, {
    actionText: state.actionText,
    ball: {
      x: state.ball.x,
      y: state.ball.y,
      direction: BallDirection.None
    }
  });

  return [{
    start,
    end
  }];
}
