import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Baseball = ({ data }: any) => {
  const { title, changes, run, will, four, six, eight, professional } = data;
  return (
    <RulePart title={title} model="baseball">
      <RuleItem title={changes.title}>
        {changes.text}
      </RuleItem>
      <RuleItem title={run.title}>
        {run.text}
      </RuleItem>
      <RuleItem title={will.title}>
        {will.text}
      </RuleItem>
      <RuleItem title={four.title}>
        {four.text}
      </RuleItem>
      <RuleItem title={six.title}>
        {six.text}
      </RuleItem>
      <RuleItem title={eight.title}>
        {eight.text}
      </RuleItem>
      <RuleItem title={professional.title}>
        {professional.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.baseball })
)(Baseball);
