import * as React from 'react';
import EventLink from '../event-link';
import DoubleLine from './double-line';
import SingleLine from './single-line';

const DesktopLayout = ({ line, event, className, rows, sport, isMobileOn, headerName, outcomes, score }: any) => {

  let Component: any = () => <div></div>;

  Component = line === 2 ? DoubleLine : SingleLine;

  return (
    <Component
      headerName={headerName}
      className={className}
      outcomes={outcomes}
      isMobileOn={false}
      event={event}
      sport={sport}
      score={score}
      line={line}
      rows={rows}
    />
  );

};

export default DesktopLayout;
