import { BasketTeam, IBasketFieldAnimationState } from '../../constants';
import { deepAssign } from '../../../../../helpers';

const t1MissedEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
  ball: {
    x: 87,
    y: 35,
    scale: 0.1
  },
  scoreText : {
    y: 15
  }
});

const t2MissedEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
  ball: {
    x: 7,
    y: 15,
    scale: 0.1
  },
  scoreText : {
    y: 15
  }
});

export function missed(start: any, nextTeam: BasketTeam) {
  return {
    start,
    end: nextTeam === BasketTeam.Team1 ? t1MissedEnd(start) : t2MissedEnd(start)
  };
}
