export const objectToFormData = (obj: any) => {
  const fd = new FormData();
  Object.keys(obj).forEach(key => fd.append(key, obj[key]));
  return fd;
};

const defaultMethodInit = (method: any, params: any, cookies?: any): RequestInit => {
  const init: any = {
    method,
    headers: {
      'Content-type': 'application/json'
    },
    credentials: 'same-origin'
  };

  if (method === 'get') {
    init.headers['Cache-Control'] = 'no-cache';
  }

  if (cookies) {
    init.headers['Cookie'] = cookies;
  }

  if (params) {
    init.body = JSON.stringify(params);
  }

  return init;
};

export const defaultPostInit = (params = {}, cookies?: any) => defaultMethodInit('post', params, cookies);
export const defaultPutInit = (params = {}, cookies?: any) => defaultMethodInit('put', params, cookies);
export const defaultDeleteInit = (params = {}, cookies?: any) => defaultMethodInit('delete', params, cookies);
export const defaultGetInit = (cookies?: any) => defaultMethodInit('get', null, cookies);
