import * as React from 'react';
import config from './../../../../config';
import { BetType } from '../../../constants';
import { mapOutcomeTitle } from '../bet-slip/helpers';
import { range } from '../../../helpers';
import { betLayout } from './../../misc/layout-helpers';
import MobileBetGrid from './../../misc/mobile-bet-grid';
import Odd from './odd';
import NoMarkets from './no-markets';

import Link from './../../form/link';
import SwitchNew from './../../misc/switch-new';
import RangeSlider from './../../misc/range-slider';

import ThreeColumnMarket from './../../misc/ThreeColumnMarket';

import { changeBetType } from '../../../actions/live';
import { hasRangeSlider } from './live-helpers';

class SingleEventOdds extends React.Component<any, any> {

  state = {
    isSlider: true,
    hasRangeSlider: hasRangeSlider(this.props.event.sport)
  };

  handleSlider(isSlider_: any, outcomeId: any) {
    // console.log(this.state.hasRangeSlider);
    let hasRangeSlider = [...this.state.hasRangeSlider];
    let idx = this.state.hasRangeSlider.indexOf(outcomeId);

    if (!isSlider_ && idx > -1) {
      let hasRangeSlider = [...this.state.hasRangeSlider]; // make a separate copy of the array
      hasRangeSlider.splice(idx, 1);
      this.setState({hasRangeSlider});

    }
    if (isSlider_) {
      // console.log('slider' + idx);
      this.setState({hasRangeSlider: [...hasRangeSlider, outcomeId]});
    }
    this.setState({ isSlider: isSlider_});
  }

  render() {
    // console.log(this.state);
    const {
      hasVideoOnFullScreen,
      parlayBySport,
      outcomeList,
      outcomes,
      dispatch,
      classList,
      betType,
      event,
      view
    }: any = this.props;

    const {
      home,
      away,
      sport,
      inverted
    } = event;

    const changeBet = () => {
      return dispatch(changeBetType(+!betType));
    };

    const hasManyOptions = (outcomes: any, outcome: any) => {
      let marketOutcomes = outcomes[outcome];
      const { hasRangeSlider } = this.state;
      if (hasRangeSlider.indexOf(outcome.id) < 0) return false;
      if (!marketOutcomes || !Object.keys(marketOutcomes[0])) return false;
      let oddsByPlayer: any = {};

      Object.keys(marketOutcomes)
        .forEach((otcId: any) => {
          if (!marketOutcomes[otcId]) return;

          if (!oddsByPlayer[marketOutcomes[otcId].bet]) {
            oddsByPlayer[marketOutcomes[otcId].bet] = [];
          }
          oddsByPlayer[marketOutcomes[otcId].bet] = [...oddsByPlayer[marketOutcomes[otcId].bet], marketOutcomes[otcId]];
        });

      let hasManyOptions_ = false;
      if (!oddsByPlayer[0]) return false;

      Object.keys(oddsByPlayer)
        .forEach((item: any) => {
          if (oddsByPlayer[item].length >= 10) {
            hasManyOptions_ = true;
          }
        });
      return hasManyOptions_;
    };

    const isPrematch = event.live === 0;

    const legitOutcomes = outcomes && outcomeList
      .filter((outcome: any) => outcomes[outcome.id] && outcomes[outcome.id][Object.keys(outcomes[outcome.id])[0]])
      .filter((outcome: any) => betType === BetType.straight || parlayBySport[sport].indexOf(outcome.id) !== -1);

    // console.log(outcomeList);
    // console.log(legitOutcomes);
    // console.log(outcomes);
    // log on console if there are odds not registered on outcomelist
    let outcomeList_ = outcomeList.map((item: any) => item.id);

    if (outcomes && Object.keys(outcomes)[0]) {

      Object.keys(outcomes).forEach(item => {

        if (outcomeList_.indexOf(parseInt(item)) < 0) {
          // console.log(`marketId ${item} is being sent by backend but it's not visible on UI because it's title is not registered on outcomeList for sport ${sport}.`);
          // console.log(outcomeList);
        }
      });
    }

    let sectionStyles: any = view === 1 ? { padding: '10px' } : {};
    if (hasVideoOnFullScreen) {
      sectionStyles.display = 'none';
    }

    return (
      <section style={sectionStyles} className={classList.join(' ') }>
      { ((config.hasParlay && parlayBySport) || config.hasPrematchLink) &&
        <div className="prematch-link-single-tablet" >
          { config.hasParlay && parlayBySport &&
            <div>
              <SwitchNew
                className_={'prematch'}
                checked_={betType === 1}
                leftText={'Straight'}
                rightText={'Parlays'}
                onToggle={() => changeBet()}
              />
            </div>
          }
          {
            config.hasPrematchLink &&
            <div>
              <Link
                activeClassName=""
                to={`${isPrematch ? '/' : '/prematch'}`}
              >
                <SwitchNew
                  className_={'prematch'}
                  checked_={isPrematch}
                  leftText={config.switchLiveText}
                  rightText={config.switchPrematchText}
                  onToggle={() => console.log('{}')}
                />
              </Link>
            </div>

          }
        </div>
      }
      {
        outcomes && outcomeList
          .filter((outcome: any) => outcomes[outcome.id] && outcomes[outcome.id][Object.keys(outcomes[outcome.id])[0]])
          .filter((outcome: any) => betType === BetType.straight || parlayBySport[sport].indexOf(outcome.id) !== -1)
          .map((outcome: any) => (
            <div key={outcome.id}>
            {  (
                typeof getOddsData(event, outcomes, outcome, this.state.hasRangeSlider) !== undefined ||
                getOddsData(event, outcomes, outcome, this.state.hasRangeSlider)[0]
              ) &&

              <div className={view !== 3 ? 'live-item' : 'box'}>
                { view !== 3 ?
                <h4 id={`se-otc_${outcome.id}`} className="live-item__title">
                  { mapOutcomeTitle(home, away, inverted, outcomes, outcome) }

                </h4> :
                <table className="league-title">
                  <tbody>
                    <tr>
                      <td id={`se-otc_${outcome.id}`}>
                        {
                          mapOutcomeTitle(home, away, inverted, outcomes, outcome)
                        }
                      </td>
                    </tr>
                  </tbody>
                </table>
                }

                <div className="live-events">

                  <div className="live-event" style={this.state.hasRangeSlider.indexOf(outcome.id) >= 0 ? {} : {} }>
                      { betLayout[sport] && betLayout[sport]['two-column'] && betLayout[sport]['two-column'].indexOf(outcome.id) >= 0 && view !== 2 &&
                        <MobileBetGrid
                          event={event}
                          cols={2}
                          otcId={outcome.id}
                          outcomeId={outcomes[outcome.id]} />
                      }
                      { betLayout[sport] && betLayout[sport]['three-column'] && betLayout[sport]['three-column'].indexOf(outcome.id) >= 0 && view !== 2 &&
                        <MobileBetGrid
                          event={event}
                          cols={3}
                          otcId={outcome.id}
                          outcomeId={outcomes[outcome.id]}
                        />
                      }
                      { hasRangeSlider(event.sport).indexOf(outcome.id) >= 0 &&
                        <div
                          style={{ position: 'relative', textAlign: 'end', margin: '5px 15px 5px 10px' }}>
                          <span
                            onClick={() => this.handleSlider(true, outcome.id)}
                            style={{cursor: 'pointer', textDecoration: `${this.state.hasRangeSlider.indexOf(outcome.id) >= 0 ? 'underline' : 'none'}`}}>
                              Slider
                          </span> |
                          <span
                            onClick={() => this.handleSlider(false, outcome.id)}
                            style={{cursor: 'pointer', textDecoration: `${this.state.hasRangeSlider.indexOf(outcome.id) >= 0 ? 'none' : 'underline'}`}}>
                            All
                          </span>
                        </div>
                      }
                    <div
                      className={`live-event__content`}
                      style={this.state.hasRangeSlider.indexOf(outcome.id) >= 0 ? { display: 'flex', flexDirection: `row`, flexWrap: 'wrap' } : {} }
                    >
                      {
                        (event.sport === 11 && (outcome.id === 1900102 || outcome.id === 1900101)) ?
                        <ThreeColumnMarket
                          event={event}
                          otc={outcome}
                          outcomes={outcomes}
                        /> :
                        getOddsData(event, outcomes, outcome, this.state.hasRangeSlider)
                      }
                    </div>
                  </div>
                </div>
              </div>
            }
            </div>
          ))
      }
      {
        legitOutcomes !== undefined && legitOutcomes.length <= 0 &&
          <div>
            <NoMarkets
              view={view}
              event={event}
              outcomes={outcomes}
              outcomeList={outcomeList}
              legitOutcomes={legitOutcomes}
            />
          </div>
      }
    </section>
    );
  }
}

export default SingleEventOdds;

function getOddsData(event: any, outcomes: any, otc: any, hasRangeSlider: any, dispatch?: any) {

  const keys = Object.keys(outcomes[otc.id]);

  let values = keys.map(id => (
    Object.assign({}, outcomes[otc.id][id], {
      id,
      outcome: otc.id,
      event: event.id
    })
  )).sort((a, b) => a.order - b.order).filter(odd => !odd.status);

  let arrFinal: any = [];

  const itemOrder = values.map(item => item.order);

  if (itemOrder.indexOf(-171718) !== 0 && itemOrder.indexOf(-171717) !== 0) {

    const maxColumns = Math.max.apply(null, values.map(item => item.column)) + 1;
    const valuesByColumn = range(maxColumns).map((x, j) => values.filter(item => item.column === j)).filter(arr => arr.length > 0);
    const maxRows = Math.max.apply(null, valuesByColumn.map(arr => arr.length));

    for (let i = 0; i < maxRows; i++) {
      arrFinal[i] = [];
      for (let j = 0; j < valuesByColumn.length; j++) {
        arrFinal[i][j] = valuesByColumn[j][i];
      }

  }

  } else {

    let vals = values;

    if (event.sport === 35 && values[0] && values[0].outcomeId === 353) {

      vals = values.sort((a, b) => a.price - b.price);

    } else {

      vals = values.map(item => item).sort((a, b) => {
        if (a.display < b.display) {
          return -1;
        }

        if (a.display > b.display) {
          return 1;
        }

        return 0;
      });

    }

    const columns = 3;
    const rows = Math.ceil(vals.length / columns);

    for (let i = 0; i < rows; i++) {
      arrFinal[i] = [];
      for (let j = 0; j < columns; j++) {
        arrFinal[i][j] = vals[j + columns * i];
      }
    }

  }

  const outcomeClass = ['live-event__outcome'];
  const betFormat = ['one-column', 'two-column', 'three-column'];
  let isOneColumn = false;
  let isGrid = false;

  betFormat.map(format => {
    if (betLayout[event.sport]) {
      if (betLayout[event.sport][format] && betLayout[event.sport][format].indexOf(otc.id) >= 0) {
        isOneColumn = format === 'one-column' ? true : false;
        isGrid = !isOneColumn ? true : false;
        outcomeClass.push(`${format}-bet`);
      }
    }
  });

  //  Display a range slider instead of buttons (some Am Football outcomes)

  if (arrFinal[0][0]) {

    if (hasRangeSlider.indexOf(arrFinal[0][0]['outcomeId']) >= 0) {

      // console.log(arrFinal.flat());
      let oddsByPlayer: any = {};

      // data structure to display slide ranger with different line options
      arrFinal
        .flat()
        .forEach((odd: any) => {

          if (!odd) return;

          if (!oddsByPlayer[odd.bet]) {
            oddsByPlayer[odd.bet] = [];
          }
          oddsByPlayer[odd.bet] = [...oddsByPlayer[odd.bet], odd];
        });

      if (!Object.keys(oddsByPlayer)[0]) return <span></span>;
      // console.log(oddsByPlayer);

      // If a given player / team has many available lines ; we display that market's slide ranger on
      //  one column (so we have space to display all lines on slider)

      let hasManyOptions = false;
      Object.keys(oddsByPlayer)
        .forEach((item: any) => {

          if (oddsByPlayer[item].length >= 10) {
            hasManyOptions = true;
          }
        });

      return Object.keys(oddsByPlayer)
              .map((player: any, c: any) => (
                <RangeSlider
                  key={c}
                  player={player}
                  hasManyOptions={hasManyOptions}
                  oddsByPlayer={oddsByPlayer[player].sort((a: any, b: any) => parseInt(a.line) - parseInt(b.line))}
                />
              ));
    }

  }

  // console.log(arrFinal);

  return arrFinal

    .map((arr: any, c: any) => (
      <div key={c} className={outcomeClass.join(' ') }>
        {
          arr.map((odd: any, i: any) => {
            const oddClassList = ['live-event__odd'];

            if (!odd || !odd.hasOwnProperty('suspended') || odd.suspended) {
              oddClassList.push('suspended');
            }

            if (!odd) {
              // oddClassList.push('empty-block');
              return <div key={i} className={oddClassList.join(' ')} />;
            }
            odd.row = c;
            return (
              <Odd
                row={c}
                key={odd.id} className={oddClassList.join(' ')}
                odd={odd} ticker={false} isGrid={isGrid}
                event={event} isSingleEvent={true}
              />
            );
          })
        }
      </div>
    ));
}
