import * as React from 'react';

const AmericanFootballIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    stroke: 'transparent',
    fill: 'transparent'
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  const [cp, ballCurves, markingColor] = ['american-ball-curves', 'M 5 240 C 5 240 -20 -20 240 5 C 250 5 270 270 5 240', '#e7e9df'];

  return (
    <svg {...svgProps}>
      <defs>
        <clipPath id={cp}>
          <path d={ballCurves} />
        </clipPath>
      </defs>
      <path fill="#bf6654" d={ballCurves} />
      <path stroke={markingColor} strokeWidth="8" strokeLinecap="round" d="M 70 180 L 180 70" />
      <path stroke={markingColor} strokeWidth="35" strokeDasharray="7, 13" d="M 80 170 L 170 80" />
      <g fill={markingColor} clipPath={`url(#${cp})`}>
        <path d="M -15 175 l 80 75 l 25 -15 l -80 -75z" />
        <path d="M 155 0 l 80 75 l 25 -15 l -80 -75z" />
      </g>
    </svg>
  );
};

export default AmericanFootballIcon;
