import { nt, CricketAction, CricketTeam, ICricketGroundAnimationState } from '../constants';
import { general } from './general';
import { throwBall } from './bowling';

export interface ICricketGroundAnimation {
  start: ICricketGroundAnimationState;
  end: ICricketGroundAnimationState;
}

export function getAnimations(state: ICricketGroundAnimationState, currentTeam: CricketTeam, currentAction: CricketAction, nextTeam?: CricketTeam, nextAction?: CricketAction): ICricketGroundAnimation[] {
  switch (nextAction) {
    case CricketAction.Wicket:
    case CricketAction.EndOfOver:
    case CricketAction.MatchEnded:
      return general();
    case CricketAction.Bowling:
      return throwBall(state, nextAction);
      default:
        return [{
          start: nt,
          end: nt
        }];
  }
}
