import { LIVE_SCHEDULE, CHANGE_SCHEDULE_SELECTED_DATE, CHANGE_SCHEDULE_SELECTED_SPORT, CHANGE_SCHEDULE_FILTER_TEXT } from '../actions/schedule';

export const scheduleInitialState: any = {
  data: [],
  selectedDate: '',
  selectedSport: null,
  filterText: ''
};

export const schedule = (state = scheduleInitialState, action: any): any => {
  switch (action.type) {
    case LIVE_SCHEDULE:
      return {
        data: action.payload,
        selectedDate: ''
      };
    case CHANGE_SCHEDULE_SELECTED_DATE:
      return Object.assign({}, state, {
        selectedDate: action.payload
      });
    case CHANGE_SCHEDULE_SELECTED_SPORT:
      return Object.assign({}, state, {
        selectedSport: action.payload
      });
    case CHANGE_SCHEDULE_FILTER_TEXT:
      return Object.assign({}, state, {
        filterText: action.payload
      });
  }

  return state;
};
