import { ISoccerFieldAnimationState, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';
import { gate } from './gate';
import { general } from './general';

const actionText = general()[0];

const t1Gate = gate(SoccerTeam.Team1);
const t2Gate = gate(SoccerTeam.Team2);

const t1GoalStart: ISoccerFieldAnimationState = deepAssign({}, t1, {
  actionText: actionText.start.actionText,
  gate: t1Gate.end.gate,
  ball: {
    x: 55,
    y: 45,
    direction: BallDirection.None
  }
});

const t1GoalEnd: ISoccerFieldAnimationState = deepAssign({}, t1GoalStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 84,
    y: 34,
    scale: 2
  }
});

const t2GoalStart: ISoccerFieldAnimationState = deepAssign({}, t2, {
  actionText: actionText.start.actionText,
  gate: t2Gate.end.gate,
  ball: {
    x: 50,
    y: 45,
    direction: BallDirection.None
  }
});

const t2GoalEnd: ISoccerFieldAnimationState = deepAssign({}, t2GoalStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 21,
    y: 34,
    scale: 2
  }
});

export function goal(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam) {
  let start = nextTeam === SoccerTeam.Team1 ? t1GoalStart : t2GoalStart;
  let end = nextTeam === SoccerTeam.Team1 ? t1GoalEnd : t2GoalEnd;

  return [nextTeam === SoccerTeam.Team1 ? t1Gate : t2Gate, {
    start,
    end
  }];
}
