import * as React from 'react';
import { connect } from 'react-redux';
import { getAsianHeaderBySport, getAsian1stLineHeaderBySport, AsianLine } from './../../../../../constants';
import AsianOdds from './asian-odds';
import EventLink from './event-link';

import DesktopLayout from './desktop-layout';
import MobileLayout from './mobile-layout';
import { mainOddsWithOdds } from './../../../../../reducers/outcomes-flatten';

const OddsCard = ({ line, event, outcomes, reducedOutcomes, isMobileOn, user, score }: any) => {

  const { sport } = event;
  let bookmakerId = user && user.bookmakerId[sport];
  outcomes = outcomes && outcomes[bookmakerId];

  let className = 'simple-line-view';
  const headerClass = ['odds-header'];
  let headerName = ['asian__header__'];

  const rows = line === AsianLine.simpleLine ? 1 : 2;

  if (line === 0) {
    headerName.push('simple-line');
  }

  if (line === AsianLine.singleLine) {
    className = 'single-line-view_';

  } else if (line === AsianLine.doubleLine) {
    className = 'double-line-view_';
    headerName.push('double-line');
  }

  if (!isMobileOn) {
    className = `${className} line-view-desktop`;
  }

  const wrapperClass = ['line-wrapper'];

  let Component: any = () => <div></div>;

  Component = !isMobileOn && line !== 0 ? DesktopLayout : MobileLayout;

  const period = score !== undefined ? score.period : '';

  return (
    <div className={`${className} odds-card`}>
      {!isMobileOn &&
        <div className="event-link-desktop">
          <EventLink event={event}/>
        </div>
      }
      <div className="main-wrapper">
        <Component
          headerName={headerName}
          isMobileOn={isMobileOn}
          className={className}
          outcomes={reducedOutcomes}
          score={score}
          event={event}
          sport={sport}
          line={line}
          rows={rows}
        />
      </div>
    </div>
  );
};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    reducedOutcomes: mainOddsWithOdds(state)[ownProps.event.id] && mainOddsWithOdds(state)[ownProps.event.id][state.user.bookmakerId[ownProps.event.sport]],
    outcomes: state.live.outcomes[ownProps.event.id],
    score: state.live.scores[ownProps.event.id],
    isMobileOn: state.uiToolbar.isMobileOn,
    line: state.live.asianLine,
    user: state.user
  })
)(OddsCard);
