import * as React from 'react';
import { connect } from 'react-redux';
import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Cricket = ({ data }: any) => {
  const { title,
    matchThree,
    drawNoBet,
    runsForBall,
    runsForOver,
    runsAtFallOfWicket
  } = data;
  return (
    <RulePart title={title} model="cricket">
      <RuleItem title={matchThree.title}>
        <p>
          {matchThree.text}
        </p>
      </RuleItem>
      <RuleItem title={drawNoBet.title}>
        <p>
          {drawNoBet.text}
        </p>
      </RuleItem>
      <RuleItem title={runsForBall.title}>
        <p>
          {runsForBall.text}
        </p>
      </RuleItem>
      <RuleItem title={runsForOver.title}>
        <p>
          {runsForOver.text}
        </p>
      </RuleItem>
      <RuleItem title={runsAtFallOfWicket.title}>
        <p>
          {runsAtFallOfWicket.text}
        </p>
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.cricket })
)(Cricket);
