import * as React from 'react';
import { connect } from 'react-redux';

let themeColor: any = {
  'main': '#fb0',
  'clean-gray': '#aabbbb',
  'dark-blue': '#476689',
  'light-blue': '#88ccee',
  'light-green': '#99dd98',
  'light-purple': '#ddccee',
  'pink': '#ffbbcc',
  'eag-grey': '#444',
  'custom-orange': '#f46523'
};

const StreamIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 19 19',
    strokeWidth: 0.5
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps}>
      <circle fill={themeColor[props.theme] || '#FFFFFF'} cx="9.5" cy="9.5" r="9.5"/>
      <rect fill="none" stroke="#FFFFFF" strokeMiterlimit="10" x="3.98" y="6.68" width="11.09" height="7.97" rx="1.22" ry="1.22"/>
      <rect fill="none" stroke="#FFFFFF" strokeMiterlimit="10" x="5.01" y="7.64" width="9.03" height="6.05" rx="1.22" ry="1.22"/>
      <line fill="none" stroke="#FFFFFF" strokeMiterlimit="10" x1="8.46" y1="6.68" x2="6.09" y2="3.41"/>
      <line fill="none" stroke="#FFFFFF" strokeMiterlimit="10" x1="10.27" y1="6.68" x2="12.64" y2="3.41"/>
    </svg>
  );
};

export default connect(
  (state: any) => {
    return {
      theme: state.settings.theme
    };
  }
)(StreamIcon);
