import {
  MAINTENANCE_CHECKER
 } from '../actions/maintenance';

export const maintenanceInitialState: any = {
  live: { maintenance: false, eta: 0 },
  prematch: { maintenance: false, eta: 0 },
  schedule: { maintenance: false, eta: 0 }
 };

export const maintenance = (state = maintenanceInitialState, action: any) => {

  switch (action.type) {
    case MAINTENANCE_CHECKER:

      // console.log(action.payload);

      return {
        ...state,
        live: action.payload.live,
        prematch: action.payload.prematch,
        schedule: action.payload.schedule
      };

  }

  return state;
};
