import { IHockeyFieldAnimationState, IceHockeyTeam, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';
import { hockeyGate } from './hockey-gate';
import { general } from './general';

const actionText = general()[0];

const t1Gate = hockeyGate(IceHockeyTeam.Team1);
const t2Gate = hockeyGate(IceHockeyTeam.Team2);

const t1ShotStart: IHockeyFieldAnimationState = deepAssign({}, t1, {
  actionText: actionText.start.actionText,
  gate: t1Gate.end.gate,
  alternRect: {
    x: 48.5
  }
});

const t1ShotEnd: IHockeyFieldAnimationState = deepAssign({}, t1ShotStart, {
  actionText: actionText.end.actionText,
   shotIcon: {
    y: 20
  },
  goalSpot: {
    x: 170,
    y: 45
  }
});

const t2ShotStart: IHockeyFieldAnimationState = deepAssign({}, t2, {
  actionText: actionText.start.actionText,
  gate: t2Gate.end.gate,
  alternRect: {
    x: 48.5
  }
});

const t2ShotEnd: IHockeyFieldAnimationState = deepAssign({}, t2ShotStart, {
  actionText: actionText.end.actionText,
   shotIcon: {
    y: 20
  },
  goalSpot: {
    x: 30,
    y: 45
  }
});

export function shots(state: IHockeyFieldAnimationState, nextTeam: IceHockeyTeam) {
  let start = nextTeam === IceHockeyTeam.Team1 ? t1ShotStart : t2ShotStart;
  let end = nextTeam === IceHockeyTeam.Team1 ? t1ShotEnd : t2ShotEnd;

  return [nextTeam === IceHockeyTeam.Team1 ? t1Gate : t2Gate, {
    start,
    end
  }];
}
