export interface ISoccerFieldAnimationState {
  polygon: {
    red: number;
    green: number;
    blue: number;
    translateX: number;
    scaleX: number;
  };
  actionText: {
    x: number;
    y: number;
    textAlign: string;
  };
  ball: {
    x: number;
    y: number;
    scale: number;
    direction: BallDirection;
  };
  extras: {
    rotate: number;
  };
  gate: {
    scaleX: number;
    translateX: number;
    translateY: number;
  };
}

