import { nt, SoccerAction, SoccerTeam, ISoccerFieldAnimationState, IBallPosition } from '../constants';
import { possession } from './possession';
import { attack } from './attack';
import { dangerousAttack } from './dangerous-attack';
import { general } from './general';
import { extras } from './extras';
import { corner } from './corner';
import { goalKick } from './goal-kick';
import { penalty } from './penalty';
import { freeKick } from './free-kick';
import { throwIn } from './throw-in';
import { goal } from './goal';
import { shotOnTarget } from './shot-on-target';
import { shotOffTarget } from './shot-off-target';

export interface ISoccerFieldAnimation {
  start: ISoccerFieldAnimationState;
  end: ISoccerFieldAnimationState;
  repeat?: boolean;
}

/**
 * Returns the initial and final animations according to current and next actions
 *
 * @export
 * @param {ISoccerFieldAnimationState} state Current animation state to perform continuous animation
 * @param {SoccerTeam} currentTeam The team that performed the last action
 * @param {SoccerAction} currentAction The last action that was performed
 * @param {SoccerTeam} nextTeam The team that will perform the next action
 * @param {SoccerAction} [nextAction] Next action to be performed
 * @param {IBallPosition} [ballPosition] If available, the new ball position
 * @returns {ISoccerFieldAnimation} The initial and final animation states
 */
export function getAnimations(state: ISoccerFieldAnimationState, currentTeam: SoccerTeam, currentAction: SoccerAction, nextTeam?: SoccerTeam, nextAction?: SoccerAction, ballPosition?: IBallPosition): ISoccerFieldAnimation[] {
  switch (nextAction) {
    case SoccerAction.Possession:
      return possession(state, currentTeam, nextTeam, currentAction);
    case SoccerAction.Attack:
      return attack(state, currentTeam, nextTeam, currentAction);
    case SoccerAction.DangerousAttack:
      return dangerousAttack(state, currentTeam, nextTeam, currentAction);
    case SoccerAction.None:
    case SoccerAction.Kickoff:
    case SoccerAction.HalfTime:
    case SoccerAction.SecondHalf:
    case SoccerAction.FullTime:
    case SoccerAction.ExtraTime:
    case SoccerAction.ExtraTimeHalfTime:
    case SoccerAction.ExtraTime2ndHalf:
    case SoccerAction.ExtraTimeFullTime:
    case SoccerAction.PenaltyShootout:
    case SoccerAction.Injury:
    case SoccerAction.Substitution:
      return general();
    case SoccerAction.YellowCard:
    case SoccerAction.RedCard:
    case SoccerAction.Offside:
      return extras();
    case SoccerAction.CornerBottom:
    case SoccerAction.CornerTop:
      return corner(state, nextTeam, nextAction);
    case SoccerAction.GoalKick:
      return goalKick(state, nextTeam);
    case SoccerAction.Penalty:
      return penalty(state, nextTeam);
    case SoccerAction.SafeFreeKick:
    case SoccerAction.HalfWayFreeKick:
    case SoccerAction.DangerousFreeKick:
      return freeKick(state, nextTeam, nextAction);
    case SoccerAction.HalfWayThrowIn:
    case SoccerAction.DangerousThrowIn:
      return throwIn(state, nextTeam, nextAction);
    case SoccerAction.Goal:
    case SoccerAction.ShootoutScore:
      return goal(state, nextTeam);
    case SoccerAction.ShotOnTarget:
      return shotOnTarget(state, nextTeam);
    case SoccerAction.ShotOffTarget:
    case SoccerAction.PenaltyMissed:
    case SoccerAction.ShootoutMiss:
      return shotOffTarget(state, nextTeam);
    default:
      return [{
        start: nt,
        end: nt
      }];
  }
}
