import * as uuid from 'uuid/v4';
import { BetType } from '../constants';
import { openBetConfirmation, addConfirmationOdds } from './bet-confirmation';

import { subscribeEvent, unsubscribeEvent } from './live';
import { lastInteraction } from './user';

export const BET_SLIP = 'BET_SLIP';
export const TOGGLE_BET_SLIP = 'TOGGLE_BET_SLIP';
export const ADD_TO_BET_SLIP = 'ADD_TO_BET_SLIP';
export const REMOVE_FROM_BET_SLIP = 'REMOVE_FROM_BET_SLIP';
export const UPDATE_BET = 'UPDATE_BET';
export const BET_SLIP_ERROR = 'BET_SLIP_ERROR';
export const LIMITS = 'LIMITS';

export const USER_UPDATE_BET_SLIP = 'USER_UPDATE_BET_SLIP';
export const UPDATE_PRICE_CHANGE = 'UPDATE_PRICE_CHANGE';

export const updatePriceChange = (odd: any) => ({
  type: UPDATE_PRICE_CHANGE,
  payload: odd
});

export const userUpdateBetSlip = (bets: any) => ({
  type: USER_UPDATE_BET_SLIP,
  payload: bets
});

export const toggleBetSlip = (open?: any) => ({
  type: TOGGLE_BET_SLIP,
  payload: open
});

export const getLimits = () => (dispatch: any, getState: any) => {
  const state = getState();
  const { user, betSlip, live } = state;
  let msg = `${LIMITS}_${user.userid_internal}_${user.uuid}_${live.betType}_${betSlip.odds.map((odd: any) => odd.id).join('_')}`;
  getState().wsm.socket.emit('message', msg);
  // getState().wsm.send(`${LIMITS}_${user.userid_internal}_${user.uuid}_${live.betType}_${betSlip.odds.map((odd: any) => odd.id).join('_')}`);
};

const addBet = (odd: any) => ({
  type: ADD_TO_BET_SLIP,
  payload: odd
});

export const addToBetSlip = (odd: any) => (dispatch: any, getState: any) => {
  const state = getState();
  if (!odd.uuid) {
    odd.uuid = uuid();
  }

  dispatch(addBet(odd));
  dispatch(getLimits());

  // Somehow, the eventListener registered to detect interactions, is not working while clicking
  // on outcomes. We send interaction on this action.

  let time = Date.now();
  dispatch(lastInteraction(time));
  dispatch(subscribeEvent(odd.event, odd.bookmakerId, odd.outcome, 'betslip'));
};

const removeBet = (odd: any) => ({
  type: REMOVE_FROM_BET_SLIP,
  payload: odd
});

export const removeFromBetSlip = (odd: any) => (dispatch: any, getState: any) => {
  const state = getState();
  dispatch(unsubscribeEvent(odd.event, odd.bookmakerId, odd.outcome, 'betslip'));
  dispatch(removeBet(odd));
  dispatch(getLimits());

  // Somehow, the eventListener registered to detect interactions, is not working while clicking
  // on outcomes or Betslip. We send interaction on this action.

  let time = Date.now();
  dispatch(lastInteraction(time));

};

export const updateBet = (odd: any, oldOdd?: any) => (dispatch: any, getState: any) => {

  dispatch({
    type: UPDATE_BET,
    payload: odd,
    oldOdd: oldOdd
  });

  // We only want to dispatch getLimits() if there was an actual change in one of the items on our slip
  // Otherwise there's no reason to request limits on every odds update that hits the state!
  if (getState().betSlip.odds.some((o: any) => o.id === odd.id && (o.status !== odd.status || o.marketPrice !== odd.marketPrice || o.suspended !== odd.suspended)) ) {
    dispatch(getLimits());
  }
};

export const updateLimits: any = (limits: any) => (dispatch: any, getState: any) => {

  dispatch({
    type: LIMITS,
    payload: limits,
    pct: getState().user.overall_limit_pct
  });
};

const betSlipError = (message: any) => ({
  type: BET_SLIP_ERROR,
  payload: message
});

export const sendTicket = () => (dispatch: any, getState: any) => {
  const { live, betSlip, languageData } = getState();
  const { errors } = languageData.betSlip;
  const { odds, parlay } = betSlip;

  let error;

  if (live.betType === BetType.straight) {
     odds.forEach((odd: any) => {
      if (odd.risk < odd.limit.min || odd.risk > odd.limit.max) {
        error = errors.betMinMax;
        return;
      }
    });
  }

  if (error) {
    return dispatch(betSlipError(error));
  }

  dispatch(addConfirmationOdds(odds.slice(), Object.assign({}, parlay)));
  dispatch(openBetConfirmation());
};
