import * as React from 'react';
import { connect } from 'react-redux';

import Odd from './odd';
import Form from './form';
import Button from '../../form/button';

import config from './../../../../config';
import { toggleBetSlip, sendTicket } from '../../../actions/bet-slip';

import { range } from '../../../helpers';

import getBestRow from './getBestRow';

class Slider extends React.Component<any, any> {

  state = {
    updateOutcomesReference: this.props.up
  };

  componentDidMount() {
    document.addEventListener('click', this.close);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.close);
  }

  keep = (e: any) => {
    e.nativeEvent.stopImmediatePropagation();
  }

  close = (e: any) => {

    // Prevent betslip to close if user is interacting with range-slider
    if (typeof e.target.className === 'string') {
      if (e.target.className.includes('range-slider-price') || e.target.className.includes('MuiSlider-root')) return;
    }
    this.props.dispatch(toggleBetSlip(false));
  }

  sendTicket = (e: any) => {
    e.preventDefault();
    this.props.dispatch(sendTicket());
  }

  componentWillReceiveProps(nextProps: any) {
    // if we're adding a new item to the Bet Slip, we want to scroll down to it
    if (Object.keys(nextProps.odds).length > Object.keys(this.props.odds).length) {
      requestAnimationFrame(() => (this.refs as any).scroller.scrollTop = 9999);
    }
  }

  render() {
    const { open, odds, betType, parlay, language, view, betslipUpdateOutcomes } = this.props;

    const bs = view !== 3 && view !== 1 ? 'bet-slip' : 'bet-slip-new';
    const className = `${bs}__slider`;
    const classList = [className];
    // console.log(odds);
    // console.log('betSlipOutcomes');
    const bets = odds.map((odd: any, i: any) => {

      const updatedOutcomes = betslipUpdateOutcomes[`${odd.event}_${odd.outcome}`];

      const shouldReplace = updatedOutcomes && (updatedOutcomes[odd.id] && updatedOutcomes[odd.id].status) || (odd.replacementBet && updatedOutcomes[odd.replacementBet.id] && updatedOutcomes[odd.replacementBet.id].status);
      // console.log('currentOutcome');
      // console.log(odd);

      if (shouldReplace) {

        let closestRow = getBestRow(updatedOutcomes, odd);

        Object.keys(updatedOutcomes).forEach(id => {
          // console.log(odd);

          if (odd.event === updatedOutcomes[id].event && updatedOutcomes[id].column === odd.column && closestRow[0] && updatedOutcomes[id].row === closestRow[0].row) {

            if (odds.indexOf(updatedOutcomes[id]) < 0) {
              // console.log(odd);
              // console.log('odd is');
              odd.replacementBet = updatedOutcomes[id];
            }
            // console.log(`line changed`);
          }
        });

        // console.log(this.props.updatedBetslipOdds[parseInt(odd.id)]);
      }

      // If bet has changed line, it is possible it dissapears from outcomes stream. We need to look for a replacement in this case...
      if (updatedOutcomes) {

        Object.keys(updatedOutcomes).forEach(id => {
          // getOddsData(updatedOutcomes.event, updatedOutcomes, updatedOutcomes.id);

          if (odd.event === updatedOutcomes[id].event) {

            if (!updatedOutcomes[odd.id]) {

              let closestRow = getBestRow(updatedOutcomes, odd);

              if (updatedOutcomes[id].column === odd.column && closestRow[0] && updatedOutcomes[id].row === closestRow[0].row) {

                if (odds.indexOf(updatedOutcomes[id]) < 0) {
                  // console.log(odd);
                  // console.log('odd is');
                  // console.log(updatedOutcomes[id])
                  odd.replacementBet = updatedOutcomes[id];
                }

              }
            }
          }
        });

      }

      return <Odd key={`${odd.id}_${i}`} odd={odd} />;
    });

    if (open && bets.length) {
      classList.push(`${className}--open`);
    }

    let style_ = {};

    if (!config.hasMaxViewHeight && open) {
      let { iframeSizer } = (window as any);
      style_ = iframeSizer.scrollTop && bets.length ? {
        top: `${iframeSizer.scrollTop}px`
      } : {};
      // console.log(iframeSizer);
    }

    return (
      <div style={style_} onClick={this.keep} className={classList.join(' ')}>
        <div ref="scroller" className={`${bs}__scroller`}>
          <form onSubmit={this.sendTicket}>
            { bets }
            { !!betType && <Form odd={parlay} /> }
            <div key="submit" className={`${bs}__submit-button`}>
              <Button type="submit" enabled={odds.every((odd: any) => odd !== undefined)}>{ language.submit }</Button>
            </div>
          </form>
        </div>
        <div className={`${bs}__slide-up`} onClick={(e) => this.close(e)}>
          <i className="i i-menu-open" />
        </div>
      </div>
    );
  }
}

export default connect(
  (state: any) => ({
    open: state.betSlip.open,
    odds: state.betSlip.odds,
    betType: state.live.betType,
    parlay: state.betSlip.parlay,
    view: state.settings.view,
    language: state.languageData.betSlip,
    betslipUpdateOutcomes: state.live.betslipUpdateOutcomes
  })
)(Slider);

function getOddsData(outcomes: any): any {

  if (outcomes) {
    let values = Object.keys(outcomes).map(item => {
      return outcomes[item];
    }).sort((a: any, b: any) => a.order - b.order).filter((odd: any) => !odd.status);

    let arrFinal = [];

    const itemOrder = values.map((item: any) => item.order);

    if (itemOrder.indexOf(-171718) !== 0 && itemOrder.indexOf(-171717) !== 0) {

      const maxColumns = Math.max.apply(null, values.map((item: any) => item.column)) + 1;
      const valuesByColumn = range(maxColumns).map((x, j) => values.filter((item: any) => item.column === j)).filter(arr => arr.length > 0);
      const maxRows = Math.max.apply(null, valuesByColumn.map(arr => arr.length));

      for (let i = 0; i < maxRows; i++) {
        arrFinal[i] = [];
        for (let j = 0; j < valuesByColumn.length; j++) {
          arrFinal[i][j] = valuesByColumn[j][i];
        }

    }

    } else {

        const vals = values.map((item: any) => item).sort((a: any, b: any) => {
          if (a.display < b.display) {
            return -1;
          }

          if (a.display > b.display) {
            return 1;
          }

          return 0;
        });

        const columns = 3;
        const rows = Math.ceil(vals.length / columns);

        for (let i = 0; i < rows; i++) {
          arrFinal[i] = [];
          for (let j = 0; j < columns; j++) {
            arrFinal[i][j] = vals[j + columns * i];
          }
        }

    }
    // console.log(arrFinal);
    return arrFinal;
  }
  return [];
}
