import {
  CHANGE_LANGUAGE, GET_TIMEZONES,
  CHANGE_TIMEZONE, CHANGE_ODDSFORMAT,
  CHANGE_THEME, CHANGE_VIEW,
  CHANGE_VIEW_PREFERENCES,
  REDIRECT_TO_SETTINGS
} from '../actions/settings';

import config from '../../config';

export const settingsInitialState: any = {
  language: 'en',
  timeZone: {},
  timeZones: [],
  oddsFormat: 0,
  view: config.viewType,
  theme: 'main',
  viewPreferences: {},
  redirectToSettings: false
};

export const settings = (state = settingsInitialState, action: any): any => {
  switch (action.type) {
    case CHANGE_LANGUAGE:
      return Object.assign({}, state, {
        language: action.payload.language
      });
    case GET_TIMEZONES:
      return Object.assign({}, state, {
        timeZones: action.payload
      });
    case CHANGE_TIMEZONE:
      return Object.assign({}, state, {
        timeZone: action.payload
      });
    case CHANGE_ODDSFORMAT:
      return Object.assign({}, state, {
        oddsFormat: action.payload
      });
    case CHANGE_THEME:
      return Object.assign({}, state, {
        theme: action.payload
      });
    case CHANGE_VIEW:
      return Object.assign({}, state, {
        view: action.payload
      });
    case CHANGE_VIEW_PREFERENCES:
      return Object.assign({}, state, {
        viewPreferences: action.payload
      });
    case REDIRECT_TO_SETTINGS:
      return Object.assign({}, state, {
        redirectToSettings: action.payload
      });
  }

  return state;
};
