import * as React from 'react';
import Time, { TimeFormat } from './../form/time';
import { Sports } from '../../constants';

class Scroller extends React.Component<any, any> {
  get initialSize() {
    return this.props.initialSize || 25;
  }

  state = {
    size: this.initialSize
  };

  incrementSize = () => {
    const scrollTop = document.documentElement.scrollTop || window.scrollY;
    let child_: any = this.props.children;
    if (!child_) return;
    const size = Math.round(Math.min(this.initialSize + (scrollTop / 35), child_.length));

    if (size > this.state.size) {
      this.setState({
        size
      });
    }
  }

  componentWillReceiveProps() {
    this.setState({
      size: this.initialSize
    });
  }

  componentDidMount() {
    window.addEventListener('scroll', this.incrementSize);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.incrementSize);
  }

  render() {
    const { tag = 'tbody', items, format, sports, content } = this.props;
    return (
      <tbody>
        { content }
        {/* {
          items.map((item: any) => (
            <tr key={item.event}>
              <td><Time format={format}>{ item.time }</Time></td>
              <td>{ sports[Sports[item.sport]] }</td>
              <td>{ item.description }</td>
            </tr>
          ))
        } */}
      </tbody>
    );
    // return React.createElement(tag, null, items.slice(0, this.state.size));
  }
}

export default Scroller;
