import { SoccerAction, SoccerTeam, ISoccerFieldAnimationState, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1DangerousAttackStart: ISoccerFieldAnimationState = t1;
const t1DangerousAttackEnd: ISoccerFieldAnimationState = deepAssign({}, t1, {
  polygon: {
    red: 255,
    translateX: -5
  },
  actionText: {
    x: 94
  }
});

const t2DangerousAttackStart: ISoccerFieldAnimationState = t2;
const t2DangerousAttackEnd: ISoccerFieldAnimationState = deepAssign({}, t2, {
  polygon: {
    red: 255,
    translateX: -5
  },
  actionText: {
    x: 11
  }
});

export function dangerousAttack(state: ISoccerFieldAnimationState, currentTeam: SoccerTeam, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  const ret = {
    start: nextTeam === SoccerTeam.Team1 ? t1DangerousAttackStart : t2DangerousAttackStart,
    end: nextTeam === SoccerTeam.Team1 ? t1DangerousAttackEnd : t2DangerousAttackEnd
  };

  if (nextTeam === currentTeam && (nextAction === SoccerAction.Possession || nextAction === SoccerAction.Attack)) {
    ret.start = state;
  }

  return [ret];
}
