import { IHockeyFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const faceOffStart: IHockeyFieldAnimationState = deepAssign({}, nt, {
 extras: {
    x: 88,
    y: 30,
    opacity: 1
  },
  faceOffCtr: {
    x: 100,
    y: 43
  }
});

const faceOffEnd: IHockeyFieldAnimationState = deepAssign({}, nt, {
  actionText: {
    x: 100,
    y: 30
  },
  faceOffCtr: {
    x: 100,
    y: 43
  },
  extras: {
    x: 88,
    y: 30,
    opacity: 1
  }
});

export function faceOff() {
  return [{
    start: faceOffStart,
    end: faceOffEnd
  }];
}
