import { BasketTeam, BasketballAction } from '../constants';

import { shoot } from './shoot';
import { scored } from './shoot/scored';
import { missed } from './shoot/missed';

function next(nextTeam: BasketTeam) {
  const isTeam1 = nextTeam === BasketTeam.Team1;

  return shoot({
    actionText: {
      x: isTeam1 ? 65 : 30
    },
    shootSpot: {
      x: isTeam1 ? 75 : 19,
      y: 25
    },
    twoPointPath: {
      x: isTeam1 ? 94 : 0
    },
    alternText: {
      x: isTeam1 ? 65 : 30
    },
    ball: {
      x: isTeam1 ? 75 : 19,
      y: 25
    }
  }, nextTeam);
}

const t1FreeShoot = next(BasketTeam.Team1);
const t2FreeShoot = next(BasketTeam.Team2);

export function freeThrow(nextTeam: BasketTeam, nextAction: BasketballAction): (any|void) {
  const ftShoot = nextTeam === BasketTeam.Team1 ? t1FreeShoot : t2FreeShoot;

  switch (nextAction) {
    case BasketballAction.FreeThrow:
      return [{
        start: ftShoot.start,
        end: ftShoot.start
      }];
    case BasketballAction.FreeThrowScored:
      return [
        ftShoot,
        scored(ftShoot.end, nextTeam)
      ];
    case BasketballAction.FreeThrowMissed:
      return [
        ftShoot,
        missed(ftShoot.end, nextTeam)
      ];
  }
}
