import * as React from 'react';
import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import { createRoute } from '../../helpers';

const handleClick = (dispatch: any, to: any, onClick: any) => (e: React.MouseEvent<any>) => {
  if (e.button === 0) {
    e.preventDefault();

    if (onClick) {
      onClick.call(null, e);
    }

    setTimeout(() => dispatch(push(to)), 10);
  }
};

const ReduxLink = ({ dispatch, className = '', activeClassName, onClick = undefined, to, path, title = '', children }: any) => {

  to = createRoute(to);

  let finalClassName = className.split(' ');

  if (activeClassName && path.slice(0, to.length) === to) {
    finalClassName.push(activeClassName);
  }

  return (
    <a style={{textDecoration: 'none'}} href={to} onClick={handleClick(dispatch, to, onClick)} title={title} className={finalClassName.join(' ')}>
      {children}
    </a>
  );
};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, { path: state.router.location.pathname })
)(ReduxLink);
