import * as React from 'react';

import { getAnimations } from './actions';
import { BaseBallAction, BaseBallTeam, actionTextBaseBall } from './constants';
import { Animation } from '../helpers';

import Bat from './bat';
import Glove from './glove';
import Diamond from './diamond';
import HomeRun from './home-run';
import BaseBall from './base-ball';
import PitchBall from './pitch-ball';
import PitchSpot from './pitch-spot';
import Batter1 from './batter/batter-1';
import Batter2 from './batter/batter-2';
import Batter3 from './batter/batter-3';
import Batter4 from './batter/batter-4';
import Pitcher1 from './pitcher/pitcher-1';
import Pitcher2 from './pitcher/pitcher-2';
import Pitcher3 from './pitcher/pitcher-3';
import Pitcher4 from './pitcher/pitcher-4';

import Bases from './bases';
import Spots from './new-spots';

let items: any = [];

export class BaseBallField extends React.Component<any, any> {
  animation = new Animation(this);
  promise: any = Promise.resolve();

  state = {
    svgHeight: 'auto',
    action: this.props.action,
    animation: getAnimations(null, this.props.team, this.props.action)[0].start

  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 200, height = 86 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.gamecastSize !== this.props.gamecastSize) {
      this.handleResize();
    }

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {
    this.promise = getAnimations(this.state.animation, this.props.team, this.props.action, newProps.team, newProps.action)
      .reduce((all, next: any) => (
        all.then(() => Promise.all(Object.keys(next.start).map(key => {
          this.setState({
            action: newProps.action
          });
          return this.animation.start(key, next.start[key], next.end[key], 750);
        })))
      ), this.promise);
    }
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillUnmount() {
    this.animation.cancel();
    window.removeEventListener('resize', this.handleResize);
  }

  get getBSO() {
    const { matchData } = this.props;
    return matchData && matchData
    .split('#')
    .slice(1, -1)
    .map((obj: any, i: any) => {

      const styles = {
        fSize: '6px',
        fill: '#ffc107',
        stroke: 'white',
        strokeW: '0.35',
        fillF: '#F44336',
        fillN: '#757575'
      };

      const numbers = [];
      let arr: any = [];
      let xSpot;
      let ySpot;
      let xSpotB: any;

      for (let j = 0; j < obj; j++) {
        numbers.push(j);
      }
      switch (i) {
        case 0:
          arr = [0, 1, 2, 3];
          xSpot = 87;
          ySpot = 75;
          xSpotB = 68;
          break;
        case 1:
          arr = [4, 5, 6];
          xSpot = 136;
          ySpot = 75;
          xSpotB = 118;
          break;
        case 2:
          arr = [7, 8, 9];
          xSpot = 181;
          ySpot = 75;
          xSpotB = 167;
          break;
      }

      return (

        <g key={i}>
        {/*
          <g transform="translate(-7, 75), scale(0.8)">
            <rect transform="translate(0, -5) rotate(45 50 50)" x="20" y="26" width="4" height="4" fill="#FFC107" stroke="white" strokeWidth="0.25"/>
            <rect transform="translate(0, -5) rotate(45 50 50)" x="20" y="20" width="4" height="4" fill="white" stroke="white" strokeWidth="1"/>
            <rect transform="translate(0, -5) rotate(45 50 50)" x="26" y="20" width="4" height="4" fill="white" stroke="white" strokeWidth="1"/>
          </g>
        */}
          <text x={xSpot} y={ySpot} style={{fontSize: styles.fSize}} fill={styles.fill} key={i}>{obj}</text>
          {arr.map((item: any, j: any) =>
            xSpotB &&
            <circle key={j + item} cx={xSpotB + (j * 8)} cy="80" r="3" fill={styles.fillN} stroke={styles.stroke} strokeWidth={styles.strokeW} />)
          }
          {numbers.map((item: any, k: any) =>
            xSpotB &&
            <circle key={item + k + obj} cx={xSpotB + (k * 8)} cy="80" r="3" fill={styles.fillF} stroke={styles.stroke} strokeWidth={styles.strokeW} />)
          }
        </g>
      );

    });

  }

  // getScoreboard = () => {
  //   const { scoreboard } = this.props;
  //   return scoreboard && scoreboard
  //     .additional.filter(item => {
  //       return (item.title === 'R');
  //     }).map(item  => {
  //       return (
  //         <text key={item} x="105" y="11" fill="black" style={{ fontSize: '6px' }} textAnchor="end">{item.t1} - {item.t2}</text>
  //       );
  //     });
  // }

  getAtBat = () => {
    const { scoreboard } = this.props;
    return scoreboard && scoreboard
      .additional.map((item: any, i: any): (any|void) => {

        if (item.t1 !== '' && item.t2 === '' && item.title !== 'R' && item.title !== 'H') {
          items = [item.title, `45,80 48,80 46.5,77`, 345, 33, 'Top'];
          return false;
        }

        if (item.t1 !== '' && item.t2 !== '' && item.title !== 'R' && item.title !== 'H') {
          items = [item.title, `45,77 48,77 46.5,80`, 35, 290, 'Bottom'];
          return true;
        }
      });
  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === BaseBallTeam.Team1 ? event.inverted ? team2 : team1 : team === BaseBallTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  teamSplit = (who: any) => {
    if (who.length >= 23) {
      return who.split('').join('').slice(0, 20) + '...';
    } else {
      return who;
    }
  }

  render() {
    const { action, width = 200, height = 86/*, team1, team2*/} = this.props;
    const [sky, ground, grass, fence, fenceLine, fenceWarn, white, black] = ['#b3e5fc', '#f57c00', '#558b2f', '#00695c', '#004d40', '#ffc107', '#ffffff', '#212121'];
    const { svgHeight, animation } = this.state;
    const { pitchSpot, actionText, diamond, extras, pitcherStart, pitcherTwo, pitcherThree, pitcherEnd, ball, batterStart, batterTwo, batterThree, batterEnd } = animation;
    const w = (p: any) => width * p / 200;
    const h = (p: any) => height * p / 86;
    // console.log(this.props);
    return (
      <div ref="gc" >
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`} strokeWidth="0.5" fill="transparent" >
          <rect width={w(200)} height={h(86)} fill={grass} />
          <path d={`M ${w(0)} ${h(0)} H ${w(200)} V ${h(15)} A ${w(200)} ${h(15)} 0 0 1 ${w(100)} ${h(20)} L ${w(0)},${h(15)} z`} fill={sky} />
          <path d={`M ${w(0)} ${h(15)} A ${w(0)} ${h(15)} 0 0 1 ${w(100)} ${h(20)} L ${w(200)},${h(15)} V ${h(30)} H ${w(0)} z`} fill={fence} />
          <path d={`M ${w(20)} ${h(16)} V ${h(30)} M ${w(60)} ${h(18)} V ${w(30)} M ${w(100)} ${h(19)} V ${h(30)} M ${w(140)} ${h(18)} V ${h(30)} M ${w(180)} ${h(16)} V ${h(30)}`} stroke={fenceLine} />
          <polyline points={`${w(0)},${h(14)} ${w(100)},${h(19)} ${w(200)},${h(14)} ${w(200)},${h(14)} ${w(200)},${h(15)} ${w(100)},${h(20)} ${w(0)},${h(15)}`} fill={fenceWarn} />
          <ellipse cx={w(41)} cy={h(37.5)} rx={w(6.5)} ry={h(2)} fill={ground}/>
          <ellipse cx={w(161)} cy={h(37.5)} rx={w(6.5)} ry={h(2)} fill={ground}/>
          <path d={`M ${w(139)}, ${w(52)} ${h(161)},${h(38)} ${w(100)},${h(34)} ${w(41)},${h(38)} ${w(62)},${h(52)}`} stroke={ground} strokeWidth="4" />
          <path d={`M ${w(139)}, ${w(52)} ${h(161)},${h(38)} ${w(100)},${h(34)} ${w(41)},${h(38)} ${w(62)},${h(52)}`} stroke={white} strokeWidth="1" />
          <ellipse cx={w(161)} cy={h(38)} rx={w(3)} ry={h(0.75)} fill={white}/>
          <ellipse cx={w(40)} cy={h(37.5)} rx={w(3)} ry={h(0.75)} fill={white}/>
          <ellipse cx={w(100)} cy={h(67)} rx={w(72.5)} ry={h(20)} fill={ground}/>
          <path d={`M ${w(82)} ${h(70)} L ${w(89)},${h(53)} ${w(58)},${h(53)} 42,65 84,65`} stroke={white} strokeWidth="2"/>
          <path d={`M ${w(120.75)} ${h(70)} L ${w(112)},${h(53)} ${w(142)},${h(53)} ${w(160)},${h(65)} ${w(118)},${h(65)}`} stroke={white} strokeWidth="2"/>
          <polygon points={`${w(93)},${h(55)} ${w(107)},${h(55)} ${w(108.5)},${h(58)} ${w(100)},${h(60)} ${w(92)},${h(58)}`} stroke={white} fill={white} strokeWidth="1"/>
          <path d={`M ${w(92)} ${h(40)} A ${w(10)} ${h(4)} 1 0 1 ${w(109)} ${h(40)} z`} fill={ground} />
          <rect width={w(200)} height={h(19)} x={w(0)} y={h(69)} fill="black"/>
         {/*
            <text x={w(25)} y={w(75)} fill={white} style={{ fontSize: '5px' }}>BASES</text>
         */}
          <text x={w(70)} y={w(75)} fill={white} style={{ fontSize: '5px' }}>BALLS</text>
          <text x={w(115)} y={h(75)} fill={white} style={{ fontSize: '5px'}}>STRIKES</text>
          <text x={w(165)} y={h(75)} fill={white} style={{ fontSize: '5px' }}>OUTS</text>
          <Pitcher1 x={w(81)} y={h(12)} width={w(40)} height={h(40)} display={pitcherStart.display}/>
          <Pitcher2 x={w(81)} y={h(12)} width={w(40)} height={h(40)} display={pitcherTwo.display}/>
          <Pitcher3 x={w(81)} y={h(12)} width={w(40)} height={h(40)} display={pitcherThree.display}/>
          <Pitcher4 x={w(81)} y={h(12)} width={w(40)} height={h(40)} display={pitcherEnd.display}/>
          { (action === BaseBallAction.Strike || action === BaseBallAction.Ball) &&
            <g opacity={pitchSpot.opacity}>
              <rect width={w(36)} height={h(36.5)} x={pitchSpot.x - 17.5} y={w(32)} fill={black} opacity="0.5"/>
              <rect width={w(20)} height={h(25)} x={pitchSpot.x - 9.75} y={w(38)} fill="rgba(255, 255, 255, 0.5)" stroke="white" strokeWidth="2" opacity="0.85" />
            </g>
          }
          <Batter1 x={w(50)} y={h(10)} width={w(75)} height={h(75)} display={batterStart.display}/>
          <Batter2 x={w(50)} y={h(10)} width={w(75)} height={h(75)} display={batterTwo.display}/>
          <Batter3 x={w(50)} y={h(10)} width={w(75)} height={h(75)} display={batterThree.display}/>
          <Batter4 x={w(50)} y={h(10)} width={w(75)} height={h(75)} display={batterEnd.display}/>
          {/*
          <rect x={w(15)} y={h(3)} rx={w(2)} ry={h(2)} width={w(170)} height={h(14)} fill="rgba(55, 55, 55, 0.25)" strokeWidth="0" stroke="rgba(55, 55, 55, 0.5)" />
          <text x={w(20)} y={h(14)} fill={black} style={{ fontSize: '7px' }} textAnchor="start">{this.teamSplit(team1)}</text>
          <text x={w(180)} y={h(14)} fill={black} style={{ fontSize: '7px' }} textAnchor="end"> {this.teamSplit(team2)}</text>
          */}
          <g opacity={pitchSpot.opacity}>
            <Diamond width={w(60)} height={h(60)} x={diamond.x} y={diamond.y} opacity={diamond.opacity}/>
            <PitchSpot x={pitchSpot.x} y={pitchSpot.y} opacity={pitchSpot.opacity} />
          </g>
          {/*
          <Bases width={w(40)} height={h(40)} x="20" y="15" opacity="1" />
          */}
          <g>
            {/*
              <Spots width={w(40)} height={h(40)} x="10" y="35" opacity="1" />
            */}
            <text x="13" y="58" fill={white} style={{ fontSize: '4px' }}>{items[4]} {items[0]}</text>
          </g>
          { action === BaseBallAction.HomeRun &&
            <HomeRun x={extras.x + 5} y={extras.y + 7} width={w(40)} height={h(40)} opacity={extras.opacity}/>
          }
          {
            (action === BaseBallAction.Out || action === BaseBallAction.DoublePlay || action === BaseBallAction.TriplePlay || action === BaseBallAction.CaughtStealing || action === BaseBallAction.StrikeOut || action === BaseBallAction.NewPitcher) &&
            <Glove x={extras.x} y={extras.y} width={w(45)} height={h(45)} opacity={extras.opacity}/>
          }
          {
            (action === BaseBallAction.SacrificeFly || action === BaseBallAction.SacrificeBunt || action === BaseBallAction.BallInPlay) &&
            <g opacity={extras.opacity}>
              <g transform="scale(0.5)">
                <BaseBall x={extras.x + 94} y={extras.y + 30} width={w(30)} height={w(30)}/>
              </g>
              <g transform="scale(0.58)">
                <Bat x={extras.x + 74} y={extras.y + 20} width={w(50)} height={h(50)}/>
              </g>
            </g>
          }
          { (action === BaseBallAction.EndOfHalfInnings || action === BaseBallAction.StartInnings) &&
            <g>
              <rect x={w(actionText.x)} y={h(actionText.y) + 15} rx={w(2)} ry={h(2)} width={w(70)} height={h(15)} fill="rgba(255, 255, 255, 0.9)" />
              <text x={w(actionText.x + 35)} y={h(actionText.y) + 24} strokeWidth="0" fill={black} textAnchor="middle" style={{ fontSize: '6px' }}>{this.teamSplit(this.team)}</text>
            </g>
          }
          <rect x={actionText.x} y={actionText.y} rx={w(2)} ry={h(2)} width={w(70)} height={h(10)} fill="rgba(55, 55, 55, 0.9)" />
          <text x={actionText.x + 35} y={actionText.y + 7} strokeWidth="0" fill={fenceWarn} textAnchor="middle" style={{ fontSize: '8px' }}>{actionTextBaseBall(this.props.action)}</text>
          {/*
          <polygon points={items[1]} fill={fenceWarn}/>
          <text x={w(25)} y={h(80)} fill={white} style={{ fontSize: '7px' }}>Inn. {items[0]}</text>

          <g transform="scale(0.5)">
            <BaseBall x={items[2]} y={h(4)} width={w(20)} height={w(20)}/>
          </g>
          <g transform="scale(0.58)">
            <Bat x={items[3]} y={h(0)} width={w(20)} height={h(20)}/>
          </g>
          */}
          <PitchBall x={w(ball.x)} y={w(ball.y)} width={width} height={height} scale={ball.scale}/>
          {this.getBSO}
          {this.getAtBat()}
         </svg>
      </div>
    );
  }
}
