import * as React from 'react';

import { getAnimations } from './actions';
import { SoccerAction, SoccerTeam } from './constants';
import { Animation } from '../helpers';

import { range } from '../../../helpers';

import Ball from './ball';
import Gate from './gate';
import Card from './card';
import Flag from './flag';
import GoalKeeper from './goal-keeper';

const gateActions = [
  SoccerAction.Goal,
  SoccerAction.ShotOnTarget,
  SoccerAction.ShotOffTarget,
  SoccerAction.ShootoutScore,
  SoccerAction.ShootoutMiss,
  SoccerAction.PenaltyMissed
];

export class SoccerField extends React.Component<any, any> {
  animation = new Animation(this);
  promise: Promise<any|void> = Promise.resolve();

  state = {
    svgHeight: 'auto',
    animation: getAnimations(null, this.props.team, this.props.action)[0].start,
    action: this.props.action
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 105, height = 68 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.gamecastSize !== this.props.gamecastSize) {
      this.handleResize();

    }

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {
      this.promise = getAnimations(this.state.animation, this.props.team, this.props.action, newProps.team, newProps.action)
        .reduce((all, next: any) => (
          all.then(() => Promise.all(Object.keys(next.start).map(key => {
            this.setState({
              action: newProps.action
            });
            return this.animation.start(key, next.start[key], next.end[key], 750);
          })))
        ), this.promise);
    }
  }

  get actionText() {
    switch (this.props.action) {
      case SoccerAction.None:
        return 'Upcoming Game';
      case SoccerAction.Possession:
        return 'In Possession';
      case SoccerAction.Attack:
        return 'Attack';
      case SoccerAction.DangerousAttack:
        return 'Dangerous Attack';
      case SoccerAction.Kickoff:
        return 'Kick Off';
      case SoccerAction.HalfTime:
        return 'Half Time';
      case SoccerAction.SecondHalf:
        return 'Second Half';
      case SoccerAction.FullTime:
        return 'Full Time';
      case SoccerAction.ExtraTime:
        return 'Extra Time';
      case SoccerAction.ExtraTimeHalfTime:
        return 'Extra Time - Half Time';
      case SoccerAction.ExtraTime2ndHalf:
        return 'Extra Time - Second Half';
      case SoccerAction.ExtraTimeFullTime:
        return 'Extra Time - Full Time';
      case SoccerAction.PenaltyShootout:
        return 'Penalty Shootout';
      case SoccerAction.Injury:
        return 'Injury';
      case SoccerAction.CornerTop:
      case SoccerAction.CornerBottom:
        return 'Corner';
      case SoccerAction.GoalKick:
        return 'Goal Kick';
      case SoccerAction.Penalty:
        return 'Penalty';
      case SoccerAction.SafeFreeKick:
      case SoccerAction.HalfWayFreeKick:
        return 'Free Kick';
      case SoccerAction.DangerousFreeKick:
        return 'Dangerous Free Kick';
      case SoccerAction.Substitution:
      case SoccerAction.SubstitutionEnhanced:
        return 'Substitution';
      case SoccerAction.HalfWayThrowIn:
      case SoccerAction.DangerousThrowIn:
        return 'Throw In';
      case SoccerAction.YellowCard:
        return 'Yellow Card';
      case SoccerAction.RedCard:
        return 'Red Card';
      case SoccerAction.Offside:
        return 'Offside';
      case SoccerAction.Goal:
        return 'Goal';
      case SoccerAction.ShootoutScore:
        return 'Penalty scored';
      case SoccerAction.ShotOnTarget:
        return 'Shot on target';
      case SoccerAction.ShotOffTarget:
        return 'Shot off target';
      case SoccerAction.PenaltyMissed:
      case SoccerAction.ShootoutMiss:
        return 'Penalty Missed';
    }

    return '';
  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === SoccerTeam.Team1 ? event.inverted ? team2 : team1 : team === SoccerTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);

  }

  componentWillUnmount() {
    this.animation.cancel();
    window.removeEventListener('resize', this.handleResize);
  }

  render() {
    const { action, team, width = 105, height = 68 } = this.props;
    const { svgHeight, animation } = this.state;
    const { polygon, actionText, ball, extras, gate } = animation;
    const [mainColor, secondaryColor, strokeColor, textColor] = ['#146715', '#1d801f', '#9c9', '#6c6'];

    let { red, green, blue } = polygon;
    [red, green, blue] = [red, green, blue].map(color => Math.round(color));

    const w = (p: any) => width * p / 105;
    const h = (p: any) => height * p / 68;

    const polygonPoints = `-1 -1, ${width - 3} -1, ${range(6).map(i => `${(i % 2 === 1 ? width : width - 3)} ${(height + 1) / 6 * i}`).join(', ')}, -1 ${height + 1}`;

    return (
      <div ref="gc">
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`} fill="transparent" stroke={strokeColor} strokeWidth="0.4">
          <defs>
            <pattern id="bg-soccer-field" patternUnits="userSpaceOnUse" width="8" height="1">
              <g strokeWidth="0">
                <rect width="8" height="1" fill={mainColor} />
                <rect width="8" height="1" x="4" fill={secondaryColor} />
              </g>
            </pattern>
            <linearGradient id="gradient-soccer-field" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stopColor="rgb(0, 0, 0)" stopOpacity="0.1" />
              <stop offset="100%" stopColor={`rgb(${red}, ${green}, ${blue})`} stopOpacity="0.25" />
            </linearGradient>
          </defs>
          <rect x="-1" y="-1" strokeWidth="0" fill={mainColor} width={width + 2} height={height + 2} />
          <rect x="0" y="0" width={width} height={height} fill="url(#bg-soccer-field)" />
          <circle cx={w(52.5)} cy={h(34)} r={w(9.15)} />
          <line x1={w(52.5)} x2={w(52.5)} y1="0" y2={height} />
          <circle cx={w(52.5)} cy={h(34)} r="0.3" fill={strokeColor} />
          <circle cx={w(11)} cy={h(34)} r={w(9.15)}/>
          <circle cx={w(94)} cy={h(34)} r={w(9.15)}/>
          <rect x="0" y={h(13.84)} width={w(16.5)} height={h(40.32)} fill="url(#bg-soccer-field)" />
          <rect x={w(88.5)} y={h(13.84)} width={w(16.5)} height={h(40.32)} fill="url(#bg-soccer-field)" />
          <rect x="0" y={h(24.84)} width={w(5.5)} height={h(18.32)} />
          <rect x={w(99.5)} y={h(24.84)} width={w(5.5)} height={h(18.32)} />
          <circle cx={w(11)} cy={h(34)} r="0.3" fill={strokeColor} />
          <circle cx={w(94)} cy={h(34)} r="0.3" fill={strokeColor} />
          <path d="M 1.2 0 A 1.2 1.2 0 0 1 0 1.2" />
          <path d={`M ${width - 1.2} 0 A 1.2 1.2 0 0 0 ${width} 1.2`} />
          <path d={`M 1.2 ${height} A 1.2 1.2 0 0 0 0 ${height - 1.2}`} />
          <path d={`M ${width} ${height - 1.2} A 1.2 1.2 0 0 0 ${width - 1.2} ${height}`} />
          <polygon fill="url(#gradient-soccer-field)" strokeWidth="0" transform={`scale(${polygon.scaleX}, 1) translate(${(team === SoccerTeam.Team1 ? 0 : -width) + w(polygon.translateX)})`} points={polygonPoints} />
          {
            this.team  && (action !== SoccerAction.HalfTime) ?
              <text x={w(actionText.x)} y={h(actionText.y) - 6} strokeWidth="0" fill={textColor} textAnchor={actionText.textAlign} style={{ fontSize: '5px' }}>{this.team}</text> :
              <rect x={w(18)} y={h(actionText.y) - 8} rx="2" ry="2" width={w(69)} height="12" fill="#257225" opacity="0.6" strokeWidth="0" />
          }
          <text x={w(actionText.x)} y={h(actionText.y)} strokeWidth="0" fill="#fff" textAnchor={actionText.textAlign} style={{ fontSize: '6px' }}>{this.actionText}</text>
          {
            gateActions.indexOf(action) >= 0 &&
              <g transform={`scale(${gate.scaleX}, 1) translate(${w(gate.translateX)}, ${h(gate.translateY)})`}>
                <Gate fieldBgColor={mainColor} />
              </g>
          }
          {
            (action === SoccerAction.ShotOnTarget || action === SoccerAction.ShotOffTarget) &&
            <g transform={`scale(${gate.scaleX}, 1) translate(${w(gate.translateX)}, ${h(gate.translateY) + 5})`}>
              <GoalKeeper />
            </g>
          }
          <Ball x={w(ball.x)} y={h(ball.y)} width={width} height={height} scale={ball.scale} direction={ball.direction} team={this.props.team} />
          {
            (action === SoccerAction.YellowCard || action === SoccerAction.RedCard) &&
              <g transform={`rotate(${extras.rotate}, ${w(35)}, ${h(21)})`}>
                <Card x={w(20)} action={action} />
              </g>
          }
          {
            action === SoccerAction.Offside &&
              <g transform={`rotate(-${extras.rotate}, ${w(32.5)}, ${h(23)})`}>
                <Flag />
              </g>
          }
        </svg>
      </div>
    );
  }
}
