import * as React from 'react';
import { connect } from 'react-redux';
import MenuItem from './menu-item';
import { legacyRedirect } from '../../actions/user';

import config from './../../../config';

const Menu = ({ dispatch, open, menuData, view, killedSession, user, openMarkets }: any) => {
  let className = 'menu';

  if (open) {
    className += ' open';
  }

  if (user && user.response && user.response.initialisation_result_code === 0) return <div></div>;

  const bookmakerId  = !user ? {} : user.bookmakerId;
  const openMarkets_ = openMarkets.map((item: any) => {
    return {
      sport: item.sport,
      hasStream: item.hasStream[bookmakerId[item.sport]]
    };
  }).filter((item: any) => item.hasStream > 0);

  return (
    <nav className={className}>
      { !killedSession &&
        <ul className="menu__left">
          {
            config.hasPrematchLink &&
            <MenuItem path="prematch" icon="prematch" view={view}>{menuData.prematch}</MenuItem>
          }
          <MenuItem path="live" icon="live" view={view}>{menuData.inPlay}</MenuItem>
          {
            config.hasVideoStream && openMarkets_[0] && openMarkets_[0].hasStream &&
            <MenuItem path="streaming" icon="live" view={view}>{menuData.streaming}</MenuItem>
          }
          <MenuItem path="rules" icon="rules" view={view}>{menuData.rules}</MenuItem>
          {
            config.hasTicketsLink &&
            <MenuItem path="tickets" icon="tickets" view={view}>{menuData.tickets}</MenuItem>
          }
          <MenuItem path="schedule" icon="schedule" view={view}>{menuData.schedule}</MenuItem>
          {
            config.hasLegacyLink &&
            <li className="menu__item" onClick={() => dispatch(legacyRedirect())}><div className="menu__link with-ripple">Legacy UI</div></li>
          }
        </ul>
      }
    </nav>
  );
};

export default connect(
  (state: any) => ({
    user: state.user,
    view: state.settings.view,
    open: state.menu.open,
    menuData: state.languageData.menu,
    openMarkets: state.live.openMarkets,
    killedSession: state.live.killedSession
  })
)(Menu);
