import { ISoccerFieldAnimationState, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';
import { gate } from './gate';
import { general } from './general';

const actionText = general()[0];

const t1Gate = gate(SoccerTeam.Team1);
const t2Gate = gate(SoccerTeam.Team2);

const t1ShotOnTargetStart: ISoccerFieldAnimationState = deepAssign({}, t1, {
  actionText: actionText.start.actionText,
  gate: t1Gate.end.gate,
  ball: {
    x: 55,
    y: 45,
    direction: BallDirection.None
  }
});

const t1ShotOnTargetEnd: ISoccerFieldAnimationState = deepAssign({}, t1ShotOnTargetStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 87,
    y: 33,
    scale: 2
  }
});

const t2ShotOnTargetStart: ISoccerFieldAnimationState = deepAssign({}, t2, {
  actionText: actionText.start.actionText,
  gate: t2Gate.end.gate,
  ball: {
    x: 50,
    y: 45,
    direction: BallDirection.None
  }
});

const t2ShotOnTargetEnd: ISoccerFieldAnimationState = deepAssign({}, t2ShotOnTargetStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 18,
    y: 33,
    scale: 2
  }
});

export function shotOnTarget(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam) {
  let start = nextTeam === SoccerTeam.Team1 ? t1ShotOnTargetStart : t2ShotOnTargetStart;
  let end = nextTeam === SoccerTeam.Team1 ? t1ShotOnTargetEnd : t2ShotOnTargetEnd;

  return [nextTeam === SoccerTeam.Team1 ? t1Gate : t2Gate, {
    start,
    end
  }];
}
