import * as React from 'react';
import { connect } from 'react-redux';
import {
  lastInteraction
} from './../../../actions/user';

const EMBED_URL = 'https://embed.twitch.tv/embed/v1.js';

let window_: any = null;

class TwitchPlayer extends React.Component<any, any> {

  constructor(props: any) {
    super(props);
    this.state = {
      embed: null,
      isVideoReady: false,
      isVideoPlaying: false,
      mockInteractionInterval: null
    };

    this.startMockInteractionInterval = this.startMockInteractionInterval.bind(this);
  }

  componentDidMount() {

    // A hack to prevent runtime error on server side rendering
    if (typeof window === undefined) return;

    window_ = window;

    const script = document.createElement('script');
    script.setAttribute(
      'src',
      EMBED_URL
    );
    script.addEventListener('load', () => {
      let embed: any = new window_.Twitch.Embed(this.props.targetID, { ...this.props });
      this.setState({ embed });

    });
    document.body.appendChild(script);
  }

  componentDidUpdate() {

    if (this.state.embed !== null && !this.state.isVideoReady) {

      const stopMockInterval = () => {
        if (!this.state.isVideoPlaying) return;
        console.log('twitch video has been stoped or paused');
        this.setState({isVideoPlaying: false});
        console.log('clearing twitch fake interval');
        clearInterval(this.state.mockInteractionInterval);
      };

      this.setState({ isVideoReady: true });

      try {
        this.state.embed.addEventListener(window_.Twitch.Embed.VIDEO_READY, () => {
          const player = this.state.embed.getPlayer();
          player.play();
        });

        this.state.embed.addEventListener(window_.Twitch.Embed.VIDEO_PLAY, () => {

          if (this.state.isVideoPlaying) return;
          console.log('twitch video is playing');
          this.setState({ isVideoPlaying: true});
          this.startMockInteractionInterval();
        });

        this.state.embed.addEventListener(window_.Twitch.Embed.VIDEO_PAUSE, () => {
          console.log('twitch video paused');
          stopMockInterval();
        });
      }

      catch (err) {
        console.log('twitch embed might have loaded...');
      }

    }
  }

  componentWillUnmount() {
    if (this.state.mockInteractionInterval) {
      console.log('unmounting twitch player...clearing fake interval');
      clearInterval(this.state.mockInteractionInterval);
    }

    const scriptExists = document.querySelectorAll(`script[src="${EMBED_URL}"]`);
    if (scriptExists[0]) {
      document.body.removeChild(scriptExists[0]);
    }

  }

  startMockInteractionInterval() {

    let that = this;
    console.log('start mock interaction interval');
    let mockInteractionInterval = setInterval(() => {
      let time = Date.now();
      console.log('send mock interaction');
      that.props.dispatch(lastInteraction(time));
    }, 45000);

    this.setState({ mockInteractionInterval });

  }

  render() {

    // A hack to prevent runtime error on server side rendering
    if (typeof window === undefined) return;

    return (
      <div>
        <div
          id={this.props.targetID}>
        </div>
      </div>
    );
  }

}

export default connect(
  (state: any) => {
    return {
    };
  })(TwitchPlayer);
