import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router';

import { loading, loadingInitialState } from './loading';
import { settings, settingsInitialState } from './settings';
import { languageData, languageDataInitialState } from './language-data';
import { menu, menuInitialState } from './menu';
import { rules, ruleInitialState } from './rules';
import { schedule, scheduleInitialState } from './schedule';
import { live, liveInitialState } from './live';
import { prematch, prematchInitialState } from './prematch';
import { betSlip, betSlipInitialState } from './bet-slip';
import { betConfirmation, betConfirmationInitialState } from './bet-confirmation';
import { user, userInitialState } from './user';
import { tickets, ticketsInitialState } from './tickets';
import { uiToolbar, uiToolbarInitialState } from './ui-toolbar';

import { eventsFlatten, eventsFlattenInitianState} from './events-flatten';
import { outcomesFlatten, outcomesFlattenInitialState } from './outcomes-flatten';

import {animations, animationsInitialState } from './animations';
import { subscriptionCache, subscriptionCacheInitialState } from './subscription-cache';

import { maintenance, maintenanceInitialState } from './maintenance';

export const initialState = {
  wsm: {},
  animations: animationsInitialState,
  loading: loadingInitialState,
  languageData: languageDataInitialState,
  settings: settingsInitialState,
  menu: menuInitialState,
  rules: ruleInitialState,
  schedule: scheduleInitialState,
  live: liveInitialState,
  prematch: prematchInitialState,
  betSlip: betSlipInitialState,
  betConfirmation: betConfirmationInitialState,
  user: userInitialState,
  tickets: ticketsInitialState,
  uiToolbar: uiToolbarInitialState,
  eventsFlatten: eventsFlattenInitianState,
  outcomesFlatten: outcomesFlattenInitialState,
  subscriptionCache: subscriptionCacheInitialState,
  maintenance: maintenanceInitialState
};

const rootReducer = (history: any) => combineReducers({
  wsm: (state = {}) => state,
  router: connectRouter(history),
  animations,
  loading,
  languageData,
  settings,
  menu,
  rules,
  schedule,
  live,
  prematch,
  betSlip,
  betConfirmation,
  user,
  tickets,
  uiToolbar,
  eventsFlatten,
  outcomesFlatten,
  subscriptionCache,
  maintenance
});

export default rootReducer;
