import * as React from 'react';
import { connect } from 'react-redux';

import { BetType } from '../../../constants';
import { mapOutcomeTitle } from '../bet-slip/helpers';

import Price from '../../form/price';

import { prematchTimeConverter } from './../live/live-helpers';
import { currencySymbol } from '../../../currencySymbols/currencySymbol';

import { removeFromBetSlip, updatePriceChange } from '../../../actions/bet-slip';
import { removeBet, closeBetConfirmation } from '../../../actions/bet-confirmation';

import OddMobile from './odd-mobile';

class Odd extends React.Component<any, any> {

  componentWillReceiveProps(newProps: any) {

    if (newProps.odd.status !== this.props.odd.status) {
      if (this.props.odd.status === 'ok') {
        newProps.odd.status = 'ok';
      }
    }

    if ((newProps.odd.display !== this.props.odd.display)) {
      newProps.odd.display = this.props.odd.display;
    }

    // if (newProps.odd && newProps.betSlipOdd && !newProps.odd.limit && newProps.betSlipOdd.limit) {
    //   console.log(newProps.odd);
    //   console.log(newProps.betSlipOdd);
    //   newProps.odd.limit = newProps.betSlipOdd.limit;
    // }

  }

  removeSelection = () => {
    const { dispatch, odd, betSlipOdds, odds } = this.props;
    const idxBetslip = odds.indexOf(odd);

    dispatch(removeFromBetSlip(odd));
    dispatch(removeBet(odd));
    // betSlipOdds.splice(idxBetslip, 1);
    odds.splice(idxBetslip, 1);

    if (odds[0] === undefined) {
      dispatch(closeBetConfirmation());
    }

  }

  render() {

    const {
      odd, odds, betSlipOdd,
      betSlipOdds,
      betType, line,
      length, parlay,
      betSlipParlay, event,
      freeze, language,
      outcomeTitle, user,
      loading, dispatch,
      isMobile, betslipUpdateOutcomes
    } = this.props;
    // If event finishes or is suspended, bet dissapears from betslip whitout notifying ws server...
    if (!event) {
      console.log(`suspended event for ${odd.display}`);
      if (odd) {
        dispatch(removeFromBetSlip(odd));
        dispatch(removeBet(odd));
        const idxBetslip = betSlipOdds && betSlipOdds.indexOf(odd);
        const idxOdd = odds && odds.indexOf(odd);

        if (betSlipOdds && idxBetslip >= 0) {
          betSlipOdds.splice(idxBetslip, 1);
          betSlipOdds.splice(idxOdd, 1);
        }

        if (idxOdd  >= 0) {
          odds.splice(idxOdd, 1);
        }

        if (!odds || !betSlipOdds) {
          dispatch(closeBetConfirmation());
        }
      }

      return <tr></tr>;

    }

    const { home, sep, away, inverted } = event;
    const classList = [];

    const isLoading = loading === 0 ? false : true;

    if (betSlipOdd === undefined) {
      return <tr></tr>;
    }

    const [risk, toWin, bRisk, bToWin, status, rowSpan] = betType === BetType.straight ?
      [odd.risk, odd.toWin, betSlipOdd.risk, betSlipOdd.toWin, odd.status, 1] :
      [parlay.risk, parlay.toWin, betSlipParlay.risk, betSlipParlay.toWin, parlay.status, length];

    const hasReplacementBet = !freeze && betSlipOdd && betSlipOdd.replacementBet && betSlipOdd.replacementBet.display !== odd.display;
    const updatedOutcomes = betslipUpdateOutcomes[`${odd.event}_${odd.outcome}`];
    const priceChanged = updatedOutcomes && updatedOutcomes[odd.id] && (odd.id === updatedOutcomes[odd.id].id && odd.price !== updatedOutcomes[odd.id].price);

    const hasUpdates = hasReplacementBet;

    const lineOutDate = betType === 0 ? odds.filter((item: any) => item.status === 'lineOutOfDate')[0] !== undefined : parlay.status === 'lineOutOfDate';
    const updatesAfterPlace = priceChanged || lineOutDate;

    // console.log(`freeze is ${freeze}`);
    if (!freeze && priceChanged && odd.status !== 'ok' && !betSlipOdd.priceChanged) {
      // console.log(`${updatedOutcomes[odd.id].price} vs ${odd.price}`);
      // console.log(`price changed ${priceChanged} so new price is ${updatedOutcomes[odd.id].american} vs ${odd.american}`);

      betSlipOdd.priceChanged = true;
      dispatch(updatePriceChange(betSlipOdd));

    }
    // console.log(odd);
    if (odd.status !== 'ok' && (!freeze && hasUpdates) || updatesAfterPlace) {
      classList.push('red');
    }

    if (odd.status !== 'ok' && !freeze && betSlipOdd.suspended) {
      if (!betSlipOdd.replacementBet) {
        const idx = classList.indexOf('red');
        if (idx >= 0) {
          classList.splice(idx, 1);
        }
        classList.push('grey');
      }
    }

    // odd changed but got back to its original values ...
    if (odd.status === 'ok' || betSlipOdd.replacementBet && betSlipOdd.display === betSlipOdd.replacementBet.display && betSlipOdd.price === betSlipOdd.replacementBet.price) {
      const idx = classList.indexOf('red');
      if (idx >= 0) {
        classList.splice(idx, 1);
      }
    }

    const isOddAccepted = betType === 0 ? odd.status === 'ok' : parlay.status === 'ok';

    const updatedOutcome = updatedOutcomes && updatedOutcomes[odd.id] && updatedOutcomes[odd.id];
    if (isMobile) {

      return (
        <OddMobile
          line={line}
          odd={odd}
          user={user}
          event={event}
          bRisk={bRisk}
          toWin={toWin}
          bToWin={bToWin}
          status={status}
          freeze={freeze}
          parlay={parlay}
          betType={betType}
          language={language}
          isLoading={isLoading}
          classList={classList}
          betSlipOdd={betSlipOdd}
          priceChanged={priceChanged}
          outcomeTitle={outcomeTitle}
          isOddAccepted={isOddAccepted}
          updatedOutcome={updatedOutcome}
          hasReplacementBet={hasReplacementBet}
          removeSelection={this.removeSelection}
        />
      );
    }

    let unknownError = odd.status && !language.errors[odd.status];

    return (
      <tr className={classList.join(' ')}>
        <td>

          <div>
            <h6 style={{padding: '0px', margin: '0px', fontSize: '12px', fontFamily: 'Ubuntu-Light'}}>
              <span> { prematchTimeConverter(event.time) }</span>
            </h6>
          </div>
          <div>{ !inverted ? home : away } { sep } { !inverted ? away : home }</div>
          <div>{ outcomeTitle }</div>
          <div>

            { hasReplacementBet ? betSlipOdd.replacementBet.display : odd.display }
            { hasReplacementBet && <span style={{fontSize: `12px`}} className="invalid">{odd.display}</span> }
            {/*odd.status*/}
            {
              freeze && odd.status !== 'ok' && <Price className="bet-slip__bet-price">{ betSlipOdd }</Price>
            }
            { !freeze && priceChanged && <Price className="bet-slip__bet-price invalid">{ odd }</Price> }
            { !freeze && odd.status !== 'ok' && <Price className="bet-slip__bet-price">{ betSlipOdd.replacementBet || priceChanged ? updatedOutcomes[odd.id] : betSlipOdd }</Price> }
            { odd.status === 'ok' && <Price className="bet-slip__bet-price">{ odd }</Price> }
            { odd.ticketNo && <div className="ticket-id" style={{fontFamily: 'Ubuntu-Light', fontSize: '12px'}}>Ticket # {odd.ticketNo}</div> }
          </div>
        </td>
        { (betType === BetType.straight || line === 0) &&
          [<td key={0} rowSpan={rowSpan}>
            { (!freeze && priceChanged) || status === 'lineOutOfDate' && <span className="invalid">{currencySymbol(user.currency, (+risk).toFixed(2))}</span> }
            <span>{currencySymbol(user.currency, (+bRisk).toFixed(2))}</span>
          </td>,
          <td key={1} rowSpan={rowSpan}>
            { (!freeze && priceChanged) || status === 'lineOutOfDate' && <span className="invalid">{currencySymbol(user.currency, (+toWin).toFixed(2))}</span> }
            <span>{currencySymbol(user.currency, (+bToWin).toFixed(2))}</span>
          </td>,
          <td key={2} rowSpan={rowSpan}>
            {
              betSlipOdd.suspended && !betSlipOdd.replacementBet ? 'Suspended' :

              (betSlipOdd.status === 'AdministrativelySuspendedLTD' || status === 'AdministrativelySuspendedLTD') ? 'Line has changed or is suspended.' : status ?
              language.errors[status] : language.errors.waiting
            }
            {
              unknownError && 'An unknown error occured when trying to place this wager. Please try again, or contact customer service for further assistance.'
            }
            </td>,
            <td key={3} style={ betType === BetType.straight ? {} : {display: 'none'} }>
              { (betType === BetType.straight) &&
                <div
                  onClick={!isOddAccepted && !isLoading ? this.removeSelection : undefined}
                  className={!isOddAccepted && !isLoading ? `bet-confirmation-remove` : `bet-confirmation-remove disabled`}>x
                </div>
              }
            </td>]
        }
        { (betType !== BetType.straight) && <td key={3}> <div onClick={!isOddAccepted && !isLoading ? this.removeSelection : undefined} className={!isOddAccepted && !isLoading ? `bet-confirmation-remove` : `bet-confirmation-remove disabled`}>x</div></td> }
      </tr>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => {

    const { odd, betSlipOdd, line, length } = ownProps;
    const { betType } = state.live;
    const isPrematch = odd.oddIsLive === 0;
    const events = isPrematch ? state.prematch.leagues : state.eventsFlatten.byId;

    const outcomeList = isPrematch ? state.prematch.outcomeList : state.live.outcomeList;
    // console.log(odd);
    const event = isPrematch ? events.filter((ev: any) => ev.id === ownProps.odd.event)[0] : events[ownProps.odd.event];

    if (event === undefined) {
     return '';
    }

    const { home, away, inverted } = event;

    const { parlay } = state.betConfirmation;
    const betSlipParlay = state.betSlip.parlay;

    return {
      odd,
      line,
      event,
      length,
      parlay,
      betType,
      betSlipOdd,
      betSlipParlay,
      user: state.user,
      loading: state.loading,
      betSlipOdds: state.betSlip.odds,
      odds: state.betConfirmation.odds,
      freeze: state.betConfirmation.freeze,
      language: state.languageData.betSlip,
      betslipUpdateOutcomes: state.live.betslipUpdateOutcomes,
      outcomeTitle: mapOutcomeTitle(home, away, inverted, null, outcomeList[event.sport][isPrematch ? 0 : 1][odd.outcome], odd)

    };
  }
)(Odd);
