import * as React from 'react';

const TennisIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    stroke: 'transparent',
    fill: 'transparent'
  };

  let bg = 'tennis-ball-bg';

  if (props.width) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
    bg += '-gc';
  }

  return (
    <svg {...svgProps}>
      <circle fill="#b0be52" cx="125" cy="125" r="125" />
      <path stroke="#f2f2f2" strokeWidth="10" d="M 44 30 A 40 104 0 0 1 44 220 M 206 30 A 40 104 0 0 0 206 220" />
    </svg>
  );
};

export default TennisIcon;
