import * as React from 'react';
import { connect } from 'react-redux';

import Link from '../../../../components/form/link';
import Time, { TimeFormat } from '../../../../components/form/time';
import Odd from '../odd';

import { getAsianOutcomesBySport, getPlaceholderByOutcome, AsianLine } from '../../../../constants';
import { homeAwayToLink } from '../../../../helpers';

import MarketCounter from './../../../../components/misc/market-counter';
import { mainOddsWithOdds } from './../../../../reducers/outcomes-flatten';

const Event = ({ path, event, outcomes, line }: any) => {
  const { time, home, away, sep, sport } = event;
  let lastOutcome: any;
  console.log(outcomes);
  const asianOutcomes = getAsianOutcomesBySport(sport, line);

  const odds = asianOutcomes.reduce((ret, outcomeId) => {

    const otc = outcomes && outcomes[outcomeId];
    let outcome: any[] = getPlaceholderByOutcome(outcomeId, '1', '2');

    outcome = outcome.map(o => {
      const id = otc ? Object.keys(otc).filter(key => otc[key].bet.slice(0, o.length) === o)[0] : '';
      const odd = id ? otc[id] : {};

      return Object.assign({}, odd, {
        id,
        outcome: outcomeId,
        event: event.id
      });
    });

    ret.push(outcome);

    return ret;
  }, [])
  .map((otc, i) => {
    const items = otc.map((odd: any, j: any) => {
      const oddClassList = ['live-event__odd'];
      const data = [];

      if (lastOutcome !== odd.outcome) {
        if (line === AsianLine.doubleLine) {
          data.push(<span className="asian__double-line">{odd.line}</span>);
        }
        else {
          data.push(<td className="asian__line">{odd.line}</td>);
        }

        lastOutcome = odd.outcome;
      }

      if (!odd || (!odd.hasOwnProperty('suspended') || odd.suspended)) {
        oddClassList.push('suspended');
      }

      if (line === AsianLine.doubleLine) {
        data.push(<span key={j}><Odd className="live-event__odd" odd={odd} asian={true} /></span>);
      }
      else {
        data.push(<td className="asian__odd" key={j}><Odd className="live-event__odd" odd={odd} asian={true} /></td>);
      }

      return data;
    });

    if (line === AsianLine.doubleLine) {
      return <td key={i} className="asian__odd"><div className="asian__double-view">{items}</div></td>;
    }
    else {
      return items;
    }
  });

  const className = line === AsianLine.doubleLine ? 'asian__double' : 'asian__single';

  const to = `/live/${path.split('/')[2]}/${homeAwayToLink(home, away)}`;
  return (
    <tr className={className}>
      <td className="asian__time">
        <Time format={TimeFormat.Time}>{ time * 1000 }</Time>
      </td>
      <td className="asian__event">
        <Link className={`live-event__link`} to={`/live/${path.split('/')[2]}/${homeAwayToLink(home, away)}`}>
          {
            line === AsianLine.simpleLine ?
              `${home} ${sep} ${away}` :
                [home, asianOutcomes.indexOf(1) !== -1 ? 'Draw' : undefined, away].filter(Boolean).map((item, i) => (
                  <div key={i} className="asian__team">{item}</div>
                ))
          }
          <MarketCounter
            isSmallDisplay={'asian'}
            event={event}
            to={to}
          />
        </Link>
      </td>
      { odds }
    </tr>
  );
};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    reducedOutcomes: mainOddsWithOdds(state)[ownProps.event.id] && mainOddsWithOdds(state)[ownProps.event.id][state.user.bookmakerId[ownProps.event.sport]],
    path: state.router.location.pathname,
    outcomes: state.live.outcomes[ownProps.event.id]
  })
)(Event);
