import * as React from 'react';
import BasketBall from '../../svgs/basketball-icon';
import { Animation } from '../helpers';

export default class Ball extends React.Component<any, any> {
  animation = new Animation(this);

  state = {
    animation: {
      translateX: {
        value: 0
      },
      translateY: {
        value: 0
      },
      rotate: {
        value: 0
      },
      width: {
        value: 4
      }
    }
  };

  animate = () => {
    this.animation.start('rotate', { value: 0 }, { value: 360 }, 1500, true);
  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    this.animation.cancel();
  }

  render() {
    let { x, y, width, height, scale } = this.props;
    [x, y, width, height] = [x, y, width, height].map(Number);

    const rotate = Math.round(this.state.animation.rotate.value);

    const translateX = this.state.animation.translateX.value;
    const translateY = this.state.animation.translateY.value;
    const size = 1.75 * scale;
    const half = size / 2;

    return (
      <svg x="-1" y="-1">
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`}>
          <g transform={`translate(${translateX}, ${translateY}) rotate(${rotate} ${x} ${y})`}>
            <BasketBall width={size} height={size} x={x - half} y={y - half} />
          </g>
        </svg>
      </svg>
    );
  }
}
