import { ISoccerFieldAnimationState, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';
import { gate } from './gate';
import { general } from './general';

const actionText = general()[0];

const t1Gate = gate(SoccerTeam.Team1);
const t2Gate = gate(SoccerTeam.Team2);

const t1ShotOffTargetStart: ISoccerFieldAnimationState = deepAssign({}, t1, {
  actionText: actionText.start.actionText,
  gate: t1Gate.end.gate,
  ball: {
    x: 55,
    y: 45,
    direction: BallDirection.None
  }
});

const t1ShotOffTargetEnd: ISoccerFieldAnimationState = deepAssign({}, t1ShotOffTargetStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 100,
    y: 32,
    scale: 2
  }
});

const t2ShotOffTargetStart: ISoccerFieldAnimationState = deepAssign({}, t2, {
  actionText: actionText.start.actionText,
  gate: t2Gate.end.gate,
  ball: {
    x: 50,
    y: 45,
    direction: BallDirection.None
  }
});

const t2ShotOffTargetEnd: ISoccerFieldAnimationState = deepAssign({}, t2ShotOffTargetStart, {
  actionText: actionText.end.actionText,
  ball: {
    x: 5,
    y: 32,
    scale: 2
  }
});

export function shotOffTarget(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam) {
  let start = nextTeam === SoccerTeam.Team1 ? t1ShotOffTargetStart : t2ShotOffTargetStart;
  let end = nextTeam === SoccerTeam.Team1 ? t1ShotOffTargetEnd : t2ShotOffTargetEnd;

  return [nextTeam === SoccerTeam.Team1 ? t1Gate : t2Gate, {
    start,
    end
  }];
}
