import User from '../middleware/api/user';
import { uuid, createRoute } from '../helpers';
import { push } from 'connected-react-router';

export const USER_SUCCESS = 'USER_SUCCESS';
export const USER_PING = 'USER_PING';
export const USER_CSRF_RECEIVED = 'USER_CSRF_RECEIVED';

export const LEGACY_REDIRECT = 'LEGACY_REDIRECT';

export const INTERACTION = 'INTERACTION';
export const IDLE_WINDOW = 'IDLE_WINDOW';
export const KILLED_SESSION = 'KILLED_SESSION';
export const ROOMS_LIST = 'ROOMS_LIST';

export const UPDATE_LAST_INTERACTION = 'UPDATE_LAST_INTERACTION';
export const SET_USER_ID_WS = 'SET_USER_ID_WS';
export const REJOIN_ROOMS = 'REJOIN_ROOMS';
export const CHANGE_LAST_SUBSCRIPTION_MESSAGE = 'CHANGE_LAST_SUBSCRIPTION_MESSAGE';
export const IDLE_ON_STREAM = 'IDLE_ON_STREAM';
export const TOGGLE_FAKE_FULL_SCREEN = 'TOGGLE_FAKE_FULL_SCREEN';
export const NATIVE_FULL_SCREEN = 'NATIVE_FULL_SCREEN';

export const nativeFullScreen = (payload: boolean) => ({
  type: NATIVE_FULL_SCREEN,
  payload
});

export const toggleFakeFullScreen = () => ({
  type: TOGGLE_FAKE_FULL_SCREEN
});

export const idleOnStream = (isIdle: boolean) => ({
  type: IDLE_ON_STREAM,
  payload: isIdle
});

const userSuccess = (user: any) => ({
  type: USER_SUCCESS,
  payload: user
});

const userPing = (user: any) => ({
  type: USER_PING,
  payload: user
});

const userCSRFReceived = (tokenValue: any) => ({
  type: USER_CSRF_RECEIVED,
  payload: tokenValue
});

export const redirectToNotLoggedInPage = () => push(createRoute(`/session-expired`));

export const makeSession: any = () => (dispatch: any) => {
  const user = new User(dispatch);

  user.makeSession().then((userData: any) => {
        // console.log(userData);
        dispatch(userSuccess(Object.assign(userData, {
          uuid: uuid()
        })));
    });
};

export const sessionPing: any = () => (dispatch: any, getState: any) => {
  const { user } = getState();
  const user_interface = new User(dispatch);

  // In case we are looking at gamecast-mock ; no redirect to session-expired

  if (window && window.location.pathname.includes('gamecast-mock')) return;

  user_interface.sessionPing(user).then((userData: any) => {

    if (!userData.status || userData.status !== 'ok') {
      // deleteCookie('CSESSID');
      console.log('rinns ?');
      dispatch(redirectToNotLoggedInPage());
    }

    dispatch(userPing(Object.assign(user, {
      tmp_ts: new Date().getTime()
    })));
  });
};

export const setCSRFHeader = (token_value: any) => (dispatch: any, getState: any) => {
  dispatch(userCSRFReceived(token_value));
};

export const isRedirecting = () => ({
  type: LEGACY_REDIRECT,
  payload: true
});

export const legacyRedirect = () => (dispatch: any) => {
  const user = new User(dispatch);

  return user.legacyRedirect().then((res: any) => {
    if (res !== undefined) {
      dispatch(isRedirecting());
      window.location.replace(res.url);
    }
  });
};

export const killedSession = (payload: any) => ({
  type: KILLED_SESSION,
  payload
});

export const isIdleWindow = (payload: any) => ({
  type: IDLE_WINDOW,
  payload
});

export const removeUser = () => (dispatch: any, getState: any) => {
  const user_interface = new User(dispatch);

  user_interface.killSession()
  .then((data: any) => {
    console.log('session killed', data);
    // deleteCookie('CSESSID');
  }).catch((err: any) => console.error(err));
};

export const updateLastInteractionTime = (payload: any) => ({
  type: UPDATE_LAST_INTERACTION,
  payload
});

export const lastInteraction: any = (time: any) => (dispatch: any, getState: any) => {
  console.log(`sending lastInteraction`, time);
  dispatch(updateLastInteractionTime(time));
  // getState().wsm.send(`${INTERACTION}_${time}`);
  let msg = `${INTERACTION}_${time}`;
  getState().wsm.socket.emit('message', msg);
};

export const setUserIDWS = () => ({
  type: SET_USER_ID_WS
});

export const setUserID: any = (csessid: string) => (dispatch: any, getState: any ) => {

  setTimeout(() => {
    if (!getState().user.userid_operator) return;
    let account = getState().user.userid_operator;
    let msg = `${SET_USER_ID_WS}-${account}-${csessid}`;
    getState().wsm.socket.emit('message', msg);
    // getState().wsm.send(`${SET_USER_ID_WS}-${account}-${csessid}`);
  }, 5000);

};

export const rejoinRooms: any = (roomsList: any[]) => (dispatch: any, getState: any) => {
  let msg = `${REJOIN_ROOMS}_${roomsList.join(':')}`;
  getState().wsm.socket.emit('message', msg);
};

export const checkSessionOnIdle: any = (ts: any) => (dispatch: any, getState: any) => {
  const user_interface = new User(dispatch);

  if (!getState().user.userid_operator || window.location.pathname.includes('expired')) return;

  let account = getState().user.userid_operator;
  user_interface.checkOnIdleSession(ts, account)
    .then((data: any) => {
      // console.log('checker data', data);

      if (data.status === 'Open') {
        dispatch(isIdleWindow(false));
      }

      if (data.status === 'Warning') {
        dispatch(isIdleWindow(true));
      }

      if (data.status === 'Terminated') {
        // deleteCookie('CSESSID');
        dispatch(isIdleWindow(false));
        dispatch(killedSession(true));
      }

      // console.log('idle window message received', data.payload);
    }).catch((err: any) => console.log(err));
};
