import { ISoccerFieldAnimationState, SoccerAction, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextTeam: SoccerTeam, nextAction: SoccerAction) {
  let ballX = 52.5;
  let ballY = nextTeam === SoccerTeam.Team1 ? 68 : 0;

  if (nextAction === SoccerAction.DangerousThrowIn) {
    ballX += (26.25 * (nextTeam === SoccerTeam.Team1 ? 1 : -1));
  }

  let actionX = nextTeam === SoccerTeam.Team1 ? ballX - 1 : ballX + 1;
  let actionY = nextTeam === SoccerTeam.Team1 ? 62 : 15;

  return {
    actionText: {
      x: actionX,
      y: actionY
    },
    ball: {
      x: ballX,
      y: ballY,
      direction: BallDirection.Goal
    }
  };
}

const t1HalfwayThrowIn: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.HalfWayThrowIn));
const t2HalfwayThrowIn: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.HalfWayThrowIn));
const t1DangerousThrowIn: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1, SoccerAction.DangerousThrowIn));
const t2DangerousThrowIn: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2, SoccerAction.DangerousThrowIn));

export function throwIn(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  let start: ISoccerFieldAnimationState;
  let end: ISoccerFieldAnimationState;

  if (nextAction === SoccerAction.HalfWayThrowIn) {
    end = nextTeam === SoccerTeam.Team1 ? t1HalfwayThrowIn : t2HalfwayThrowIn;
  }
  else {
    end = nextTeam === SoccerTeam.Team1 ? t1DangerousThrowIn : t2DangerousThrowIn;
  }

  start = deepAssign({}, end, {
    actionText: state.actionText,
    ball: {
      x: state.ball.x,
      y: state.ball.y,
      direction: BallDirection.None
    }
  });

  return [{
    start,
    end
  }];
}
