import * as React from 'react';
import { connect } from 'react-redux';
import Odd from '../../odd';
import { getAsianOutcomesBySport, getPlaceholderByOutcome, AsianLine } from '../../../../../constants';
import { homeAwayToLink } from '../../../../../helpers';
import { addToBetSlip } from '../../../../../actions/bet-slip';

class AsianOdds extends React.Component<any, any> {

  addToBetSlip = (e: React.MouseEvent<any>, odd: any) => {
    const { dispatch } = this.props;
    if (odd && odd.suspended === false) {
      dispatch(addToBetSlip(odd));
      e.nativeEvent.stopImmediatePropagation();
    }
  }

  render() {

    const { event, line, outcomes, isLeft, isSimple, isInBetSlip } = this.props;
    const { sport } = event;
    let lastOutcome: any;

    const asianOutcomes = getAsianOutcomesBySport(sport, line);

    const odds = asianOutcomes.reduce((ret, outcomeId) => {

      const otc = outcomes && outcomes[outcomeId];
      let outcome: any[] = getPlaceholderByOutcome(outcomeId, '1', '2');

      outcome = outcome.map(o => {
        const id = otc ? Object.keys(otc).filter(key => otc[key].bet.slice(0, o.length) === o)[0] : '';
        const odd = id ? otc[id] : {};

        return Object.assign({}, odd, {
          id,
          outcome: outcomeId,
          event: event.id
        });
      });

      ret.push(outcome);

      return ret;
    }, [])
    .map((otc, i) => {

      let numbers;
      if (line !== 2) {
        numbers = isSimple ? [0, 1] : [2, 3];
      } else {
        numbers = !isSimple ? [0, 2] : [3, 5];
      }

      const shouldDisplay = line !== 2 ? isLeft && i === numbers[0] || !isLeft && i === numbers[1] :
      isLeft && i < numbers[0] || !isLeft && i > numbers[1];

      if (shouldDisplay) {
        const items = otc.map((odd: any, j: any) => {
          const oddClassList = ['live-event__odd_'];
          const data = [];

          if (lastOutcome !== odd.outcome) {
            if (line !== AsianLine.doubleLine) {
              data.push(<div key={odd.id} className="asian__line_">{odd.line}</div>);
            }

            lastOutcome = odd.outcome;
          }

          if (!odd || (!odd.hasOwnProperty('suspended') || odd.suspended)) {
            oddClassList.push('suspended');
          }

          if (isInBetSlip.some((odd_: any) => odd_.id === odd.id)) {
            oddClassList.push('added');
          }

          if (line === AsianLine.doubleLine) {
            oddClassList.push('double');

            const hasLine = j % 2 === 0 && (odd.line !== undefined && odd.line !== '');

            const oddLine = (odd: any) => {
              return (
                <div
                  key={j}
                  onClick={(e) => this.addToBetSlip(e, odd)}
                  className={`asian__odd__line ${oddClassList.join(' ')} ${hasLine ? 'show' : 'hide'}`}>
                  {j % 2 === 0 &&
                    <div className={`asian__double-line_`}>{odd.line}</div>
                  }
                  <Odd className="live-event__odd_ double-line" odd={odd} asian={true} />
                </div>
              );
            };

            data.push(oddLine(odd));

          }
          else {

            const asianOdd = (odd: any, j: any) => {
              return(
                <div className={`asian__odd_ ${oddClassList.join(' ')}`} key={j}>
                  <Odd className="live-event__odd_" odd={odd} asian={true} />
                </div>
              );
            };

            data.push(asianOdd(odd, j));
          }

          return data;
        });

        if (line === AsianLine.doubleLine) {

          const cells = (side: any) => {

            const text = i === 0 || i === 3 ? ['H', 'A'] : ['O', 'U'];

            return (
              <div className={`item ${side}`}>
                <div className={`item_ top ${sport === 1 && 'soccer'}`}>{text[0]}</div>
                { sport === 1 && side === 'left' && <div className="item_ mid">D</div>}
                <div className={`item_ bottom ${sport === 1 && text[1] === 'A' ? 'soccer' : ''}`}>{text[1]}</div>
              </div>
            );
          };

          return (
            <div key={i} className="asian__odd_ double_line">
              <div className="double_odd">
                { (i === 0 || i === 3) &&
                  cells('left')
                }
                <div className={`odds-cont ${i === 1 ? 'no-text' : ''}`}>
                  {items}
                </div>
                { (i === 2 || i === 5) &&
                  cells('right')
                }
              </div>
            </div>
          );
        }
        else {
          return items;
        }
      }
    });

    return(
      <div className="asian-odds">{odds}</div>
    );
  }

}

export default connect(
  (state: any, ownProps: any) => ({
    isInBetSlip: state.betSlip.odds
  })
)(AsianOdds);
