import {
  GET_CRICKET_ANIMATIONS_SUCCESS,
  GET_FOOTBALL_ANIMATIONS_SUCCESS,
  GET_BASKETBALL_ANIMATIONS_SUCCESS
} from '../actions/animations';

export const animationsInitialState: any = {
  'cricket': [],
  'football': [],
  'basketball': []
};

export const animations = (state = animationsInitialState, action: any) => {
  switch (action.type) {
    case GET_CRICKET_ANIMATIONS_SUCCESS:
      return {
        ...state,
        'cricket': action.payload
      };
    case GET_FOOTBALL_ANIMATIONS_SUCCESS:
      return {
        ...state,
        'football': action.payload
      };
    case GET_BASKETBALL_ANIMATIONS_SUCCESS:
      return {
        ...state,
        'basketball': action.payload
      };
  }

  return state;
};
