import { Store } from 'redux';
import config from '../config';
import { getCookie, deleteCookie } from './../common/helpers';

import { LIVE_SCHEDULE, schedule } from '../common/actions/schedule';
import { LIMITS, updateLimits } from '../common/actions/bet-slip';
import {
  LIVE_EVENTS_FLATTEN, liveEventsFlatten,
  NEW_EVENT_FLATTEN, newEventFlatten,
  REMOVE_EVENT_FLATTEN, removeEventFlatten,
  MARKET_COUNT_UPDATE, updateMarketCount,
  LIVE_EVENTS_ASIAN, liveEventsAsian,
  VIDEO_URL_UPDATE, updateVideoUlr,
  HAS_GAMECAST_UPDATE, hasGamecastUpdate
} from '../common/actions/events-flatten';

import {
  UPDATE_MAIN_OUTCOMES,
  updateMainOutcome, deleteMainOutcome,
  getMainOutcomes, addMainOutcome,
  UPDATE_EV_OUTCOMES,
  updateEvOutcome, deleteEvOutcome,
  getEvOutcomes, addEvOutcome,
  UPDATE_ASIAN_OUTCOMES, getAsianOutcomes,
  deleteAsianOutcome, updateAsianOutcome,
  addAsianOutcome,
  deletePrematchOutcome, updatePrematchOutcome,
  addPrematchOutcome

} from '../common/actions/outcomes';

import {
  NEW_EVENT, newEvent,
  REMOVE_EVENT, removeEvent,
  UPDATE_OUTCOMES, updateOutcomes,
  UPDATE_OUTCOME_TITLE, updateOutcomeTitle,
  UPDATE_EVENT_OUTCOMES, updateEventOutcomes,

  DELETE_OUTCOME, deleteOutcome,
  ADD_OUTCOME, addOutcome,

  UPDATE_BETSLIP_OUTCOMES, updateBetslipOutcomes,
  UPDATE_EVENT_GAMECAST, updateEventGamecast,
  UPDATE_EVENT_SCORE, updateEventScore,
  UPDATE_EVENT_CLOCK, updateEventClock,
  CLOSED_CONNECTION_LIVE, closedConnectionLive,
  CLOSED_CONNECTION_PREMATCH, closedConnectionPrematch,
  EVENT_UPDATE, updateEvent,
  checkGameClocks,
  LIVE_CLOCKS, liveClocks, OPEN_MARKETS_UPDATE, openMarketsUpdate,
  SUBSCRIBE_SPORT_DATA, subscribeSportData
} from '../common/actions/live';

import {
  GET_PREMATCH, prematchEvents,
  PREMATCH_LEAGUES, getLeagues,
  NEW_PREMATCH_EVENT, newPrematchEvent,
  REMOVE_EVENT_PREMATCH, removeEventPrematch,
  updatePrematchList, UPDATE_PREMATCH, updatePrematch,
  UPDATE_PREMATCH_OUTCOMES, getPrematchEvOutcomes
} from '../common/actions/prematch';

import {
  redirectToNotLoggedInPage,
  isIdleWindow,
  lastInteraction,
  killedSession,
  removeUser,
  KILLED_SESSION,
  IDLE_WINDOW,

  checkSessionOnIdle,
  setUserID,
  rejoinRooms,
  ROOMS_LIST
} from '../common/actions/user';

import {
  MAINTENANCE_CHECKER,
  maintenanceChecker
} from '../common/actions/maintenance';

import { finished } from '../common/actions/loading';

// import { gamecastMock } from './../common/components/views/gamecast-mock/mock-data';

class SocketIOManager {
  socket: SocketIO.Socket = require('socket.io-client')(`https://${location.hostname}${location.port ? `:${location.port}` : '' }`, {
    path: `${config.host.trail}/socket.io`,
    reconnection: true,
    reconnectionDelay: 5000,
    reconnectionDelayMax: 15000,
    transports: config.hasWebSocketsConnection ? ['polling', 'websocket'] : ['polling']
  });
  store: Store<any> = null;
  lastInteractionTimmer: any = false;
  clockCheckInterval: any = false;
  lastInteraction: number;
  idleTimmerInterval: any;
  lastKnownRooms: any[] = [];

  hasBothFeedsOnMaintenance: boolean = false;

  configureSocketManager(store: Store<any>) {

    this.store = store;
    console.log('looking for socket io on');
    console.log(`https://${location.hostname}${location.port ? `:${location.port}` : '' }${config.host.trail}`);
    console.log(this.socket);
    let _state = this.store.getState();
    if (_state.live.killedSession)  return;

    let getInteractions = () => {

      let _state = this.store.getState();
      if (_state.live.isIdleWindow) return;

      let time = Date.now();
      this.store.dispatch(lastInteraction(time));

    };

    // Prevent starting a connection on session expired

    if (this.store.getState().maintenance.live.maintenance && this.store.getState().maintenance.prematch.maintenance) {
      if (this.socket) {
        console.log('hey!!!');
        this.hasBothFeedsOnMaintenance = true;
        this.socket.disconnect(true);
      }
    } else {
      this.hasBothFeedsOnMaintenance = false;
    }

    if (window.location.pathname.includes('session-expired') || !getCookie('CSESSID') || this.store.getState().user.killedSession) {
      // deleteCookie('CSESSID');
      if (this.socket) {
        this.socket.disconnect(true);
      }

      if (!window.location.pathname.includes('gamecast-mock')) {
        this.store.dispatch(killedSession(true));
        this.store.dispatch(checkSessionOnIdle('terminate'));
        this.store.dispatch(redirectToNotLoggedInPage());
      }

      return;
    }

    this.socket.on('connect', () => {

      this.socket.on('message', this.handleMessage);
      this.store.dispatch(closedConnectionLive(false));
      this.store.dispatch(closedConnectionPrematch(false));

      // Will send user.userid_operator & CSSEID to server to check illegitimate
      // connections to ws

      this.store.dispatch(setUserID(getCookie('CSESSID')));

      if (this.clockCheckInterval !== false)
        clearInterval(this.clockCheckInterval);
      this.clockCheckInterval = setInterval(() => {
        this.triggerClockCheck();
      }, 1000);

      document.addEventListener('click', getInteractions);

      // Connect event is taken as the first interaction - user got on ti UI, user refreshed browser, lost connection
      let time = Date.now();
      this.store.dispatch(lastInteraction(time));
      this.store.dispatch(checkSessionOnIdle(store.getState().user.lastInteractionTime));

      this.idleTimmerInterval = setInterval(function() {

        let lastInteractionTime = store.getState().user.lastInteractionTime;
        // This sends to the server the lastInteractionTime for this user. Server will grab the CSESSID
        // from request headers. This is used to check idle time per CSESSID
        // even for users with more than one tab open

        store.dispatch(checkSessionOnIdle(lastInteractionTime));
        // console.log('check session for', CSESSID);
        // console.log(store.getState().user.lastInteractionTime);
      }, 10000);

      // let count = 0;

      // setInterval(() => {
      //   this.store.dispatch(updateEventGamecast(gamecastMock[count * 10]));
      //   if (count * 13 >= gamecastMock.length) {
      //     console.log('reset ...');
      //     count = 0;
      //   }
      //   count ++;
      //   console.log('update gamecast');
      // }, 2000);

    });

    const THAT = this;

    this.socket.on('disconnect', (reason: any) => {

      console.log('disconnectio');
      if (!this.hasBothFeedsOnMaintenance) {
        this.store.dispatch(closedConnectionLive(true));
        this.store.dispatch(closedConnectionPrematch(true));
      }

      if (reason === 'io server disconnect') {

        console.log(`Disconnected from sio - server-side disconnect`);

        document.removeEventListener('click',  getInteractions);

        if (this.idleTimmerInterval !== false)
          clearInterval(this.idleTimmerInterval);

        if (this.clockCheckInterval !== false)
          clearInterval(this.clockCheckInterval);
      }
      else {
        console.log(`Disconnected from sio for reason other than server-side disconnect`);
      }
      // else the socket will automatically try to reconnect
    });

    this.socket.on('reconnect', (attemptNumber) => {
      console.log(`SIO reconnected on ${attemptNumber} attempt.  Re-subscribe to: ${THAT.lastKnownRooms.join(',')}`);

      // THAT.store.dispatch(rejoinRooms(THAT.lastKnownRooms));
      const lastSubscribeMessage = THAT.store.getState().subscriptionCache.subscriptionMsg || '';
      console.log(`Last subscribe before disconnect: ${lastSubscribeMessage}`);
      if (lastSubscribeMessage.length > 0)
        THAT.socket.emit('message', lastSubscribeMessage);
    });
  }

  handleMessage = (e: string) => {
    let dat: any = false;
    try {
      dat = JSON.parse(e);
    }
    catch (_err) {
      console.log(`handleMessage error:`, _err);
    }

    const data = dat;
    const msg = data.type;

    if (msg === UPDATE_MAIN_OUTCOMES || msg === UPDATE_EV_OUTCOMES || msg === UPDATE_ASIAN_OUTCOMES || msg === UPDATE_PREMATCH_OUTCOMES) {
      // Payload may be empty.
      // Note:  Payload === false may happen if the payload is the logical value `false`, hence the conditional below.
      if (!data || (!data.payload && data.payload !== false)) {
        console.log(`Ignored message ${msg} with empty payload.  ${data}`);
        return;
      }

      let msgType = msg.split('_')[1];
      let otcGroup = ['MAIN', 'EV', 'ASIAN', 'PREMATCH'].indexOf(msgType);
      // console.log(msgType, 'otcGroup', otcGroup);
      // If payload has 'ADD', 'DELETE' or 'UPDATE' keys, then its an update from already loaded sports main outcomes or event outcomes.
      // We need to loop the update payload here, if we do it inside reducer, we are forced to copy-clone state risking to render the whole document on each update batch

      if (data.payload['ADD'] || data.payload['DELETE'] || data.payload['UPDATE']) {

        Object.keys(data.payload).forEach(type => {
          Object.keys(data.payload[type]).forEach(odd => {

            switch (type) {
              case 'ADD':
                let addOutcome = [addMainOutcome, addEvOutcome, addAsianOutcome, addPrematchOutcome][otcGroup];
                this.store.dispatch(addOutcome({ [odd]: data.payload[type][odd] }));
                break;
              case 'DELETE':
                let deleteOutcome = [deleteMainOutcome, deleteEvOutcome, deleteAsianOutcome, deletePrematchOutcome][otcGroup];
                this.store.dispatch(deleteOutcome({ [odd]: data.payload[type][odd] }));
                break;
              case 'UPDATE':
                let updateOutcome = [updateMainOutcome, updateEvOutcome, updateAsianOutcome, updatePrematchOutcome][otcGroup];
                this.store.dispatch(updateOutcome({ [odd]: data.payload[type][odd] }));
                break;
              default:
                break;
            }
          });
        });
        return;

      }

      // Otherwise its because user switched sports or went to an event, so we are sending all outcomes for that sport (main) / event
      // console.log('dispatch get outcomes');
      let getOutcomes = [getMainOutcomes, getEvOutcomes, getAsianOutcomes, getPrematchEvOutcomes][otcGroup];
      this.store.dispatch(getOutcomes(data.payload));
      return;

    } else {
      // console.log(data.type);
      switch (data.type) {
        case SUBSCRIBE_SPORT_DATA:
          this.store.dispatch(subscribeSportData(data.payload));
          break;
        case MARKET_COUNT_UPDATE:
          Object.keys(data.payload).forEach(evId => {
            let payload = {
              ...data.payload[evId],
              evId
            };
            this.store.dispatch(updateMarketCount(payload));
          });
          break;
        case OPEN_MARKETS_UPDATE:
          this.store.dispatch(openMarketsUpdate(data.payload));
          break;
        case LIVE_EVENTS_FLATTEN:
          this.store.dispatch(liveEventsFlatten(data.payload));
          break;
        case LIVE_EVENTS_ASIAN:
          this.store.dispatch(liveEventsAsian(data.payload));
          break;
        case NEW_EVENT_FLATTEN:
          this.store.dispatch(newEventFlatten(data.payload));
          break;
        case REMOVE_EVENT_FLATTEN:
          this.store.dispatch(removeEventFlatten(data.payload));
          break;
        case LIVE_SCHEDULE:
          this.store.dispatch(schedule(data.payload));
          break;
        case LIVE_CLOCKS:
          // console.log(data.payload);
          this.store.dispatch(liveClocks(data.payload));
          break;
        case UPDATE_EVENT_GAMECAST:
          this.store.dispatch(updateEventGamecast(data.payload));
          break;
        case UPDATE_EVENT_CLOCK:
          this.store.dispatch(updateEventClock(data.event, data.payload));
          break;
        case UPDATE_EVENT_SCORE:
          this.store.dispatch(updateEventScore(data.event, data.payload));
          break;
        case UPDATE_EVENT_OUTCOMES:
          this.store.dispatch(updateEventOutcomes(data.payload));
          break;
        case UPDATE_OUTCOME_TITLE:
          this.store.dispatch(updateOutcomeTitle(data.sport, data.payload));
          break;
        case UPDATE_BETSLIP_OUTCOMES:
          this.store.dispatch(updateBetslipOutcomes(data.payload));
          break;
        case LIMITS:
          this.store.dispatch(updateLimits(data.payload));
          break;
        case GET_PREMATCH:
          this.store.dispatch(prematchEvents(data.payload));
          break;
        case PREMATCH_LEAGUES:
          this.store.dispatch(finished());
          this.store.dispatch(getLeagues(data.payload));
          break;
        case NEW_PREMATCH_EVENT:
          this.store.dispatch(newPrematchEvent(data.payload));
          break;
        case UPDATE_PREMATCH:
          this.store.dispatch(updatePrematch(data.payload));
          break;
        case CLOSED_CONNECTION_LIVE:
          this.store.dispatch(closedConnectionLive(data.payload));
          break;
        case CLOSED_CONNECTION_PREMATCH:
          this.store.dispatch(closedConnectionPrematch(data.payload));
          break;
        case KILLED_SESSION:
          this.store.dispatch(killedSession(data.payload));
          // this.store.dispatch(removeUser());
          this.store.dispatch(redirectToNotLoggedInPage());
          console.log('server kills connection', data.payload);
          break;
        case ROOMS_LIST:
          this.lastKnownRooms = data.payload;
          break;
        case VIDEO_URL_UPDATE:
          console.log(VIDEO_URL_UPDATE);
          this.store.dispatch(updateVideoUlr(data.payload));
          break;
        case MAINTENANCE_CHECKER:
          this.store.dispatch(maintenanceChecker(data.payload));
          break;
        case HAS_GAMECAST_UPDATE:
          this.store.dispatch(hasGamecastUpdate(data.payload));
          break;

      }

    }

    // console.log('a message has been received to handleMessage', msg);

  }

  triggerClockCheck() {
    let _state = this.store.getState();
    if (_state.eventsFlatten && _state.live.clocks) {
      let subscribedSport = _state.live.subscribedSport;
      let events = _state.eventsFlatten && _state.eventsFlatten.bySport && _state.eventsFlatten.bySport[subscribedSport];
      // console.log(_state.live.clocks[1002206641]);
      // console.log('from triggerClockCheck');
      this.store.dispatch(checkGameClocks({ events, clocks: _state.live.clocks }));
    }
  }
}

export default SocketIOManager;
