import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../../../config';

import { Sports,  GetSportName } from '../../../../../constants';

import BaseballIcon from '../../../../svgs/baseball-icon';
import BasketballIcon from '../../../../svgs/basketball-icon';
import IceHockeyIcon from '../../../../svgs/ice-hockey-icon';
import SoccerIcon from '../../../../svgs/soccer-icon';
import TennisIcon from '../../../../svgs/tennis-icon';
import CricketIcon from '../../../../svgs/cricket-icon';
import AmericanFootballIcon from '../../../../svgs/american-football-icon';
import RugbyIcon from '../../../../svgs/rugby-icon';

import Link from '../../../../form/link';
import TypeSwitcher from '../../type-switcher';
import SwitchNew from './../../../../misc/switch-new';
import PrematchLink from './../../../../misc/prematch-link';
import { changeBetType } from './../../../../../actions/live';

const icons: any = {
  [Sports.americanFootball]: <AmericanFootballIcon />,
  [Sports.baseball]: <BaseballIcon />,
  [Sports.basketball]: <BasketballIcon />,
  [Sports.iceHockey]: <IceHockeyIcon />,
  [Sports.soccer]: <SoccerIcon />,
  [Sports.tennis]: <TennisIcon />,
  [Sports.cricket]: <CricketIcon />,
  [Sports.rugbyUnion]: <RugbyIcon />,
  [Sports.rugbyLeague]: <RugbyIcon />
};

class SelectedSportBar extends React.Component<any, any> {

  state = {
    betOptions: false
  };

  changeBet = () => {
    const { dispatch } = this.props;
    return dispatch(changeBetType(+!this.props.betType));
  }

  render() {

    let { view, path, sport, betType, isMobileOn, parlayBySport, hasVideoOnFullScreen } = this.props;

    if (hasVideoOnFullScreen) return null;

    const selectedSport = sport !== undefined ? GetSportName(sport).toUpperCase().replace('-', ' ') : '';
    let isPrematch = path.includes('prematch');
    // console.log(view);
    let prematchButtClass = ['prematch-link-desktop-cont'];
    if (config.hasParlay && parlayBySport) {
      prematchButtClass.push('has-parlay');
    }
    return (
      <div>
        <div className="selected-sport-bar">
          <div className="sport-box">
            <div className="sports__icon">{ icons[sport] }</div>
            <div className="selected-sport">{selectedSport}</div>
          </div>
          {
            config.hasPrematchLink && !isMobileOn && (view === 3)  &&
            <div className={`switch-live-events prematch-switch ${ config.hasParlay && parlayBySport ? 'has-parlay' : ''} `}>
              <Link
                activeClassName=""
                to={`${isPrematch ? '/' : '/prematch'}`}
              >
                <SwitchNew
                  className_={`live-event-switch`}
                  checked_={isPrematch}
                  leftText={config.switchLiveText}
                  rightText={config.switchPrematchText}
                  onToggle={() => console.log('{}')}
                />
              </Link>
            </div>
          }
         { config.hasParlay && parlayBySport &&
              <div className="switch-live-events parlays-switch">
                <SwitchNew
                  leftText={'Straight'}
                  rightText={'Parlays'}
                  checked_={betType === 1}
                  onToggle={() => this.changeBet()}
                  className_={'live-event-switch'}
                />
              </div>
            }
        </div>

      </div>
    );
    }
  }

export default connect(
  (state: any, ownProps: any) => {

    const pathState = state.router.location.pathname;
    const path = pathState.slice(pathState.lastIndexOf('/') + 1, pathState.length);

    return {
      path,
      view: state.settings.view,
      betType: state.live.betType,
      language: state.languageData.betSlip,
      isMobileOn: state.uiToolbar.isMobileOn,
      parlayBySport: state.live.parlayBySport,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
    };
  }
)(SelectedSportBar);
