import { IceHockeyTeam, IHockeyFieldAnimationState, t1, t2 } from  '../constants';
import { deepAssign } from '../../../../helpers';

export const t1PossessionStart: IHockeyFieldAnimationState = t1;
export const t1PossessionEnd: IHockeyFieldAnimationState = deepAssign({}, t1, {
  polygon: {
    translateX: 0
  },
  actionText: {
    x: 85
  },
  jersey: {
    x: 40
  },
  brick: {
    x: 89,
    y: 32,
    rotate: -270
  }
});

export const t2PossessionStart: IHockeyFieldAnimationState = t2;
export const t2PossessionEnd: IHockeyFieldAnimationState = deepAssign({}, t2, {
  polygon: {
    translateX: 200,
    scaleX: -1
  },
  actionText: {
    x: 115
  },
  jersey: {
    x: 120
  },
  brick: {
    x: 114,
    y: 32,
    rotate: 450
  }
});

export function possession(nextTeam: IceHockeyTeam) {
  return [{
    start: nextTeam === IceHockeyTeam.Team1 ? t1PossessionStart : t2PossessionStart,
    end: nextTeam === IceHockeyTeam.Team1 ? t1PossessionEnd : t2PossessionEnd
  }];
}
