import * as React from 'react';
import Event from './event';
import PrematchDateHeader from './../../misc/prematch-date-header';

const className = 'live-item';

const League = ({ events, view }: any) => (
  <div className={className}>
    <h4 className={`${className}__title`}>
      { events[0].league }
    </h4>
    <div className="live-events">
      {
        events.map((ev: any, i: any) => {
          return (
            <div key={ev.id}>
              { ev && ev.live === 0 && <PrematchDateHeader events={events} ev={ev} i={i} view={`classic`}/> }
              <Event event={ev} ticker={false} view={view}/>
            </div>
          );
        })
      }
    </div>
  </div>
);

export default League;
