import * as React from 'react';
import { connect } from 'react-redux';
import SportsItem from './sports-item';

class SportsList extends React.Component<any, any> {
  render() {
    const { user, openMarkets, filterStreaming } = this.props;
    const bookmakerId  = !user ? {} : user.bookmakerId;

    let openMarkets_ = openMarkets.map((item: any) => {

      let qt = filterStreaming ? item.hasStream : item.qt;

      return {
        sport: item.sport,
        qt: qt[bookmakerId[item.sport]]
      };
    }).filter((item: any) => item.qt > 0);

    if (!openMarkets_) {
      return <div className="sports__list"></div>;
    }

    return (
      <div className="sports__list">
        {
          openMarkets_.map((sport: any)  =>
            <SportsItem key={sport.sport} sport={sport.sport || ''} qt={sport.qt || 0} />
          )
        }
      </div>
    );
  }
}

export default connect(
  (state: any) => {
    return {
      user: state.user,
      openMarkets: state.live.openMarkets,
      filterStreaming: state.live.filterStreaming
    };
  }
)(SportsList);
