import * as React from 'react';
import Price from './../../../../../components/form/price';

const PrematchMobileOdd = ({ sportsList, odd, addToBetSlip, oddDisplay, event, classList }: any) => {

  const isOTB = !oddDisplay || odd.suspended || odd.status === 'TemporarilySuspended';

  if (sportsList) {
    return (
      <div id={odd.id} className="prematch odd-button" onClick={addToBetSlip}>
        <div className={classList.join(' ')} style={{minHeight: '40px'}}>

          {/*
            isHandicap &&
            <span>{odd.display}</span>
          */}
          { odd.line !== '' && <div style={{position: 'relative', top: '3px', fontSize: '1em'}}>{ odd.line }</div> }

          <span style={{position: 'relative', top: `${odd.line !== '' ? '0' : '5px'}`, fontSize: `${odd.line !== '' ? '11px' : '1em'}`}}>
            { !isOTB ? <Price>{ odd }</Price> : 'OTB' }

          </span>
        </div>
      </div>
    );
  } else {
    return (
      <div id={odd.id} onClick={addToBetSlip}>
        <div className={classList.join(' ')} style={{minHeight: '45px'}}>
          <span>
            { !isOTB ? <Price>{ odd }</Price> : 'OTB' }
          </span>
        </div>
        <span >{ !isOTB ? oddDisplay : '' }</span>
      </div>
    );
  }
};

export default PrematchMobileOdd;
