import { IHockeyFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const generalStart: IHockeyFieldAnimationState = deepAssign({}, nt, {
 extras: {
    x: 77,
    opacity: 1
  }
});

const generalEnd: IHockeyFieldAnimationState = deepAssign({}, nt, {
  actionText: {
    x: 100,
    y: 30
  },
  extras: {
    x: 77,
    y: 35,
    opacity: 1
  }
});

export function general() {
  return [{
    start: generalStart,
    end: generalEnd
  }];
}
