import * as React from 'react';
import { connect } from 'react-redux';
import { homeAwayToLink } from '../../../../../helpers';
import { GetSportName } from '../../../../../constants';
import Link from '../../../../../components/form/link';
import MarketCounter from './../../../../../components/misc/market-counter';
import ClockComponent from './../../../../../components/misc/clock-component';
import { prematchTimeConverter } from './../../live-helpers';

import ClockIcon from './../../../../../components/svgs/clock-icon';
import LiveStreamButton from './../../../../misc/live-stream-button';

const LinkEventNew = ({ event, path, score, clock, numberMarkets }: any) => {
  const { home, away, sport } = event;
  const { min, sec }: any = clock || {};
  const sportName = GetSportName(sport);

  const isPrematch = event.live === 0;
  const to = isPrematch  ? `/prematch/${sportName}/${event.id}/${homeAwayToLink(home, away)}` :
  `/live/${sportName}/${homeAwayToLink(home, away)}`;

  const scoreTeam1 = score !== undefined ? score.scoreTeam1 : 0;
  const scoreTeam2 = score !== undefined ? score.scoreTeam2 : 0;

  const getTeam = (team: any, score: any) => {
    let scoreReal = score;
    if (sport === 9)
      scoreReal = '';
    return (
      <div className={`team ${sportName}`} style={{padding: isPrematch && '0px'}}>
        <div className="team-name">{team}</div>
        {
          !isPrematch &&
            <div
              style={ score > 99 ? { fontSize: '9px' } : {} }
              className={`team-score ${sportName} score-real-20190717`}>
                { typeof scoreReal === 'string' ? scoreReal.replace('All Out', 'AO') : scoreReal }
              </div>
        }
    </div>
    );
  };

  let ClockComp: any = () => isPrematch ? <div></div> : <ClockComponent clock={clock} sport={sport} score={score} isMobile={true}/>;

  return (
    <Link to={to} title="click to view more odds">
      <div className="link-table">
        {event.inverted ? getTeam(away, scoreTeam1) : getTeam(home, scoreTeam1)}
        {event.inverted ? getTeam(home, scoreTeam2) : getTeam(away, scoreTeam2)}
        <LiveStreamButton event={event} liveStreamStyles={{fontSize: '12px', marginBottom: '5px'}}/>
        <div className="game-clock" style={{padding: (sport === 7 || isPrematch) && '0px', paddingBottom: isPrematch && '3px'}}>
        { sport !== 7 && sport !== 37 && sport !== 11 && <ClockComp /> }
        { sport === 7 &&

          <div className="tennis-score-container">
            {
              score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                if (item[0] !== '') {
                  return (
                    <div className="tennis-score-box" key={i}>
                    <div>{item[0]} - {item[1]}</div>
                    </div>
                  );
                }
                return '';
              })
            }
             <div style={{position: 'relative', top: '5px'}}>
              {
                isPrematch &&
                  <span>
                    <span><ClockIcon /></span>
                    <span style={{position: 'relative', top: '-4px', left: '1px'}}>
                      {`${prematchTimeConverter(event.time).split(' ')[3]}`}
                    </span>
                  </span>
              }
              <MarketCounter event={event} to={to} isSmallDisplay={true}/>
            </div>
          </div>
          }
          { sport === 17 && <div className="tennis-score-container">
            {
              score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                if (item.t1 !== '' && item.t1 !== '') {
                  return (
                    <div className="tennis-score-box" key={i}>
                    <div>{item.t1} - {item.t2}</div>
                    </div>
                  );
                }
                return '';
              })
            }

            </div>
          }
          { sport === 37 &&
            <>
              <div className="tennis-score-container">
                  {
                    score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                      if (item.t1 !== '' && item.t2 !== '' && item.description !== 'Total') {
                        return (
                          <div className="tennis-score-box" key={i}>
                          <div>{item.t1} - {item.t2}</div>
                          </div>
                        );
                      }
                      return '';
                    })
                  }
                <div style={{position: 'relative', top: '5px'}}>{isPrematch && <span><span><ClockIcon /></span><span style={{position: 'relative', top: '-4px', left: '1px'}}>{`${prematchTimeConverter(event.time).split(' ')[3]}`}</span></span>}</div>
              </div>
             <ClockComp />
             <MarketCounter event={event} to={to} isSmallDisplay={true}/>
            </>
          }
          { sport === 9 && <div className="tennis-score-container">
            {
              score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                // console.log('link-event-new', score.scoreByPeriod);
                const validDescriptions = ['1st', '2nd', '3rd', '4th', '5th'];
                if (item.description && typeof item.team1_data1 !== 'undefined' && typeof item.team2_data1 !== 'undefined' && validDescriptions.indexOf(item.description) >= 0 && item.team1_data1 !== '' && item.team2_data1 !== '') {
                  return (
                    <div className="tennis-score-box" key={i}>
                    <div>{item.team1_data1} - {item.team2_data1}</div>
                    </div>
                  );
                }
                return '';
              })
            }
             <div style={{position: 'relative', top: '5px'}}>
               {
                isPrematch &&
                  <span>
                    <span><ClockIcon /></span>
                    <span style={{position: 'relative', top: '-4px', left: '1px'}}>
                      {`${prematchTimeConverter(event.time).split(' ')[3]}`}
                    </span>
                  </span>
                }
                <MarketCounter event={event} to={to} isSmallDisplay={true}/>
              </div>
            </div>
          }
          { sport !== 7 && sport !== 9 && sport !== 37 &&
            <div style={{position: 'relative', top: '5px'}}>
              {
                isPrematch &&
                  <span>
                    <span><ClockIcon /></span>
                    <span style={{position: 'relative', top: '-4px', left: '1px'}}>
                      {`${prematchTimeConverter(event.time).split(' ')[3]}`}
                    </span>
                  </span>
                }
                <MarketCounter event={event} to={to} isSmallDisplay={true}/>
              </div>
          }
        </div>
      </div>
    </Link>

  );
};

export default connect(
  (state: any, initialOwnProps: any) => Object.assign({}, initialOwnProps, {
    path: state.router.location.pathname,
    score: state.live.scores[initialOwnProps.event.id],
    clock: state.live.clocks[initialOwnProps.event.id],
    numberMarkets: state.live.numberMarkets
  })
)(LinkEventNew);
