import * as React from 'react';

let color = '#333';

const GolfIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    strokeWidth: 2,
    fill: 'transparent',
    stroke: color
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  const coords = [
    [33, 139], [60, 150], [86, 158], [111, 165], [138, 165], [164, 158], [190, 150], [216, 139],
    [45, 170], [73, 184], [98, 190], [125, 189], [150, 190], [176, 184], [202, 170], [86, 210],
    [112, 215], [137, 215], [164, 210]
  ];

  return (
    <svg {...svgProps}>
      <circle cx="125" cy="125" r="123" fill="#fff" />
      {coords.map((item, i) => <circle key={i} cx={item[0]} cy={item[1]} r="6" fill={color} />)}
    </svg>
  );
};

export default GolfIcon;
