import * as React from 'react';

import { getAnimations } from './actions';
import { BasketballAction, BasketTeam, actionTextBasket } from './constants';
import { Animation } from '../helpers';

import Basket from './basket';

import TimeOut  from './timeout-icon';
import Referee from './referee';
// import Jersey from './jersey';

import Ball from './ball';
import Spot from './spot';

import TeamSplit from '../team-split';
import TeamSplitter from '../team-split/helpers';

export class BasketballCourt extends React.Component<any, any> {
  animation = new Animation(this);
  promise: Promise<any|void> = Promise.resolve();

  state: any = {
    splitTeams: [],
    svgHeight: 'auto',
    animation: getAnimations(null, this.props.team, this.props.action)[0].start,
    action: this.props.action
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 94, height = 50 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentWillReceiveProps(newProps: any) {
    // console.log(newProps);
    // console.log(this.props);
    if (newProps.gamecastSize !== this.props.gamecastSize) {
      this.handleResize();
    }

    if (newProps.team1 !== this.props.team1 || newProps.team2 !== this.props.team2) {
      this.setState({splitTeams: [new TeamSplitter(newProps.team1).resultArr, new TeamSplitter(newProps.team2).resultArr]});
    }

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {

      this.promise = getAnimations(this.state.animation, this.props.team, this.props.action, newProps.team, newProps.action)
        .reduce((all: any, next: any) => (
          all.then(() => Promise.all(Object.keys(next.start).map(key => {
            this.setState({
              action: newProps.action
            });
            return this.animation.start(key, next.start[key], next.end[key], 750);
          })))
        ), this.promise);
    }

  }

  get actionText() {
    let actionText = actionTextBasket(this.props.action, this.props.matchData);
    switch (this.props.action) {
      case 1074:
      case 1075:
        return actionText.split('Scored')[0];
      case 1094:
      case 1095:
        return actionText.split('Missed')[0];
      case 1078:
      case 1079:
      case 1080:
        return actionText.split(' ')[0] + actionText.split(' ')[1];
      default:
        return actionText;
    }
  }

  get scoreText() {
    switch (this.state.action) {
      case BasketballAction.FreeThrowScored:
      case BasketballAction.TwoPointShootScored:
      case BasketballAction.ThreePointShootScored:
        return 'SCORED';
      case BasketballAction.FreeThrowMissed:
      case BasketballAction.TwoPointShootMissed:
      case BasketballAction.ThreePointShootMissed:
        return 'MISSED';
    }

    return '';
  }

  freeThrows(team: string, x: number, y: number, textAnchor: string) {
    let { matchData, team1 } = this.props;
    let who, x1, x2;

    // In case we get strange string format...
    const transformMatchData = (str: string) => {

      let res = '';

      try {
        res = str.split(':').filter((it: any) => it <= 3).join(':');
      }

      catch (err) {
        console.log(err);
        // console.log('matchData unknown format error');
      }

      return res !== '' ? res : str;

    };

    try {

      matchData = transformMatchData(matchData);
      return matchData && matchData
        .split(':')
        .map((obj: any, i: any) => {
          who = team === team1 ? (x1 = -5, x2 = 0 ) : (x1 = 0, x2 = 10);

          if (i === 0) {
            return <text x={x + x1} y={y + 5} style={{fontSize: '5px'}} stroke="#fff" fill="#fff" textAnchor={textAnchor} key={i}>{obj} of </text>;
          } else {
            return <text x={x + x2} y={y + 5} style={{fontSize: '5px'}} stroke="#fff" fill="#fff" textAnchor={textAnchor} key={i}>{obj}</text>;
          }
        });

    }

    catch (err) {
      console.log(err);
      return null;
    }

  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === BasketTeam.Team1 ? event.inverted ? team2 : team1 : team === BasketTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  componentDidMount() {

    this.setState({splitTeams: [new TeamSplitter(this.props.team1).resultArr, new TeamSplitter(this.props.team2).resultArr]});
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillUnmount() {

    this.animation.cancel();
    window.removeEventListener('resize', this.handleResize);
  }

  render() {
    const { action, width = 94, height = 50, event, team } = this.props;
    const { animation, splitTeams } = this.state;
    const { polygon, actionText, ball, shootSpot, extras, /*jersey, */twoPointPath, threePointPath, alternText, scoreText } = animation;
    const [mainColor, secondaryColor, keyColor, strokeColor] = ['#cc9f55', '#c99755', '#cc7755', '#dab577'];

    let className = 'gc-basketball';

    let splitTeam;

    if (event) {
      splitTeam = team === BasketTeam.Team1 ? event.inverted ? splitTeams[1] : splitTeams[0] : team === BasketTeam.Team2 ? event.inverted ? splitTeams[0] : splitTeams[1] : '';
    }

    const w = (p: any) => width * p / 94;
    const h = (p: any) => height * p / 50;

    const threePointsArea = `M 0 ${h(3)} H ${w(14)} C ${w(34)} ${h(11)}, ${w(34)} ${h(39)}, ${w(14)} ${h(47)} H 0 z`;
    const microLines = `M ${w(7)} ${h(16)} L ${w(7)} ${h(17)} M ${w(8)} ${h(16)} L ${w(8)} ${h(17)} M ${w(11)} ${h(16)} L ${w(11)} ${h(17)} M ${w(14)} ${h(16)} L ${w(14)} ${h(17)}`;

    const polygonPoints = `-1 -1, ${(width / 2) - 1} -1, ${w(62)} ${h(25)}, ${(width / 2) - 1} ${height + 1}, -1 ${height + 1}`;

    return (
      <div ref="gc" className={className} >
        <svg className={`${className}__court`} viewBox={`-1 -1 ${width + 2} ${height + 2}`} fill="transparent" stroke={strokeColor} strokeWidth="0.3">
          <defs>
            <pattern id="bg-basketball-court" patternUnits="userSpaceOnUse" width="4" height="1">
              <g strokeWidth="0">
                <rect width="2" height="1" fill={mainColor} />
                <rect width="2" height="1" x="2" fill={secondaryColor} />
              </g>
            </pattern>
            <mask id="three-point" x="0" y="0" width={width} height={height}>
              <rect x="0" y="0" width={width} height={height} fill="#fff" />
              <path strokeWidth="0" d={threePointsArea} fill="black"/>
            </mask>
          </defs>
          <rect x="-1" y="-1" fill={secondaryColor} strokeWidth="0" width={width + 2} height={height + 2} />
          <rect x="0" y="0" width={width} height={height} fill="url(#bg-basketball-court)" />
          <circle cx={w(47)} cy={h(25)} r="6" fill={mainColor} />
          <circle cx={w(47)} cy={h(25)} r="2" />
          <path d={`M ${w(47)} 0 L ${w(47)} ${h(50)}`} />
          <path d={`M ${w(28)} 0 L ${w(28)} ${h(3)} M ${w(66)} 0 L ${w(66)} ${h(3)} M ${w(28)} ${h(50)} L ${w(28)} ${h(47)} M ${w(66)} ${h(50)} L ${w(66)} ${h(47)}`} />
          <path d={threePointsArea} />
          <path d={threePointsArea} transform={`scale(-1, 1) translate(-${width})`} />
          <path d={threePointsArea} fill="#dd6000" opacity={twoPointPath.opacity} strokeWidth="0" transform={`translate(${twoPointPath.x}, 0) rotate(${twoPointPath.rotate} 0 25)`} />
          <path d={`M ${w(19)} ${h(19)} A 6 6 0 0 1 ${w(19)} ${h(31)}`} fill={mainColor} />
          <path d={`M ${w(75)} ${h(19)} A 6 6 0 0 0 ${w(75)} ${h(31)}`} fill={mainColor} />
          <rect x="0" y={h(17)} width={w(19)} height={h(16)} fill={mainColor} />
          <rect x={w(75)} y={h(17)} width={w(19)} height={h(16)} fill={mainColor} />
          <rect x="0" y={h(19)} width={w(19)} height={h(12)} fill={keyColor} />
          <rect x={w(75)} y={h(19)} width={w(19)} height={h(12)} fill={keyColor} />
          <g strokeDasharray="1, 1">
            <path d={`M ${w(19)} ${h(19)} A 6 6 0 0 0 ${w(19)} ${h(31)}`} />
            <path d={`M ${w(75)} ${h(19)} A 6 6 0 0 1 ${w(75)} ${h(31)}`} />
          </g>
          <path d={`M 0 ${h(14)} L 0.5 ${h(14)} M 0 ${h(36)} L 0.5 ${h(36)} M ${w(94)} ${h(14)} L ${w(93.5)} ${h(14)} M ${w(94)} ${h(36)} L ${w(93.5)} ${h(36)}`} />
          <path d={microLines} />
          <path d={microLines} transform={`scale(-1, 1) translate(-${width})`} />
          <path d={microLines} transform={`scale(1, -1) translate(0, -${height})`} />
          <path d={microLines} transform={`scale(-1, -1) translate(-${width}, -${height})`} />
          <path d={`M ${w(13)} ${h(20)} L ${w(13)} ${h(20.5)} M ${w(13)} ${h(30)} L ${w(13)} ${h(29.5)} M ${w(81)} ${h(20)} L ${w(81)} ${h(20.5)} M ${w(81)} ${h(30)} L ${w(81)} ${h(29.5)}`} />
          <Basket x="3" y={h(21.5)} size="6" />
          <g transform={`scale(-1, 1) translate(-${width})`}>
            <Basket x="3" y={h(21.5)} size="6" />
          </g>
          <rect x="0" y="0" width={width} height={height} fill="#dd6000" opacity={threePointPath.opacity} mask="url(#three-point)" transform={`rotate(${threePointPath.rotate} ${w(47)} ${h(25)})`} />
          <polygon fill="#666" opacity="0.3" strokeWidth="0" transform={`scale(${w(polygon.scaleX)}, 1) translate(${w(polygon.translateX)})`} points={polygonPoints} />
          {
            this.team && (action !== BasketballAction.Half1 && action !== BasketballAction.Half2 && action !== BasketballAction.EndOfQuarter && action !== BasketballAction.Quarter1 && action !== BasketballAction.Quarter2 && action !== BasketballAction.Quarter3 && action !== BasketballAction.Quarter4 && action !== BasketballAction.EndOfHalf && action !== BasketballAction.EndOfMatch) ?
              <TeamSplit
                size="5"
                splitIndex="18"
                team={this.team}
                x={actionText.x}
                y={actionText.y - 5}
                splitTeam={splitTeam}
                textAnchor={actionText.textAlign}
                br="0" bx="0" by="-10" /> :
              <rect x={w(12)} y={h(actionText.y) - 8} rx="2" ry="2" width={w(69)} height={h(12)} fill="rgba(55, 55, 55, 0.25)" strokeWidth="0" stroke="rgba(55, 55, 55, 0.5)" />
          }
          <text wordSpacing="0.5" x={w(actionText.x)} y={h(actionText.y)} strokeWidth="0" stroke="#333" fill="#fff" textAnchor={actionText.textAlign} style={{ fontSize: '6px' }}>{this.actionText}</text>
          <rect x={w(32.5) } y={h(scoreText.y) - 5} rx="2" ry="2" width={w(30)} height={h(7)} fill="rgba(55, 55, 55, 0.25)" strokeWidth="0" stroke="rgba(55, 55, 55, 0.5)" />
          <text x={w(alternText.x)} y={h(alternText.y)} strokeWidth="0" stroke="#333" fill="#fff" textAnchor={alternText.textAlign} style={{ fontSize: '4px' }}>{/* TODO: see if we have alternative text info (e.g 1 of 2) */}</text>
          <text x={w(scoreText.x)} y={h(scoreText.y)} strokeWidth="0" stroke="#333" fill="#fff" textAnchor={scoreText.textAlign} style={{ fontSize: '4px' }}>{ this.scoreText }</text>
          {
            (action === BasketballAction.FreeThrow || action === BasketballAction.FreeThrowScored || action === BasketballAction.FreeThrowMissed) &&
            this.freeThrows(this.team, actionText.x, actionText.y, actionText.textAlign)
          }
          {
            action === BasketballAction.Foul &&
              <g opacity={extras.opacity}>
                <Referee x={w(extras.x)} y={h(extras.y)} width={w(14.5)} height={h(14.5)} transform={`scale(${w(extras.scale)}, 1)`} />
              </g>
          }
          {
            action === BasketballAction.TimeOut &&
              <g opacity={extras.opacity}>
                <TimeOut x={w(extras.x)} y={h(extras.y)} width={w(14.5)} height={h(14.5)} transform={`scale(${w(extras.scale)}, 1)`}/>
              </g>
          }
          <Spot x={w(shootSpot.x)} y={h(shootSpot.y)}/>
          <Ball x={ball.x} y={ball.y} width={width} height={height} scale={ball.scale} />
          {/* <Jersey x={w(jersey.x)} y={h(jersey.y)} width={w(14.5)} height={h(14.5)} transform={`scale(${jersey.scale})`}/> */}
        </svg>
      </div>
    );
  }
}
