import * as React from 'react';
import { connect } from 'react-redux';
import Price from '../../form/price';
import { BetType } from '../../../constants';
import { prematchTimeConverter } from './../live/live-helpers';
import { currencySymbol } from '../../../currencySymbols/currencySymbol';

export const getOldDisplay = (display: any) => {
  const isValue = display.indexOf('+') >= 0 ?
  display.indexOf('+') :
  display.indexOf('-') >= 0 ? display.indexOf('-') : false;

  if (!isValue) {
    return display;
  }

  return display.slice(0, isValue);
};

const OddMobile = ({ betSlipOdds, line, updatedOutcome, isOddAccepted, hasReplacementBet, parlay, betType, classList, odd, freeze, user, isLoading, outcomeTitle, priceChanged, language, betSlipOdd, event, bRisk, toWin, bToWin, status, removeSelection  }: any) => {
  const { home, sep, away, inverted } = event;

  let cellsClass = [];

  classList.push('bet-confirmation-mobile');
  const statusClass = [`bet-status`];
  const isRed = (!freeze && hasReplacementBet || !freeze && priceChanged) ||
    odd.status && odd.status !== 'ok';
  const isGreen = odd.status === 'ok';

  if (isGreen) {
    statusClass.push(`green`);
  }

  if (isRed) {
    statusClass.push('red');
  }

  let unknownError = odd.status && !language.errors[odd.status];

  if (betType === 1) {
    cellsClass.push('parlay');
  }

  let hasParlayLayout = betType !== BetType.straight && betSlipOdds.length > 1;
  // console.log(betSlipOdds.length);
  return (
    <div>
      <div className={classList.join(' ')}>
        <div className="bet-info">
          <div>
            <p style={{padding: '0px', margin: '0px', fontSize: '12px', fontFamily: 'Ubuntu-Light'}}>
              <span> { prematchTimeConverter(event.time) }</span>
            </p>
          </div>
          <span className="bet-display"> { hasReplacementBet ? betSlipOdd.replacementBet.display : odd.display } </span>
          <span>
            {
              freeze && odd.status !== 'ok' && <Price className="bet-slip__bet-price">{ betSlipOdd }</Price>
            }
            { !freeze && priceChanged && <Price className="bet-slip__bet-price invalid">{ odd }</Price> }
            { !freeze && odd.status !== 'ok' && <Price className="bet-slip__bet-price">{ updatedOutcome && (betSlipOdd.replacementBet || priceChanged) ? updatedOutcome : betSlipOdd }</Price> }
            { odd.status === 'ok' && <Price className="bet-slip__bet-price">{ odd }</Price> }
          </span>

          { hasReplacementBet && <div className="invalid">{odd.display}</div> }

          <div className="bet-title">{ outcomeTitle }</div>
          <div className="bet-event">{ !inverted ? home : away } { sep } { !inverted ? away : home }</div>
          { (betType === BetType.straight || line === 0) &&
            <div className={statusClass.join(' ')}>
              <span>
                Status:
                {
                  language.errors[status] ? language.errors[status] :
                  unknownError ? 'An unknown error occured when trying to place this wager. Please try again, or contact customer service for further assistance.' :
                  status === 'AdministrativelySuspendedLTD' || isRed ? 'Line has changed or is suspended.' : language.errors.waiting
                }
              </span>
              { odd.ticketNo && <div className="ticket-id" style={{fontFamily: 'Ubuntu-Light', fontSize: '10px'}}>Ticket # {odd.ticketNo}</div> }
            </div>
          }
        </div>
        { (betType === BetType.straight || line === 0) ?

          <div className="price-info" style={ hasParlayLayout ? { position: 'relative', top: `${betSlipOdds.length * 25 }px`, background: '#fff'} : {}}>

          <div className="price-type">
            <span className="price-title">RISK</span>
            <div >{currencySymbol(user.currency, (+bRisk).toFixed(2))}</div>
              {
                (!freeze && priceChanged) || status === 'lineOutOfDate' &&
                  <div className="price-title invalid">
                    {currencySymbol(user.currency, (+bRisk).toFixed(2))}
                  </div>
              }
            </div>

            <div className="price-type">
              <span className="price-title">TO WIN</span>
              <div className="price-currency">{currencySymbol(user.currency, (+bToWin).toFixed(2))}</div>

                { (!freeze && priceChanged) || status === 'lineOutOfDate' &&
                  <div className="price-currency invalid">
                    {currencySymbol(user.currency, (+toWin).toFixed(2))}
                  </div>
                }
            </div>

          </div> :
          <div style={{width: '100px'}}></div>
        }
        <div
          onClick={!isOddAccepted && !isLoading ? removeSelection : undefined}
          className={!isOddAccepted && !isLoading ? `bet-confirmation-remove` : `bet-confirmation-remove disabled`}>
            <span>x</span>
        </div>
      </div>
    </div>
  );
};

export default connect(
  (state: any, ownProps: any) => {
    const { betType } = state.live;
    const betSlipOdds = state.betSlip.odds;

    return {
      betType,
      betSlipOdds
    };
   }
  )(OddMobile);
