import { IBasketFieldAnimationState, BasketTeam, BasketballAction } from '../constants';
import { shoot } from './shoot';
import { scored } from './shoot/scored';
import { missed } from './shoot/missed';

function next(nextTeam: BasketTeam) {
  const isTeam1 = nextTeam === BasketTeam.Team1;

  return shoot({
    actionText: {
      x: isTeam1 ? 64 : 30
    },
    shootSpot: {
      x: isTeam1 ? 65 : 29,
      y: isTeam1 ? 40 : 10
    },
    threePointPath: {
      x: isTeam1 ? 180 : 0,
      opacity: 0.4
    },
    ball: {
      x: isTeam1 ? 65 : 29,
      y: isTeam1 ? 40 : 10
    }
  }, nextTeam);
}

const t1ThreePointShoot = next(BasketTeam.Team1);
const t2ThreePointShoot = next(BasketTeam.Team2);

export function threePoints(state: IBasketFieldAnimationState, nextTeam: BasketTeam, nextAction: BasketballAction): (any|void) {
  const tpShoot = nextTeam === BasketTeam.Team1 ? t1ThreePointShoot : t2ThreePointShoot;

  switch (nextAction) {
    case BasketballAction.ThreePointShootScored:
      return [
        tpShoot,
        scored(tpShoot.end, nextTeam)
      ];
    case BasketballAction.ThreePointShootMissed:
      return [
        tpShoot,
        missed(tpShoot.end, nextTeam)
      ];
  }
}
