import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const AmericanFootball = ({ data }: any) => {
  const { title, abandoned, changes, timeliness, overtime, double, teamToCall, total, penalty, teamPassing, teamRushing, fourth } = data;
  return (
    <RulePart title={title} model="americanFootball">
      <RuleItem title={abandoned.title}>
        {abandoned.text}
      </RuleItem>
      <RuleItem title={changes.title}>
        {changes.text}
      </RuleItem>
      <RuleItem title={timeliness.title}>
        {timeliness.text}
      </RuleItem>
      <RuleItem title={overtime.title}>
        {overtime.text}
      </RuleItem>
      <RuleItem title={double.title}>
        {double.text}
      </RuleItem>
      <RuleItem title={teamToCall.title}>
        {teamToCall.text}
      </RuleItem>
      <RuleItem title={total.title}>
        {total.text}
      </RuleItem>
      <RuleItem title={penalty.title}>
        {penalty.text}
      </RuleItem>
      <RuleItem title={teamPassing.title}>
        {teamPassing.text}
      </RuleItem>
      <RuleItem title={teamRushing.title}>
        {teamRushing.text}
      </RuleItem>
      <RuleItem title={fourth.title}>
        {fourth.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.americanFootball })
)(AmericanFootball);
