import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const IceHockey = ({ data }: any) => {
  const { title, threeWay, markets, marketsPrematch } = data;
  return (
    <RulePart title={title} model="iceHockey">
      <RuleItem title={threeWay.title}>
        {threeWay.text}
      </RuleItem>
      <RuleItem title={markets.title}>
        <p>
          {markets.text.p1}
        </p>
        <ul>
          <li>{markets.text.item1}</li>
          <li>{markets.text.item2}</li>
          <li>{markets.text.item3}</li>
          <li>{markets.text.item4}</li>
          <li>{markets.text.item5}</li>
          <li>{markets.text.item6}</li>
        </ul>
      </RuleItem>
      <RuleItem title={marketsPrematch.title}>
        <p>
          {marketsPrematch.text.p1}
        </p>
        <ul>
          <li>{marketsPrematch.text.item1}</li>
          <li>{marketsPrematch.text.item2}</li>
          <li>{marketsPrematch.text.item3}</li>
          <li>{marketsPrematch.text.item4}</li>
        </ul>
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.iceHockey })
)(IceHockey);
