import * as React from 'react';
import { connect } from 'react-redux';

import StreamIcon from './../../svgs/stream-icon';
import GamecasIconSwitch from './../../svgs/gamecast-icon-switch';

class VideoPlayerHeader extends React.Component<any, any> {

  render() {

    let {
      event,
      isVideo,
      handleSwitch,
      hasVideoOnFullScreen
    } = this.props;

    // console.log(event);

    if (hasVideoOnFullScreen || !event.hasGameCast) return null;

    let wrapperStyle: any = {
      padding: '10px',
      background: '#333',
      color: '#fff'
    };

    if (event && event.sport === 24) {
      wrapperStyle = {
        ...wrapperStyle,
        position: 'relative',
        marginTop: '25px'
      };
    }

    let switchLabelStyles: any = {
      position: 'relative',
      display: 'inline-block',
      width: '50px',
      height: '25px',
      backgroundColor: '#212121',
      borderRadius: '20px',
      border: '1px solid #212121',
      transition: 'all 0.3s'
    };

    return (
      <div className="wrapper-header" style={ wrapperStyle }>
        <div style={ {display: 'flex', justifyContent: 'space-between'} }>
          <div className="switch-tv" style={{width: '100%'}}>

            <div className="slide-container" style={{display: 'flex', position: 'relative', float: 'right'}}>
              <div
                style={{fontSize: '10px', position: 'relative', marginTop: '5px', marginRight: '3px', cursor: 'pointer'}}
                onClick={() => handleSwitch('vid-switch')}
              >Live Stream
              </div>
              <input
                type="checkbox"
                checked={isVideo}
                id={`toggle_`}
                className="checkbox"
                onChange={() => handleSwitch(`${isVideo ? 'gc-switch' : 'vid-switch'}`)} />
              <label
                htmlFor={`toggle_`}
                style={switchLabelStyles}
                >
                  <div style={{display: 'flex'}}>
                    <div style={{position: 'relative', width: '18px', top: '2px', left: !isVideo ? '27px' : '3px'}} >
                      { isVideo ? <StreamIcon /> : <GamecasIconSwitch /> }
                    </div>
                    <span style={{position: 'relative', top: '5px', left: !isVideo ? '0px' : '10px', fontSize: '10px'}}>
                      { isVideo ? '' : '' }
                    </span>
                  </div>
              </label>
              <span
                onClick={() => handleSwitch('gc-switch')}
                style={{fontSize: '10px', position: 'relative', marginTop: '5px', marginLeft: '3px', cursor: 'pointer'}}>Gamecast</span>
            </div>

          </div>
        </div>
      </div>
    );
  }
}

export default connect(
  (state: any) => {
    return {
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
    };
  })(VideoPlayerHeader);
