import * as React from 'react';
import StreamIcon from './../svgs/stream-icon';

class LiveStream extends React.Component<any, any> {

  state = {};

  render() {

    const {
      event,
      theme,
      handleOnClick,
      liveStreamStyles
    } = this.props;

    if (!event || event.videoUrl === '' || event.videoSource <= 0) return null;

    let styles: any = {
      cursor: 'pointer',
      textAlign: 'start',
      fontFamily: 'Ubuntu-Light'
    };

    if (liveStreamStyles) {
      styles = {
        ...styles,
        ...liveStreamStyles
      };
    }

    return (
      <div
        id={event.id}
        style={styles}
        onClick={handleOnClick}
        className="live-stream-button">
        <div style={{display: 'flex'}}>
          <div style={{width: '20px'}}>
            <StreamIcon />
          </div>
          <span style={{padding: '3px', fontSize: '10px'}}>Live Stream</span>
        </div>
      </div>
    );
  }
}

export default LiveStream;
