export function hourScoreToValue(score: string): number {
  switch (score) {
    case '0':
      return 0;
    case '15':
      return 1;
    case '30':
      return 2;
    case '40':
      return 3;
    case 'A':
      return 4;
    default:
      return +score;
  }
}

export function actionTextTennis(action: TennisAction) {
  switch (action) {
    case TennisAction.Serving:
      return 'Serving';
    case TennisAction.Point:
      return 'Point';
    case TennisAction.Fault:
      return 'Fault';
    case TennisAction.DoubleFault:
      return 'Double Fault';
    case TennisAction.Ace:
      return 'Ace';
    case TennisAction.BreakPoint:
      return 'Break Point';
    case TennisAction.Game:
    case TennisAction.ShowScore:
      return 'Game';
    case TennisAction.LetFirstServe:
      return 'Let - First Serve';
    case TennisAction.LetSecondServe:
      return 'Let - Second Serve';
    case TennisAction.GameSetMatch:
      return 'Game, Set and Match';
    case TennisAction.InjuryBreak:
      return 'Injury';
    case TennisAction.Challenge:
      return 'Challenge';
    case TennisAction.ChallengeSuccess:
      return 'Challenge Success';
    case TennisAction.ChallengeFail:
      return 'Challenge Fail';
    case TennisAction.EndOfSet:
      return 'End of Set';
    case TennisAction.TieBreak:
      return 'Tie Break';
    case TennisAction.RainDelay:
      return 'Rain Delay';
    case TennisAction.FirstSet:
      return 'First Set';
    case TennisAction.SecondSet:
      return 'Second Set';
    case TennisAction.ThirdSet:
      return 'Third Set';
    case TennisAction.FourthSet:
      return 'Fourth Set';
    case TennisAction.FinalSet:
      return 'Final Set';
  }
  return '';
}
