import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Snooker = ({ data }: any) => {
  const { title, session, race, total, leader } = data;
  return (
    <RulePart title={title} model="snooker">
      <RuleItem title={session.title}>
        <p>
          {session.text.p1}
        </p>
        <p>
          {session.text.p2}
        </p>
        <ol>
          <li>
            <strong>{session.text.item1.title} - </strong>
            <span>{session.text.item1.text}</span>
          </li>
          <li>
            <strong>{session.text.item2.title} - </strong>
            <span>{session.text.item2.text}</span>
          </li>
          <li>
            <strong>{session.text.item3.title} - </strong>
            <span>{session.text.item3.text}</span>
          </li>
          <li>
            <strong>{session.text.item4.title} - </strong>
            <span>{session.text.item4.text}</span>
          </li>
        </ol>
      </RuleItem>
      <RuleItem title={race.title}>
        {race.text}
      </RuleItem>
      <RuleItem title={total.title}>
        {total.text}
      </RuleItem>
      <RuleItem title={leader.title}>
        {leader.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.snooker })
)(Snooker);
