import * as React from 'react';
import DialogTitle from './dialog-title';
import DialogContent from './dialog-content';
import DialogButtons from './dialog-buttons';

import config from './../../../../config';

export interface IDialogProps {
  title: string;
  open: boolean;
  className?: string;
  children?: any;
  iframeValues?: any;
}

const Dialog = ({iframeValues, title = '', open = false, className, children }: IDialogProps) => {
  if (process.env.NODE_ENV !== 'production') {
    if (Array.isArray(children)) {
      if (children[0].type !== DialogContent) {
        throw new TypeError('The first child of a Dialog must be a DialogContent component');
      }
      if (children[1].type !== DialogButtons) {
        throw new TypeError('The second child of a Dialog must be a DialogButtons component');
      }
    }
    else if (React.isValidElement(children)) {
      if (children.type !== DialogContent) {
        throw new TypeError('When using a single child element in a Dialog, it must be of tye DialogContent');
      }
    }
    else {
      throw new TypeError(`A Dialog must have predefined children. They must be similar to the examples below:
        1- Two children
        <Dialog>
          <DialogContent>
            Your content goes here.
          </DialogContent>
          <DialogButtons>
            <Button>OK</Button>
          </DialogButtons>
        </Dialog>

        2- One child
        <Dialog>
          <DialogContent>
            Your content goes here.
          </DialogContent>
        </Dialog>
      `);
    }
  }

  const cn = 'md-dialog';
  const classList = [cn];
  const outerClassList = [`${cn}__outer`];

  if (open) {
    outerClassList.push(`${cn}--open`);
  }

  if (className) {
    classList.push(className);
    outerClassList.push(className.split('-')[0]);
  }
  // console.log(iframeValues);

  let styleWindow: any = {};

  if (!config.hasMaxViewHeight) {
    styleWindow.position = 'absolute';

    if (iframeValues && iframeValues.scrollTop && iframeValues.scrollTop > 0) {
      styleWindow.top = (iframeValues.scrollTop) + 175 + 'px';
    } else {
      styleWindow.top = 200 + 'px';
    }
  }
  // console.log(style_);

  return (
    <div className={outerClassList.join(' ')}>
      <section id="dialogue-outter" style={styleWindow} className={classList.join(' ')}>
        <DialogTitle text={title} />
        {children}
      </section>
    </div>
  );
};

export default Dialog;
