import * as React from 'react';
import { connect } from 'react-redux';
import * as moment from 'moment-timezone';
import { Dialog, DialogContent, DialogButtons } from './../form/dialog';
import Button from '../form/button';
import Table from '../form/table';
import Link from '../form/link';
import { reqLiveEvents } from '../../actions/live';
import { reqPrematchEvents } from '../../actions/prematch';
import {
  isIdleWindow,
  lastInteraction,
  killedSession,
  checkSessionOnIdle,
  redirectToNotLoggedInPage
} from '../../actions/user';

import {
  redirectToWaggering
} from '../../actions/maintenance';

import config from '../../../config';

class MiniModal extends React.Component <any, any> {

  constructor(props: any) {
    super(props);
    this.state = {
      idleCounter: parseInt(config.onIdleExpire) / 1000,
      idleCountdown: null,
      hasLoaded: false
    };
  }

  componentDidMount() {
    this.setState({ hasLoaded: true });
  }

  componentWillReceiveProps(newProps: any) {

    if (!newProps.closedConnection && (this.props.closedConnection !== newProps.closedConnection)) {
      let self = this;
      setTimeout(() => {
        if (!self.props.eventsLoaded) {
          if (self.props.isPrematch) {
            self.props.dispatch(reqPrematchEvents());
            console.log(`requesting prematch events mini modal`);
          } else {
            let isAsian = newProps.view === 1;
            self.props.dispatch(reqLiveEvents(isAsian));
            console.log(`requesting live events mini modal => `);
          }
        }
      }, 3500);

    }

    // In case warning pop up was disabled by user interacting on other tab
    if (this.props.idleWindow && !newProps.idleWindow) {
      clearInterval(this.state.idleCountdown);
      this.setState({ idleCounter: parseInt(config.onIdleExpire) / 1000 });
    }

    if ((!this.props.idleWindow && newProps.idleWindow) && !newProps.closedConnection && !newProps.legacyRedirect) {

      let idleCountdown = setInterval(() => {
        this.tick();
      }, 1000);
      this.setState({ idleCountdown });
      // console.log('start timmer');
    }

  }

  componentWillUnmount() {

    clearInterval(this.state.idleCountdown);
    this.setState({ idleCounter: parseInt(config.onIdleExpire) / 1000 });

  }

  tick() {

    this.setState({ idleCounter: this.state.idleCounter - 1 });
    if (this.state.idleCounter <= 0) {

      this.resetIdleCounter();
      this.killSession('tick');

    }
  }

  refresh = () => {
    location.reload(true);
  }

  resetIdleCounter() {
    clearInterval(this.state.idleCountdown);
    this.setState({ idleCounter: parseInt(config.onIdleExpire) / 1000 });
  }

  killSession(source: any) {

    clearInterval(this.state.idleCountdown);
    this.setState({ idleCounter: parseInt(config.onIdleExpire) / 1000 });

    if (source === 'click') {
      // No need to send removeUser - session is terminated by server with 'terminate' msg
      // this.props.dispatch(removeUser());
      this.props.dispatch(checkSessionOnIdle('terminate'));

    }

    this.props.wsm.socket.disconnect();
    this.props.dispatch(isIdleWindow(false));
    // this.props.dispatch(lastInteraction(-1));
    this.props.dispatch(killedSession(true));
    this.props.dispatch(redirectToNotLoggedInPage());

  }

  keepSession() {
    let time = Date.now();
    this.props.dispatch(checkSessionOnIdle(time));
    this.props.dispatch(isIdleWindow(false));
    this.props.dispatch(lastInteraction(0));
    this.resetIdleCounter();
    clearInterval(this.state.idleCountdown);
    this.setState({ idleCounter: parseInt(config.onIdleExpire) / 1000 });
  }

  idleAction = (key: number) => {
    this.resetIdleCounter();
    return key === 0 ? this.killSession('click') : this.keepSession();
  }

  getMessage() {
    const { closedConnection, idleWindow, maintenance, router } = this.props;
    let isBlocked = router && router.location.pathname.includes('blocked');

    const obj = {
      closedConnection_: {
        msg: 'We are connecting to the wagering servers. This message will close when connectivity has been established. Or, try refreshing your browser.',
        title: `Connecting...`
      },
      idleWindow_: {
        msg: `You will automatically be logged out in ${this.state.idleCounter} seconds due to inactivity. Do you wish to stay logged in?`,
        title: 'Automatic Logout'
      },
      legacy_: {
        msg: 'Redirecting to legacy UI, please wait.',
        title: `Redirecting...`
      },
      isBlocked_: {
        msg: 'Live betting is currently not enabled for your account. Please contact your agent for more information about this product',
        title: `Account Disabled`
      }
    };

    if (isBlocked) {
      return obj.isBlocked_;
    }

    if (maintenance && !idleWindow) {
      const isLiveOnMaintenance = this.props.maintenanceAll && this.props.maintenanceAll['live'] && this.props.maintenanceAll['live'].maintenance;
      const isPrematchOnMaintenance = this.props.maintenanceAll && this.props.maintenanceAll['prematch'] && this.props.maintenanceAll['prematch'].maintenance;
      // const isScheduleOnMaintenance = this.props.maintenanceAll && this.props.maintenanceAll['schedule'] && this.props.maintenanceAll['schedule'].maintenance;

      let streamOnMaintenance = this.props.maintenanceKey.charAt(0).toUpperCase() + this.props.maintenanceKey.slice(1);
      streamOnMaintenance = this.props.maintenanceKey === 'schedule' ? streamOnMaintenance : streamOnMaintenance + ' wagering';

      let timestamp = this.props.maintenanceETA;
      let toHumanReadable = '';

      try {
        toHumanReadable = moment.unix(timestamp).format('h:mm:ss A');
      }
      catch (err) {
        toHumanReadable = 'ETA';
        console.log('error on unix value..');
      }

      let message: any = `${streamOnMaintenance} is currently down for routine maintenance. We expect normal operations to resume at ${toHumanReadable}.`;

      if (!isLiveOnMaintenance || (!isPrematchOnMaintenance && config.hasPrematchLink)) {

        let availableKey = isLiveOnMaintenance ? 'Prematch' : 'Live';
        message = <>{ `${message} In the mean time, feel free to use `} <Link to ={`/${availableKey.charAt(0).toLowerCase() + availableKey.slice(1)}`}>{`${availableKey} Wagering.`}</Link></>;

      }

      let o = {
        title: `System Maintenance`,
        msg: message
      };

      return o;
    }

    if (closedConnection) {
      return obj.closedConnection_;
    }
    if (idleWindow) {
      return obj.idleWindow_;
    }

    return obj.legacy_;

  }

  render() {

    const { closedConnection, legacyRedirect, idleWindow, killedSession, maintenance, initialisationResultCode, router } = this.props;

    let isBlocked = router && router.location.pathname.includes('blocked');
    // User is not logged-in. No need to show a thing.

    if (!isBlocked && initialisationResultCode === 0) return <div></div>;

    const content = this.getMessage();

    let iframeValues;
    let open = !isBlocked && (maintenance && !killedSession) || (closedConnection && !killedSession) || legacyRedirect || (idleWindow && !killedSession);

    if (this.state.hasLoaded && !config.hasMaxViewHeight && open) {
      let { iframeSizer } = (window as any);
      iframeValues = iframeSizer;
    }

    // If any of our streams is on maintenance. We need to let user redirect to an available waggering or schedule.
    // Check which wagering stream is available...

    const isLiveOnMaintenance = this.props.maintenanceAll && this.props.maintenanceAll['live'] && this.props.maintenanceAll['live'].maintenance;
    const isPrematchOnMaintenance = this.props.maintenanceAll && this.props.maintenanceAll['prematch'] && this.props.maintenanceAll['prematch'].maintenance;

    const hasAvailableWaggering = maintenance && (!isLiveOnMaintenance || (!isPrematchOnMaintenance && config.hasPrematchLink));
    let availableWaggeringKey = '';

    if (!isLiveOnMaintenance) {
      availableWaggeringKey = 'live';
    } else if (!isPrematchOnMaintenance) {
      availableWaggeringKey = 'prematch';
    }

    return (
      <Dialog iframeValues={iframeValues} open={ open || isBlocked } title={content.title} className="legacy-modal">
        <DialogContent>
          <Table columns={[]}>
            <tbody>
              <tr><td>{content.msg}</td></tr>
            </tbody>
          </Table>
        </DialogContent>

        <DialogButtons>

          <Button
            enabled={ hasAvailableWaggering }
            style={{ display: hasAvailableWaggering && !idleWindow ? 'block' : 'none' }}
            onClick={() => hasAvailableWaggering && !idleWindow ? this.props.dispatch(redirectToWaggering(availableWaggeringKey)) : () => ''}

          >Go</Button>

          { !idleWindow ?
            <Button
              style={{'display': legacyRedirect || maintenance || isBlocked ? 'none' : 'block'}}
              onClick={this.refresh}
              enabled={closedConnection}
              key="refresh">Refresh
            </Button> :

            ['Log Out Now', 'Stay Logged'].map((it, key) =>
              <Button
                style={{'display': closedConnection ? 'none' : 'block'}}
                type={ key === 0 ? 'warning' : 'success' }
                onClick={() => this.idleAction(key)}
                key={key}
                >{it}
              </Button>
            )
          }

        </DialogButtons>

      </Dialog>
    );
  }
}

export default connect(
  (state: any) => {

    const pathState = state.router.location.pathname;
    const path = pathState.slice(pathState.lastIndexOf('/') + 1, pathState.length);

    let eventsBySport = state.eventsFlatten && state.eventsFlatten.byId;
    const isPrematch = pathState.includes('prematch');
    let events = isPrematch ? state.prematch.leagues : Object.keys(eventsBySport);
    const eventsLoaded = events && events.length > 0;

    const userPathLocation = ['live', 'prematch', 'schedule']
      .filter(it => pathState.includes(it));

    const maintenanceKey = userPathLocation && userPathLocation[0];

    return {
      path,
      isPrematch,
      eventsLoaded,
      wsm: state.wsm,
      router: state.router,
      view: state.settings.view,
      maintenanceAll: state.maintenance,
      maintenanceKey: maintenanceKey || '',
      idleWindow: state.user && state.user.isIdleWindow,
      killedSession: state.user && state.user.killedSession,
      legacyRedirect: state.user && state.user.isLegacyRedirect,
      maintenanceETA: maintenanceKey && state.maintenance[maintenanceKey].eta,
      maintenance: maintenanceKey && state.maintenance[maintenanceKey].maintenance,
      closedConnection: isPrematch ? state.prematch.closedConnectionPrematch : state.live.closedConnectionLive,
      initialisationResultCode: state.user && state.user.response && state.user.response.initialisation_result_code
    };
  }
)(MiniModal);
