import { ICricketGroundAnimationState, CricketAction } from '../constants';
import { bowling } from './bowl';
import { shots } from './throw';

function next() {

  return bowling({
    ball: {
      // opacity:1 ,
      x: 45,
      y: 25
    }
  });
}

const pitchMe = next();

export function throwBall(state: ICricketGroundAnimationState, nextAction: CricketAction): (any|void) {
  const pitchYou = pitchMe;

  switch (nextAction) {
      case CricketAction.Bowling:
      return [
        pitchYou,
        shots(pitchYou.end)
      ];
  }

}
