import * as React from 'react';
import { connect } from 'react-redux';
import { getScoreboardByPeriod } from './live-helpers';

import Card from '../../svgs/card';
import Corner from '../../svgs/corner';
import SoccerIcon from '../../svgs/soccer-icon';
import ClockComponent from './../../../components/misc/clock-component';

class Scoreboard extends React.Component<any, any> {

  getIcon(icon: string, sport: number) {
    if (sport === 1) {
      switch (icon) {
        case 'yellow':
        case 'red':
          return <Card />;
        case 'corner':
          return <Corner />;
        case 'penalty':
          return <SoccerIcon />;
        default:
          return '';
      }
    }
    return '';
  }

  render() {

    const { event, gamecast, league, score, clock } = this.props;

    const noShow = gamecast === undefined ||
    gamecast.scoreboard === undefined ||
    event.sport === 24 || score === undefined;

    if (noShow) return <div></div>;

    const { sport } = event;
    const { scoreboard } = gamecast;
    const { scoreByPeriod } = score;
    const scoreTeam1 = score !== undefined ? score.scoreTeam1 : '0';
    const scoreTeam2 = score !== undefined ? score.scoreTeam2 : '0';
    const period = score !== undefined ? score.period : '';
    const { min, sec }: any = clock || { min: 0, sec: 0 };

    if (scoreboard.additional[0] !== undefined && sport === 1) {
      if (scoreboard.additional[0].title !== 'corner') {
        scoreboard.additional = scoreboard.additional.reverse();
      }
    }

    return (
      <div className="scoreboard-b">
        <div className="league">{league}</div>
        <div className="clock-container">
          <ClockComponent clock={clock} sport={sport} score={score} isMobile={true}/>
        </div>
        <div className="teams-cont">
          <div className="teams">
            <div className="team" >{event.inverted ? event.away : event.home}</div>
            <div className="team">{event.inverted ? event.home : event.away}</div>
          </div>
          <div className="scores-cont">
            <div className="scores">
            <div className="score-home">{scoreTeam1}</div>
            { (scoreboard && (event.sport === 2 || event.sport === 6)) &&
              <table className="score-period">
                  <thead>
                    <tr className="periods">
                      { getScoreboardByPeriod(event.sport, scoreByPeriod).map((item: any, i: any)  => <th key={i} className={item.description ===  'T' ? 'total' : ''}>{item.description}</th>)}
                    </tr>
                  </thead>
                  <tbody >
                    <tr>
                      { getScoreboardByPeriod(event.sport, scoreByPeriod).map((item: any, i: any)  => <td key={i} className={item.description ===  'T' ? 'total' : ''}>{item.t1}</td>) }
                    </tr>
                    <tr>
                      { getScoreboardByPeriod(event.sport, scoreByPeriod).map((item: any, i: any)  => <td key={i} className={item.description ===  'T' ? 'total' : ''}>{item.t2}</td>) }
                    </tr>
                  </tbody>
                </table>
            }
            {
             (scoreboard && (event.sport === 10 || event.sport === 7)) &&
              <table className="base-table">
              <thead >
               <tr>
                  { scoreboard.additional.map((item: any, i: any) => {
                      const className = period === item.title ? 'runs' : 'inns';
                      return item.title !== 'R' && <td  className= {className} key={i}>{item.title}</td>;
                    })
                    }
                  { scoreboard.additional.map((item: any, i: any) => ( item.title === 'R' ? <td className="runs" key={i}>{item.title}</td> : null))}
                  { scoreboard.sets.map((set: any, i: any) => <td className="bordered-td innings" key={i}>{i + 1}</td>)}
                  { !!scoreboard.setsTotalTitle && <td className="sets-total">{ scoreboard.setsTotalTitle }</td> }
                  { !!scoreboard.pointsTitle && <td className="points-total" >{ scoreboard.pointsTitle }</td> }
                </tr>
              </thead>
              <tbody>
                <tr>
                { scoreboard.additional.map((item: any, i: any) => ( item.title !== 'R' ? <td key={i}>{item.t1}</td> : null)) }
                { scoreboard.additional.map((item: any, i: any) => ( item.title === 'R' ? <td className="runs" key={i}>{item.t1}</td> : null)) }
                { scoreboard.sets.map((set: any, i: any) => <td key={i}>{ set[0] }</td>) }
                { !!scoreboard.setsTotalTitle && <td>{ scoreboard.setsTotal[0] }</td> }
                { !!scoreboard.pointsTitle && <td>{ scoreboard.points[0] }</td> }
                </tr>
                <tr>
                { scoreboard.additional.map((item: any, i: any) => ( item.title !== 'R' ? <td key={i}>{item.t2}</td> : null)) }
                { scoreboard.additional.map((item: any, i: any) => ( item.title === 'R' ? <td className="runs" key={i}>{item.t2}</td> : null)) }
                { scoreboard.sets.map((set: any, i: any) => <td key={i}>{ set[1] }</td>) }
                { !!scoreboard.setsTotalTitle && <td>{ scoreboard.setsTotal[1] }</td> }
                { !!scoreboard.pointsTitle && <td>{ scoreboard.points[1] }</td> }
                </tr>
              </tbody>
            </table>
            }
            <div className="score-away">{scoreTeam2}</div>
          </div>
           { (scoreboard && (event.sport === 1)) &&
              <table className="table-additional">
                <tbody>
                  <tr>
                  {
                    scoreboard.additional.map((item: any, i: any) => <td key={i} style={{width: '35px'}} className={`home-add icon ${item.title}`}><a title={item.title}>{this.getIcon(item.title, sport)}<span>{item.t1}</span></a></td> )

                  }
                  <td className="period"></td>
                  {
                    scoreboard.additional.reverse().map((item: any, i: any) => <td key={i} style={{width: '35px'}} className={`away-add icon ${item.title}`}><a title={item.title}>{this.getIcon(item.title, sport)}<span>{item.t2}</span></a></td> )
                  }
                  </tr>
                </tbody>
              </table>
            }
          </div>
        </div>
      </div>
    );
    }
  }

export default connect(
  (state: any, ownProps: any) => ({
    event: ownProps.event,
    score: state.live.scores[ownProps.event.id],
    gamecast: state.live.eventGamecast,
    clock: state.live.clocks[ownProps.event.id]
  })
)(Scoreboard);
