import * as React from 'react';

export interface IRadioButtonProps {
  text: string;
  value?: string;
  name?: string;
  checked?: boolean;
  enabled?: boolean;
  onChange?: (e: React.ChangeEvent<any>) => void;
}

export default function RadioButton({ text, value, name = '', checked = false, enabled = true, onChange = f => f }: IRadioButtonProps): React.ReactElement<IRadioButtonProps> {
  const className = 'md-radio';
  if (!value)
    value = text;

  return (
    <div className={className}>
      <label className={ className + '__label' }>
        <input type="radio" onChange={onChange} value={value} className={ className + '__input' } checked={checked} name={name} disabled={!enabled} />
        <i className={ className + '__circle' }></i><span className={ className + '__text' }>{text}</span>
      </label>
    </div>
  );
}
