import { TOGGLE_RULE_STATUS } from '../actions/rules';

export const ruleInitialState: any = {
  status: {
    general: false,
    offerTypes: false,
    parlay: false,
    sports: false,
    baseball: false,
    basketball: false,
    boxing: false,
    australianRules: false,
    soccer: false,
    iceHockey: false,
    golf: false,
    americanFootball: false,
    tennis: false,
    snooker: false,
    eSports: false,
    tableTennis: false
  }
};

export const rules = (state = ruleInitialState, action: any): any => {
  switch (action.type) {
    case TOGGLE_RULE_STATUS:
      return Object.assign({}, state, {
        status: Object.assign({}, state.status, action.payload)
      });
  }
  return state;
};
