import { createSelector } from 'reselect';

import {LIVE_EVENTS_ASIAN,  LIVE_EVENTS_FLATTEN, NEW_EVENT_FLATTEN, REMOVE_EVENT_FLATTEN } from '../actions/events-flatten';
import { MARKET_COUNT_UPDATE, VIDEO_URL_UPDATE, HAS_GAMECAST_UPDATE } from '../actions/events-flatten';

import { IFlattenEvents } from './../../data-handlers/events/interfaces';
import { CLOSED_CONNECTION_LIVE } from '../actions/live';

import EventsDataHandlerClient from './../../data-handlers/events/client';

import { dashedToCamelCase } from './../helpers';
import { Sports } from './../constants';

export const eventsFlattenInitianState: IFlattenEvents = {
  byId: {},
  byLeague: {},
  bySport: {},
  leagueIdsBySport: {},
  leagues: {}
};

export const eventsFlatten = (state = eventsFlattenInitianState, action: any): any => {

  let ev_: any = {};

  switch (action.type) {

    case VIDEO_URL_UPDATE:
    case HAS_GAMECAST_UPDATE:
      let ev_: any = action.payload;
      // console.log(ev_);
      if (!state.byId[ev_.id]) return state;
      // console.log('passes');
      // console.log(state.byId[ev_.id]);
      return {
        ...state,
        byId: {
          ...state.byId,
          [ev_.id]: action.payload
        }
      };

    case CLOSED_CONNECTION_LIVE:
      if (!action.payload) return state;
      return {
        byId: {},
        byLeague: {},
        bySport: {},
        leagueIdsBySport: {},
        leagues: {}
      };
    case LIVE_EVENTS_ASIAN:
      return Object.assign({}, state, action.payload.flattenEvents);
    case LIVE_EVENTS_FLATTEN:
      return Object.assign({}, state, action.payload.flattenEvents);
    case NEW_EVENT_FLATTEN:
      const ev = action.payload;
      return EventsDataHandlerClient.addEvent(state, ev);
    case REMOVE_EVENT_FLATTEN:
      return EventsDataHandlerClient.removeEvent(state, action.payload);
    case MARKET_COUNT_UPDATE:
      return EventsDataHandlerClient.updateNumberMarkets(state, action.payload);

    default:
      return state;

  }

};

// Selectors
const propsSelector = (state: any, props: any) => props;
const bookMakerSelector = (state: any) => state.user.bookmakerId;
const openMarketsSelector = (state: any) => state.live.openMarkets;
const outcomeListSelector = (state: any) => state.live.outcomeList;
const eventsByIdSelector = (state: any) => state.eventsFlatten.byId;
const hasStreamSelector = (state: any) => state.live.filterStreaming;
const eventsBySportSelector = (state: any) => state.eventsFlatten.bySport;
const eventsleagueIdsBySportSelector = (state: any) => state.eventsFlatten.leagueIdsBySport;

export const getOutcomes_ = createSelector(
  [outcomeListSelector, propsSelector],
  (outcomeList: any, event: any) => {

    if (!outcomeList[event.sport]) return;

    let _outcomeList = outcomeList[event.sport][event.live];

    if (!_outcomeList) return [];

    return Object
      .keys(_outcomeList)
      .filter(key => _outcomeList[key].display)
      .sort((a, b) => _outcomeList[a].order - _outcomeList[b].order)
      .map(key => ({
        id: +key,
        title: _outcomeList[key].title,
        order: _outcomeList[key].order
      }));
  }
);

export const leaguesInPlay_ = createSelector(
  [propsSelector, eventsleagueIdsBySportSelector, eventsByIdSelector, eventsBySportSelector, bookMakerSelector, hasStreamSelector],
  (sportProps: any, eventsleagueIdsBySport: any, eventsById: any, eventsBySport: any, bookMakerSelector: any) => {
    const ccSport: any = dashedToCamelCase(sportProps.match.params.sport);
    let bookmakerId = bookMakerSelector;

    let evsBySport = eventsBySport[Sports[ccSport]]
      .map((evId: string) => eventsById[evId])
      .filter((ev: any) => ev.hasOpenMarkets[bookmakerId[Sports[ccSport]]] || ev.numberMarkets[bookmakerId[Sports[ccSport]]] >= 1);

    return eventsleagueIdsBySport[Sports[ccSport]]
      .filter((league: any) => evsBySport.map((ev: any) => ev.leagueid).indexOf(league) >= 0);
  }
);

export const eventsBySport_ = createSelector(
  [propsSelector, eventsByIdSelector, eventsBySportSelector, bookMakerSelector, hasStreamSelector],
  (sportProps: any, eventsById: any, eventsBySport: any, bookMakerSelector: any, hasStreamFilter: any) => {
    const ccSport: any = dashedToCamelCase(sportProps.match.params.sport);
    let bookmakerId = bookMakerSelector;

    if (!eventsBySport[Sports[ccSport]]) return [];

    let evsBySport = eventsBySport[Sports[ccSport]]
      .map((evId: string) => eventsById[evId])
      .filter((ev: any) => ev.hasOpenMarkets[bookmakerId[Sports[ccSport]]] || ev.numberMarkets[bookmakerId[Sports[ccSport]]] >= 1);

    return hasStreamFilter ? evsBySport.filter((ev: any) => ev.videoUrl !== '') : evsBySport;

  }
);

export const getEvent = createSelector(
  [eventsByIdSelector, eventsBySportSelector, bookMakerSelector, openMarketsSelector, hasStreamSelector],
  (eventsById: any, eventsBySport: any, bookMakerSelector: any, openMarkets: any, hasStreamFilter: any) => {

    let streamEvent: any = {};
    const bookmakerId = bookMakerSelector;

    if (hasStreamFilter && eventsById) {
      Object.keys(eventsById)
        .forEach(evId => {
          let ev = eventsById[evId];
          let hasMarkets = ev.hasOpenMarkets[bookmakerId[ev.sport]] || ev.numberMarkets[bookmakerId[ev.sport]] >= 1;
          if (!streamEvent.id && hasMarkets && ev.videoUrl !== '' && ev.videoSource > 0) {
            streamEvent = eventsById[evId];
          }
        });

      return streamEvent;

    }

    let openMarkets_ = [...openMarkets]
      .filter((item: any) => Object.keys(item.qt)[0] && item.sport)
      .sort((a: any, b: any) => b.qt[bookmakerId[b.sport]] - a.qt[bookmakerId[a.sport]]);

    if (!openMarkets_[0]) return null;

    let sportWithMostEvents = openMarkets_[0].sport;

    let eventsBySport_ = eventsBySport[sportWithMostEvents]
      .map((evId: any) => eventsById[evId]);

    return eventsBySport_[0];

  }
);
