import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const ESports = ({ data }: any) => {
  const { title, general, wagerTypes, specificRulesLoL, specificRulesCsGo, specificRulesDoTa } = data;

  return (
    <RulePart title={title} model="e-sports">
      <RuleItem title={general.title}>
        {general.text}
      </RuleItem>
      <RuleItem title={wagerTypes.title}>
        <ol>
          {
            Object.keys(wagerTypes.text)
              .map((it, i) => {
                return (
                  <li key={i} style={{marginTop: '10px'}}>
                    <strong>{wagerTypes.text[it].title} </strong>
                    <span>
                      { wagerTypes.text[it].text }
                    </span>
                  </li>
                );
              })
          }
        </ol>
      </RuleItem>
      <RuleItem title={specificRulesLoL.title}>
        <ol>
        {
          Object.keys(specificRulesLoL.text)
            .map((it, i) => {
              return (
                <li key={i} style={{marginTop: '10px'}}>
                  <strong>{specificRulesLoL.text[it].title} </strong>
                  <span>
                    { specificRulesLoL.text[it].text }
                  </span>
                </li>
              );
            })
        }
        </ol>
      </RuleItem>
      <RuleItem title={specificRulesCsGo.title}>
        <ol>
        {
          Object.keys(specificRulesCsGo.text)
            .map((it, i) => {
              return (
                <li key={i} style={{marginTop: '10px'}}>
                  <strong>{specificRulesCsGo.text[it].title} </strong>
                  <span>
                    { specificRulesCsGo.text[it].text }
                  </span>
                </li>
              );
            })
        }
        </ol>
      </RuleItem>
      <RuleItem title={specificRulesDoTa.title}>
        <ol>
        {
          Object.keys(specificRulesDoTa.text)
            .map((it, i) => {
              return (
                <li key={i} style={{marginTop: '10px'}}>
                  <strong>{specificRulesDoTa.text[it].title} </strong>
                  <span>
                    { specificRulesDoTa.text[it].text }
                  </span>
                </li>
              );
            })
        }
        </ol>
      </RuleItem>
    </RulePart>
  );

};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.eSports })
)(ESports);
