import * as React from 'react';
import { connect } from 'react-redux';
import { gameCastOn, gameCastOff } from '../../../../actions/ui-toolbar';
import Button from './button';

const UiButtons =  ({ hidecast, showTooltip, hideTooltip, uiToolbar, dispatch }: any) => {

  const handleClick = (e: any) => {
    let ic_name =  e.target.closest('svg').className.baseVal;

    if (ic_name.includes('gamecast-ic')) {
      !uiToolbar.gamecast ? dispatch(gameCastOn()) : dispatch(gameCastOff());
    }

  };

  return (
    <div className={!hidecast ? 'toolbar-buttons' : 'toolbar-buttons live'}>
      <ul>
        {!hidecast &&
          <Button
            name="gamecast"
            handleClick={handleClick}
            iconClassName={!uiToolbar.gamecast ? 'gamecast-ic btn' : 'gamecast-ic btn active'}
          />
        }
      </ul>
    </div>
  );

};

export default connect(
  (state: any, ownProps: any) => {
    return {
      uiToolbar: state.uiToolbar
    };
  }
)(UiButtons);
