import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Australian = ({ data }: any) => {
  const { title, overtime, double } = data;
  return (
    <RulePart title={title} model="australian">
      <RuleItem title={overtime.title}>
        {overtime.text}
      </RuleItem>
      <RuleItem title={double.title}>
        {double.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.australian })
)(Australian);
