export const GET_PREMATCH = 'GET_PREMATCH';
export const UPDATE_PREMATCH = 'UPDATE_PREMATCH';
export const PREMATCH_LEAGUES = 'PREMATCH_LEAGUES';
export const NEW_PREMATCH_EVENT = 'NEW_PREMATCH_EVENT';
export const REMOVE_EVENT_PREMATCH = 'REMOVE_EVENT_PREMATCH';
export const UPDATE_PREMATCH_OUTCOMES = 'UPDATE_PREMATCH_OUTCOMES';

export const getPrematchEvOutcomes = (payload: any) => ({
  type: UPDATE_PREMATCH_OUTCOMES,
  payload
});

export const updatePrematch = (payload: any) => ({
  type: UPDATE_PREMATCH,
  payload
});

export const prematchEvents = (payload: any) => ({
  type: GET_PREMATCH,
  payload
});

export const getLeagues = (payload: any) => ({
  type: PREMATCH_LEAGUES,
  payload
});

export const updatePrematchList: any = () => (dispatch: any, getState: any) => {
  let msg = `${UPDATE_PREMATCH}_${JSON.stringify(getState().user.bookmakerId)}`;
  getState().wsm.socket.emit('message', msg);
};

export const reqPrematchEvents = () => (dispatch: any, getState: any) => {
  let msg = `${GET_PREMATCH}_${JSON.stringify(getState().user.bookmakerId)}`;
  getState().wsm.socket.emit('message', msg);
};

export const getPrematchLeagues = (sportId: any, country: any) => (dispatch: any, getState: any) => {
  let msg = `${PREMATCH_LEAGUES}_${sportId}_${country}_${JSON.stringify(getState().user.bookmakerId)}`;
  getState().wsm.socket.emit('message', msg);
};

export const newPrematchEvent = (payload: any) => ({
  type: NEW_PREMATCH_EVENT,
  payload
});

export const removeEventPrematch = (payload: any) => ({
  type: REMOVE_EVENT_PREMATCH,
  payload
});
