import * as React from 'react';
import { Sports } from './../../constants';

import { MockBasketball, MockIceHokey, MockBaseball, MockFootBall, MockCricket, MockSoccer, MockTennis } from './../gamecast';
import ActionTicker from './../views/live/action-ticker';
import ScoreboardNew from './../views/live/european/scoreboard-new';
import ScoreboardPeriod from './../views/live/european/scoreboard-period';

import Scoreboard from './../views/live/scoreboard';
import config from './../../../config';

class GamecastPreloader extends React.Component<any, any> {

  render() {
    const { isSingleEvent, isUnabled, isGamecastOn, event, view } = this.props;
    const { sport } = event;

    let Component: any = () => <div></div>;

    if (config.allowAnimations) {
      switch (event.sport) {
        case Sports.baseball:
          Component = MockBaseball;
          break;
        case Sports.basketball:
          Component = MockBasketball;
          break;
        case Sports.iceHockey:
          Component = MockIceHokey;
          break;
        case Sports.soccer:
          Component = MockSoccer;
          break;
        case Sports.tennis:
          Component = MockTennis;
          break;
        case Sports.americanFootball:
          Component = MockFootBall;
          break;
        case Sports.cricket:
          Component = MockCricket;
      }
    }

    const isByPeriod = sport === 6 || sport === 10 || sport === 7 || sport === 2 || sport === 8;
    let ScoreboardComponent: any = () => <div></div>;
    ScoreboardComponent = isByPeriod ? ScoreboardPeriod : ScoreboardNew;

    const wrapperCont = ['wrapper-cont'];

    if (!isSingleEvent) {
      wrapperCont.push('live');
    }

    if (view === 0) {
      ScoreboardComponent = Scoreboard;
      wrapperCont.push('classic');
    }

    return (
      <div className="scoreboard-wrapper">
        <ScoreboardComponent
          event={event}
          league={isUnabled ? 'Gamecast Unavailable' : 'Loading...'} />
           <ActionTicker
            action={'999'}
            matchData={''}
            team={0}
            event={event}
            isPreloader={true}
            isUnabled={isUnabled}
          />
          { isGamecastOn && !isUnabled &&
            <div className="mock-gamecast">
              <Component />
            </div>
          }
      </div>
    );
  }
}

export default GamecastPreloader;
