import * as React from 'react';
import Lottie from 'react-lottie';
import { connect } from 'react-redux';
import config from './../../../../config';

import { CricketActions, hasLottieAnimation } from './constants';

import { getAnimations } from './../../../actions/animations';

class CricketLottie extends React.Component<any, any> {

  public defaultOptions: any;

  constructor(props: any) {
    super(props);

    this.state = {
      action: this.props.action,
      defaultOptions: {
        loop: false,
        autoplay: true,
        animationData: '',
        rendererSettings: {
          preserveAspectRatio: 'xMidYMid slice'
        }
      },
      lottieAnimImagePath: './../images/cricket/',
      isNoRuns: false
    };

  }

  componentDidMount() {

    const { path } = this.props;

    // Request animations only if they are not already on store :)
    if (!Object.keys(this.props.animations['cricket'])[0]) {
      this.props.dispatch(getAnimations('cricket'));
    }
    // For some reason, we have to change image's paths on json file
    // if gamecast is on SingleEvent section

    const pathSplit = path ? path.split('/') : [];

    // Get path to see if this is single or live events section
    if (path === '/gamecast-mock') {
      this.setState({
        lottieAnimImagePath: `./../..${config.host.trail}/images/cricket/`
      });
    } else {
      this.setState({
        lottieAnimImagePath: path && pathSplit[pathSplit.length - 1].includes('-') ? './../../images/cricket/' : './../images/cricket/'
      });
    }

    // If there is no lottie animation for that action;
    // we have to avoid requesting an unexistent json file

    // There is no animation for 0 runs and delivery can result in a 'wicket'.
    // Display 'idle' animation on those cases.

    let runsScored = this.deliveries[0] && this.deliveries[0][this.deliveries[0].length - 1];
    this.setState({ isNoRuns: this.state.action === 168 && (runsScored === '0' || parseInt(runsScored) === NaN) });

    // console.log(this.state.isNoRuns, 'isNoRuns');
    if (!hasLottieAnimation(this.state.action) || this.state.isNoRuns) return;

    let cricketAction = CricketActions[this.state.action];

    // This in case action === 8888

    if (!cricketAction) return;

    cricketAction = cricketAction
      .toLowerCase()
      .replace(' ', '_');

    // There is no animation for 0 runs

    if (cricketAction === 'Runs') {
      // let runsScored = this.deliveries[0] && this.deliveries[0][this.deliveries[0].length - 1];

      if (runsScored === '2' || runsScored === '4' || runsScored === '6') {
        cricketAction = `${runsScored}_${cricketAction}`;
      }

      // console.log(runsScored, 'runsScored');
      // console.log('change cricketAction to', `${cricketAction}_${runsScored}`);
    }

    // console.log('animation data will run');

    let animationData_ = this.props.animations['cricket'][cricketAction];
    // let animationData_ = require(`./actions/${cricketAction}.json`);

    // We modify lottie's JSON file to adapt image path

    // Prevent UI from crashing if there is no JSON animations file
    if (!animationData_) return;

    animationData_ = {
      ...animationData_,
      assets: animationData_.assets.map((item: any) => {
        item.u = this.state.lottieAnimImagePath;
        return item;
      })
    };

    this.setState({
      defaultOptions: {
        ...this.state.defaultOptions,
        animationData: animationData_
      }
    });

  }

  // Not worry about not using prevProps, it is a funciton's parameter
  componentDidUpdate(prevProps: any, prevState: any) {

    if (!Object.keys(this.props.animations['cricket'])[0]) {
      console.log('animations file might not have been loaded properly');
      return;
    }

    if (prevState.action !== this.state.action) {

      let cricketAction = CricketActions[this.state.action];

      // There is no animation for 0 runs and delivery can result in a 'wicket'.
      // Display 'idle' animation on those cases.

      let runsScored = this.deliveries[0] && this.deliveries[0][this.deliveries[0].length - 1];
      this.setState({ isNoRuns: this.state.action === 168 && (runsScored === '0' || parseInt(runsScored) === NaN) });
      // console.log(this.state.isNoRuns, 'isNoRuns');
      if (!cricketAction || !hasLottieAnimation(this.state.action) || this.state.isNoRuns) return;

      cricketAction = cricketAction
        .toLowerCase()
        .replace(' ', '_');

      // Get number of runs to retrieve JSON file.
      if (cricketAction === 'runs') {
        let runsScored = this.deliveries[0] && this.deliveries[0][this.deliveries[0].length - 1];

        if (runsScored === '2' || runsScored === '4' || runsScored === '6') {
          cricketAction = `${runsScored}_${cricketAction}`;
        }

        // console.log('change cricketAction to', `${cricketAction}_${runsScored}`);
      }

      let animationData_ = this.props.animations['cricket'][cricketAction];
      // Prevent UI from crashing if there is no JSON animations file
      if (!animationData_) return;
      // let animationData_ = require(`./actions/${cricketAction}.json`);

      // We modify lottie's JSON file to adapt image path

      animationData_ = {
        ...animationData_,
        assets: animationData_.assets.map((item: any) => {
          item.u = this.state.lottieAnimImagePath;
          return item;
        })
      };

      this.setState({
        defaultOptions: {
          ...this.state.defaultOptions,
          animationData: animationData_
        }
      });
    }
  }

  // We use this static function to avoid componentWillReceiveProps (deprecated method)

  static getDerivedStateFromProps(nextProps: any, prevState: any) {

    if (nextProps.action === 8888) return null;

    return nextProps.action !== prevState.action ?
      { action: nextProps.action } :
      null;
  }

  // This transforms matchData to get 'last 6 deliveries'

  get deliveries() {
    if (this.props) {
      const { matchData } = this.props;
      // console.log(matchData);
      return matchData && matchData
      .split('#')
      .map((s: any) => s.split(':'));
    }
    return '';
  }

  // This transforms extraData to get bowler & batter data
  get extraData(): any {
    // BowlerName
    if (this.props && this.props.extraData) {

      let extraData_: any = false;

      try {

        extraData_ = JSON.parse(this.props.extraData);
      }

      catch (_err) {
        console.log('error on JSON format cricket extraData');

      }

      const extraData = extraData_;

      if (!extraData) return '';

      let bowlerData = {
        name: extraData['T1-S7'] ? extraData['T1-S7'].split('#')[0] : '',
        overs: extraData['T1-S1'],
        m: extraData['T1-S2'],
        runs: extraData['T1-S3'],
        w: extraData['T1-S4']
      };

      let batterData = {
        name: extraData['T2-S7'] ? extraData['T2-S7'].split('#')[0] : '',
        runs: extraData['T2-S1'],
        pship: extraData['T2-S2']
      };

      return  {
        bowlerData,
        batterData
      };

    }
    return '';

  }

  // Builds extra data layout

  createDataTable(data: any) {

    if (!data || !Object.keys(data)[0]) return <tbody><tr></tr></tbody>;

    return (
      <tbody>
        <tr>
          {
            Object.keys(data)
              .filter((item: any) => item !== 'name')
              .map((item: any, i: any) => (
                <td key={i}>{item.toUpperCase()}</td>
              ))
        }
        </tr>
        <tr>
          {
            Object.keys(data)
              .filter((item: any) => item !== 'name')
              .map((item: any, i: any) => (
                <td key={i}>{data[item]}</td>
              ))
          }
        </tr>
      </tbody>
    );
  }

  render() {

    // console.log(this.props.action, 'action');
    // console.log(CricketActions[this.props.action]);
    // console.log(this.deliveries[0]);
    console.log(this.state.lottieAnimImagePath);

    let teamScore = (team: string, score: string ) => (
      <div className="team-container">
        <div className="team" style={{ backgroundImage: `url("${this.state.lottieAnimImagePath}action_bar_crop.png")`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', fontSize: '10px'}}>
          <span>{team}&nbsp; {score}</span>
        </div>
      </div>
    );

    let topMargin = hasLottieAnimation(this.state.action) && !this.state.isNoRuns ? 0 : 40;
    let cropedBarStyle = {
      backgroundImage: `url("${this.state.lottieAnimImagePath}action_bar_crop.png")`,
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      fontSize: '10px'
    };

    let wrapperStyle: any = {};

    if (this.props.event.videoUrl !== '') {
      wrapperStyle = {
        position: 'relative',
        top: '-20px'
      };
    }

    return(
      <div className="lottie-cricket-wrapper" style={wrapperStyle}>
        <div className="score-container">
          { teamScore(this.props.event.home, this.props.score ? this.props.score.scoreTeam1 : 0) }
          { teamScore(this.props.event.away, this.props.score ? this.props.score.scoreTeam2 : 0) }
        </div>

        <div>
          <div style={{ position: 'absolute', width: '100%'}}>
            <div style={{  position: 'relative', top: `${topMargin}px`,  color: 'white', textAlign: 'center' }}>
              {
                hasLottieAnimation(this.state.action) && !this.state.isNoRuns ?
                <div>
                  <Lottie options={this.state.defaultOptions} />
                </div> :

                <span style={{...cropedBarStyle, padding: '5px 70px' }}>
                  {this.state.isNoRuns && `NO ` }
                  {
                    CricketActions[this.state.action] &&
                    CricketActions[this.state.action].toUpperCase()
                  }
                </span>
              }
            </div>
          </div>
          <img style={{ width: '100%', height: '100%' }} src={`${this.state.lottieAnimImagePath}idle-bg.png`} />

          <div style={{...cropedBarStyle, color: 'white', textAlign: 'center', position: 'relative', top: '-10px', minHeight: '40px' }} className="deliveries-container">
              <div>
                { this.deliveries[1] ? this.deliveries[1][0] : ''} over - ball { this.deliveries[1] ? this.deliveries[1][1] : ''}
              </div>
            <div style={{...cropedBarStyle }}>
                Last 6 deliveries
            </div>
            <div>
              {
                this.deliveries && this.deliveries[0].length > 1 &&
                  this.deliveries[0].map((item: any, i: any) =>
                  <span key={i} style={{ padding: '10px' }}>{item} | </span>
                )
              }
              </div>
          </div>

        </div>

        <div className="data-container">
          <div className="data-item">

            <div>

              <div className="item" style={cropedBarStyle}>
                <span>
                  {
                    this.extraData &&
                    this.extraData.bowlerData.name.split('#')[0]
                  }
                </span>
              </div>
              <div className="data-table-cont">
                <table className="data-table-bowler">
                  {
                    !this.extraData ? <tbody></tbody> :
                    this.createDataTable(this.extraData.bowlerData)
                  }
                </table>
              </div>

            </div>

          </div>

          <div className="data-item">
          <div>

            <div className="item" style={cropedBarStyle}>
              <span style={{}}>
                {
                  this.extraData &&
                  this.extraData.batterData.name.split('#')[0]
                }
              </span>
            </div>
              <div className="data-table-cont">
                <table className="data-table-bowler">
                  { !this.extraData ? <tbody></tbody> :
                    this.createDataTable(this.extraData.batterData)
                  }
                </table>
              </div>

            </div>
          </div>
        </div>
        <div style={{ fontFamily: 'Roboto', position: 'relative', top: '-75px', fontSize: '10px', padding: '5px'}}>Gamecast scores, plays, and live streams may not be real-time or accurate. We assume no liability for any discrepancies. Official scoresheets produced at event intervals are used for bet settlement.</div>
      </div>
    );
  }
}

export default connect(
  (state: any) => ({
    animations: state.animations
  })
)(CricketLottie);
