import User from '../middleware/api/user';

export const CHANGE_TICKETS_SELECTED_DATE = 'CHANGE_TICKETS_SELECTED_DATE';
export const CHANGE_TICKETS_SELECTED_TYPE = 'CHANGE_TICKETS_SELECTED_TYPE';
export const TICKETS_DATA_SUCCESS = 'TICKETS_DATA_SUCCESS';

export const changeTicketsSelectedDate = (date: any) => ({
  type: CHANGE_TICKETS_SELECTED_DATE,
  payload: date
});

export const changeTicketsSelectedType = (date: any) => ({
  type: CHANGE_TICKETS_SELECTED_TYPE,
  payload: date
});

const ticketsDataSuccess = (data: any) => ({
  type: TICKETS_DATA_SUCCESS,
  payload: data
});

export const getTickets = (date: any) => (dispatch: any) => {
  const user = new User(dispatch);

  user
    .getTickets(date || 365)
    .then((data: any) => dispatch(ticketsDataSuccess(data)));
};
