import * as React from 'react';

const enum ScrollDirection {
  left = -1,
  right = 1
}

let buttonScrollAnimation: any;
class HorizontalScrollableContainer extends React.Component<any, any> {
  inner: HTMLDivElement;
  outer: HTMLDivElement;

  state = {
    hasScroll: false,
    scrollLeft: 0
  };

  startButtonScroll = (direction: ScrollDirection) => () => {
    clearTimeout(buttonScrollAnimation);

    this.inner.scrollLeft += direction * 5;
    buttonScrollAnimation = setTimeout(this.startButtonScroll(direction), 50);
  }

  stopButtonScroll = (e: any) => {
    e.preventDefault();
    clearTimeout(buttonScrollAnimation);
  }

  handleWindowResize = () => {
    if (this.inner) {
      const hasScroll = this.inner.scrollWidth > this.inner.clientWidth;

      if (hasScroll !== this.state.hasScroll) {
        this.setState({
          hasScroll
        });
      }
    }
  }

  handleWheel = (e: any) => {
    e.preventDefault();
    this.inner.scrollLeft += 5 * (e.deltaY > 0 ? 1 : -1);
  }

  componentDidUpdate() {
    requestAnimationFrame(this.handleWindowResize);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleWindowResize);
    requestAnimationFrame(this.handleWindowResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowResize);
  }

  scroller = (type: ScrollDirection) => (
    <div
      className={`hzc__scroller-${type === ScrollDirection.left ? 'left' : 'right'}`}
      onMouseDown={this.startButtonScroll(type)}
      onTouchStart={this.startButtonScroll(type)}
      onMouseUp={this.stopButtonScroll}
      onMouseLeave={this.stopButtonScroll}
      onTouchEnd={this.stopButtonScroll}>
    </div>
  )

  render() {
    const props = {
      className: 'hzc',
      style: {}
    };

    if (typeof this.props.style === 'string') {
      props.className += ` ${this.props.style}`;
    }
    else {
      props.style = this.props.style;
    }

    if (this.state.hasScroll) {
      props.className += ' with-scroll';
    }

    return (
      <div ref={el => this.outer = el} {...props}>
        {this.scroller(ScrollDirection.left)}
        <div ref={el => this.inner = el} className="hzc__scrollable" onWheel={this.handleWheel}>
          {this.props.children}
        </div>
        {this.scroller(ScrollDirection.right)}
      </div>
    );
  }
}

export default HorizontalScrollableContainer;
