import * as React from 'react';
import config from './../../config';
import { Route, Switch, Redirect } from 'react-router-dom';

import Live from './views/live';
import Rules from './views/rules';
import Tickets from './views/tickets';
import FourZeroFour from './views/404';
import Schedule from './views/schedule';
import Prematch from './views/prematch';
import GamecastMock from './views/gamecast-mock';
import SingleEvent from './views/live/single-event';
import SessionExpired from './views/session-expired';

import Mock from './views/mock';

let trail = config.host.trail;

const routes = (
    <div>
      <Switch>
        <Route exact path={`${trail}/`} component={ () => <Redirect to={`${trail}/live`} /> } />
        <Route exact path={`${trail}/mock`} component={Mock} />
        <Route exact path={`${trail}/live`} component={Live} />
        <Route exact path={`${trail}/streaming`} component={Live} />
        <Route exact path={`${trail}/streaming/:sport`} component={Live} />
        <Route exact path={`${trail}/live/:sport`} component={Live} />
        <Route exact path={`${trail}/live/:sport/:game`} component={SingleEvent} />
        <Route exact path={`${trail}/prematch/:sport/:gameid/:game`} component={SingleEvent } />
        <Route path={`${trail}/prematch`} component={Prematch} />
        <Route path={`${trail}/prematch/:id`} component={ Prematch } />
        <Route path={`${trail}/rules`} component={Rules} />
        <Route path={`${trail}/tickets`} component={ Tickets } />
        <Route path={`${trail}/schedule`} component={Schedule } />
        <Route exact path={`${trail}/gamecast-mock`} component={GamecastMock} />
        <Route path={`${trail}/session-expired/:status`} component={ SessionExpired } />
        {/* <Route path="*" component={FourZeroFour } /> */}
      </Switch>
    </div>

);

export default routes;
