import * as React from 'react';
import { connect } from 'react-redux';

import { TextField, CheckBox, RadioButtonList } from './';

export interface IFormProps {
  onSubmit?: any;
  model: any;
  onFieldChange: any;
}

const Form = ({ loading, onSubmit, children, model, dispatch, onFieldChange }: any) => {
  const handleSubmit = (e: any) => {
    e.preventDefault();
    if (onSubmit) {
      onSubmit(e);
    }
  };

  const handleFieldChange = (fieldValue: any, fieldModel: any) => {
    dispatch(onFieldChange(fieldModel, fieldValue));
  };

  const validTypes = [TextField, CheckBox, RadioButtonList];
  const isFormField = (el: any) => validTypes.indexOf(el.type) >= 0;

  const cloneChildren = (fields: any): any => {
   return React.Children.map(fields, child => {
      if (!React.isValidElement(child)) {
        return child;
      }

      const el = child as React.ReactElement<any>;

      if (!isFormField(el)) {
        return React.cloneElement(el, {
          children: cloneChildren(el.props.children)
        });
      }

      return React.cloneElement(el, {
        onChange: (value: any) => handleFieldChange(value, el.props.model),
        value: model[el.props.model]
      });
    });
  };

  return (
    <form onSubmit={handleSubmit}>
      <fieldset disabled={loading}>
        { cloneChildren(children) }
      </fieldset>
    </form>
  );
};

export default connect(
  (state: any, ownProps: IFormProps) => Object.assign({}, ownProps, { loading: state.loading })
)(Form);
