import  {
  GAMECAST_ON, GAMECAST_OFF,
  SHOW_SPORTS_LIST_ON, SHOW_SPORTS_LIST_OFF,
  IS_MOBILE_ON, IS_MOBILE_OFF
} from '../actions/ui-toolbar';

export const uiToolbarInitialState: any = {
  change: false,
  gamecast: true,
  gamecast_rz: true,
  modal_open: false,
  bet_markets_open: false,
  show_sports_list: true,
  isMobileOn: false
};

export const uiToolbar = (state = uiToolbarInitialState, action: any): any => {
  switch (action.type) {

    case GAMECAST_ON:
      return Object.assign({}, state, {
        gamecast: true
      });
    case GAMECAST_OFF:
      return Object.assign({}, state, {
        gamecast: false
      });

    case SHOW_SPORTS_LIST_ON:
      return Object.assign({}, state, {
        show_sports_list: true
      });

    case SHOW_SPORTS_LIST_OFF:
      return Object.assign({}, state, {
        show_sports_list: false
      });

    case IS_MOBILE_ON:
      return Object.assign({}, state, {
        isMobileOn: true
      });

    case IS_MOBILE_OFF:
      return Object.assign({}, state, {
        isMobileOn: false
      });

  }
  return state;
};
