import * as React from 'react';
import { connect } from 'react-redux';
import { homeAwayToLink } from '../../../../../helpers';
import { GetSportName } from '../../../../../constants';

import Link from '../../../../../components/form/link';
import Clock from './clock';

import LiveStreamButton from './../../../../misc/live-stream-button';

class LinkEventDesktop extends React.Component<any, any> {

  render() {

    const { event, path, score, clock, handleClick, subscribedEvent, betType } = this.props;
    const { home, away, sport /*, sep*/ } = event;
    const sportName = GetSportName(sport);
    // console.log(score);
    const isPrematch = event.live === 0;
    const to = isPrematch ? `/prematch/${sportName}/${event.id}/${homeAwayToLink(home, away)}` :
    `/live/${sportName}/${homeAwayToLink(home, away)}`;

    const scoreTeam1 = score !== undefined ? score.scoreTeam1 : 0;
    const scoreTeam2 = score !== undefined ? score.scoreTeam2 : 0;

    const hasClock = sport !== 10 && sport !== 7 && sport !== 9 ? true : false;
    const hasInning = sport === 7 || sport === 10 || sport === 9;

    const eventGamecast = (e: any, id: any) => handleClick(e, event.id);

    const linkEventClass = ['link-event-desktop'];

    if (sport === 1)
      linkEventClass.push('soccer');
    if (sport === 24)
      linkEventClass.push('cricket');

    const getTeam = (team: any, score: any) => {
      let scoreReal = score;
      if (sport === 9 || sport === 17)
        scoreReal = '';
      return (
        <div className={`team ${sportName}`}>
          <div className="team-name">{team}</div>
          {
            !isPrematch &&
            <div
              style={ score > 99 ? { fontSize: '9px' } : {} }
              className={`team-score ${sportName} score-real-desktop-20190717`}>
                { typeof scoreReal === 'string' ? scoreReal.replace('All Out', 'AO') : scoreReal }
              </div>
          }
      </div>
      );
    };

    return (
      <span className={linkEventClass.join(' ')}>
        {
          (hasClock || hasInning) && (sport !== 1 && sport !== 24) ?
           <Clock
            score={score}
            clock={clock}
            sport={sport}
            event={event}
            betType={betType}
            eventGamecast={eventGamecast}
            subscribedEvent={subscribedEvent}/> : ''
        }
        <Link to={to} title="click to view more odds" className={'link-event-desktop'}>
          <div id={event.id} className="link-table" >
            {event.inverted ? getTeam(away, scoreTeam1) : getTeam(home, scoreTeam1)}
            {event.inverted ? getTeam(home, scoreTeam2) : getTeam(away, scoreTeam2)}
            { (sport === 37 && score && score.scoreByPeriod && score.scoreByPeriod[0]) &&
              <div style={{display: 'flex' }}>
                {
                  score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                    if (item.t1 !== '' && item.t2 !== '' && item.description !== 'Total') {
                      return (
                        <div className="tennis-score-mobile" key={i}>
                          <div>{item.t1} - {item.t2}</div>
                        </div>
                      );
                    }
                    return '';
                  })
                }

              </div>
            }
            { (sport === 17) &&

              <div style={{display: 'flex' }}>
                {
                  score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {

                    if (item.t1 !== '' && item.t2 !== '') {
                      return (
                        <div className="tennis-score-mobile" key={i}>
                          <div>{item.t1} - {item.t2}</div>
                        </div>
                      );
                    }
                    return '';
                  })
                }

              </div>
            }
            { (sport === 7) &&

              <div style={{display: 'flex' }}>
                {
                  score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                    if (item[0] !== '') {
                      return (
                        <div className="tennis-score-mobile" key={i}>
                          <div>{item[0]} - {item[1]}</div>
                        </div>
                      );
                    }
                    return '';
                  })
                }

              </div>
            }
            {/* JSON.stringify(event.videoUrl) */}
            { (sport === 9) &&
              <div style={{display: 'flex' }}>
              {
                score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
                  const validDescriptions = ['1st', '2nd', '3rd', '4th', '5th'];
                  if (item.description && typeof item.t1 !== 'undefined' && typeof item.t2 !== 'undefined' && validDescriptions.indexOf(item.description) >= 0 && item.t1 !== '' && item.t2 !== '') {
                    return (
                      <div className="tennis-score-mobile" key={i}>
                        <div>{item.t1} - {item.t2}</div>
                      </div>
                    );
                  }
                  return '';
                })
              }
              </div>
            }
          </div>
        </Link>
        { (sport === 7 || sport === 17) &&
          <span style={{position: 'absolute', margin: '3px', fontSize: '12px'}}>
            <LiveStreamButton
              event={event}
              liveStreamStyles={{}}
              handleOnClick={eventGamecast}
            />
          </span>
        }
        { (sport === 1 || sport === 24) &&
          <Clock
            score={score}
            clock={clock}
            sport={sport}
            event={event}
            betType={betType}
            eventGamecast={eventGamecast}
            subscribedEvent={subscribedEvent}/>
        }
      </span>
    );
  }
}

export default connect(
  (state: any, initialOwnProps: any) => Object.assign({}, initialOwnProps, {
    path: state.router.location.pathname,
    score: state.live.scores[initialOwnProps.event.id],
    clock: state.live.clocks[initialOwnProps.event.id],
    betType: state.live.betType
  })
)(LinkEventDesktop);
