import { IBaseBallFieldAnimationState, nt } from '../../constants';
import { deepAssign } from '../../../../../helpers';

const pitchStartT1 = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, {
  ball: {
    x: 100,
    y: 22,
    scale: 0
  }
});

const pitchEndT1 = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, {
   pitcherStart: {
    display: 'block'
  },
  pitcherTwo : {
    display: 'none'
  }
});

export function pitch(pitchStart: any) {
  const start = pitchStartT1(pitchStart);
  const end = pitchEndT1(start);
  return {
    start,
    end
  };
}
