import * as React from 'react';
import Link from '../form/link';
import { connect } from 'react-redux';
import { filterStreaming } from './../../actions/live';

interface IMenuItemProps {
  path: string;
  icon: string;
  children?: any;
  view: number;
  menuData: any;
  dispatch: any;
}

const MenuItem = ({ path, icon, children, view, menuData, dispatch  }: IMenuItemProps) => (
  <li className="menu__item" onClick={() => dispatch(filterStreaming(children === menuData.streaming))}>
    <Link className="menu__link with-ripple" activeClassName={`menu__link--active`} to={ `/${path}` }>
      { view !== 3 && view !== 1 && <i className={`i i-${icon}`}></i> } {children}
    </Link>
  </li>
);

export default connect(
  (state: any) => ({
    menuData: state.languageData.menu
  })
)(MenuItem);
