import * as React from 'react';

let color = '#333';

const Card = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250'
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps} >
      <rect x="50" y="10" width="150" height="230" rx="15" ry="15" strokeWidth="0"/>
    </svg>
  );
};

export default Card;
