import * as React from 'react';

let className = 'md-list__item';

const ListItem = ({ onClick, item, selected, children }: { onClick: (item: any) => void, item: any, selected: boolean, children?: any }) => (
  <li className={`${className}${ selected ? ' md-list__item--selected' : '' }`} onClick={e => onClick(item)}>{children}</li>
);

export default ListItem;
