import * as React from 'react';
import config from './../../../../config';
import { FootBallTeam, actionTextFootBall } from './constants';

import { yardsMap } from './constants';

export class FootBallFieldNew extends React.Component<any, any> {

  promise: Promise<any|void> = Promise.resolve();

  state = {
    svgHeight: 'auto',
    animation: '',
    action: this.props.action,
    lottieAnimImagePath: './../images/football/'
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 105, height = 68 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentDidMount() {

    const { path } = this.props;

    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
    const pathSplit = path ? path.split('/') : [];
    // console.log(pathSplit);
    // Get path to see if this is single or live events section
    if (path === '/gamecast-mock') {
      this.setState({
        lottieAnimImagePath: `./../..${config.host.trail}/images/football/`
      });
    } else {
      if (pathSplit[pathSplit.length - 1] === 'american-football') {
        this.setState({
          lottieAnimImagePath: path && './../images/football/'
        });
      } else {
        this.setState({
          lottieAnimImagePath: path && pathSplit[pathSplit.length - 1].includes('-') ? './../../images/football/' : './../images/football/'
        });

      }
    }
  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {

      if ((newProps.action !== 1401 || this.props.action !== 1401) && (newProps.action !== 1473 || this.props.action !== 1473) && (newProps.action !== 1474 || this.props.action !== 1474)) {

        this.setState({
          action: newProps.action
        });

      }
    }

  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);

  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === FootBallTeam.Team1 ? event.inverted ? team2 : team1 : team === FootBallTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  getTimeouts() {

    const { width = 105, height = 68, action, timeouts } = this.props;
    // console.log(`get ${action}`);
    // console.log(this.props.matchData);
    let mockTimeout = timeouts;
    if (!timeouts) return <g></g>;

    // console.log(this.props);
    const t1TO = mockTimeout.split(',')[0] && parseInt(mockTimeout.split(',')[0]);
    const t2TO = mockTimeout.split(',')[1] && parseInt(mockTimeout.split(',')[1]);

    return [1, 6, 11, 4, 9, 14].map((it, i) => {
     return (
      <g key={i}>
        { i <= 2 ?
          <rect x={it} y={(height - 10) + 2} opacity={t1TO >= i + 1 ? 1 : 0} strokeWidth="0" fill={'red'} width={3} height={1} /> :
          <rect x={ width - it } y={(height - 10) + 2} opacity={t2TO >= i - 3 ? 1 : 0} strokeWidth="0" fill={'red'} width={3} height={1} />
        }
      </g>
     );
   });
  }

  render() {
    const { width = 105, height = 68, action, matchData } = this.props;

    const getPrefix = (num: any) => {
      switch (num) {
        case '1':
          return '1st';
        case '2':
          return '2nd';
        case '3':
          return '3rd';
        case '4':
          return '4th';
        default:
          return '';
      }
    };

    const toGo = () => {

      let isGoal = false;

      if (!this.props.matchData) return '';

      if (this.props.extraData) {
        let { team, extraData } = this.props;
        isGoal = team === 2 && extraData.yellowLine <= 0 || team === 1 && extraData.yellowLine >= 100;
      }

      let down = getPrefix(this.props.matchData.split('#')[1]);
      let _toGo: any = parseFloat(this.props.matchData.split('#')[0]) * 100;
      if (!down || !_toGo) return '';
      _toGo = isGoal ? 'Goal' : Math.floor(_toGo);

      return this.props.matchData && `${down} and ${_toGo}`;
    };

    let className = 'gc-football';

    let _actionText = actionTextFootBall(action);

    if (action === 1459 || action === 1460) {

      try {
        let matchData_ = matchData.includes('^') ? matchData.split('^')[0] : matchData;
        let yards = matchData_.split('##')[matchData_.split('##').length - 1];
        yards = Math.floor(parseFloat(yards) * 100);
        _actionText = `${_actionText} for ${yards} yards`;

      }

      catch (err) {
        console.log(err);
        _actionText = ``;
      }

    }

    const PrintLines = () => {
      let { extraData, team, matchData } = this.props;

      if (!Object.keys(extraData)[1] || matchData === '####' || !extraData.redLine || !extraData.yellowLine) return <g></g>;
      if (extraData.redLine === extraData.yellowLine) return <g></g>;

      let yellowLine = typeof extraData.yellowLine === 'number' ? Math.ceil(extraData.yellowLine) : 0;
      let redLine =  typeof extraData.yellowLine === 'number' ? Math.ceil(extraData.redLine) : 0;

      if (!yardsMap[yellowLine] || !yardsMap[redLine] || !yardsMap[yellowLine].x1 || !yardsMap[redLine].x1 || !yardsMap[redLine].x2 || !yardsMap[yellowLine].x2) return <g></g>;

      return (
        <g>
          <line
            x1={team === 1 ? yardsMap[yellowLine].x1 : yardsMap[redLine].x1 } y1="63.5"
            x2={team === 1 ? yardsMap[yellowLine].x2 : yardsMap[redLine].x2} y2="43"
            stroke={ team === 1 ? 'yellow' : '#da1820' } strokeWidth="0.75" />

          <line
            x1={team === 1 ? yardsMap[redLine].x1 : yardsMap[yellowLine].x1 } y1="63.5"
            x2={team === 1 ? yardsMap[redLine].x2 : yardsMap[yellowLine].x2 } y2="43"
            stroke={ team === 1 ? '#da1820' : 'yellow' } strokeWidth="0.75" />
        </g>
      );

    };
    console.log(`${this.state.lottieAnimImagePath}foot_field_.png`);
    return (
      <div ref="gc" className={className}>
        <svg className={`${className}__court`} viewBox={`0 0 ${width} ${height + 2}`} fill="transparent">
          <image x="0" width={width} height={height} xlinkHref={`${this.state.lottieAnimImagePath}foot_field_.png`} />
          <PrintLines />

          { ( action !== 1402 && action !== 1406 && action !== 1460 && action !== 1459 && action !== 1461 ) && (this.team === this.props.team1 || this.team === this.props.team2) &&
            <g>
              <text fontFamily="Ubuntu-Light" x={width * 0.5} y="7" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
                { toGo() !== '' && this.team}
              </text>
              <text fontFamily="Ubuntu-Light" x={width * 0.5} y="12" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
                { toGo() !== '' && toGo()}
              </text>
            </g>
          }

          {
            (action !== 1401 && action !== 1473 && action !== 1474 && _actionText !== '') &&
            <g>
              <text fontFamily="Ubuntu-Medium" x={width * 0.5} y="20" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '5px' }}>
                { (action !== 451 && action !== 1452 && action !== 1470 && action !== 1469 && action !== 468 && action !== 1454 && action !== 1453 && action !== 1422 ) && this.team }
              </text>
              <text fontFamily="Ubuntu-Medium" x={width * 0.5} y="27" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '5px' }}>
                { _actionText }
              </text>
            </g>
          }

          {
            (action === 1402 || action === 1406 || action === 1403) &&
              <text fontFamily="Ubuntu-Light" x={width * 0.5} y="2.5" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
                { this.props.matchData && this.props.matchData.replace(/[^a-zA-Z ]/g, '') }
              </text>
          }

        </svg>
      </div>
    );

  }
}
