import * as React from 'react';

class TeamSplit extends React.Component<any, any> {

  renderSplitTeam() {

    const { x, y, size, splitTeam } = this.props;

    let getY = (i_: number) => {
      return i_ !== splitTeam.length - 1 ? (y - (size * ((splitTeam.length - 1) - i_)) + 0.25) : y;
    };

    return (
      splitTeam && splitTeam.map((item: any, i: any) =>
        <tspan key={i} x={x} y={getY(i)}>{item}</tspan>
      )
    );

  }

  render() {

    const { splitIndex, team, x, y, size, textAnchor, br, bx, by, color } = this.props;
    let fontSize = size + 'px';

    if ( team.length >= (splitIndex) ) {
      return (
        <g>
          <rect x={bx} y={by} width="23" height="3" fill={color} transform={`rotate(${br}, ${bx + 3}, ${by - 2})`}  />
          <text x={x} y={y - 0.5} fontSize={fontSize} strokeWidth="0" fill="rgba(55, 55, 55, 0.75)" textAnchor={textAnchor} >
            {/*
              splitTeam && splitTeam.map((item, i) =>
                <tspan key={i} x={x} y={i === 0 ? y - size + 0.25 : y }>{item}</tspan>
              )
            */}
            {
              this.renderSplitTeam()
            }
          </text>
        </g>
      );

    } else {
        return (
          <g>
            <rect x={bx} y={by} width="18" height="3" fill={color} transform={`rotate(${br}, ${bx}, ${by})`}  />
            <text x={x} y={y} fontSize={fontSize} strokeWidth="0" fill="rgba(55, 55, 55, 0.75)" textAnchor={textAnchor} >{team}</text>;
          </g>
        );
    }

  }
}

export default TeamSplit;
