import { ICricketGroundAnimationState, nt } from  '../constants';
import { deepAssign } from '../../../../helpers';

const bowlingStart = (start: any): ICricketGroundAnimationState => deepAssign({}, nt, start, {
  ball: {
    opacity: 1
  }
});
const bowlingEnd = (start: any): ICricketGroundAnimationState => deepAssign({}, nt, start, {
  ball: {
    opacity: 1,
    x: 45,
    y: 25

  }
});

export function bowling(bowlStart: any) {
  const start = bowlingStart(bowlStart);
  const end = bowlingEnd(start);
  return {
    start,
    end
  };
}
