import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../config';
import { Sports } from '../../../constants';
import {
  BasketballCourt,
  BaseBallField,
  IceHockeyRink,
  FootBallField,
  FootBallFieldNew,
  SoccerField,
  TennisCourt
} from '../../gamecast';

import VideoPlayer from './../../misc/VideoComponent';
import VideoPlayerHeader from '../../misc/VideoComponent/player-header';

import CricketLottie from './../../gamecast/cricket-lottie';
import FootballLottie from './../../gamecast/football-lottie';

import { toggleVideoTab } from './../../../actions/live';

class GamecastWrapper extends React.Component<any, any> {

  constructor(props: any) {
    super(props);

    this.state = {
      loaded : false,
      isVideo: false,
      videoUrl: ''
    };

    this.handleSwitch = this.handleSwitch.bind(this);
  }

  componentDidMount() {
    this.setState({ loaded: true });

    if (this.props.event) {
      this.setState({ videoUrl: this.props.event.videoUrl });

      const { balance } = this.props;
      let balance_ = typeof balance === 'number' ?
      balance : Number(balance);

      const shouldDisplayVideoOnNoGamecast = this.evHasVideoNoGamecast(this.props.event);
      const shouldDisplayVideoIfAutoplay = config.hasAutoStreamPlay && balance_ > 0 && this.props.event && this.props.event.videoUrl !== '';

      // Set video tab true to play display video component on mount if event has
      // no gamecast but video or on event that has video and config.hasAutoStreamPlay...
      this.props.dispatch(toggleVideoTab(shouldDisplayVideoOnNoGamecast || shouldDisplayVideoIfAutoplay));
    }

  }

  componentWillUnmount() {
    this.setState({ loaded: false });
    // this.props.dispatch(toggleVideoTab(false));
  }

  componentWillReceiveProps(newProps: any) {

    if (!newProps.event || !this.props.event) return;

    const resetVideoStream = () => {
      console.log('reset video stream');
      // this.setState({ isVideo: false });
      this.props.dispatch(toggleVideoTab(false));
      this.setState({ videoUrl: newProps.event.videoUrl });

      if (this.props.isVideoTab || config.hasAutoStreamPlay || this.evHasVideoNoGamecast(newProps.event)) {
        let that = this;
        setTimeout(() => {
          that.props.dispatch(toggleVideoTab(true));
          // that.setState({ isVideo: true });
        }, 500);
      }
    };

    let switchEvent = newProps.event.id !== this.props.event.id;
    let videoUrlHasChanged = (newProps.event.id === this.props.event.id) && (newProps.event.videoUrl !== this.props.event.videoUrl);

    if (switchEvent) {
      console.log('from switching video');
      if (newProps.event.videoUrl !== '' || this.evHasVideoNoGamecast(newProps.event)) {
        resetVideoStream();

        const { balance } = this.props;
        let balance_ = typeof balance === 'number' ?
        balance : Number(balance);

      } else {
        this.props.dispatch(toggleVideoTab(false));
      }

    }

    if (videoUrlHasChanged) {
      console.log('reseting video steram....');
      resetVideoStream();
    }

  }

  evHasVideoNoGamecast(ev: any) {

    if (!ev) return false;

    const { hasGameCast, videoUrl, videoSource } = ev;
    const evHasVideoNoGamecast = config.hasVideoStream && (!hasGameCast && videoUrl !== '' && videoSource > 0);
    return evHasVideoNoGamecast;
  }

  handleSwitch(source: any) {
    this.props.dispatch(toggleVideoTab(source === 'vid-switch'));
  }

  render() {
    const { event, view, uiToolbar } = this.props;
    // console.log(this.props.score);
    if (!event) return null;
    // console.log(event);
    const videoJsOptions = {
      event,
      autoplay: true,
      controls: true,
      fluid: true,
      sources: [{
        src: this.state.videoUrl,
        type: 'application/x-mpegURL'
      }]
    };

    let Component: any = () => <div></div>;

    if (config.allowAnimations && event.hasGameCast) {
      switch (event.sport) {
        case Sports.baseball:
          Component = BaseBallField;
          break;
        case Sports.basketball:
          Component = BasketballCourt;
          break;
        case Sports.iceHockey:
          Component = IceHockeyRink;
          break;
        case Sports.soccer:
          Component = SoccerField;
          break;
        case Sports.tennis:
          Component = TennisCourt;
          break;
        case Sports.americanFootball:
          let FootballGamecast: any = config.footballGamecast === 'modern' ?
          FootBallFieldNew : FootBallField;

          if (config.footballGamecastAnimated) {
            FootballGamecast = FootballLottie;
          }

          Component = FootballGamecast;

          break;
        case Sports.cricket:
          Component = CricketLottie;
          break;
      }
    }

    const hasVideo = config.hasVideoStream && this.props.isVideoTab;
    const className_: any = event && event.sport === 24 ? 'gc-wrap-lottie' : 'gc-wrap-container';

    if (!uiToolbar.gamecast) return null;

    return (
      <div className={className_}>
        { hasVideo &&
          <VideoPlayerHeader
            event={event}
            isVideo={this.props.isVideoTab}
            handleSwitch={this.handleSwitch}

          />
        }

        { !hasVideo ?
          <Component
            view={view}
            event={event}
            team1={event.home}
            team2={event.away}
            path={this.props.path}
            team={this.props.team}
            score={this.props.score}
            action={this.props.action}
            loaded={this.state.loaded}
            timeouts={this.props.timeouts}
            extraData={this.props.extraData}
            matchData={this.props.matchData}
          /> :
          <VideoPlayer
            { ...videoJsOptions }
          />
        }
      </div>
    );

  }
}

export default connect(
  (state: any, ownProps: any) => ({
    view: state.settings.view,
    uiToolbar: state.uiToolbar,
    isVideoTab: state.live.isVideoTab,
    path: state.router.location.pathname,
    score: state.live.scores[ownProps.event.id],
    balance: state.user ? state.user.balance_real : 0,
    event: ownProps.event && state.eventsFlatten.byId[ownProps.event.id]
  })
)(GamecastWrapper);
