import { Sports } from './constants';
import config from '../config';

export const getCookie = (name: string): any => {

  let match = document.cookie.match(new RegExp('(^| )' + name + '=([^;]+)'));

  if (match) return match[2];

};

export const deleteCookie = (name: string) => {
  let cookieValue = getCookie(name);
  let fixedPathPrefix = config.host.trail === '' ? '/' : config.host.trail;
  // To delete cookie, set it's new expiry date to the past time
  if (getCookie(name)) {
    document.cookie = `${name}=${cookieValue}; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=${fixedPathPrefix}`;
  }

};

export const deepAssign = (target: any, ...args: any) => {
  args.forEach((obj: any) => {
    Object.keys(obj).forEach(key => {
      if (typeof obj[key] === 'object') {
        if (!target.hasOwnProperty(key)) {
          target[key] = {};
        }
        deepAssign(target[key], obj[key]);
      }
      else {
        Object.assign(target, ...args);
      }
    });
  });
  return target;
};

export const range = (size: number) => {
  const arr = [];
  for (let i = 0; i < size; i++) {
    arr.push(i + 1);
  }
  return arr;
};

export const normalizePath = (path: any) => {

  if (path.length > 0) {
    while (path[path.length - 1] === '/') {
      path = path.slice(0, path.length - 1);
    }
  }

  return path;
};

export const spaceToCamelCase = (str: string) => str.slice(0, 1).toLowerCase() + str.split(' ').join('').slice(1);
export const spaceToDashed = (str: string) => str.toLowerCase().split(' ').join('-');
export const dashedToSpace = (str: string) => str.split('-').join(' ');
export const camelCaseToDashed = (str: string) => str.replace(/([a-zA-Z])(?=[A-Z])/g, '$1-').toLowerCase();
export const dashedToCamelCase = (str: string) => str.slice(0, 1).toLowerCase() + str.split('-').map(s => s.slice(0, 1).toUpperCase() + s.slice(1)).join('').slice(1);
export const underlineToCamelCase = (str: string) => str.slice(0, 1).toLowerCase() + str.split('_').map(s => s.slice(0, 1).toUpperCase() + s.slice(1)).join('').slice(1);
export const replaceSlashs = (str: string, to: string = '+') => str.replace(/\//g, to);
export const homeAwayToLink = (home: string, away: string) => `${spaceToDashed(replaceSlashs(home))}-vs-${spaceToDashed(replaceSlashs(away))}`;
export const sportHasTime = (sport: Sports) => [Sports.baseball, Sports.tennis].indexOf(sport) === -1;

export const groupBy = (arr: any[], propName: string): any[] => {
  return arr.reduce((a, b) => {
    let currArr = a.filter((array: any) => array[0][propName] === b[propName])[0];
    if (!currArr) {
      currArr = [];
      a.push(currArr);
    }
    currArr.push(b);
    return a;
  }, []);
};

const randomHex = () => {
    let window_: any = window;
    let cryp = crypto || window_['msCrypto'];
    return cryp ? cryp.getRandomValues(new Uint8Array(1))[0] % 16 : Math.random() * 16;
};

export const sportUsesReverseTickTimer = (sportID: any) => {
  return (sportID === Sports.americanFootball || sportID === Sports.basketball || sportID === Sports.iceHockey);
};

export const uuid: any = (a?: any) => a ? (a ^ randomHex() >> a / 4).toString(16) : ('' + 1e7 + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, uuid);

export const upperCaseFirst = (str: any) => str.charAt(0).toUpperCase() + str.substring(1);
export const createRoute = (partialPath: any) => config.host.trail + partialPath;
