import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../config';
import { getOutcomes, detectmob } from './../live/live-helpers';
import { reqPrematchEvents, getPrematchLeagues  } from '../../../actions/prematch';

import Events from './events';
import Title from './../../title';
import Link from './../../form/link';
import SwitchNew from './../../misc/switch-new';
import PrematchLink from './../../misc/prematch-link';
import TypeSwitcher from './../live/type-switcher';
import SidebarSportsMenu from './sidebar-sports-menu';
import NoEventsLink from './../live/european/no-events-link';

import { loading } from '../../../actions/loading';
import { changeBetType } from '../../../actions/live';
import { subscribeEvent, unsubscribeEvent } from '../../../actions/live';
import { redirectToNotLoggedInPage } from '../../../actions/user';

class Prematch extends React.Component<any, any> {

  constructor(props: any) {
    super(props);

    this.state = {
      subscribedTo: {sport: null, country: null},
      activeSport: 0,
      activeCountry: -1,
      activeLeague: '',
      isTablet: false,
      isMobile: false,
      isOpen: false,
      headerHeight: 27
    };

    this.getActiveSport = this.getActiveSport.bind(this);
    this.getActiveCountry = this.getActiveCountry.bind(this);
    this.getActiveLeague = this.getActiveLeague.bind(this);
    this.openHeader = this.openHeader.bind(this);
    this.setRef = this.setRef.bind(this);

  }

  componentDidMount() {
    const { dispatch, eventsLoaded, user, prematchLeagues } = this.props;

    if (!eventsLoaded) {
      dispatch(reqPrematchEvents());
      // console.log('request prematch');
    }

    window.addEventListener('resize', this.getWidth);
    this.setState({ isMobile : window.innerWidth < 760 || detectmob() ? true : false });
    this.setState({ isTablet : window.innerWidth > 765 && detectmob()  ? true : false });

    const isOverflowHidden = window.innerWidth < 1008 === false && !detectmob();
    document.body.style.overflow = `hidden ${!isOverflowHidden ? 'scroll' : ''}`;

    // We need to subscribe our client to ws updates as soon as component mounts
    this.subscribeToCountryOnMount();

  }

  subscribeToCountryOnMount() {

    const { dispatch } = this.props;
    let that = this;

    setTimeout(() => {

      let ev = that.props.prematchLeagues && that.props.prematchLeagues[0];
      let country = ev && ev.countryname;
      let sport = ev && ev.sport;

      let bookmakerId = that.props.user && that.props.user.bookmakerId[sport];

      if (!bookmakerId) return;

      that.setState({subscribedTo: {sport, country: `${country.replace(' ', '').toUpperCase()}`} });
      dispatch(subscribeEvent(`${country.replace(' ', '').toUpperCase()}`, sport, bookmakerId, 'prematch'));
      // console.log(that.state.subscribedTo);

      // console.log(that.props.prematchLeagues);
      // console.log('subscribing on mount');
    }, 2000);

  }

  componentWillUnmount() {

    const { dispatch, user } = this.props;
    let bookmakerId = user && this.state.subscribedTo.sport !== '' ? user.bookmakerId[this.state.subscribedTo.sport] : '';

    window.removeEventListener('resize', this.getWidth);
    if (window.innerWidth < 1008 === false && !detectmob()) {
      document.body.style.overflow = null;
    }

    // We need to unsubscribe our client to ws updates as soon as component unmounts
    if (bookmakerId && this.state.subscribedTo.sport !== null || this.state.subscribedTo.country !== null) {
      dispatch(unsubscribeEvent(this.state.subscribedTo.country, this.state.subscribedTo.sport, bookmakerId, 'prematch'));
    }

    console.log('unmounting prematch');
  }

  getWidth = () => {
    this.setState({
      isMobile: window.innerWidth < 760 || detectmob() ? true : false
    });
    const isOverflowHidden = window.innerWidth < 1008;
    document.body.style.overflow = `hidden ${isOverflowHidden ? 'scroll' : ''}`;
  }

  getActiveSport(sport: any) {
    const { activeSport } = this.state;
    this.setState({ activeSport: activeSport === sport ? 0 : sport });
    this.setState({ activeLeague: '' });
    this.setState({ activeCountry: -1 });

  }

  getActiveCountry(sport: any, country: any) {
    const { dispatch, user } = this.props;
    let bookmakerId = user.bookmakerId[sport];
    // const { activeCountry } = this.state;
    dispatch(loading());
    dispatch(getPrematchLeagues(sport, country));
    dispatch(loading());

    if (this.state.subscribedTo.sport !== null || this.state.subscribedTo.country !== null) {
      dispatch(unsubscribeEvent(this.state.subscribedTo.country, this.state.subscribedTo.sport, bookmakerId, 'prematch'));
    }

    console.log(user.bookmakerId[sport]);
    dispatch(subscribeEvent(`${country.replace(' ', '').toUpperCase()}`, sport, bookmakerId, 'prematch'));

    console.log('unsubscribe from', this.state.subscribedTo);
    console.log('subscribe me to ', `${sport}-${country.replace(' ', '').toUpperCase()}`);
    this.setState({subscribedTo: {sport, country: `${country.replace(' ', '').toUpperCase()}`} });

    this.setState({ activeCountry: country });
    this.setState({ activeLeague: '' });

  }

  getActiveLeague(league: any) {
    this.setState({ activeLeague: league });
    this.openHeader();

    // console.log(this.state.headerHeight.clientHeight);
  }

  openHeader() {
    this.setState({ isOpen: this.state.isOpen ? false : true });
  }

  setRef(ref: any) {
    this.setState({ headerHeight: ref });
  }

  componentWillReceiveProps() {
    const { prematchEvents, eventsLoaded, prematchLeagues, dispatch } = this.props;
    // console.log('receiving props');
    if (prematchEvents && eventsLoaded && prematchLeagues.length <= 0) {
      dispatch(reqPrematchEvents());
      // console.log(`requesting events a`);
    }
    if (!eventsLoaded) {
      // dispatch(reqPrematchEvents());
      // console.log(`requesting events b`);
    } else {
      // console.log(prematchEvents.length);
    }
    if (!prematchEvents && (prematchLeagues && prematchLeagues.length <= 0)) {
      dispatch(reqPrematchEvents());
      // console.log(`requesting events c`);
    } else {
      if (prematchLeagues) {
        // console.log(prematchLeagues.length);
      }

    }
  }

  changeBet = () => {
    const { dispatch } = this.props;
    return dispatch(changeBetType(+!this.props.betType));
  }

  render() {

    const {
      view,
      user,
      title,
      sports,
      betType,
      outcomeList,
      eventsLoaded,
      killedSession,
      prematchEvents,
      prematchLeagues,
      userMainOutcomes
    } = this.props;

    const { activeSport, activeCountry, activeLeague } = this.state;
    let { isMobile, isTablet } = this.state;

    if (killedSession || user && user.response && user.response.initialisation_result_code === 0) {
      this.props.dispatch(redirectToNotLoggedInPage());
     }

    const isDisplaySmall = isMobile && !isTablet;

    const bookmakerId  = !user ? {} : user.bookmakerId;

    if (!eventsLoaded) {
      return (
        <NoEventsLink
          dispatch={this.props.dispatch}
          isPrematch={true}
          sport={0}
          title={title}
          view={view}
        />
      );
    }

    const {
      loading,
      betSlipOpen,
      settingsOpen,
      parlayBySport,
      betConfirmationOpen
    } = this.props;

    // 'Hidding' events when betslip or betconfirmation are open to prevent iFrame height bug on iOS
    const showEvents = (!betSlipOpen && !betConfirmationOpen && !settingsOpen) || !detectmob();

    return (
      <Title title={title}>

        <div className="prematch md-container">
          <div className="header-cont">
            {/* config.hasParlay && parlayBySport &&
              <div className={view === 3 ? `bet-type-european` : `bet-type`}>
                <div className="bet-type-select">
                  <TypeSwitcher />
                </div>
              </div>
            */}
            { config.hasParlay && parlayBySport &&
              <div className={view === 3 ? `bet-type-european` : `bet-type`}>
                <SwitchNew
                  className_={'prematch'}
                  checked_={betType === 1}
                  leftText={'Straight'}
                  rightText={'Parlays'}
                  onToggle={() => this.changeBet()}
                />
              </div>
            }
            {
              config.hasPrematchLink &&
              <div className="prematch-link-desktop-cont prematch">
                <Link
                  activeClassName=""
                  to={`/`}
                >
                  <SwitchNew
                    className_={'prematch'}
                    checked_={true}
                    leftText={config.switchLiveText}
                    rightText={config.switchPrematchText}
                    onToggle={() => console.log('{}')}
                  />
                </Link>
              </div>

            }
          </div>
          <div className="section-container" style={{width: '100%'}}>
            <SidebarSportsMenu
              getActiveCountry={this.getActiveCountry}
              getActiveLeague={this.getActiveLeague}
              getActiveSport={this.getActiveSport}
              prematchLeagues={prematchLeagues}
              prematchEvents={prematchEvents}
              isDisplaySmall={isDisplaySmall}
              activeCountry={activeCountry}
              parlayBySport={parlayBySport}
              openHeader={this.openHeader}
              activeLeague={activeLeague}
              isOpen={this.state.isOpen}
              activeSport={activeSport}
              betType={this.props.betType}
              setRef={this.setRef}
              sports={sports}
              view={view}
            />
            { prematchLeagues && showEvents &&

              <Events
                divElement={this.state.headerHeight}
                userMainOutcomes={userMainOutcomes}
                prematchLeagues={prematchLeagues}
                isDisplaySmall={isDisplaySmall}
                activeLeague={activeLeague}
                bookmakerId={bookmakerId}
                outcomeList={outcomeList}
                view={view}

              />

            }
          </div>
        </div>

      </Title>
    );
  }

}

export default connect(
  (state: any, ownProps: any) => {
    const prematchEvents = state.prematch.events;
    const eventsLoaded = prematchEvents.length > 0;
    const killedSession = state.user.killedSession;

    if (!eventsLoaded) {
      return {
        eventsLoaded,
        killedSession
      };
    }
    const prematchLeagues = state.prematch.leagues;

    const event = prematchLeagues && prematchLeagues[0];
    if (!event) {
      return {
        eventsLoaded,
        killedSession
      };
    }
    // console.log(prematchLeagues);
    // console.log('events');
    // console.log(prematchEvents);
    // console.log(state.prematch);
    return {
      title: 'Prematch',
      sports: state.languageData.sports,
      prematchEvents,
      eventsLoaded,
      prematchLeagues,
      killedSession,
      reducedOutcomes: state.prematch.reducedOutcomes,
      outcomeList: event ? getOutcomes(state.prematch.outcomeList[event.sport], event.live) : [],
      view: state.settings.view,
      loading: state.loading,
      userMainOutcomes: state.prematch.userMainOutcomes,
      user: state.user,
      betType: state.live.betType,
      betConfirmationOpen: state.betConfirmation.open,
      betSlipOpen: state.betSlip.open,
      parlayBySport: state.live.parlayBySport,
      settingsOpen: ownProps.location.search === '?settings'
    };
  })(Prematch);
