import * as React from 'react';

import { getAnimations } from './actions';
import { IceHockeyAction, IceHockeyTeam, actionTextHockey } from './constants';
import { Animation } from '../helpers';

import Sticks from './sticks';
import TimeOutIce from './time-out-ice';
import HockeyGate from './hockey-gate';
import Fire from './fire';
import Puck from './puck';
import Goal from './goal';
import Goalie from './goalie';
import RefHockey from './referee-hockey';

import TeamSplit from '../team-split/';
import TeamSplitter from '../team-split/helpers';

export class IceHockeyRink extends React.Component<any, any> {
  animation = new Animation(this);
  promise: Promise<any|void> = Promise.resolve();

  state: any = {
    splitTeams: [],
    svgHeight: 'auto',
    animation: getAnimations(null, this.props.team, this.props.action)[0].start,
    action: this.props.action
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 200, height = 86 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });

  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.gamecastSize !== this.props.gamecastSize) {
      this.handleResize();
    }

    if (newProps.team1 !== this.props.team1 || newProps.team2 !== this.props.team2) {
      this.setState({splitTeams: [new TeamSplitter(newProps.team1).resultArr, new TeamSplitter(newProps.team2).resultArr]});
    }

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {
    this.promise = getAnimations(this.state.animation, this.props.team, this.props.action, newProps.team, newProps.action)
      .reduce((all, next: any) => (
        all.then(() => Promise.all(Object.keys(next.start).map((key: any) => {
          this.setState({
            action: newProps.action
          });
          return this.animation.start(key, next.start[key], next.end[key], 750);
        })))
      ), this.promise);
    }
  }

  get actionText() {
    let actionText = actionTextHockey(this.props.action, this.props.matchData);

    if (this.props.action === 1270 && actionText.length > 14) {
      return actionText.split(this.props.matchData)[0];
    } else {
      return actionText;
    }
  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === IceHockeyTeam.Team1 ? event.inverted ? team2 : team1 : team === IceHockeyTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  componentDidMount() {
    this.setState({splitTeams: [new TeamSplitter(this.props.team1).resultArr, new TeamSplitter(this.props.team2).resultArr]});
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillUnmount() {

    this.animation.cancel();
    window.removeEventListener('resize', this.handleResize);
  }

  render() {
    const { action, width = 200, height = 86,  event, team } = this.props;
    const { animation, splitTeams } = this.state;
    const { polygon, actionText, brick, extras, alternRect, faceOffCtr, gate, goalSpot, shotIcon } = animation;
    const [bg, blue, red, grey, possPath, actionTxtColor, alternRectColor] = ['#f8f8ff', '#77f', '#f77', '#424242', '#607d8b', '#f44336', '#e0e0e0'];

    const w = (p: any) => width * p / 200;
    const h = (p: any) => height * p / 86;

    const crosses = `M ${w(29)} ${h(17)} V ${h(20)} H ${w(25)} M ${w(33)} ${h(17)} V ${h(20)} H ${w(37)} M ${w(25)} ${h(22)} H ${w(29)} V ${h(25)} M ${w(37)} ${h(22)} H ${w(33)} V ${h(25)}`;
    const circleLines = `M ${w(29)} ${h(4)} V ${h(38)} M ${w(33)} ${h(4)} V ${w(38)}`;

    let splitTeam;
    if (event) {
      splitTeam = team === IceHockeyTeam.Team1 ? event.inverted ? splitTeams[1] : splitTeams[0] : team === IceHockeyTeam.Team2 ? event.inverted ? splitTeams[0] : splitTeams[1] : '';
    }

    return (
      <div ref="gc">
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`} strokeWidth="0.5" fill="transparent">
        <defs>
          <mask id="rink-stroke" x="0" y="0" width={width}  height={height}>
            <rect x="0" y="0" width={width} height={height} fill="#fff" />
            <rect width="200" height="86" fill="black" rx={w(15)} ry={h(15)} stroke="transparent" />
          </mask>
        </defs>
          <rect width="200" height="86" rx={w(15)} ry={h(15)} fill={bg} stroke="#d3d3dd" />
          <path d={`M ${w(11)} 0.8 V ${w(86) - 0.8} M ${w(189)} 0.8 V ${w(86) - 0.8}`} stroke={red} />
          <path d={`M ${w(75)} 0.3 V ${w(86) - 0.3} M ${w(125)} 0.3 V ${w(86) - 0.3}`} stroke={blue} strokeWidth="1.5" />
          <path d={`M ${w(100)} 0.3 V ${w(86) - 0.3}`} stroke={red} strokeWidth="2"/>
          <path d={`M ${w(100)} 0.3 V ${w(86) - 0.3}`} stroke="#fff" strokeDasharray="2 2" strokeWidth="1"/>
          <g stroke={red}>
            <path d={circleLines} />
            <path d={circleLines} transform={`translate(0, ${h(44)})`} />
            <path d={circleLines} transform={`translate(${w(138)}, ${h(44)})`} />
            <path d={circleLines} transform={`translate(${w(138)}, 0)`} />
          </g>
          <g fill={bg} stroke={red}>
            <circle cx={w(31)} cy={h(21)} r={w(15)} />
            <circle cx={w(31)} cy={h(65)} r={w(15)} />
            <circle cx={w(169)} cy={h(21)} r={w(15)} />
            <circle cx={w(169)} cy={h(65)} r={w(15)} />
          </g>
          <g fill={red}>
            <circle cx={w(31)} cy={h(65)} r="1.25" />
            <circle cx={w(80)} cy={h(21)} r="1.25" />
            <circle cx={w(169)} cy={h(21)} r="1.25" />
            <circle cx={w(80)} cy={h(65)} r="1.25" />
            <circle cx={w(169)} cy={h(65)} r="1.25" />
            <circle cx={w(120)} cy={h(21)} r="1.25" />
            <circle cx={w(31)} cy={h(21)} r="1.25" />
            <circle cx={w(120)} cy={h(65)} r="1.25" />
          </g>
          <circle cx={w(100)} cy={h(43)} r={w(15)} stroke={blue} />
          <circle cx={w(100)} cy={h(43)} r="1" fill={blue} />
          <g stroke={red}>
            <path d={crosses} />
            <path d={crosses} transform={`translate(0, ${h(44)})`} />
            <path d={crosses} transform={`translate(${w(138)}, ${h(44)})`} />
            <path d={crosses} transform={`translate(${w(138)}, 0)`} />
          </g>
          <path d={`M ${w(11)} ${h(37)} A ${w(6)} ${w(6)} 0 0 1 ${w(11)} ${h(49)}`} stroke={red} />
          <path d={`M ${w(189)} ${h(37)} A ${w(6)} ${w(6)} 0 0 0 ${w(189)} ${h(49)}`} stroke={red} />
          <rect width="200" height="86" rx={w(15)} ry={h(15)} fill={bg} stroke="#d3d3dd" opacity="0.45" />
          <path d={`M ${w(0)} ${h(15)} A15,15 0 0,1 ${w(15)}, ${h(0)} M ${w(15)} ${h(0)} L ${polygon.pointX}, ${h(0)} ${w(101)}, ${h(86)} ${w(15)}, ${h(86)} A15,15 0 0,1 ${w(0)}, ${h(71)} L ${w(0)}, ${h(15)}z`} fill={possPath} opacity="0.5" transform={`translate(${polygon.translateX}, 0) scale(${polygon.scaleX}, 1)`}/>
          <rect width="200" height="86" fill="#fff" mask="url(#rink-stroke)"/>
          <rect x={w(alternRect.x)} y={h(alternRect.y)} rx="2" ry="2" width={w(105)} height={h(30)} fill={alternRectColor} opacity="0.6" strokeWidth="0" />
          <g transform={`scale(${gate.scaleX}, 1)`}>
            <HockeyGate x={gate.x} y={gate.y} />
          </g>
          {
            action !== IceHockeyAction.EndPeriod &&
              this.team ? <TeamSplit splitIndex="21" team={this.team} splitTeam={splitTeam} x={actionText.x} y={actionText.y - 10} size="8" textAnchor={actionText.textAlign} bx={brick.x} by={brick.y} br={brick.rotate} color={actionTxtColor} /> :
              <rect x={w(50)} y={h(actionText.y) - 12} rx="2" ry="2" width={w(100)} height={h(18)} fill={alternRectColor} opacity="0.8" strokeWidth="0" />
          }
          {
            (action === IceHockeyAction.FaceOff || action === IceHockeyAction.PuckDropped || action === IceHockeyAction.FaceOffWinner)   &&
            <g opacity={extras.opacity}>
              <circle cx={w(faceOffCtr.x)} cy={h(faceOffCtr.y)} r={w(15)} fill={grey} opacity="0.4" />
              <Sticks x={w(extras.x)} y={h(extras.y)} width={w(25)} height={h(25)} />
            </g>
          }
          {
            action === IceHockeyAction.PowerPlay &&
              <Sticks x={w(extras.x)} y={h(extras.y)} width={w(30)} height={h(35)} opacity={extras.opacity} />
          }
          {
            action === IceHockeyAction.TimeOut &&
              <g opacity={extras.opacity}>
                <TimeOutIce x={w(extras.x)} y={h(extras.y)} width={w(35)} height={h(35)} transform={`scale(${w(extras.scale)}, 1)`} />
              </g>
          }
          {
            (action === IceHockeyAction.TimePenalty || action === IceHockeyAction.Icing || action === IceHockeyAction.TeamAlert) &&
            <g opacity={extras.opacity}>
              <RefHockey x={w(extras.x)} y={h(extras.y)} width={w(35)} height={h(35)} transform={`scale(${w(extras.scale)}, 1)`} />
            </g>
          }
          {
            (action === IceHockeyAction.Shot || action === IceHockeyAction.PenaltyShot) &&
            <g  opacity={extras.opacity}>
              <Fire x={w(extras.x)} y={h(extras.y)} width={w(35)} height={h(35)}/>
              <Puck x={w(extras.x)} y={h(extras.y)} width={w(35)} height={h(35)}/>
            </g>
          }
          {
            (action === IceHockeyAction.PenaltyMissed || action === IceHockeyAction.PulledKeeper || action === IceHockeyAction.KeeperBack) &&
            <g>
              <Goalie x={shotIcon.x} y={shotIcon.y} width={w(50)} height={h(50)} opacity={shotIcon.opacity}/>
            </g>
          }
          {
            action === IceHockeyAction.Goal &&
            <g>
              <Goal x={goalSpot.x} y={goalSpot.y}/>
            </g>
          }
          <text x={w(actionText.x)} y={h(actionText.y)} strokeWidth="0" fill={actionTxtColor} textAnchor={actionText.textAlign} style={{ fontSize: '9px' }}>{this.actionText}</text>
          <line x1="-1" y1="0" x2="-1" y2="86" stroke="#fff" strokeWidth="2" />
          <line x1={w(201)} y1="0" x2={w(201)} y2={h(86)} stroke="#fff" strokeWidth="2" />
          <rect width={w(201)} height={h(86)}  fill="transparent" stroke="#fff" strokeWidth="0.05" />
        </svg>
      </div>
    );
  }
}
