import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Volleyball = ({ data }: any) => {
  const { title,
    incompleteMatches,
    setLeadAfterXPoints,
    setSpecificBets
  } = data;
  return (
    <RulePart title={title} model="volleyball">
      <RuleItem title={incompleteMatches.title}>
        <p>
          {incompleteMatches.text}
        </p>
      </RuleItem>
      <RuleItem title={setLeadAfterXPoints.title}>
        <p>
          {setLeadAfterXPoints.text}
        </p>
      </RuleItem>
      <RuleItem title={setSpecificBets.title}>
        <p>
          {setSpecificBets.text}
        </p>
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.volleyball })
)(Volleyball);
