export const updateBetslipOutcomesAction: any = (payload: any) => ({
  type: UPDATE_BETSLIP_OUTCOMES,
  payload
});

export const updateBetslipOutcomes: any = (payload: any) => (dispatch: any, getState: any) => {
  dispatch(updateBetslipOutcomesAction(payload));

  const { live, betSlip } = getState();
  const { betslipOutcomes } = live;
  const { odds, parlay } = betSlip;

  if (!odds.length || betslipOutcomes === null) {
    return;
  }

  Object.keys(betslipOutcomes).forEach(bookmakerId => {
    Object.keys(betslipOutcomes[bookmakerId]).forEach(outcomeId => {
      if (Object.keys(betslipOutcomes[bookmakerId][outcomeId]).length > 0) {
        updateBetSlip(betslipOutcomes[bookmakerId][outcomeId], odds, parlay, dispatch);
      }
    });

  });

  odds.forEach((odd: any) => {
    Object.keys(betslipOutcomes).forEach(bookmakerId => {
      const outcome = betslipOutcomes[bookmakerId][odd.outcome];
      checkBetslipOdds(outcome, odd);
    });
  });
};

