export const subscribeEvent = (eventId: any, sport?: any, bookmakerId?: any, section?: any) => (dispatch: any, getState: any) => {
  // console.log(`subscribing ? ${section} ${eventId}`);
  if (sport && section === 'live' || section === 'single') {
    dispatch(updateSubscribedSport(sport));
  }
  let msg = `${SUBSCRIBE}_${eventId}_${sport || -1 }_${bookmakerId || 0}_${section}`;
  getState().wsm.socket.emit('message', msg);
  dispatch(updateLastSubscriptionMessage(msg));
  // getState().wsm.send(`${SUBSCRIBE}_${eventId}_${sport || -1 }_${bookmakerId || 0}_${section}`);
};

export const unsubscribeEvent = (eventId: any, sport?: any, bookmakerId?: any, section?: any) => (dispatch: any, getState: any) => {
  // console.log(`unsubscribing ${eventId} ${sport} ${section}?`);
  // empty gamecastQueueArray
  dispatch(emptyGamecastQueue());
  let msg = `${UNSUBSCRIBE}_${eventId}_${sport || -1}_${bookmakerId || 0}_${section || ''}`;
  getState().wsm.socket.emit('message', msg);
  // getState().wsm.send(`${UNSUBSCRIBE}_${eventId}_${sport || -1}_${bookmakerId || 0}_${section || ''}`);
};

