// ---
function updateBetSlip(odds: any, betSlipOdds: any, parlay: any, dispatch: any) {
  if (typeof odds !== 'object') return;
  if (!Object.keys(odds).length) {
    return;
  }

  Object.keys(odds).forEach(oddId => {
    const odd = betSlipOdds.filter((o: any) => o.id === oddId)[0];
    // Skip doing anything if the odd is locked.
    // This is to stop "confirmed" bets from having their attributes updated after they were already confirmed
    if (odd && odd.locked) {
      // console.log('skipping odds update because this odd is locked', odd);
      return;
    }

    if (odd) {
      // console.log('continuing odds update because this odd is not locked', odd);
      const obj = Object.assign({}, odd, odds[oddId]);

      if (obj.intent) {
        if (obj.intent === 'risk') {
          obj.toWin = (obj.risk * (obj.price - 1)).toFixed(2);
        }
        else {
          obj.risk = (obj.toWin / (obj.price - 1)).toFixed(2);
        }
      }

      dispatch(updateBet(obj));
    }
  });

  // Don't touch the parlay data if the parlay data is locked.
  if (parlay && parlay.locked)
    return;

  const factor = betSlipOdds.map((o: any) => o.price).reduce((a: any, b: any) => a *= b, 1);
  const newParlay = Object.assign({}, parlay);

  if (parlay.intent === 'risk') {
    newParlay.toWin = (parlay.risk * (factor - 1)).toFixed(2);
  }
  else {
    newParlay.risk = (parlay.toWin / (factor - 1)).toFixed(2);
  }

  if (newParlay.risk !== parlay.risk || newParlay.toWin !== parlay.toWin) {

    dispatch(updateBet(newParlay));
  }
}

function checkBetslipOdds(outcome: any, odd: any) {
  if (outcome && outcome[odd.id]) {
    return;
  }

  const newOddId = outcome && Object.keys(outcome)
    .filter(id => outcome[id].column === odd.column)[0];

  if (newOddId) {
    if (odd && !odd.locked) {
      // console.log('Continuing checkBetslipOdds because odd is not locked', odd);
      updateBet(outcome[newOddId], odd);
    }
    else {
      // console.log('Skipping checkBetslipOdds because odd is locked', odd);
    }
  }
  else {
    removeFromBetSlip(odd);
  }
}
