import * as React from 'react';
import ListItem from './list-item';

const List = ({ items, itemTextProp, itemClick, selectedItem = null, style = {} }: any) => (
  <ul className="md-list" style={style}>
    {
      items.map((item: any) => {
        const text = typeof item === 'string' ? item : item[itemTextProp];
        return (
          <ListItem key={text} selected={item === selectedItem} onClick={itemClick} item={item}>{text}</ListItem>
        );
      })
    }
  </ul>
);

export default List;
