import { groupBy } from '../../../helpers';

export const hasRangeSlider = (sport: any) => sport === 8 ? [
  1900001, 1900002, 1900003, 1900004, 1900005, 1900006, 1900007, 1900008, 1900009
] : [];

export function getOutcomes(outcomeList: any, isLive: any) {

  if (!outcomeList) return;

  let _outcomeList = outcomeList[isLive];

  if (!_outcomeList)
    return [];

  return Object
    .keys(_outcomeList)
    .filter(key => _outcomeList[key].display)
    .sort((a, b) => _outcomeList[a].order - _outcomeList[b].order)
    .map(key => ({
      id: +key,
      title: _outcomeList[key].title,
      order: _outcomeList[key].order
    }));
}

export function groupEventsByLeague (events: any, sport: any, view?: any) {
  const items = groupBy(events.filter((ev: any) => ev.sport === sport), 'league');
  return items;
  // return view === 2 ? items.sort((a, b) => a.length - b.length) :
  //   items.map(evs => evs.sort((a, b) => a.time - b.time));
}

export function getScoreboardByPeriod(sport: any, score: any) {

    if (!score) return;
    if (sport === 2) {
      return score.filter((item: any) => item.description !== 'OT');
    }
    if (sport === 37) {
      return score.filter((item: any) => item.description !== 'OT')
        .map((item: any) => {
          if (item.description !== 'Total') {
            item.description = item.description.substring(0, 3).toUpperCase();
          }
          return item;
        });
    }
    if (sport === 6 || sport === 8 || sport === 9 || sport === 17) {
      return score.filter((item: any) => item.description !== 'Half' && item.description !== 'OT');
    }

    return [];
}

export function detectmob() {
 if ( navigator.userAgent.match(/Android/i)
 || navigator.userAgent.match(/webOS/i)
 || navigator.userAgent.match(/iPhone/i)
 || navigator.userAgent.match(/iPad/i)
 || navigator.userAgent.match(/iPod/i)
 || navigator.userAgent.match(/BlackBerry/i)
 || navigator.userAgent.match(/Windows Phone/i)
 ) {
    return true;
  }
 else {
    return false;
  }
}

export function mockScoreboard(sport: any) {
  if (sport === 10) {
    return [
      {t1: '0', t2: '0', description: '1'},
      {t1: '', t2: '', description: '2'},
      {t1: '', t2: '', description: '3'},
      {t1: '', t2: '', description: '4'},
      {t1: '', t2: '', description: '5'},
      {t1: '', t2: '', description: '6'},
      {t1: '', t2: '', description: '7'},
      {t1: '', t2: '', description: '8'},
      {t1: '', t2: '', description: '9'},
      {t1: '0', t2: '0', description: 'R'},
      {t1: '0', t2: '0', description: 'H'}
    ];
  }

  if (sport === 6) {
    return [
      {t1: '0', t2: '0', description: '1'},
      {t1: '', t2: '38', description: '2'},
      {t1: '', t2: '64', description: 'Half'},
      {t1: '', t2: '31', description: '3'},
      {t1: '', t2: '3', description: '4'},
      {t1: '', t2: '', description: 'OT'},
      {t1: '0', t2: '0', description: 'T'}
    ];
  }

  if (sport === 2) {

    return [
      {t1: '0', t2: '0', description: '1'},
      {t1: '', t2: '', description: '2'},
      {t1: '', t2: '', description: '3'},
      {t1: '', t2: '', description: 'OT'},
      {t1: '0', t2: '0', description: 'T'}
    ];
  }

  return ``;
}

export const prematchTimeConverter = (ts: any) => {
  const a = new Date(ts * 1000);
  const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  const year = a.getFullYear();
  const month = months[a.getMonth()];
  const date = a.getDate();
  const hour = a.getHours();
  const min = a.getMinutes() < 9 ? `0${a.getMinutes()}` : a.getMinutes();
  const time = `${date} ${month} ${year} ${hour}:${min}`;
  return time;
};
