import * as React from 'react';
import { Animation } from '../helpers';

export default class PitchSpot extends React.Component<any, any> {
  animation = new Animation(this);

  state = {
    animation: {
      circle1: {
        r: 0
      },
      circle2: {
        r: 0
      }
    }
  };

  animate = () => {
    this.animation.start('circle2', { r: 0 }, { r: 4 }, 350, true, 1200);
    this.animation.start('circle1', { r: 0 }, { r: 6 }, 350, true, 1200 );
  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    this.animation.cancel();
  }

  render() {
    const x = +this.props.x;
    const y = +this.props.y;
    const circle1 = Math.abs(this.state.animation.circle1.r);
    const circle2 = Math.abs(this.state.animation.circle2.r);

    return (
      <svg>
        <g fill="#fff">
          <circle cx={x} cy={y} r={circle1} opacity="1" fill="transparent" stroke="#e65100" strokeWidth="1"/>
          <circle cx={x} cy={y} r={circle2} opacity="1" fill="transparent" stroke="#e65100" strokeWidth="1"/>
          <circle cx={x} cy={y} r="1.5" fill="#e65100" strokeWidth="0"/>
        </g>
      </svg>
    );

  }
}
