import * as React from 'react';
import { connect } from 'react-redux';
import { getScoreboardByPeriod, mockScoreboard } from '../live-helpers';

// import { Sports } from '../../../../constants';

class ScoreboardPeriod extends React.Component<any, any> {

  render() {

    const { event, gamecast, clock, score } = this.props;
    // console.log(score);
    // console.log(gamecast.scoreboard);
    if (!gamecast || !gamecast.scoreboard || event.sport === 24 || !score) {
      return <div></div>;
    }

    const { scoreboard } = gamecast;
    const { scoreByPeriod } = score;
    const { timerTicking, min, sec, lastUpdate }: any = clock || { min: 0, sec: 0 };

    const scoreTeam1 = score !== undefined ? score.scoreTeam1 : '0';
    const scoreTeam2 = score !== undefined ? score.scoreTeam2 : '0';
    const period = score !== undefined ? score.period : '';

    const scoreByPeriod_ = !scoreByPeriod || (scoreByPeriod && scoreByPeriod.length === 0) ? mockScoreboard(event.sport) : scoreByPeriod;
    // console.log(scoreByPeriod_);
    return (
      <div className="scoreboard-period">
        <table className="clock-table">
          <thead>
            { event.sport === 6 || event.sport === 2 || event.sport === 8 || event.sport === 37 ?
            <tr>
              <th style={{display: 'flex'}}>
                <span className="game-clock">
                  { min && min !== 0 && sec && sec !== 0 && `${min}:${sec}`}
                </span>
                <span className="period">{event.sport === 37 ? '' : period}</span>
              </th>
            </tr> :
             <tr>

              <th>
                <span className="game-clock" style={{ opacity: (event.sport === 9 || event.sport === 17) ? 0 : 1 }}>
                  {event.sport === 10 && period ? `${period.slice(0, 3)} ${period.slice(period.length - 3, period.length)}` : period}
                </span>
              </th>
            </tr>
            }
          </thead>
          <tbody>
            <tr>
              <td className="team">{ event.inverted ? event.away : event.home }</td>
            </tr>
            <tr>
              <td className="team">{ event.inverted ? event.home : event.away }</td>
            </tr>
          </tbody>
        </table>
        <table className="score-table">
          <thead>
            { event.sport !== 10 && event.sport !== 7 || event.sport === 17 ?
            <tr className="periods">

              { scoreByPeriod_.length > 0 &&
                getScoreboardByPeriod(event.sport, scoreByPeriod_).map((item: any, i: any) => {
                  return (
                    <th key={i}
                        className={item.description ===  'T' ? 'total' : ''}>
                        {item.description}
                    </th>
                  );
                })
              }
            </tr> :
            <tr>
            { scoreboard.additional.map((item: any, i: number) => {
                const className = period === item.title ? 'runs' : 'inns';
                return item.title !== 'R' && <th  className={className} key={i}>{item.title}</th>;
              })
            }
            { scoreboard.additional.map((item: any, i: number) => ( item.title === 'R' ? <th className="runs" key={i}>{item.title}</th> : null))}
            { scoreboard.sets.map((set: any, i: number) => <th key={i}>{i + 1}</th>)}
            { !!scoreboard.setsTotalTitle && <th >{ scoreboard.setsTotalTitle }</th> }
            { !!scoreboard.pointsTitle && <th>{ scoreboard.pointsTitle === 'Points' ? 'Pts' : scoreboard.pointsTitle}</th> }
            </tr>
            }
          </thead>

            { event.sport !== 10 && event.sport !== 7 ?
            <tbody>
              <tr>
                { scoreByPeriod_.length > 0 &&
                  getScoreboardByPeriod(event.sport, scoreByPeriod_).map((item: any, i: number) => {
                    return (
                      <td
                        key={i}
                        className={item.description ===  'T' ? 'total' : ''}>
                          {item.description ===  'T' ? scoreTeam1 : item.t1}
                      </td>
                    );
                  })
                }
              </tr>
              <tr>
                { scoreByPeriod_.length > 0 &&
                  getScoreboardByPeriod(event.sport, scoreByPeriod_).map((item: any, i: number) => {
                    return (
                      <td
                        key={i}
                        className={item.description ===  'T' ? 'total' : ''}>
                          {item.description ===  'T' ? scoreTeam2 : item.t2}
                      </td>
                    );
                  })
                }
              </tr>
            </tbody> :
            <tbody>
                <tr>
                { scoreboard.additional.map((item: any, i: number) => ( item.title !== 'R' ? <td key={i}>{item.t1}</td> : null)) }
                { scoreboard.additional.map((item: any, i: number) => ( item.title === 'R' ? <td key={i}>{item.t1}</td> : null)) }
                { scoreboard.sets.map((set: any, i: number) => <td key={i}>{ set[0] }</td>) }
                { !!scoreboard.setsTotalTitle && <td>{ scoreboard.setsTotal[0] }</td> }
                { !!scoreboard.pointsTitle && <td>{ scoreboard.points[0] }</td> }
                </tr>
                <tr>
                { scoreboard.additional.map((item: any, i: number) => ( item.title !== 'R' ? <td key={i}>{item.t2}</td> : null)) }
                { scoreboard.additional.map((item: any, i: number) => ( item.title === 'R' ? <td key={i}>{item.t2}</td> : null)) }
                { scoreboard.sets.map((set: any, i: number) => <td key={i}>{ set[1] }</td>) }
                { !!scoreboard.setsTotalTitle && <td>{ scoreboard.setsTotal[1] }</td> }
                { !!scoreboard.pointsTitle && <td>{ scoreboard.points[1] }</td> }
                </tr>
              </tbody>
            }

        </table>
      </div>

    );
    }
  }

export default connect(
  (state: any, ownProps: any) => ({
    event: ownProps.event,
    score: state.live.scores[ownProps.event.id],
    gamecast: state.live.eventGamecast,
    clock: state.live.clocks[ownProps.event.id]
  })
)(ScoreboardPeriod);
