import * as React from 'react';
import { connect } from 'react-redux';

import Event from './event';

import { getAsianHeaderBySport, AsianLine } from '../../../../constants';

const League = ({ dispatch, line, events }: any) => {
  const multiplier = line === AsianLine.simpleLine ? 1 : 2;

  return (
    <tbody>
      <tr>
        <td className="asian__league" colSpan={getAsianHeaderBySport(events[0].sport, line).length * multiplier + 2}>
          { events[0].league }
        </td>
      </tr>
      {
        events.map((ev: any) => (
          <Event key={ev.id} event={ev} line={line} />
        ))
      }
    </tbody>
  );
};

export default connect(
  (state: any) => ({ line: state.live.asianLine })
)(League);
