import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../config';

import { reqLiveEvents, subscribeEvent, unsubscribeEvent, getCurrentEvent, redirectLive, toggleVideoTab } from '../../../actions/live';
import { dashedToCamelCase, homeAwayToLink } from '../../../helpers';
import { redirectToNotLoggedInPage } from '../../../actions/user';
import { reqPrematchEvents } from '../../../actions/prematch';
import { showSportsListOn } from '../../../actions/ui-toolbar';
import { clearEventOutcomes } from '../../../actions/outcomes';
import { ViewType } from '../../../constants';
import { getOutcomes, detectmob } from './live-helpers';
import TypeSwitcher from './type-switcher';
import Button from '../../form/button';
import Navbar from './ui-toolbar/navbar';
import Title from '../../title';

import SingleEventEuropean from './european/single-event-european';
import SingleEventOdds from './single-event-odds';
import Wrapper from './wrapper';

import { Sports } from './../../../constants';

import { eventOddsWithOdds } from './../../../reducers/outcomes-flatten';

// import data2 from './data2';

class SingleEvent extends React.Component<any, any> {

  state = {
    isMobile: false
  };

  checkData(prevProps: any = {}, prevState: any = {}) {

    const { view, eventsLoaded, event, dispatch, isPrematch, bookmakerId } = this.props;

    if (!eventsLoaded && !isPrematch) {

      let isAsian = view === 1;
      dispatch(reqLiveEvents(isAsian));

    } else if (event && event.id !== prevState.eventId) {

      let section = isPrematch ? 'prematchSingle' : 'single';

      dispatch(subscribeEvent(event.id, event.sport, bookmakerId[event.sport], section));

      dispatch(getCurrentEvent(event.id));
    }

    if (isPrematch && !eventsLoaded) {
      dispatch(reqPrematchEvents());
    }

  }

  componentDidMount() {
    this.checkData();
    window.addEventListener('resize', this.getWidth);

    if (window.innerWidth <= 1008) {
      this.setState({ isMobile : true });
    }

    if (window.innerWidth <= 1008 && detectmob()) {
      this.setState({ isMobile : true });
    }

    if (this.props.view !== ViewType.european) {
      this.props.dispatch(showSportsListOn());

    }

    if (this.props.event && !this.evHasVideoNoGamecast(this.props.event)) {
      this.props.dispatch(toggleVideoTab(false));
    }

  }

  componentDidUpdate() {
    const { isReplacing } = this.props;
    if (!isReplacing) {
      // this.checkData();
    }
  }

  componentWillReceiveProps(newProps: any) {
    const { event, dispatch, isPrematch, bookmakerId } = this.props;

    let section = isPrematch ? 'prematchSingle' : 'single';

    if (event && !newProps.event) {
      dispatch(unsubscribeEvent(event.id, event.sport, 0, section));
    }
    else if (newProps.event && !event) {
      dispatch(subscribeEvent(newProps.event.id, newProps.event.sport, bookmakerId[newProps.event.sport], section));
      dispatch(getCurrentEvent(newProps.event.id));
    }
    else if (newProps.event && event && newProps.event.id !== event.id) {

      dispatch(unsubscribeEvent(event.id, event.sport));
      dispatch(subscribeEvent(newProps.event.id, newProps.event.sport, bookmakerId[newProps.event.sport]), section);
      dispatch(getCurrentEvent(newProps.event.id));
    }

    // Object.keys(newProps)
    //   .forEach(prop => {
    //     if (newProps[prop] !== this.props[prop]) {
    //       console.log(prop);
    //       console.log(newProps[prop]);
    //       console.log(this.props[prop]);
    //     }
    //   });

  }

  componentWillUnmount() {
    const { dispatch, event, betSlipOdds, isPrematch } = this.props;
    let section = isPrematch ? 'prematchSingle' : 'single';

    if (event) {
      dispatch(unsubscribeEvent(event.id, event.sport, 0, section));
      // console.log('empty ev outcomes');
      dispatch(clearEventOutcomes());
    }

    this.props.dispatch(toggleVideoTab(false));

    window.removeEventListener('resize', this.getWidth);
  }

  getWidth = () => {

    let that = this;

    setTimeout(() => {
      if (that.state.isMobile && that.props.hasNativeFullScreen) return;
      that.setState({ isMobile: window.innerWidth < 1008  ? true : false });
    }, (that.state.isMobile && this.props.event.videoUrl !== '') ? 150 : 0);

  }

  goTop = () => {
    window.scroll(0, 0);
  }

  evHasVideoNoGamecast(ev: any) {

    if (!ev) return false;

    const { hasGameCast, videoUrl, videoSource } = ev;
    const evHasVideoNoGamecast = config.hasVideoStream && (!hasGameCast && videoUrl !== '' && videoSource > 0);
    return evHasVideoNoGamecast;
  }

  render() {

    let {
      view,
      event,
      title,
      betType,
      uiToolbar,
      betSlipOpen,
      betSlipOdds,
      settingsOpen,
      parlayBySport,
      killedSession,
      initialisationCode,
      betConfirmationOpen,
      hasVideoOnFullScreen,
      reducedEventOutcomes
    } = this.props;

    const { isMobile } = this.state;
    let data;
    const classList = ['single-event'];
    const bigContClass = ['md-container'];
    const gameCastClass = ['gamecast'];
    const backToTopStyles = hasVideoOnFullScreen ? {
      display: 'none'
    } : {};
    // console.log(reducedEventOutcomes);
    // console.log(outcomes);
    bigContClass.push('big-cont');
    // console.log(event);

    if (!event) {
      this.props.dispatch(redirectLive());
      return null;
    }

    const outcomeList: any = getOutcomes(this.props.outcomeList_[event.sport], event.live);

    if (killedSession || initialisationCode === 0) {
      this.props.dispatch(redirectToNotLoggedInPage());
      return null;
    }

    if (view === ViewType.european || view === ViewType.asian) {
      return (
        <SingleEventEuropean
          view={view}
          title={title}
          event={event}
          betType={betType}
          outcomes={reducedEventOutcomes}
          isMobile={isMobile}
          uiToolbar={uiToolbar}
          outcomeList={outcomeList}
          betSlipOdds={betSlipOdds}
          betSlipOpen={betSlipOpen}
          settingsOpen={settingsOpen}
          parlayBySport={parlayBySport}
          dispatch={this.props.dispatch}
          eventsLoaded={this.props.eventsLoaded}
          betConfirmationOpen={betConfirmationOpen}
          hasVideoOnFullScreen={hasVideoOnFullScreen}
          hasNativeFullScreen={this.props.hasNativeFullScreen}
        />
      );
    }

    if (view === ViewType.northAmerican) {
      classList.push('north-american');
    } else {
      gameCastClass.push('classic');
    }

    if (!uiToolbar.gamecast) {
      bigContClass.push('close-cast');
    }

    if (event) {
      const { home, sep, away, sport, isPrematch } = event;
      title += ` - ${home} ${sep} ${away}`;

      const gamecastTop = (!uiToolbar.gamecast && !isMobile) || isMobile;

      data = (

        <div className="sv-container" >
          <div className={bigContClass.join(' ')} >
          {
            gamecastTop && !isPrematch &&
            <div className={`${bigContClass.join(' ')} score`}>
              <div className="gamecast">
                <Wrapper
                  sport={sport}
                  event={event}
                  isSingleEvent={true}
                />
              </div>
            </div>
          }

            <SingleEventOdds
              hasVideoOnFullScreen={hasVideoOnFullScreen}
              outcomes={reducedEventOutcomes}
              parlayBySport={parlayBySport}
              dispatch={this.props.dispatch}
              outcomeList ={outcomeList}
              classList={classList}
              betType={betType}
              event={event}
              view={view}

            />

            <div className="mobile back-to-top" style={backToTopStyles}>
              <Button onClick={this.goTop}>Back to Top</Button>
            </div>

          </div>
          { (uiToolbar.gamecast && !isMobile && !isPrematch) ?
            <section id="gc-container" className="gc-container">
              <div className={gameCastClass.join(' ')} >
              <Wrapper
                event={event}
                isMobile={isMobile}
                isSingleEvent={true}/>
              </div>
            </section> : <div></div>
          }
        </div>
      );
    }

    return (
        <div>

          <Navbar
            isMobile = {isMobile}
            event = {event}
            view = {view}
          />

          <Title title={title}>
            <div>
              { config.hasParlay && view !== ViewType.asian && <TypeSwitcher /> }
              { data }
            </div>
          </Title>

        </div>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => {
    const path = state.router.location.pathname;
    const isPrematch = ownProps.match.params.gameid !== undefined || path.split('/').indexOf('prematch') >= 0;
    const outcomeList_ = isPrematch ? state.prematch.outcomeList : state.live.outcomeList;

    let user = state.user;
    let view = state.settings.view;
    let uiToolbar = state.uiToolbar;

    let sport: any = `${dashedToCamelCase(ownProps.match.params.sport)}`;
    sport = Sports[sport];

    let eventsBySport = isPrematch ? state.prematch.leagues : state.eventsFlatten && state.eventsFlatten.bySport[sport];
    let settingsOpen = state.router.location.search.hasOwnProperty('settings');
    let title = `${state.languageData.menu.inPlay} - ${state.languageData.sports[dashedToCamelCase(ownProps.match.params.sport)]}`;

    let eventsLoaded = eventsBySport && eventsBySport.length > 0;

    if (!eventsBySport || !eventsLoaded) {
      return {
        settingsOpen,
        uiToolbar,
        title,
        view,
        user,
        path
      };
    }

    const events = isPrematch ? state.prematch.leagues : eventsBySport.map((evId: string) => state.eventsFlatten.byId[evId]);
    let event;

    if (isPrematch) {
      let gameId = ownProps.match.params.gameid;
      event = events.filter((ev: any) => ev.id === parseInt(gameId))[0];

    } else {
      event = events[events.map((ev: any) => encodeURI(homeAwayToLink(ev.home, ev.away))).indexOf(encodeURI(ownProps.match.params.game))];
    }

    const bookmakerId = !state.user || !event ? {} : state.user.bookmakerId[event.sport];
    const outcomes = state.live.eventOutcomes[bookmakerId];

    const reducedEventOutcomes = eventOddsWithOdds(state, ownProps);
    // const reducedEventOutcomes = data2;
    // console.log(reducedEventOutcomes);

    // const redirectSport = Sports[event.sport];
    return {
      view,
      title,
      event,
      outcomes,
      uiToolbar,
      isPrematch,
      settingsOpen,
      eventsLoaded,
      outcomeList_,
      eventsBySport,
      reducedEventOutcomes,
      betType: state.live.betType,
      betSlipOdds: state.betSlip.odds,
      betSlipOpen: state.betSlip.open,
      isReplacing: state.live.isReplacing,
      prematchLeagues: state.prematch.leagues,
      parlayBySport: state.live.parlayBySport,
      betConfirmationOpen: state.betConfirmation.open,
      bookmakerId: state.user && state.user.bookmakerId,
      hasNativeFullScreen: state.user.hasNativeFullScreen,
      killedSession: state.user && state.user.killedSession,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen,
      // outcomeList: event ? getOutcomes(outcomeList_[event.sport], event.live) : [],
      initialisationCode: state.user && state.user.response && state.user.response.initialisation_result_code
    };
  }
)(SingleEvent);
