import { nt, IceHockeyAction, IceHockeyTeam, IHockeyFieldAnimationState } from '../constants';
import { possession } from './possession';
import { faceOff } from './face-off';
import { faceOffWin } from './face-offNew';
import { extras } from './extras';
import { shots } from './shot';
import { general } from './general';

export interface IHockeyFieldAnimation {
  start: IHockeyFieldAnimationState;
  end: IHockeyFieldAnimationState;
}

export function getAnimations(state: IHockeyFieldAnimationState, currentTeam: IceHockeyTeam, currentAction: IceHockeyAction, nextTeam?: IceHockeyTeam, nextAction?: IceHockeyAction): IHockeyFieldAnimation[] {
  switch (nextAction) {
    case IceHockeyAction.Possession:
      return possession(nextTeam);
    case IceHockeyAction.Goal:
    case IceHockeyAction.PenaltyMissed:
    // case IceHockeyAction.ShootOutMissed:
      return shots(state, nextTeam);
    // case IceHockeyAction.ShootOut:
    case IceHockeyAction.PenaltyShot:
    case IceHockeyAction.TeamAlert:
    case IceHockeyAction.TimePenalty:
    case IceHockeyAction.Icing:
    case IceHockeyAction.PowerPlay:
    case IceHockeyAction.TimeOut:
    case IceHockeyAction.Shot:
    case IceHockeyAction.PulledKeeper:
    case IceHockeyAction.KeeperBack:
      return extras(nextTeam);
    case IceHockeyAction.FaceOff:
      return faceOff();
    case IceHockeyAction.PuckDropped:
    case IceHockeyAction.FaceOffWinner:
      return faceOffWin(nextTeam);
      case IceHockeyAction.None:
    case IceHockeyAction.EndPeriod:
      return general();
      default:
        return [{
          start: nt,
          end: nt
        }];
  }
}
