import { BasketTeam, IBasketFieldAnimationState, t1, t2 } from  '../constants';
import { deepAssign } from '../../../../helpers';

export const t1PossessionStart: IBasketFieldAnimationState = t1;
export const t1PossessionEnd: IBasketFieldAnimationState = deepAssign({}, t1, {
  polygon: {
    translateX: 0
  },
  actionText: {
    x: 46,
    y: 30
  },
  jersey: {
    x: 30
  }
});

export const t2PossessionStart: IBasketFieldAnimationState = t2;
export const t2PossessionEnd: IBasketFieldAnimationState = deepAssign({}, t2, {
  polygon: {
    translateX: -94
  },
  actionText: {
    x: 48
  },
  jersey: {
    x: 50
  }
});

export function possession(nextTeam: BasketTeam) {
  return [{
    start: nextTeam === BasketTeam.Team1 ? t1PossessionStart : t2PossessionStart,
    end: nextTeam === BasketTeam.Team1 ? t1PossessionEnd : t2PossessionEnd
  }];
}
