import * as uuid from 'uuid/v4';
import {
  USER_UPDATE_BET_CONFIRMATION,
  OPEN_BET_CONFIRMATION,
  CLOSE_BET_CONFIRMATION,
  ADD_CONFIRMATION_ODDS,
  FREEZE_CONFIRMATION_ODDS,
  UNFREEZE_UPDATE_CONFIRMATION_ODDS,
  UPDATE_STATUS_CONFIRMATION_ODDS,
  REMOVE_FROM_BET_CONFIRMATION
} from '../actions/bet-confirmation';

export const betConfirmationInitialState: any = {
  open: false,
  freeze: false,
  parlay: { id: 'multi' },
  odds: []
};

export const betConfirmation = (state = betConfirmationInitialState, action: any) => {
  switch (action.type) {
    case REMOVE_FROM_BET_CONFIRMATION:
      if (action.payload.uuid) {
        return Object.assign({}, state, {
          odds: state.odds.filter((o: any) => o.uuid !== action.payload.uuid)
        });
      }
      return Object.assign({}, state, {
        odds: state.odds.filter((o: any) => o.id !== action.payload.id)
      });
    case USER_UPDATE_BET_CONFIRMATION:
      return {
        ...state,
        odds: action.payload
      };
    case OPEN_BET_CONFIRMATION:
      return Object.assign({}, state, {
        open: true
      });
    case CLOSE_BET_CONFIRMATION:
      return Object.assign({}, state, {
        open: false,
        freeze: false,
        odds: []
      });
    case ADD_CONFIRMATION_ODDS:

      return Object.assign({}, state, {
        odds: action.payload,
        parlay: action.parlay
      });

    case FREEZE_CONFIRMATION_ODDS:
      return Object.assign({}, state, {
        freeze: true
      });
    case UNFREEZE_UPDATE_CONFIRMATION_ODDS:
      return Object.assign({}, state, {
        freeze: false
      });
    case UPDATE_STATUS_CONFIRMATION_ODDS:
      // console.log('UPDATE_STATUS_CONFIRMATION_ODDS');
      // console.log('payload');
      // console.log(action.payload);
      // console.log('state');
      // console.log(state.odds);
      return Object.assign({}, state, {
        odds: state.odds.map((odd: any) => Object.assign({}, odd, { status: (action.payload.filter((o: any) => o.uuid === odd.uuid)[0] || {}).status })),
        parlay: Object.assign({}, state.parlay, { status: action.payload[0].status })
      });
  }

  return state;
};
