import { IBaseBallFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const newBatterStart: IBaseBallFieldAnimationState = deepAssign({}, nt, {
  pitcherStart: {
    display: 'none'
  },
  pitcherEnd: {
    display: 'block'
  }
});

const newBatterEnd: IBaseBallFieldAnimationState = deepAssign({}, nt, {
  pitcherStart: {
    display: 'block'
  },
  pitcherEnd: {
    display: 'none'
  }
});

export function newBatter() {
  return [{
    start: newBatterStart,
    end: newBatterEnd
  }];
}
