import * as React from 'react';
import { connect } from 'react-redux';

import Select from '../../../form/select';

import { AsianLines } from '../../../../constants';
import { changeAsianLine } from '../../../../actions/live';

const Line = ({ line, dispatch }: any) => (
  <div className="asian-line">
    <Select label="Asian view" defaultSelected={AsianLines.filter(l => l.value === line)[0]} dataSource={AsianLines} pk="value" model="text" onChange={l => dispatch(changeAsianLine(l.value))} />
  </div>
);

export default connect(
  (state: any) => ({ line: state.live.asianLine })
)(Line);
