import * as React from 'react';

let color = '#333';

const GamecastIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    strokeWidth: 10,
    fill: '#fff',
    stroke: color
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps} >

      <rect x="5" y="40" width="240" height="170" />
      <rect x="5" y="80" width="45" height="90" />
      <rect x="200" y="80" width="45" height="90" />
      <line x1="125" y1="40" x2="125" y2="210" />
      <circle cx="125" cy="125" r="40" fill="transparent"/>
    </svg>
  );
};

export default GamecastIcon;
