import * as React from 'react';

import { TennisTeam, hourScoreToValue, actionTextTennis, nt } from './constants';
import Balls from './balls';
import Score from './score';

export class TennisCourt extends React.Component<any, any> {
  state = {
    svgHeight: 'auto'
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 78, height = 36 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.gamecastSize !== this.props.gamecastSize) {
      this.handleResize();
    }

  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  get data() {
    // The game data comes in a string as follows -> '3:6:1~6:2:2#2~7#27% (3/11)~30% (3/10)#58~71#58~48'
    // 1st: 1st/2nd/3rd/4th/5th sets | 2nd: Aces | 3rd: Break Points | 4th: 1st serve % | 5th: 2nd serve %
    // So, we must convert it in an array like [[["3","6"],["6","2"],["1","2"]],["2","7"],["27% (3/11)","30% (3/10)"],["58","71"],["58","48"]]
    const { matchData } = this.props;

    return matchData && matchData
      .split('#')
      .map((s: any) => s.split('~'))
      .map((x: any, i: any) => {
        if (i === 0) {
          const arr = x.map((a: any) => a.split(':'));
          return arr[0].map((col: any, j: any) => arr.map((row: any) => row[j]));
        }
        return x;
      });
  }

  getTeamPatch(team: string, x: number, y: number, textAnchor = 'start') {
    const [part1, part2] = team.indexOf('/') !== -1 ? team.split('/').map(s => s.trim()) : team.split(' ');
    const actionY = y === nt.teams.team1Y ? 6 : -3;

    return (
      <text x={x} y={y} style={{ fontSize: '3px' }} textAnchor={textAnchor}>
        <tspan>{part1}</tspan>
        <tspan x={x} y={y + 3.5}>{part2}</tspan>
        {
          this.team === team &&
            <tspan x={x} y={y + actionY} style={{ fontSize: '2px' }}>{actionTextTennis(this.props.action)}</tspan>
        }
      </text>
    );
  }

  get team() {
    const { team, team1, team2 } = this.props;
    return team === TennisTeam.Team1 ? team1 : team === TennisTeam.Team2 ? team2 : '';
  }

  get isTeamOneBelow() {
    const { scoreTeam1, scoreTeam2 } = this.props;
    const total = hourScoreToValue(scoreTeam1) + hourScoreToValue(scoreTeam2);

    return total % 2 === 0;
  }

  getBallsProperties() {
    const { team, action } = this.props;
    let [x, y, scaleX, scaleY, translateX, translateY] = [3.5, 16, 1, 1, 0, 0];

    if (team === TennisTeam.Team2) {
      x = -3.5;
      scaleX = -1;
      translateX = -78;
    }

    if ((team === TennisTeam.Team1 && this.isTeamOneBelow) || (team === TennisTeam.Team2 && !this.isTeamOneBelow)) {
      y = -16;
      scaleY = -1;
      translateY = -36;
    }

    return {
      x,
      y,
      scaleX,
      scaleY,
      translateX,
      translateY,
      action
    };
  }

  render() {
    const { svgHeight } = this.state;
    const { width = 78, height = 36, team, team1, team2, action } = this.props;
    const [mainColor, strokeColor, textColor] = ['#a24524', 'rgba(255, 255, 255, 0.3)', '#fff'];

    const { teams } = nt;
    let { team1Y, team2Y } = teams;

    const w = (p: any) => width * p / 78;
    const h = (p: any) => height * p / 36;

    if (this.isTeamOneBelow) {
      const aux = team1Y;
      team1Y = team2Y;
      team2Y = aux;
    }

    return (
      <div ref="gc">
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`} fill="transparent" stroke={strokeColor} strokeWidth="0.3">
          <rect x="-1" y="-1" strokeWidth="0" fill={mainColor} width={w(80)} height={h(38)} />
          <rect x="0" y="0" width={w(78)} height={h(36)} />
          <path d={`M ${w(39)} ${h(1.5)} L ${w(39)} ${h(34.5)}`} strokeWidth="0.7" stroke="#ddbbb0" />
          <path d={`M 0 ${h(4.5)} L ${w(78)} ${h(4.5)} M 0 ${h(31.5)} L ${w(78)} ${h(31.5)}`} />
          <path d={`M ${w(18)} ${h(4.5)} L ${w(18)} ${h(31.5)} M ${w(60)} ${h(4.5)} L ${w(60)} ${h(31.5)}`} />
          <path d={`M 0 ${h(18)} L ${w(0.5)} ${h(18)} M ${w(18)} ${h(18)} L ${w(60)} ${h(18)} M ${w(77.5)} ${h(18)} L ${w(78)} ${h(18)}`} />
          <g strokeWidth="0" fill={textColor}>
            { this.getTeamPatch(team1, 3, team1Y) }
            { this.getTeamPatch(team2, width - 3, team2Y, 'end') }
            <rect x="1.5" y={team1Y - 2} width="0.5" height="5.5" fill="yellow" />
            <rect x={width - 2.2} y={team2Y - 2} width="0.5" height="5.5" fill="yellow" />
          </g>
          <Balls {...this.getBallsProperties()} />
          <Score team={team} action={action} data={this.data} w={w} h={h} textColor={textColor} />
        </svg>
      </div>
    );
  }
}
