import * as React from 'react';

interface KeyValuePair {
  [key: string]: any;
}

export class Animation {
  constructor(private instance: React.Component<any, any>) {
    // document.addEventListener('visibilitychange', () => {
    //   if (document.hidden) {
    //     this.cancel();
    //   }
    // });
  }

  cancelations: Function[] = [];

  cancel() {
    this.cancelations.forEach(cancel => cancel());
  }

  /**
   * Animate a SVG path by changing its React state
   *
   * @export
   * @param {string} key The unique key for the object that will be used in the instance's animation state
   * @param {KeyValuePair} start Initial properties before the animation begins (eg translate, rotate etc)
   * @param {KeyValuePair} end Final properties after the end of the animation
   * @param {number} [duration=500] The animation duration in miliseconds (Optional: default value is 500ms)
   * @param {boolean} [repeat=false] Set if the animation should repeat until cancelled (Optional: default value is false)
   * @param {number} [pause=0] Set the pause between repeats (Optional: default value is 0 - no pause)
   */
  start(key: string, start: KeyValuePair, end: KeyValuePair, duration = 500, repeat = false, pause = 0): Promise<any> {
    const { instance } = this;
    let t: any, a: any, startTime: any;

    function cancel() {
      cancelAnimationFrame(a);
      clearTimeout(t);
    }

    this.cancelations.push(cancel);

    return new Promise((resolve, reject) => {
      // if the tab is not visible, we don't need to run the animation
      // if (document.hidden) {
      //   return resolve();
      // }

      setState(start);

      const diff: any = {};
      Object.keys(start).forEach(k => diff[k] = typeof start[k] === 'string' ? end[k] : end[k] - start[k]);

      startAnimation();

      function startAnimation() {
        startTime = performance.now();
        a = requestAnimationFrame(anim);
      }

      function anim(time: number) {
        const remaining: any = duration - (time - startTime);
        let rate = 1 - remaining / duration;

        // if there are less than 2 frames remaining, run the final state
        rate = remaining < (1000 / 60 * 2) ? 1 : rate;
        run(rate);

        if (rate < 1) {
          a = requestAnimationFrame(anim);
        }
        else if (repeat) {
          if (pause) {
            t = setTimeout(startAnimation, pause);
          }
          else {
            startAnimation();
          }
        }
        else {
          resolve();
        }
      }

      function run(rate: number) {
        const curr: any = {};
        Object.keys(diff).forEach(k => {
          if (typeof diff[k] === 'string') {
            curr[k] = diff[k];
          }
          else {
            curr[k] = diff[k] !== 0 ? rate * diff[k] + start[k] : end[k];
          }
        });
        setState(curr);
      }

      function setState(prop: KeyValuePair) {
        instance.setState({
          animation: Object.assign({}, instance.state.animation, {
            [key]: prop
          })
        });
      }
    });
  }
}
