import { nt, BasketballAction, BasketTeam, IBasketFieldAnimationState } from '../constants';
import { possession } from './possession';
import { extras } from './extras';
import { freeThrow } from './free-throw';
import { twoPoints } from './two-points';
import { threePoints } from './three-points';
import { general } from './general';

export interface IBasketFieldAnimation {
  start: IBasketFieldAnimationState;
  end: IBasketFieldAnimationState;
}

export function getAnimations(state: IBasketFieldAnimationState, currentTeam: BasketTeam, currentAction: BasketballAction, nextTeam?: BasketTeam, nextAction?: BasketballAction): IBasketFieldAnimation[] {
  switch (nextAction) {
    case BasketballAction.Possession:
      return possession(nextTeam);
    case BasketballAction.FreeThrow:
    case BasketballAction.FreeThrowScored:
    case BasketballAction.FreeThrowMissed:
      return freeThrow(nextTeam, nextAction);
    case BasketballAction.TwoPointShootScored:
    case BasketballAction.TwoPointShootMissed:
      return twoPoints(state, nextTeam, nextAction);
    case BasketballAction.ThreePointShootScored:
    case BasketballAction.ThreePointShootMissed:
      return threePoints(state, nextTeam, nextAction);
    case BasketballAction.Foul:
    case BasketballAction.TimeOut:
      return extras(nextTeam);
    case BasketballAction.Quarter1:
    case BasketballAction.Quarter2:
    case BasketballAction.Quarter3:
    case BasketballAction.Quarter4:
    case BasketballAction.Half1:
    case BasketballAction.Half2:
    case BasketballAction.EndOfQuarter:
    case BasketballAction.EndOfHalf:
    case BasketballAction.EndOfMatch:
    case BasketballAction.OverTime:
    case BasketballAction.None:
      return general();
      default:
        return [{
          start: nt,
          end: nt
        }];
  }
}
