import * as React from 'react';
import { findDOMNode } from 'react-dom';
import TextField from './text-field';
import List from './list';

interface ISelectProps {
  label: string;
  onChange: (item: any) => void;
  dataSource: any[];
  model?: string;
  pk?: string;
  defaultSelected?: any;
  allowFiltering?: boolean;
}

class Select extends React.Component<ISelectProps, any> {
  state: any = {
    open: false,
    value: '',
    selectedItem: null,
    listMaxHeight: null
  };

  handleChange = (item: any) => {
    if (this.state.selectedItem !== item) {
      this.setState({
        selectedItem: item,
        value: typeof item === 'string' ? item : item[this.props.model]
      });
      this.props.onChange(item);
    }
  }

  handleOpen = () => {
    const open = !this.state.open;

    this.setState({
      open
    });

    if (open) {
      requestAnimationFrame(this.handleResize);
      requestAnimationFrame(() => window.addEventListener('click', this.handleClose));
    }
  }

  handleClose = () => {
    this.setState({
      open: false
    });
    window.removeEventListener('click', this.handleClose);
  }

  handleTxtChange = (value: any) => {
    this.setState({
      value
    });
  }

  handleTxtKeyDown = (e: React.KeyboardEvent<any>) => {
    const { dataSource: items, model, allowFiltering } = this.props;
    const { selectedItem } = this.state;
    let index = selectedItem ? items.indexOf(selectedItem) : 0;
    let shouldChange = false;

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        if (++index >= items.length) {
          index = 0;
        }
        shouldChange = true;
        break;
      case 'ArrowUp':
        e.preventDefault();
        if (--index < 0) {
          index = items.length - 1;
        }
        shouldChange = true;
        break;
      case 'Tab':
      case 'Enter':
        this.handleClose();
        this.props.onChange(selectedItem);
        break;
      default:
        if (!allowFiltering && e.key.length === 1 && /[a-z]/i.test(e.key)) {
          const found = items.slice(index + 1).filter(item => {
            const text = typeof item === 'string' ? item : item[model];
            return text.charAt(0).toLowerCase() === e.key.toLowerCase();
          })[0];

          index = items.indexOf(found);
          shouldChange = true;
        }
        break;
    }

    if (shouldChange) {
      if (!this.state.open) {
        this.handleOpen();
      }
      const item = items[index === -1 ? 0 : index];
      this.setState({
        selectedItem: item,
        value: typeof item === 'string' ? item : item[this.props.model]
      });
    }
  }

  componentWillMount() {
    const { defaultSelected, allowFiltering, dataSource, model } = this.props;
    if (defaultSelected) {
      this.setState({
        selectedItem: defaultSelected,
        value: typeof defaultSelected === 'string' ? defaultSelected : defaultSelected[model]
      });
    }
    else {
      if (!allowFiltering && dataSource.length > 0) {
        const firstItem = dataSource[0];
        this.setState({
          selectedItem: firstItem,
          value: typeof firstItem === 'string' ? firstItem : firstItem[model]
        });
      }
    }
  }

  handleResize = () => {
    let findDOMNode_: any = findDOMNode(this);

    const list = findDOMNode_.querySelector('.md-list');
    this.setState({
      listMaxHeight: `calc(100vh - ${list.getBoundingClientRect().top}px)`
    });
  }

  componentWillUpdate(nextProps: any, nextState: any) {
    const { dataSource, pk, model } = this.props;
    const { selectedItem } = nextState;

    if (nextProps.dataSource !== dataSource) {
      if (selectedItem && (pk || typeof selectedItem === 'string')) {
        const item = nextProps.dataSource.filter((dataItem: any) => typeof selectedItem === 'string' ? dataItem === selectedItem : dataItem[pk] === selectedItem[pk])[0];

        if (item) {
          this.setState({
            selectedItem: item,
            value: typeof item === 'string' ? item : item[model]
          });
        }
      }
    }
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  render() {
    const { label, dataSource, model, allowFiltering = false } = this.props;
    const { open, selectedItem, value, listMaxHeight } = this.state;

    let className = 'md-select';
    if (open) {
      className += ' open';
    }

    const txtProps = {
      label,
      value,
      onClick: this.handleOpen,
      onChange: this.handleTxtChange,
      onKeyDown: this.handleTxtKeyDown,
      readOnly: !allowFiltering
    };

    let items = dataSource;

    if (allowFiltering) {
      (txtProps as any).onFocus = (e: any) => e.target.select();

      const regExp = new RegExp(value, 'i');
      items = dataSource.filter(item => regExp.test(typeof item === 'string' ? item : item[model]));
    }

    return (
      <div className={className}>
        <TextField {...txtProps} />
        <i className="i i-select"></i>
        <List
          items={items}
          selectedItem={selectedItem}
          itemTextProp={model}
          itemClick={this.handleChange}
          style={{ overflowY: 'auto', maxHeight: listMaxHeight }}
        />
      </div>
    );
  }
}

export default Select;
