export const LIVE_SCHEDULE = 'LIVE_SCHEDULE';
export const CHANGE_SCHEDULE_SELECTED_DATE = 'CHANGE_SCHEDULE_SELECTED_DATE';
export const CHANGE_SCHEDULE_SELECTED_SPORT = 'CHANGE_SCHEDULE_SELECTED_SPORT';
export const CHANGE_SCHEDULE_FILTER_TEXT = 'CHANGE_SCHEDULE_FILTER_TEXT';

export const schedule = (payload: any) => ({
  type: LIVE_SCHEDULE,
  payload
});

export const changeDate = (payload: any) => ({
  type: CHANGE_SCHEDULE_SELECTED_DATE,
  payload
});

export const changeSport = (payload: any) => ({
  type: CHANGE_SCHEDULE_SELECTED_SPORT,
  payload
});

export const changeFiltering = (payload: any) => ({
  type: CHANGE_SCHEDULE_FILTER_TEXT,
  payload
});

export const reqSchedule = () => (dispatch: any, getState: any) => {

  getState().wsm.socket.emit('message', LIVE_SCHEDULE);
};
