import { IceHockeyTeam, IHockeyFieldAnimationState, t1, t2 } from  '../constants';
import { deepAssign } from '../../../../helpers';

let faceOffX, faceOffY, extrasX, extrasY, actionTextX, actionTextY, actionAlign, brickX, brickY;

function next(nextTeam: IceHockeyTeam) {

  faceOffX = nextTeam === IceHockeyTeam.Team1 ? 169 : 31;
  faceOffY = nextTeam === IceHockeyTeam.Team1 ? 65 : 21;
  extrasX = nextTeam === IceHockeyTeam.Team1 ? 157 : 19;
  extrasY = nextTeam === IceHockeyTeam.Team1 ? 53 : 8;
  actionTextX = nextTeam === IceHockeyTeam.Team1 ? 147 : 62;
  actionTextY = nextTeam === IceHockeyTeam.Team1 ? 75 : 30;
  actionAlign = nextTeam === IceHockeyTeam.Team1 ? 'end' : 'start';
  brickX = nextTeam === IceHockeyTeam.Team1 ? 152 : 60;
  brickY = nextTeam === IceHockeyTeam.Team1 ? 57 : 12;

  return {
    actionText: {
      x: actionTextX,
      y: actionTextY,
      textAlign: actionAlign
    },
    extras: {
      x: extrasX,
      y: extrasY,
      opacity: 1
    },

    faceOffCtr: {
      x: faceOffX,
      y: faceOffY
    }
  };

}

export const t1ExtrasStart: IHockeyFieldAnimationState = deepAssign({}, t1, next(IceHockeyTeam.Team1), {});
export const t1ExtrasEnd: IHockeyFieldAnimationState = deepAssign({}, t1, next(IceHockeyTeam.Team1), {
   brick: {
    x: brickX,
    y: brickY,
    rotate: -270
  }

});

export const t2ExtrasStart: IHockeyFieldAnimationState = deepAssign({}, t2, next(IceHockeyTeam.Team2), {});
export const t2ExtrasEnd: IHockeyFieldAnimationState = deepAssign({}, t2, next(IceHockeyTeam.Team2), {
   brick: {
    x: brickX,
    y: brickY,
    rotate: 450
  }
});

export function faceOffWin(nextTeam: IceHockeyTeam) {
  return [{
    start: nextTeam === IceHockeyTeam.Team1 ? t1ExtrasStart : t2ExtrasStart,
    end: nextTeam === IceHockeyTeam.Team1 ? t1ExtrasEnd : t2ExtrasEnd
  }];
}
