import * as React from 'react';

const CricketIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    stroke: 'transparent',
    fill: 'transparent'
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  const clip = 'cricket-ball-clip';

  return (
    <svg {...svgProps}>
      <defs>
        <clipPath id={clip}>
          <circle cx="125" cy="125" r="124" />
        </clipPath>
      </defs>
      <circle fill="#d23" cx="125" cy="125" r="125" />
      <g clipPath={`url(#${clip})`} stroke="#fff" strokeWidth="5" strokeLinecap="round" strokeDasharray="10,16">
        <path d="M 141 4 a 162 162 0 0 0 -78 228" />
        <path d="M 153 7 a 162 162 0 0 0 -80 229" />
        <path d="M 187 20 a 162 162 0 0 0 -82 226" />
        <path d="M 197 25 a 162 162 0 0 0 -78 231" />
      </g>
      <path clipPath={`url(#${clip})`} stroke="#333" strokeWidth="7" d="M 173 14 a 160 160 0 0 0 -83 227" />
    </svg>
  );
};

export default CricketIcon;
