import * as React from 'react';

export interface IButtonProps {
  type?: string;
  enabled?: boolean;
  flat?: boolean;
  style?: { [name: string]: string };
  className?: string;
  onClick?: (e: React.MouseEvent<any>) => void;
  children?: any;
}

export default function Button({ enabled = true, type = 'button', flat = false, style = undefined, className = undefined, onClick, children }: IButtonProps) {

  const defaultClassName = 'md-button';
  const classList = [defaultClassName, 'with-ripple'];

  const containerClassList = ['md-container'];

  if (flat) {
    classList.push(`${defaultClassName}--flat`);
  }

  if (className) {
    containerClassList.push(className);
  }

  if (type === 'warning') {
    classList.push(`warning`);
  }

  if (type === 'success') {
    classList.push(`success`);
  }

  let type_: any = type;
  return (
    <div className="md-container" style={style}>
      <button type={type_} className={classList.join(' ')} onClick={onClick} disabled={!enabled} >
        <span className={defaultClassName + '__text'}>
          { children }
        </span>
      </button>
    </div>
  );
}
