import * as React from 'react';
import { range } from '../../../helpers';

function generateNet(numOfLines: number, iniX1: number, iniX2: number, iniY1: number, iniY2: number, init: number, subtractX = 0, subtractY = 0) {
  return range(numOfLines).map(num => {
    num = init + (num * 9);
    const x1 = iniX1 || num;
    const x2 = iniX2 || num - subtractX;
    const y1 = iniY1 || num;
    const y2 = iniY2 || num - subtractY;

    return (
      <line key={num} x1={x1} x2={x2} y1={y1} y2={y2} />
    );
  });
}

export default class Gate extends React.Component<any, any> {
  shouldComponentUpdate(newProps: any) {
    return newProps.team !== this.props.team;
  }

  render() {
    const { fieldBgColor } = this.props;
    const leftSideNet = <path d="M 12 17 L 12 136 L 128 121 L 71 13 z" />;
    const rightSideNet = <path d="M 184 88 L 262 81.5 L 366 252 L 184 275 z" />;

    return (
      <svg viewBox="0 0 380 290" width="28.5" height="21.75" fill="transparent" stroke="#333" strokeWidth="2">
        <defs>
          <clipPath id="side-nets">
            {leftSideNet}
            {rightSideNet}
          </clipPath>
          <clipPath id="back-net">
            <path d="M 76 7 L 266 74 L 370 250 L 135 120" />
          </clipPath>
          <clipPath id="top-net">
            <path d="M 76 7 L 266 74 L 182 81 L 15 12" />
          </clipPath>
        </defs>
        <g strokeLinejoin="round" fill="#fff">
          <path d="M 369 257 L 133 126 L 6 143 L 6 10 L 76 6 L 135 120 L 370 250" />
          <path d="M 182 81 L 266 74 L 376 257 L 177 282 L 177 87" />
        </g>
        <g fill={fieldBgColor}>
          {leftSideNet}
          {rightSideNet}
        </g>
        <g strokeWidth="0.7" stroke="#eee" clipPath="url(#back-net)">
          {generateNet(20, 0, 0, 7, 250, 76)}
          {generateNet(21, 76, 370, 0, 0, 2, 0, -150)}
        </g>
        {rightSideNet}
        <g strokeWidth="0.7" stroke="#eee" clipPath="url(#top-net)">
          {generateNet(20, 0, 0, 7, 81, 15, -150)}
          {generateNet(15, 76, 266, 0, 0, 7, 0, 20)}
        </g>
        <path fill="#fff" d="M 361 252 L 366 252 L 361 245 L 184 147 L 184 154 z" />
        <g strokeWidth="0.7" stroke="#eee" clipPath="url(#side-nets)">
          {generateNet(12, 0, 0, 13, 136, 12)}
          {generateNet(14, 12, 128, 0, 0, 13, 0, 10)}
        </g>
        <g strokeWidth="0.7" stroke="#eee" clipPath="url(#side-nets)">
          {generateNet(20, 0, 0, 81, 275, 184)}
          {generateNet(21, 184, 366, 0, 0, 84, 0, 18)}
        </g>
        <g strokeLinejoin="round" fill="#fff">
          <path d="M 179 88 L 11 16 L 25 16 L 182 82" stroke="#fff" />
          <line x1="177" x2="11.5" y1="89" y2="18" />
          <line x1="183" x2="25" y1="81" y2="16" />
        </g>
      </svg>
    );
  }
}
