import { ISoccerFieldAnimationState, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextTeam: SoccerTeam) {
  return {
    actionText: {
      x: nextTeam === SoccerTeam.Team1 ? 85 : 19,
      y: 50
    },
    ball: {
      x: nextTeam === SoccerTeam.Team1 ? 94 : 11,
      y: 34,
      direction: BallDirection.Goal
    }
  };
}

const t1Penalty: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1));
const t2Penalty: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2));

export function penalty(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam) {
  let start: ISoccerFieldAnimationState;
  let end: ISoccerFieldAnimationState = nextTeam === SoccerTeam.Team1 ? t1Penalty : t2Penalty;

  start = deepAssign({}, end, {
    actionText: state.actionText,
    ball: {
      x: state.ball.x,
      y: state.ball.y,
      direction: BallDirection.None
    }
  });

  return [{
    start,
    end
  }];
}
