import * as React from 'react';
import { connect } from 'react-redux';

import { sportHasTime } from '../../../helpers';

const GameClock = ({ period, event, clock, className }: any) => {
  const { sport } = event;
  const { timerTicking, min, sec, lastUpdate }: any = clock || { min: 0, sec: 0 };
  const isDash = event.sport !== 1 && event.sport !== 24
  && event.sport !== 7 && event.sport !== 10 ? '-' : '';

  if (sport === 24 || sport === 37) {
    return <div></div>;
  }

  return (
    <div className={className}>

      { sport !== 1 && <span>{period}{isDash}</span> }
      { sport !== 10 && sport !== 7 ?
        min !== 0 && sec !== 0 &&
        sportHasTime(sport) && min && sec ? `${min}:${sec}` : '0:00' : ''
      }
    </div>
  );

};

export default connect(
  (state: any, ownProps: any) => ({
    clock: state.live.clocks[ownProps.event.id]
  })
)(GameClock);
