import * as React from 'react';
import { connect } from 'react-redux';
import Slider from '@material-ui/core/Slider';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { addToBetSlip } from './../../actions/bet-slip';

import { OddsFormat } from '../../constants';

const PrettoSlider = withStyles({
  root: {
    width: 'calc(100% - 15px)',
    height: 8,
    margin: '0px 10px 0px 10px'
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    marginTop: -8,
    marginLeft: -12,
    '&:focus,&:hover,&$active': {
      boxShadow: 'inherit'
    }
  },
  active: {},
  markLabel: {
    fontSize: '10px'
  },
  valueLabel: {
    left: 'calc(-50% + 4px)'
  },
  track: {
    height: 8,
    borderRadius: 4
  },
  rail: {
    height: 8,
    borderRadius: 4
  }
})(Slider);

class RangeSlider extends React.Component<any, any>  {

  state: any = {
    selection: null
  };

  setSelection(marks: any) {
    const { dispatch } = this.props;
    let odd = this.state.selection !== null ?
    this.state.selection :
    marks[0].odd;
    dispatch(addToBetSlip(odd));
  }

  handleSelection(oddId: any) {
    const { oddsByPlayer } = this.props;
    let odd = oddsByPlayer.filter((odd: any) => odd.id === oddId)[0];
    this.setState({ selection: odd });
  }

  render() {

    const { oddsByPlayer, hasManyOptions, player, oddsFormat } = this.props;

    if (!Object.keys(oddsByPlayer)[0]) return <span></span>;

    const getMarks = () => {
      let marks: any = [];

      oddsByPlayer
        .forEach((item: any, i: any) => {
          marks = [...marks, { value: i, label: item.line, id: item.id, price: item.price, odd: item }];
        });
      return marks;
    };
    let marks = getMarks();

    // console.log(oddsByPlayer.map((it: any) => it.line));

    if (!marks[0]) return <span></span>;

    const selectedOdd = this.state.selection ? this.state.selection : marks[0].odd;
    const isOTB = !selectedOdd.display || selectedOdd.suspended || selectedOdd.status === 'TemporarilySuspended';
    let odd_ = this.state.selection !== null ? this.state.selection : marks[0].odd;

    const rangeSliderClass = ['range-slider-wrap'];

    if (hasManyOptions) {
      rangeSliderClass.push('one-col');
    }

    return (
      <>
        <div className={rangeSliderClass.join(' ')}>
            <div style={{display: 'flex', justifyContent: 'space-between'}}>
              <Typography
                gutterBottom
                id="discrete-slider-small-steps">
                { player }
              </Typography>
              {!isOTB ?
                <div
                  className="range-slider-price"
                  style={ {
                    cursor: 'pointer',
                    marginTop: '5px',
                    position: 'relative'} }
                  onClick={() => this.setSelection(marks)}>
                  <Typography
                    gutterBottom
                    id="discrete-slider-small-steps">
                    <span
                      className="range-slider-price"
                      style={ {
                        borderRadius: '3px',
                        border: 'solid 1px #212121',
                        background: '#fff',
                        padding: '3px'
                      } }>
                      { odd_ && oddsFormat === OddsFormat.american ?
                        odd_.american.slice(0, 1) + Math.round(odd_.american.slice(1)) :
                        odd_ && odd_.price.toFixed(2)
                      }
                    </span>
                  </Typography>

                </div> : 'OTB'
              }
            </div>

            <PrettoSlider
              step={null}
              marks={marks}
              min={marks[0].value}
              valueLabelDisplay="auto"
              defaultValue={marks[0].value}
              max={marks[marks.length - 1].value}
              valueLabelFormat={(v: any, i: any) => marks[v].label}
              onChangeCommitted={(e: any, v: any) => this.handleSelection(marks[v].id)}
            />
        </div>
      </>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => ({
    oddsFormat: state.settings.oddsFormat
  })
)(RangeSlider);
