import * as React from 'react';
import Time, { TimeFormat } from '../../../components/form/time';

const className = 'schedule-bl';

const ButtonList = ({ items, scheduleData, onSelect, selectedDate = '' }: any) => (
  <div className={className}>
    {
      items.map((item: any) => (
        <button className={`${className}__button${item.time === selectedDate ? ' selected' : ''}`} key={item.time} onClick={onSelect(item.time)}>
          <div>
            <Time format={TimeFormat.Date}>{ scheduleData[item.index].time }</Time>
          </div>
          <div>
            <Time format={TimeFormat.Weekday}>{ scheduleData[item.index].time }</Time>
          </div>
        </button>
      ))
    }
  </div>
);

export default ButtonList;
