import * as React from 'react';
import { prematchTimeConverter } from './../views/live/live-helpers';

const PrematchDateHeader = ({ ev, i, events, view }: any) => {
  const startDate = prematchTimeConverter(ev.time).split(' ');
  const startDatePrev = i !== 0 && prematchTimeConverter(events[i - 1].time).split(' ');
  const date = `${startDate[1]}, ${startDate[0]}`;
  const datePrev = i !== 0 && `${startDatePrev[1]}, ${startDatePrev[0]}`;
  const isUnique = i === 0 || date !== datePrev;
  if (!isUnique) return <span></span>;
  return (
    <div className={`prematch-date-header ${view}`} >
      {`${date}`}
    </div>
  );

};

export default PrematchDateHeader;
