import * as React from 'react';
import { connect } from 'react-redux';
import {
  chromeIcon,
  fireFoxIcon,
  safariIcon
} from './../../../../base64icons';

let browsers = [
  {
    icon: chromeIcon,
    url: 'bit.ly/37R2zvx'
  },
  {
    icon: fireFoxIcon,
    url: 'mzl.la/35EDYbK'
  },
  {
    icon: safariIcon,
    url: 'apple.co/2R6fHWU'
  }
];

let addFundsStyle: any = {
  background: '#212121',
  padding: '35px',
  textAlign: 'center',
  color: '#fff'
};

let iconsStyle: any = {
  maxWidth: '25px'
};

const NoVideo = (props: any) => {

  const { language, isModernBrowser }: any = props;
  // console.log(props);
  return (
    <div style={addFundsStyle}>
      { !isModernBrowser ? language.videoLegacyBrowser : language.addBalanceVideo }
      <div style={{ position: 'relative', marginTop: '15px', display: 'flex' , justifyContent: 'space-evenly'}}>
        {
          !isModernBrowser &&
          browsers.map((browser: any, i: any) =>
            <a
              key={i}
              href={`https://${browser.url}`}
              target="_blank"
            >
              <img
                style={iconsStyle}
                src={`data:image/svg+xml;utf8;base64,${browser.icon}`}
              />
            </a>
          )
        }
      </div>
    </div>
  );

};

export default connect(
  (state: any) => ({
    language: state.languageData.settings
  })
)(NoVideo);
