import {
  IDLE_WINDOW,
  USER_SUCCESS,
  KILLED_SESSION,
  IDLE_ON_STREAM,
  LEGACY_REDIRECT,
  USER_CSRF_RECEIVED,
  TOGGLE_FAKE_FULL_SCREEN,
  NATIVE_FULL_SCREEN,
  UPDATE_LAST_INTERACTION } from '../actions/user';
import { bookmakerMock } from './bookmaker-mock';

let time = Date.now();

export const userInitialState: any = {
  isLegacyRedirect: false,
  bookmakerId: {},
  isIdleWindow: false,
  killedSession: false,
  maintenance: false,
  lastInteractionTime: time,
  isIdleOnStream: false,
  hasVideoOnFullScreen: false,
  hasNativeFullScreen: false
};

const convertBookmakerId = () => {
  const obj_: any = {};

  bookmakerMock().forEach(sport => {
    if (!obj_[sport.sportid]) {
      obj_[sport.sportid] = sport.bookmakerid;
    }
  });
  return obj_;
};

export const user = (state = userInitialState, action: any) => {
  // console.log(action.type);
  switch (action.type) {
    case NATIVE_FULL_SCREEN:
    return {
      ...state,
      hasNativeFullScreen: action.payload
    };
    case TOGGLE_FAKE_FULL_SCREEN:
      let isFullScreen = state.hasVideoOnFullScreen;
      return {
        ...state,
        hasVideoOnFullScreen: !isFullScreen
      };
    case IDLE_ON_STREAM:
      console.log('idle on stream');
      return {
        ...state,
        isIdleOnStream: action.payload
      };
    case USER_SUCCESS:
      state.bookmakerId = convertBookmakerId();
      return Object.assign({}, action.payload, state);
    case USER_CSRF_RECEIVED:
      return Object.assign({}, state, {csrf_token: action.payload});
    case LEGACY_REDIRECT:
      return Object.assign({}, state, {
        isLegacyRedirect: action.payload
      });
    case IDLE_WINDOW:
      return Object.assign({}, state, { isIdleWindow: action.payload });

    case KILLED_SESSION:
      return Object.assign({}, state, { killedSession: action.payload });
    // return Object.assign({}, state, { clocks: revisedClocks });
    case UPDATE_LAST_INTERACTION:

        return {
          ...state,
          lastInteractionTime: action.payload
        };
  }

  return state;
};
