import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { connect } from 'react-redux';

import Scoreboard from './scoreboard';
import Sticky from './sticky-component';
import GamecastWrapper from './gamecast-wrapper';
import ActionTicker from './action-ticker/';
import ScoreboardNew from './european/scoreboard-new';
import ScoreboardPeriod from './european/scoreboard-period';
import GamecastPreloader from './../../misc/gamecast-preloader';
import PrematchEventHeader from  './../../misc/prematch-event-header';

import { hasLottieAnimation } from './../../gamecast/football-lottie/constants';

import { emptyGamecastQueue } from './../../../actions/live';

import config from './../../../../config';

let dequeueInterval;

class Wrapper extends React.Component<any, any> {

  state: any = {
    renderGamecast: true,
    enqueObject: null,
    actionsSent_: 0,
    scrollingLock: false,
    visState: null,
    intervalValue: 2500

  };

  componentWillMount() {
    const self = this;

    // Delays gamecast animation queue if gamecast has lottie animations
    const isCricket = this.props.sport === 24 || (this.props.event && this.props.event.sport === 24);
    const isAmericanFootball = config.footballGamecastAnimated && this.props.sport === 8 || (this.props.event && this.props.event.sport === 8);

    let startInterval = isCricket ? 8000 : isAmericanFootball ? 6000 : this.state.intervalValue;

    let enqueObject = setInterval(() => {
      self.tick();
    }, startInterval);

    this.setState({ enqueObject });
    this.setState({ intervalValue: startInterval });
    this.setState({ visState: document.visibilityState });

    this.handleVisibiltyChange = this.handleVisibiltyChange.bind(this);
    document.addEventListener('visibilitychange', this.handleVisibiltyChange);

  }

  componentWillReceiveProps(newProps: any) {

    let findDOMNode_: any = findDOMNode(this);

    // If user resizes window, or window is too small to display gamecast on live events section;
    // stream will start playing even if its not visible. Prevent this ...
    if (this.state.renderGamecast && findDOMNode_ && window.getComputedStyle(findDOMNode_).display === 'none') {
      this.setState({ renderGamecast: false });
    }

    // Render gamecast again if viewport size allows gamecast to be visible again...
    if (!this.state.renderGamecast && findDOMNode_ && window.getComputedStyle(findDOMNode_).display !== 'none') {
      this.setState({ renderGamecast: true });
    }

    if (this.props.event && newProps.event.id !== this.props.event.id) {

      this.setState({ actionsSent_: 0 });

    }

  }

  componentWillUnmount() {
    // console.log('sport change');
    clearInterval(this.state.enqueObject);
    document.removeEventListener('visibilitychange', this.handleVisibiltyChange);
  }

  handleVisibiltyChange() {

    if (document.visibilityState === 'visible') {

      let lastAction = this.props.gamecastQueue.length - 1;
      this.setState({visState: document.visibilityState });
      this.setState({actionsSent_: lastAction });

    } else {
      this.setState({visState: document.visibilityState });
      // console.log(`state_ is ${this.state.visState}`);
    }

  }

  tick() {

    if (this.state.visState !== 'hidden') {

      // If this keeps updating when visibility change is "visible",
      // all animations will trigger when tab becomes active again.

      if (this.props.gamecastQueue[this.state.actionsSent_ + 1]) {
        this.setState({ actionsSent_: this.state.actionsSent_ + 1 });

        // We need to change the animation duration on some cricket actions.
        // We will terminate the interval, start a new one with the new value, reset to original value.
        // console.log(this.props);
        if (this.props.sport === 24 || (this.props.event && this.props.event.sport === 24)) {

          // console.log('sport in wrapper is cricket');
          // console.log(this.state.intervalValue, 'intervalValue in ticker');

          let currentAction = this.props.gamecastQueue[this.state.actionsSent_].action;

          if (currentAction === 168 || currentAction === 171 || currentAction === 172 || currentAction === 176 || currentAction === 178 || currentAction === 179) {
            // console.log('current action is', currentAction);
            let runsScored = this.deliveries && this.deliveries[0] && this.deliveries[0][this.deliveries[0].length - 1];
            // console.log('number of runs is ', runsScored);

            // 3 runs action has a duration of some 10 secs,
            // set a high value for the interval/animation duration.

            if (currentAction === 168 && runsScored === '3') {

              if (this.state.intervalValue !== 12000) {
                // console.log('interval needs to change to 12000');
                this.setState({ intervalValue: 12000 });

                clearInterval(this.state.enqueObject);

                let enqueObject = setInterval(() => {
                  this.tick();
                }, this.state.intervalValue);
                this.setState({ enqueObject });

              }

            } else if (runsScored !== '0') {

              // Lottie cricket animations hava a duration of some 6secs

              if (this.state.intervalValue !== 9000) {
                // console.log('interval needs to change to 8000');
                this.setState({ intervalValue: 9000 });

                clearInterval(this.state.enqueObject);

                let enqueObject = setInterval(() => {
                  this.tick();
                }, this.state.intervalValue);
                this.setState({ enqueObject });

              }

              clearInterval(this.state.enqueObject);

              let enqueObject = setInterval(() => {
                this.tick();
              }, this.state.intervalValue );
              this.setState({ enqueObject });

            }

          } else {
            // Cricket action but not one of long duration,
            // reset to default 2500 ms value
            if (this.state.intervalValue !== 2500) {
              // console.log('interval needs to change to 2500');
              this.setState({ intervalValue: 2500 });

              clearInterval(this.state.enqueObject);

              let enqueObject = setInterval(() => {
                this.tick();
              }, this.state.intervalValue );
              this.setState({ enqueObject });

            }

          }

        }

        // console.log('interval is', this.state.intervalValue);

        // console.log('update');
        // console.log(this.props.gamecastQueue[this.state.actionsSent_].action);

      }

    }

  }

  // Will give us the number of runs on cricket so we can know
  // if it is three runs (an action that needs a larger interval to
  // display the whole animation

  get deliveries() {

    if (this.props) {

      const { gamecastQueue } = this.props;

      if (!gamecastQueue[this.state.actionsSent_]) return;
      let matchData = gamecastQueue[this.state.actionsSent_].matchData;

      return matchData && matchData
        .split('#')
        .map((s: any) => s.split(':'));
    }
    return '';
  }

  render() {
    const { event, isMobile, uiToolbar, isSingleEvent, view, gamecastQueue, hasVideoOnFullScreen } = this.props;

    let gamecast = this.props.gamecast;

    // console.log(this.props.gamecastQueue);
    // console.log(gamecast);
    if (!event) return <span></span>;

    const { sport, league } = event;
    const setPreloader = event.videoSource < 0 && (!gamecastQueue || !gamecast || gamecast.action === '999' || gamecast.action === 999);
    // console.log(event.videoSource, 'ev');
    // console.log(setPreloader, 'setPreloader');
    let isByPeriod = sport === 6 || sport === 10 || sport === 7 || sport === 2 || sport === 8 || sport === 9 || sport === 17;
    // console.log(isByPeriod);

    if (sport === 37 && this.props.score_ && this.props.score_.scoreByPeriod[0]) {
      // console.log(this.props.score_);
      isByPeriod = true;
    }
    let ScoreboardComponent: any = () => <div></div>;
    ScoreboardComponent = isByPeriod ? ScoreboardPeriod : ScoreboardNew;

    if (this.props.score_ && sport === 8 && this.props.score_.scoreByPeriod && this.props.score_.scoreByPeriod.length <= 0) {
      ScoreboardComponent = ScoreboardNew;
    }

    const wrapperCont = ['wrapper-cont'];

    if (!isSingleEvent) {
      wrapperCont.push('live');
    }

    if (view === 0) {
      ScoreboardComponent = Scoreboard;
      wrapperCont.push('classic');
    }

    let score = this.props.score_ === undefined ? [0, 0] : [this.props.score_.scoreTeam1, this.props.score_.scoreTeam2];
    if (sport === 37 && !isByPeriod) {
      score = this.props.gamecast === undefined ? [0, 0] : this.props.gamecast.scoreboard && this.props.gamecast.scoreboard.total;
    }
    const matchData = gamecastQueue[this.state.actionsSent_] ? gamecastQueue[this.state.actionsSent_].matchData : '';
    const action = gamecastQueue[this.state.actionsSent_] ? gamecastQueue[this.state.actionsSent_].action : '';
    const team = gamecastQueue[this.state.actionsSent_] ? gamecastQueue[this.state.actionsSent_].team : '';

    const extraData = gamecastQueue[this.state.actionsSent_] ? gamecastQueue[this.state.actionsSent_].extraData : '';
    // let team = 1;
    // let action = 1078;
    // let matchData = "1:2^2:2";
    const timeouts = this.props.score_ && this.props.score_.timeouts;

    // console.log('action playing', action);

    const isPrematch = event.live === 0;

    if (isPrematch) {
      // console.log(event);
      return <PrematchEventHeader event={event} wrapperCont={wrapperCont} />;
    }
    // console.log(gamecast);
    // console.log(Object.keys(gamecast).length);
    let gamecastMessageStyles: any = {
      position: 'relative',
      top: this.props.view === 0 ? '10px' : '0px',
      fontSize: '10px', padding: '5px'
    };

    const gamecastAnimationClass = ['gamecast__animation'];

    if (hasVideoOnFullScreen) {
      gamecastMessageStyles.display = 'none';
      gamecastAnimationClass.push('full-screen-video');
      wrapperCont.push('full-screen-video');
    }
    // console.log(setPreloader, 'set');
    // console.log(action);
    // console.log(gamecast);
    return (
      <Sticky
        className={wrapperCont.join(' ')}
        enter={130}
        exit={120}>
        {
          setPreloader ?
            <GamecastPreloader
              isUnabled={gamecast === undefined || Object.keys(gamecast).length === 0 || gamecast.action === '999' || gamecast.action === 999 ? true : false }
              isGamecastOn={uiToolbar.gamecast}
              isSingleEvent={isSingleEvent}
              isMobile={isMobile}
              event={event}
              view={view}
              /> :
        <div style={hasVideoOnFullScreen ? {display: 'none'} : {}}>
          <ScoreboardComponent
            event={event}
            league={league}
          />
          <ActionTicker
            team={team}
            score={score}
            event={event}
            action={action}
            timeouts={timeouts}
            isPreloader={false}
            matchData={matchData}
          />

        </div>
        }

        { this.state.renderGamecast && uiToolbar.gamecast && !setPreloader ?
          <div className={ gamecastAnimationClass.join(' ') }>
            <GamecastWrapper
              team={team}
              event={event}
              action={action}
              timeouts={timeouts}
              extraData={extraData}
              matchData={matchData}
            />
          </div> : <div></div>
        }

        {
          event.sport !== 24 &&
          <div style={gamecastMessageStyles}>Gamecast scores, plays, and live streams may not be real-time or accurate. We assume no liability for any discrepancies. Official scoresheets produced at event intervals are used for bet settlement.</div>
        }
      </Sticky>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => ({
    hasVideoOnFullScreen: state.user.hasVideoOnFullScreen,
    ev: ownProps.event && state.eventsFlatten.byId[ownProps.event.id],
    score_: ownProps.event && state.live.scores[ownProps.event.id],
    gamecastQueue: state.live.gamecastQueueAnimations,
    gamecast: state.live.eventGamecast,
    uiToolbar: state.uiToolbar,
    view: state.settings.view
  })
)(Wrapper);
