import * as React from 'react';

const [gate, net] = ['#f44336', '#9e9e9e'];

function doCageCenter (xPoint: number, yPoint: number) {
  const netCenter = [];
  for (let i = 1; i <= 19; i++) {
    const numb = i * 5;
    netCenter.push(<line x1={xPoint + numb } y1="75" x2={xPoint + numb + 20} y2 ="145" key={i} />);
    if (i <= 16) {
      netCenter.push(<line x1="65" y1={yPoint + numb} x2="183" y2={yPoint + numb - 20} key={i + 100 } />);
    }
  }
  return (
    netCenter
  );
}

function doCageSides (xPoint: number, yPoint: number, xPoint2: number, yPoint2: number) {
  const netSides = [];
  for (let i = 1; i <= 13; i++) {
    const numb = i * 5;
    netSides.push(<line x1={xPoint} y1={yPoint + numb} x2={xPoint2} y2 ={yPoint2 + numb} key={i}/>);
    netSides.push(<line x1={xPoint} y1={yPoint - 20 + numb} x2={xPoint2} y2={yPoint + numb + 20} key={i + 100} />);
  }
  return (
    netSides
  );
}

const HockeyGate = (props: React.SVGAttributes<any> = {}) => (
  <svg viewBox="0 0 250 250" strokeWidth="" fill="transparent" {...props}>
  <defs>
    <clipPath id="center">
      <path d="M 60 83 A 90 90 0 0 1 80 138 L 185 150 A 45 95 0 0 0 155 84 z" />
    </clipPath>
    <clipPath id="left">
      <path d="M 60 133 V 83 A 90 90 0 0 1 80 138 z" />
    </clipPath>
    <clipPath id="right">
       <path d="M 150 80 V 160 L 185 150 A 45 95 0 0 0 150 83 z" />
    </clipPath>
  </defs>
    <g strokeWidth="0.5" stroke={net} clipPath="url(#center)" >
      {doCageCenter(60, 80)}
    </g>
    <g strokeWidth="0.5" stroke={net} clipPath="url(#left)">
      {doCageSides(50, 90, 80, 85)}
    </g>
    <g strokeWidth="0.5" stroke={net} clipPath="url(#right)">
      {doCageSides(150, 105, 185, 85)}
    </g>
    <path d="M 60 143 V 83 H 150 V 160 L 185 150 A 45 95 0 0 0 150 83 M 115 85 A 45 95 0 0 1 125 144 M 60 83 A 90 90 0 0 1 80 138 M 60 140 L 80 135 L 185 150" stroke={gate} strokeWidth="4" strokeLinejoin="round" />
  </svg>
);

export default HockeyGate;
