import * as React from 'react';
import { connect } from 'react-redux';

import Label from '../form/label';
import { changeTheme } from '../../actions/settings';

import config from '../../../config';

import { themes } from './../../constants';

const className = 'theme-switcher';

const ThemeSwitcher = ({ themeTitle, currentTheme, dispatch }: any) => {
  const handleClick = (theme: any) => {

    if (theme !== currentTheme) {
      dispatch(changeTheme(theme));
    }
  };

  return (
    <div className="md-container">
      <Label>{ themeTitle }</Label>
      <div className={className}>
        {
          themes.map(theme => (
            <span
              key={theme.id} onClick={() => handleClick(theme.id)}
              className={`${className}__item${theme.id === currentTheme ? ' selected' : ''}`}
              style={{ backgroundColor: theme.color }}
            >
              { theme.id === currentTheme ? '\u2713' : '' }
            </span>
          ))
        }
      </div>
    </div>
  );
};

export default connect(
  (state: any) => ({
    themeTitle: state.languageData.settings.theme,
    currentTheme: state.settings.theme
  })
)(ThemeSwitcher);
