import * as React from 'react';
import { connect } from 'react-redux';

import { changeTimeZone } from '../../actions/settings';

let askedForTimezone = false;
let timeZoneCache: any = null;

export const enum TimeFormat {
  Date,
  Time,
  DateTime,
  Weekday
}

class Time extends React.Component<any, any> {
  static getTimeZoneParam() {
    if (timeZoneCache) {
      return timeZoneCache;
    }

    const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    if (timeZone) {
      timeZoneCache = timeZone;
      return timeZoneCache;
    }

    timeZoneCache = offsets();
    return timeZoneCache;
  }

  get formattedTime() {
    const { children, timeZone: tz, format, language } = this.props;

    const offset = tz.offset || new Date().getTimezoneOffset();

    let children_: any = children;
    const date = new Date(children_);
    date.setMinutes(date.getMinutes() - offset);

    let options = {};

    const { month, day, hour = '2-digit', minute = '2-digit', timeZone } = new Intl.DateTimeFormat(language, { timeZone: 'UTC' }).resolvedOptions();

    switch (format) {
      case TimeFormat.Date:
        options = {
          month,
          day,
          timeZone
        };
        break;
      case TimeFormat.Time:
        options = {
          hour,
          minute,
          timeZone
        };
        break;
      case TimeFormat.Weekday:
        options = {
          weekday: 'short',
          timeZone
        };
        break;
      default:
        options = {
          month,
          day,
          hour,
          minute,
          timeZone
        };
        break;
    }

    return date.toLocaleString(language, options);
  }

  askForTimeZone = () => {
    if (!this.props.timeZone.offset && !askedForTimezone) {
      this.props.dispatch(changeTimeZone(Time.getTimeZoneParam()));
      askedForTimezone = true;
    }
  }

  componentDidMount() {
    requestAnimationFrame(this.askForTimeZone);
  }

  render() {
    let children_: any = this.props.children;

    return (
      <time className={this.props.className} dateTime={children_}>
        {this.formattedTime}
      </time>
    );
  }
}

export default connect(
  (state: any, ownProps: { className?: string, format?: number }) => ({
    language: state.settings.language,
    timeZone: state.settings.timeZone,
    className: ownProps.className,
    format: ownProps.format })
)(Time);

// ---- helper functions
function getOffset(at: Date) {
  const timeString = at.toTimeString();
  let match = timeString.match(/\([a-z ]+\)/i);
  let abbr: string;

  if (match && match[0]) {
    match = match[0].match(/[A-Z]/g);
    abbr = match ? match.join('') : undefined;
  } else {
    match = timeString.match(/[A-Z]{3,5}/g);
    abbr = match ? match[0] : undefined;
  }

  if (abbr === 'GMT') {
    abbr = undefined;
  }

  return {
    at: +at,
    abbr,
    offset: at.getTimezoneOffset()
  };
}

function findChange(low: any, high: any) {
  let mid, diff = ((high.at - low.at) / 12e4 | 0) * 6e4;

  while (diff) {
    mid = getOffset(new Date(low.at + diff));

    if (mid.offset === low.offset) {
      low = mid;
    }
    else {
      high = mid;
    }

    diff = ((high.at - low.at) / 12e4 | 0) * 6e4;
  }

  return low;
}

function offsets() {
  const startYear = new Date().getFullYear() - 2;
  let last = getOffset(new Date(startYear, 0, 1));
  const offsets = [last];
  let change;
  let next;

  for (let i = 1; i < 48; i++) {
    next = getOffset(new Date(startYear, i, 1));

    if (next.offset !== last.offset) {
      change = findChange(last, next);
      offsets.push(change);
      offsets.push(getOffset(new Date(change.at + 6e4)));
    }

    last = next;
  }

  for (let i = 0; i < 4; i++) {
    offsets.push(getOffset(new Date(startYear + i, 0, 1)));
    offsets.push(getOffset(new Date(startYear + i, 6, 1)));
  }

  return offsets;
}
