import { IFlattenEvents, IEventFinal } from './interfaces';

interface IEventsDataHandlerPayload {
  ev: IEventFinal;
  newLeagueIdx: number;
}

// newLeagueIdx is the position of a sorted league

class EventsDataHandlerClient {

  // 'state' refers to either client's redux state or to server's cachedData.flattenEvents object - as both data structures are identical
  static addEvent(state: IFlattenEvents, payload: IEventsDataHandlerPayload ) {

    const { ev, newLeagueIdx } = payload;

    if (state.byId[ev.id]) {
      console.log('event is already there. Dont add twice');
      return state;
    }

    return {
      ...state,
        byId: {
          // we add evId to state
          ...state.byId,
          [ev.id]: ev
        },
        leagues: {
          ...state.leagues,
          // We create league entry if it is not on state, or we do nothing
          [ev.leagueid]: !state.leagues[ev.leagueid] ? {
            sportid: ev.sport,
            name: ev.league,
            ui_sort: ev.ui_sort
          } : state.leagues[ev.leagueid]
        },
        bySport: {
          ...state.bySport,
          // We add item to existing array, or we create sport array and add item
          [ev.sport]: [...state.bySport[ev.sport] || [], ev.id]
        },
        byLeague: {
          ...state.byLeague,
          // We add item to existing array, or we create leagueid array and add item
          [ev.leagueid]: [...state.byLeague[ev.leagueid] || [], ev.id]
        },
        leagueIdsBySport: {
          ...state.leagueIdsBySport,
          // If sport entry does not exist, we create it an add league id to array; if league entry does not exist...
          [ev.sport]: !state.leagueIdsBySport[ev.sport] ? [ev.leagueid] : state.leagueIdsBySport[ev.sport] && state.leagueIdsBySport[ev.sport].indexOf(ev.leagueid) < 0 ?
          // we add item to existing sport array, or we create sport array and add item; or we add item at specific position depending on its ui_sort rule
          newLeagueIdx < 0 ?  [...state.leagueIdsBySport[ev.sport] || [], ev.leagueid] : this.insertItem(state.leagueIdsBySport[ev.sport], newLeagueIdx, ev.leagueid) :
          // if sport array exist and league entry exist we do nothing
          state.leagueIdsBySport[ev.sport]
        }

    };

  }

  static insertItem(arr: any[], index: number, item: any) {
    let newOrder =
    [
      ...arr.slice(0, index),
      item,
      ...arr.slice(index)
    ];

    return newOrder;
  }

  static removeEvent(state: IFlattenEvents, id: any) {

    // In case user was redirected to Prematch on UI load and has not requested live events
    if (!state.byId[id]) return state;

    const { sport, leagueid } = state.byId[id];

    // Remove item from its parent object
    let { [id]: deleteEvent, ...restStateById } = state.byId;
    let { [sport]: deleteBySport, ...restStateBySport } = state.bySport;
    let { [leagueid]: delateLeagueName, ...restStateLeague } = state.leagues;
    let { [leagueid]: deleteLeague, ...restStateByLeague } = state.byLeague;
    let { [sport]: deleteSportById, ...restStateLeagueIds } = state.leagueIdsBySport;

    // Check if the item to remove is the last item on the array, we will need to remove its key from the object
    let removeBySport = state.bySport[sport].filter(ev => ev !== id);
    let removeByLeague = state.byLeague[leagueid].filter(ev => ev !== id);
    let removeLeagueIdBySport = state.leagueIdsBySport[sport].filter(lg => lg !== leagueid);

    return {

      ...state,
      byId: restStateById,
       // If item is last on array, we remove league entry from state; if not, we do nothing
      leagues: removeByLeague.length <= 0 ? restStateLeague : {
        ...state.leagues
      },
      // If item is last on array, we remove sport key from object; if not, we only remove ev id from sport array
      bySport: removeBySport.length <= 0 ? restStateBySport : {
        ...state.bySport,
        [sport]: removeBySport
      },
      // If item is last on array, we remove leagueid key from object; if not, we only remove ev id from league array
      byLeague: removeByLeague.length <= 0 ? restStateByLeague : {
        ...state.byLeague,
        [leagueid]: removeByLeague
      },
        // If there are more events for that league, we do nothing; if ev item is last on byLeague array, we remove leagueid item from leagueIdsBySport array; if there is no more league items on the sports array, we remove sport key from object
      leagueIdsBySport: removeByLeague.length >= 1 ? state.leagueIdsBySport : removeLeagueIdBySport.length <= 0 ? restStateLeagueIds : {
        ...state.leagueIdsBySport,
        [sport]: removeLeagueIdBySport
      }

    };

  }

  static updateNumberMarkets(state: IFlattenEvents, payload: any) {

    let { evId, numberMarkets, hasOpenMarkets } = payload;

    let ev_ = state.byId[evId];

    if (!ev_) return state;

    let shouldOpenMarketsUpdate = false;

    if (Object.keys(ev_.hasOpenMarkets)) {
      Object.keys(ev_.hasOpenMarkets).forEach(bookmakerId => {
        if (ev_.hasOpenMarkets[bookmakerId] === false || ev_.hasOpenMarkets[bookmakerId] === undefined ) {
          shouldOpenMarketsUpdate = true;
        }
      });
    }  else {
      // console.log(ev_.hasOpenMarkets, 'undefined');
      shouldOpenMarketsUpdate = true;
    }

    let updateNumberMarkets = JSON.stringify(numberMarkets) !== JSON.stringify(ev_.numberMarkets);

    if (updateNumberMarkets || shouldOpenMarketsUpdate) {

      return {
        ...state,
        byId: {
          ...state.byId,
          [evId] : {
            ...state.byId[evId],
            numberMarkets,
            hasOpenMarkets
          }
        }
      };
    }

    return state;

  }
}

export default EventsDataHandlerClient;
