import { OPEN_MENU, CLOSE_MENU } from '../actions/menu';

export const menuInitialState = {
  open: false
};

export const menu = (state = menuInitialState, action: any): any => {
  switch (action.type) {
    case OPEN_MENU:
      return {
        open: true
      };
    case CLOSE_MENU:
      return {
        open: false
      };
  }
  return state;
};
