import * as React from 'react';
import SoccerBall from '../../svgs/soccer-icon';
import { Animation } from '../helpers';
import { SoccerTeam, BallDirection } from './constants';

export default class Ball extends React.Component<any, any> {
  animation = new Animation(this);

  state = {
    animation: {
      rotate: {
        value: 0
      },
      direction: {
        x: 0,
        y: 0
      }
    }
  };

  animate = () => {
    this.animation.start('rotate', { value: 0 }, { value: 360 }, 800, true);
    this.animation.start('direction', { x: 0, y: 0 }, { x: 1800, y: 400 }, 600, true, 900);
  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    this.animation.cancel();
  }

  render() {
    let { x, y, width, height, scale } = this.props;
    [x, y, width, height] = [x, y, width, height].map(Number);

    const w = (p: any) => width * p / 105;
    const h = (p: any) => height * p / 68;

    const rotate = Math.round(this.state.animation.rotate.value);

    let { direction, team } = this.props;
    const showDirection = direction === BallDirection.Goal || direction === BallDirection.PenaltyMark;

    let { x: dx, y: dy } = this.state.animation.direction;
    let drotate = 0;

    if (showDirection) {
      let x2, y2 = h(34);

      if (direction === BallDirection.Goal) {
        x2 = team === SoccerTeam.Team1 ? width : 0;
      }
      else {
        x2 = team === SoccerTeam.Team1 ? w(94) : w(11);
      }

      drotate = Math.atan2(y2 - y, x2 - x) * 180 / Math.PI;
      dx /= 100;
      dy /= 100;
    }

    const size = 1.75 * scale;
    const half = size / 2;

    return (
      <svg x="-1" y="-1">
        <svg viewBox={`-1 -1 ${width + 2} ${height + 2}`}>
          <defs>
            <linearGradient id="bg-ball-direction" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stopColor="#000" stopOpacity="0.2" />
              <stop offset="50%" stopColor="#aaa" stopOpacity="0.3" />
              <stop offset="100%" stopColor="#fff" stopOpacity="0.4" />
            </linearGradient>
          </defs>
          {
            showDirection &&
              <path fill="url(#bg-ball-direction)" strokeWidth="0"
                transform={`rotate(${drotate} ${x} ${y})`} d={`M ${x} ${y} L ${x + dx} ${y - dy} A 1 3 0 0 1 ${x + dx} ${y + dy} z`} />
          }
          {
            x >= 0 &&
              <g transform={`rotate(${rotate} ${x} ${y})`}>
                <SoccerBall width={size} height={size} x={x - half} y={y - half} />
              </g>
          }
        </svg>
      </svg>
    );
  }
}
