import * as React from 'react';
import { groupEventsByLeague } from './../live/live-helpers';
import League from './../live/league';
import LeagueNew from './../live/european/live-events-league-card/league-new';
import { sortAlpha } from './sports-item';

const Events = ({ divElement, prematchLeagues, activeLeague, isDisplaySmall, outcomeList, view, userMainOutcomes, bookmakerId }: any) => {

  const events = prematchLeagues;

  if (!events[0]) return <div></div>;

  let data;

  let _events = events.filter((ev: any) => {
    const sportBookId = bookmakerId[ev.sport];
    return ev.hasOpenMarkets[sportBookId];
  });
  _events = sortAlpha(_events, 'league');

  if (!_events[0]) return <div></div>;

  data = groupEventsByLeague(_events, _events[0].sport, view).map((eventsByLeague, i) => {
    // console.log(`event 0 ${eventsByLeague[0].league}`);
    // console.log(`active league ${activeLeague}`);

    if (activeLeague === '' || activeLeague === eventsByLeague[0].league) {
      if (view === 3) {
        return (
          <LeagueNew
            userMainOutcomes={userMainOutcomes}
            isDisplaySmall={isDisplaySmall}
            getGamecast={''}
            outcomeList={outcomeList}
            events={eventsByLeague}
            subscribedEvent={0}
            isPrematch={true}
            key={i}
          />
        );
      } else {
        return (
          <League
            key={eventsByLeague[0].league}
            events={eventsByLeague}
            view={view}
          />
        );
      }
    }
    return '';
  }).filter(item => item !== '');

  return (
    <div className="games-wrapper">
      <div className={ view === 3 ? `games` : `games classic`}>
        <div className="section-wrapper">
          <div className="leagues-wrapper prematch">
            { data }
          </div>
        </div>
      </div>
    </div>
  );
};

export default Events;
