import * as React from 'react';
import { connect } from 'react-redux';

import Switch from '../../misc/switch';

import { changeBetType } from '../../../actions/live';

class TypeSwitcher extends React.Component <any, any> {

  changeBet = (props: any) => {
    return this.props.dispatch(changeBetType(+!this.props.betType));
  }

  render () {
    const { betType, language, view } = this.props;

    if (view !== 3 && view !== 1) {
      return (
        <div className="md-container type-switcher">
          <span>{language.straightBets}</span>
          <Switch on={!!betType} onToggle={this.changeBet} />
          <span>{language.parlays}</span>
        </div>
      );
    } else {
      return(
        <div className="bet-type-select" onClick={this.changeBet}>
          { betType === 0 ? language.straightBets : language.parlays } <span> [+] </span>
        </div>
      );
    }
  }
}

export default connect(
  (state: any) => ({
    betType: state.live.betType,
    language: state.languageData.betSlip,
    view: state.settings.view
  })
)(TypeSwitcher);
