import * as React from 'react';

function NewSwitch({ className_, leftText, rightText, onToggle, checked_ }: any) {

  const onToggle_ = () => {
    onToggle();
  };

  let contClass = ['switch-container'];
  let leftTextClass = checked_ ? 'switch_text left' : 'switch_text left active';
  let rightTextClass = !checked_ ? 'switch_text right' : 'switch_text right active';

  if (leftText.length <= 8) {
    leftTextClass += ' small-text';
  }

  if (rightText.length <= 8) {
    rightTextClass += ' small-text';
  }

  contClass.push(className_);

  return (
    <div className={contClass.join(' ')}>
      <div className={leftTextClass}>{leftText}</div>
      <div className="slide-container">
        <input
          type="checkbox"
          checked={checked_}
          id={`toggle_${leftText.replace(' ', '')}`}
          className="checkbox"
          onChange={onToggle_} />
        <label
          htmlFor={`toggle_${leftText.replace(' ', '')}`}
          className="switch" >
        </label>
      </div>
      <div className={rightTextClass}>{rightText}</div>
    </div>
  );

}

export default NewSwitch;
