import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../../../config';

import { Sports, GetSportName } from '../../../../../constants';

import BaseballIcon from '../../../../svgs/baseball-icon';
import BasketballIcon from '../../../../svgs/basketball-icon';
import IceHockeyIcon from '../../../../svgs/ice-hockey-icon';
import SoccerIcon from '../../../../svgs/soccer-icon';
import TennisIcon from '../../../../svgs/tennis-icon';
import CricketIcon from '../../../../svgs/cricket-icon';
import AmericanFootballIcon from '../../../../svgs/american-football-icon';
import RugbyIcon from '../../../../svgs/rugby-icon';

import UiButtons from '../gamecast-button/ui-buttons';

import { changeBetType } from './../../../../../actions/live';

import TypeSwitcher from './../../type-switcher';
import SwitchNew from './../../../../misc/switch-new';
import PrematchLink from './../../../../misc/prematch-link';

import Link from '../../../../form/link';

const icons: any = {
  [Sports.americanFootball]: <AmericanFootballIcon />,
  [Sports.baseball]: <BaseballIcon />,
  [Sports.basketball]: <BasketballIcon />,
  [Sports.iceHockey]: <IceHockeyIcon />,
  [Sports.soccer]: <SoccerIcon />,
  [Sports.tennis]: <TennisIcon />,
  [Sports.cricket]: <CricketIcon />,
  [Sports.rugbyUnion]: <RugbyIcon />,
  [Sports.rugbyLeague]: <RugbyIcon />
};

class SingleEventLeagueHeader extends React.Component<any, any> {

  changeBet = () => {
    const { dispatch } = this.props;
    return dispatch(changeBetType(+!this.props.betType));
  }

  render() {
    const { event, sports, betType, hideTooltip, parlayBySport, hasVideoOnFullScreen } = this.props;
    const { league, sport } = event;
    // const to = `/${path.split('/')[1]}/${ this.mySport()}`;
    const to = event.live === 1 ? `/live/${GetSportName(sport)}` : `/prematch/${sports[Sports[sport]]}`;
    const isPrematch = event.live === 0;
    let prematchButtClass = ['prematch-link-desktop-cont single-event'];

    if (isPrematch) {
      prematchButtClass.push('is-prematch');
    }

    const headerStyles = hasVideoOnFullScreen ? {
      display: 'none'
    } : {};

    return (
      <table className="single-event-league-header" style={headerStyles}>
        <tbody>
          <tr>
            <td className="back">
              <Link to={to}>
                <div className="back-button"><i className="i i-select"></i></div>
              </Link>
            </td>
            <td className="league">
              <div className="title"><span className="sports__icon">{ icons[sport] }</span>{league.toUpperCase()}</div>
            </td>
            <td className="gamecast-button">
              { event.live === 1 &&
                <UiButtons hideTooltip={hideTooltip}/>
              }
            </td>
          </tr>
          <tr className="bet-type">

            <td style={ config.hasPrematchLink ? {height: '30px'} : {} }>
            { config.hasParlay && parlayBySport &&
              <div className="switch-single-event parlays-switch">
                <SwitchNew
                  leftText={'Straight'}
                  rightText={'Parlays'}
                  checked_={betType === 1}
                  onToggle={() => this.changeBet()}
                  className_={'single-event-switch'}
                />
              </div>
            }
            </td>
            { config.hasPrematchLink &&
              <td>
                <div className={`switch-single-event ${isPrematch ? 'prematch' : 'live'}-switch`}>
                  <Link
                    activeClassName=""
                    to={`${isPrematch ? '/' : '/prematch'}`}
                  >
                    <SwitchNew
                      className_={`single-event-switch-${isPrematch ? 'prematch' : 'live'}`}
                      checked_={isPrematch}
                      leftText={config.switchLiveText}
                      rightText={config.switchPrematchText}
                      onToggle={() => console.log('{}')}
                    />
                  </Link>
                </div>
              </td>
            }
          </tr>
        </tbody>
      </table>
    );
    }
  }

export default connect(
  (state: any, ownProps: any) => {
    const pathState = state.router.location.pathname;
    const path = pathState.slice(pathState.lastIndexOf('/') + 1, pathState.length);
    return {
      pathState,
      path,
      betType: state.live.betType,
      sports: state.languageData.sports,
      parlayBySport: state.live.parlayBySport,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
    };

  }
)(SingleEventLeagueHeader);
