export function numberSuffix(str: string) {
  const lastTwo = str && str.slice(str.length - 2);
  const isDecimal = str && parseInt(lastTwo) >= 11 && parseInt(lastTwo) <= 13;
  const char = str && isDecimal
    ? str && str
    : str && str.slice(str.length - 1);

  switch (char) {
    case '1':
      return 'st';
    case '2':
      return 'nd';
    case '3':
      return 'rd';
  }

  return 'th';
}

export function mapOutcomeTitle(home: any, away: any, inverted: any, outcomes: any, otc: any, odd?: any) {
  const { replacers } = odd || outcomes[otc.id][Object.keys(outcomes[otc.id])[0]];
  const [oddHome, oddAway] = [home, away];

  // const titleBefore = otc.title; // DBG
  return otc.title
  // const titleAfter = otc.title // DBG
    .replace('{HOME}', home)
    .replace('{AWAY}', away)
    .replace('{X}', replacers[0])
    .replace('{X0}', replacers[0] + numberSuffix(replacers[0]))
    .replace('{Y}', replacers[1])
    .replace('{Y0}', replacers[1] + numberSuffix(replacers[1]))
    .replace('{Z0}', replacers[2] + numberSuffix(replacers[2]))
    .replace('{Z}', replacers[2])
    .replace('{TEAM}', replacers[1] === 'HOMETEAM' || replacers[2] === 'HOMETEAM' ? oddHome : oddAway)
    .replace('{TEAM0}', replacers[0] === 'HOMETEAM' ? oddHome : oddAway)
    .replace('{NAME}', replacers[0]);

  // DBG:
  // console.log(`Transformed outcome title: [${titleBefore}] --> [${titleAfter}];
  //   replacers: ${replacers}`);
  // return titleAfter;
}
