import * as React from 'react';
import { connect } from 'react-redux';
import Odd from './odd';

import config from './../../../../config';

import { Dialog, DialogContent, DialogButtons } from '../../form/dialog';
import Button from '../../form/button';
import Table from '../../form/table';
import { BetType } from '../../../constants';

import { userUpdateBetConfirmation, confirmBets, addConfirmationOdds, closeBetConfirmation, fakeArray, removeBet, uploadConfirmationScreenshot, unfreezeUpdateConfirmationOdds } from '../../../actions/bet-confirmation';
import { userUpdateBetSlip, removeFromBetSlip, addToBetSlip, updateBet } from '../../../actions/bet-slip';

// let iframeValues = require('./../../../../client/iframe-sizer').values;

class BetConfirmation extends React.Component<any, any> {

  state: any = {
    isFromConfirm : false,
    makeVisibleConfirm: false,
    currentEvent: this.props.currentEvent,
    confirmedTickets: []
  };

  componentDidMount() {
    this.setState({ currentEvent: this.props.currentEvent});
  }

  componentWillReceiveProps(newProps: any) {
    const { dispatch } = this.props;
    if (newProps.odds && typeof newProps.odds.length !== 'undefined') {
      newProps.odds.forEach((odd: any) => {
        if (odd.ticketNo && this.state.confirmedTickets.indexOf(odd.ticketNo) === -1) {
          let arr = this.state.confirmedTickets;
          arr.push(odd.ticketNo);
          this.setState({ confirmedTickets: arr });
          dispatch(uploadConfirmationScreenshot(odd.ticketNo));
        }
      });
    }

    if (this.state.currentEvent === 0) {
      this.setState({ currentEvent: this.props.currentEvent});
    }

    if (newProps.closedConnection && (newProps.closedConnection !== this.props.closedConnection)) {
      this.closeIt();
    }

    if (newProps.betSlipOdds.length > 0) {
      // this.closeIt();
    }

  }

  closeIt = () =>  {
    const { dispatch, freeze, betType, odds } = this.props;

    dispatch(closeBetConfirmation());
    if (freeze) {
      if (betType === BetType.parlay && fakeArray[0].status !== 'lineOutOfDate') {
        fakeArray.forEach((bet: any) => {
          dispatch(removeBet(bet));
          dispatch(removeFromBetSlip(bet));
        });
      }
      else {
        fakeArray.forEach((bet: any) => {
          if (bet.status !== 'lineOutOfDate') {
            dispatch(removeBet(bet));
            dispatch(removeFromBetSlip(bet));
          }
        });
      }
    }
    this.setState({ isFromConfirm: false });

  }

  updateBetslip = () => {
    const { dispatch, betSlipOdds, betSlipParlay, odds, betType, parlay } = this.props;
    const lineOutDate = betType === 0 ? odds.filter((item: any) => item.status === 'lineOutOfDate')[0] !== undefined : parlay.status === 'lineOutOfDate';

    odds.map((odd: any) => {

      // Odds that have already been accepted/processed by the server get removed,
      // leaving only unsubmitted odds
      if (odd.status === 'ok') {
        // console.log(odd);
        // console.log(`remove`);
        dispatch(removeFromBetSlip(odd));
        dispatch(removeBet(odd));
        const idxBetslip = odds.indexOf(odd);
        betSlipOdds.splice(idxBetslip, 1);
        odds.splice(idxBetslip, 1);
      }
      else {
        // Unlock the betslip so that a change can happen
        odd.locked = false;

        // Unlock BetSlipodds instance as well.
        betSlipOdds.forEach((_bsOdd: any) => {
          if (_bsOdd.id === odd.id)
            _bsOdd.locked = false;
        });
      }
    });

    // Unlock the parlay.
    if (betSlipParlay)
      betSlipParlay.locked = false;
    if (parlay)
      parlay.locked = false;

    const hasReplacement = betSlipOdds.filter((odd: any) => odd.replacementBet)[0];
    // console.log(hasReplacement, 'hasReplacement');
    // dispatch(addConfirmationOdds(betSlipOdds, betSlipParlay));

    betSlipOdds.map((odd_: any, i: any) => {

      if (odd_.replacementBet && (odd_.id !== odd_.replacementBet.id)) {
        // console.log(odd_.id, 'odd_.id');
        // console.log(odd_.replacementBet.id, 'odd_.replacementBet.id');

        let replacementOdd;

        if (betType === 0) {
          const { outcome, intent, risk } = odd_;
          replacementOdd = Object.assign(odd_.replacementBet, {
            outcome, intent, risk
          });
        } else {
          replacementOdd = Object.assign(odd_.replacementBet, {});
        }

        let userUpdateBetSlipOdds = betSlipOdds;
        userUpdateBetSlipOdds[i] = replacementOdd;

        dispatch(userUpdateBetSlip(betSlipOdds));

        const factor = betType === BetType.straight ? replacementOdd.price :
        betSlipOdds.map((o: any) => o.price).reduce((a: any, b: any) => a *= b, 1);

        if (betType === 0) {
          const { intent, risk, toWin, limit } = odd_;
          if (intent) {

            if (intent === 'risk') {
              replacementOdd = Object.assign(replacementOdd, {
                risk,
                toWin: (risk * (factor - 1)).toFixed(2),
                intent,
                limit
              });
            } else {

              replacementOdd = Object.assign(replacementOdd, {
                risk: (toWin / (factor - 1)).toFixed(2),
                toWin,
                intent,
                limit
              });

            }

          }

        }

        let updatedBetConfirmationOdds = odds;
        updatedBetConfirmationOdds[i] = replacementOdd;

        dispatch(userUpdateBetConfirmation(updatedBetConfirmationOdds));

      } else if (odd_.priceChanged) {
        // console.log('this is just price change', Date.now());
        let updatedBet = this.props.betslipUpdateOutcomes[`${odd_.event}_${odd_.outcome}`][odd_.id];
        delete odd_.priceChanged;
        let replacementBet;

        const factor = betType === BetType.straight ? updatedBet.price :
        betSlipOdds.map((o: any) => o.price).reduce((a: any, b: any) => a *= b, 1);

        if (betType === 0) {
          const { intent, toWin, risk, limit } = odd_;

          if (intent) {

            if (intent === 'risk') {

              replacementBet = updatedBet && Object.assign(updatedBet, {
                risk: risk,
                toWin: (risk * (factor - 1)).toFixed(2),
                intent,
                limit
              });

            } else {

              replacementBet = updatedBet && Object.assign(updatedBet, {
                risk: (toWin / (factor - 1)).toFixed(2),
                toWin: toWin,
                intent,
                limit
              });

            }

          }

        } else {
          replacementBet = Object.assign(updatedBet, {});
        }

        betSlipOdds[i] = replacementBet;
        this.setState(this.state);

        // console.log('addConfirmationOdds', Date.now());
        this.setState(this.state);

        dispatch(addConfirmationOdds(betSlipOdds, betSlipParlay));
      } else {
        // console.log(`false`);
      }
    });

    if (this.props.freeze) {
      dispatch(unfreezeUpdateConfirmationOdds());
    }

    dispatch(addConfirmationOdds(betSlipOdds, betSlipParlay));

  }

  confirmBets = () => {
    const { dispatch, odds, parlay, betSlipOdds, betSlipParlay } = this.props;

    // Lock each odds item to prevent any updates to it.

    odds.forEach((odd: any) => {
      odd.locked = true;
    });
    betSlipOdds.forEach((odd: any) => {
      odd.locked = true;
    });

    if (parlay)
      parlay.locked = true;
    if (betSlipParlay)
      betSlipParlay.locked = true;

    dispatch(confirmBets(odds, parlay));
    // this.setState({ makeVisibleConfirm: false });
    this.setState({ isFromConfirm: true });

  }

  render() {

    // let iframeValues = {
    //   scrollTop: {},
    //   odds: {}
    // };

    const { open, freeze, odds, betSlipOdds, parlay, betSlipParlay, language, betType, loading, betslipUpdateOutcomes } = this.props;
    // console.log(betSlipOdds);
    // console.log(odds);
    const buttons = [];
    // console.log(odds);
    // console.log(odds[1]);
    // const limitMinimum = betType === 0 && odds.filter(item => item.status === 'limitMinimumPrice') !== betSlipOdds.length;
    const lineClosed = betType === 0 ? odds.filter((item: any) => item.status === 'lineClosed')[0] !== undefined : parlay.status === 'lineClosed';
    const lineOutDate = betType === 0 ? odds.filter((item: any) => item.status === 'lineOutOfDate')[0] !== undefined : parlay.status === 'lineOutOfDate';
    const isLoading = loading === 0 ? false : true;
    const allDone = betType === 0 ? odds.filter((item: any) => item.status === 'ok' || item.status === 'emergencyMode' || item.status === 'lineClosed').length === odds.length : parlay.status === 'ok' || parlay.status === 'lineClosed' || parlay.status === 'emergencyMode';
    const allClosed = betType === 0 ? odds.filter((item: any) => item.status === 'lineClosed').length === odds.length : parlay.status === 'lineClosed';
    // const isLimits = betType === 1 || odds.filter(item => item.limit).length === betSlipOdds.length;

    const hasReplacement = betSlipOdds.filter((odd: any) => odd.replacementBet)[0];
    const updateAfterPlace = lineOutDate;

    let allUnknownError = odds.filter((item: any) => item.status && !language.errors[item.status]).length === odds.length;
    let allNonUpdatable = odds.filter((item: any) => item.status && (item.status !== 'lineOutOfDate' || !language.errors[item.status])).length === odds.length;

    const hasPriceChanged = odds && odds.filter((odd: any) => {
      const updatedOutcome = betslipUpdateOutcomes && betslipUpdateOutcomes[`${odd.event}_${odd.outcome}`] && betslipUpdateOutcomes[`${odd.event}_${odd.outcome}`];
      if (updatedOutcome && updatedOutcome[odd.id] && updatedOutcome[odd.id].id === odd.id) {
        if (updatedOutcome[odd.id].price === odd.price) {
          return odd;
        }
      } else {
        return odd;
      }
    }).length !== odds.length;

    const hasUpdates = hasPriceChanged || hasReplacement;

    if (!freeze && !allDone && hasUpdates || (updateAfterPlace)) {
      if (hasUpdates || updateAfterPlace) {
        buttons.push(<Button key="confirm" onClick={this.updateBetslip}>{language.update}</Button>);
      }
    }

    const parlayBlock = betType === 1 && (parlay.status && parlay.status !== 'lineOutOfDate');

    if (!allDone && !parlayBlock && !allClosed && !allUnknownError && !allNonUpdatable) {
      buttons.push(<Button enabled={(!isLoading && !hasUpdates && !updateAfterPlace) } key="confirm_" onClick={this.confirmBets}>{language.confirm}</Button>);
    }

    buttons.push(<Button enabled={!isLoading} key="close" onClick={this.closeIt}>{freeze ? language.close : language.cancel}</Button>);

    let iframeValues;

    if (!config.hasMaxViewHeight && open) {
      let { iframeSizer } = (window as any);
      iframeValues = iframeSizer;

    }
    // console.log(parlay.status);
    return (
      <Dialog iframeValues={iframeValues} open={open} title={betType === BetType.straight ? language.straightBets : language.parlays} className="bet-confirmation">
        <DialogContent>
          <section className="bet-confirmation__message">
            { isLoading && <div className="ticket-proccessing">We are processing your tickets...</div> }
            {(!isLoading && !freeze && hasUpdates && updateAfterPlace && !allDone && (language.errors.betChanged || language.errors.lineOutOfDate)) }
          </section>

          <div className="bet-confirmation__mobile">
          {
              odds.filter((odd: any) => odd !== undefined).map((odd: any, i: any) =>
                <Odd
                  line={i}
                  odd={odd}
                  key={odd.uuid}
                  isMobile={true}
                  length={odds.length}
                  betSlipOdd={betSlipOdds.filter((o: any) => o.uuid === odd.uuid)[0]}
                />)
            }
          </div>

          <Table columns={[language.betDescription, language.risk, language.toWin, language.status, language.removeTicket]}>
            <tbody className="bet-confirmation__table">
              {
                odds.filter((odd: any) => odd !== undefined).map((odd: any, i: any) =>
                  <Odd
                    line={i}
                    odd={odd}
                    isMobile={false}
                    length={odds.length}
                    key={odd.uuid}
                    betSlipOdd={betSlipOdds.filter((o: any) => o.uuid === odd.uuid)[0]}
                  />)
              }
            </tbody>
          </Table>
        </DialogContent>
        <DialogButtons>
          { buttons }
        </DialogButtons>
      </Dialog>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => {

    const freeze = state.betConfirmation.freeze;
    const odds = state.betConfirmation.odds;
    const betSlipOdds = state.betSlip.odds;

    const hasChanges = () => {
      if (odds.some((odd: any) => odd && odd.price !== betSlipOdds.filter((o: any) => o.id === odd.id)[0] === undefined)) {
        return false;
      }
      return !freeze && odds.some((odd: any) => odd && betSlipOdds.filter((o: any) => o.id === odd.id)[0] !== undefined && odd.price !== betSlipOdds.filter((o: any) => o.id === odd.id)[0].price);
    };

    return {
      odds,
      freeze,
      betSlipOdds,
      user: state.user,
      loading: state.loading,
      hasChanges: hasChanges(),
      events: state.live.events,
      betType: state.live.betType,
      outcomes: state.live.outcomes,
      open: state.betConfirmation.open,
      betSlipParlay: state.betSlip.parlay,
      isReplacing: state.live.isReplacing,
      parlay: state.betConfirmation.parlay,
      language: state.languageData.betSlip,
      currentEvent: state.live.currentEvent,
      prematchEvents: state.prematch.events,
      availableMarkets: state.live.eventOutcomes,
      closedConnection: state.live.closedConnection,
      betslipUpdateOutcomes: state.live.betslipUpdateOutcomes
    };
  }
)(BetConfirmation);
