import * as React from 'react';
import { connect } from 'react-redux';
import { getPlaceholderByOutcome, BetType } from './../../../../../../constants';
import OutcomesSportPrematchMobile from './outcomes-sport-prematch-mobile';
import OutcomesSportDesktop from './outcomes-sport-desktop';

import { mainOddsWithOdds, prematchOddsWithOdds } from './../../../../../../reducers/outcomes-flatten';

let className = 'live-event-new';

const OutcomesSportNew = ({ events, user, event, outcomes, betType, parlayBySport, isDisplaySmall, outcomeList, userMainOutcomes, reducedOutcomes }: any) => {

  const { home, away, sport } = event;
  const twoLinesOdd = sport === 10 || sport === 6;
  // let mainOutcomes = isDisplaySmall ? getMainOutcomesBySport(sport) : getMainOutcomesBySportDesktop(sport);
  let mainOutcomes: any;
  // console.log(reducedOutcomes);
  // console.log(outcomes);

  if (userMainOutcomes[sport] !== undefined) {
    mainOutcomes = userMainOutcomes[sport];
  } else {
    const otcIdx = isDisplaySmall ? 0 : 2;
    mainOutcomes = outcomeList.filter((outcome: any, i: any) => i <= otcIdx ).map((outcome: any) => outcome.id);
  }

  const mainOutcomesClass = twoLinesOdd ? 'main-outcomes two-lines' : 'main-outcomes';
  let oddTableClass = isDisplaySmall ? 'odd-table-wrap' : 'odd-table-desktop';

  if (isDisplaySmall && betType !== BetType.straight && sport === 6) {
    oddTableClass = 'odd-table-wrap basket';
  }

  let newOutcomes = reducedOutcomes;

  if (sport === 24 && newOutcomes) {
    // mainOutcomes = mainOutcomes.filter((item: any, i: any) => Object.keys(newOutcomes).indexOf(item.toString()) >= 0);
  }

  // console.log(mainOutcomes);
  // console.log(outcomes);
  return (
    <div className={mainOutcomesClass}>

    {
      mainOutcomes
        .filter((outcomeId: any) => betType === BetType.straight || parlayBySport[sport].indexOf(outcomeId) !== -1)
        .map((outcomeId: any) => {
          const oddClassList = [`${className}__odd`];
          const otc = newOutcomes && newOutcomes[outcomeId];
          let outcome: any[] = getPlaceholderByOutcome(outcomeId, home, away);
          // console.log(outcome);
          // console.log(outcomeId);
          if (otc) {

            let otcByColumn = (
              Object
                .keys(otc)
                .reduce((arr, id) => {

                  arr[otc[id].column] = Object.assign({}, otc[id], {
                    id,
                    outcome: outcomeId,
                    event: event.id
                  });

                  return arr;
                }, [])
                .filter(Boolean)
            );

            // We want to re-order otcByColumn using the 'bet' value (we've seen 'messed' columns)
            // bet === 1 ; that's bet for team number 1 who should be at top on non inverted events - and at botoom on inverted events;
            // bet === 2 ; that's bet for team number 2 who should be at bottom on non inverted events - and at top on inverted events;

            // Check if we get team values on this market's outcomes

            let hasTeamValue = otcByColumn && otcByColumn.filter(otc => {
              let bet = parseInt(otc.bet);
              return !isNaN(bet);
            });

            // isBetInt means bet has team value on it (this way we exclude Over / Under bets)
            if (hasTeamValue[0]) {

              // Get first outcome to grab event id
              let odd = otcByColumn[0];

              // Prematch events is an array ; live events are an object
              let isPrematch = events[0];

              // Find this outcomes event to get its 'inverted' value

              let ev = isPrematch ? events.filter((ev: any) => ev.id === odd.event)[0] : events[odd.event];

              let isInverted = ev.inverted;

              if (!isInverted) {
                // make sure team's 1 outcome goes at top of column
                // console.log('not inverted');
                otcByColumn = otcByColumn.sort((a, b) => parseInt(a.bet) - parseInt(b.bet));
              } else {
                // console.log('is inverted');
                // make sure team's 2 outcome goes at top of column
                otcByColumn = otcByColumn.sort((a, b) => parseInt(b.bet) - parseInt(a.bet));
              }

              // If our bet has 'Draw', we need to relocate odd at the las position of the
              // array so the outcome is displayed at the bottom of the column

              let hasDraw = otcByColumn.findIndex(otc => otc.bet === 'X');

              if (hasDraw >= 0) {
                let drawOtc = otcByColumn[hasDraw];
                otcByColumn.splice(hasDraw, 1);
                otcByColumn = [...otcByColumn, drawOtc];
              }

              // We check for any unexpected values from feed

              if (ev.rightHandTeam !== 1 && ev.rightHandTeam !== 2) {
                console.log('EVENT HAS UNKNOWN rightHandTeam');
                console.log(ev);
              }

              let isBetValid = otcByColumn.filter(odd_ => odd.bet === '1' || odd.bet === '2' || odd.bet === 'X');

              if (isBetValid.length !== otcByColumn.length) {
                console.log('UNKNOWN bet value for...');
                console.log(otc);
              }

              // console.log(otcByColumn);
            }

            outcome.forEach((odd, i) => outcome[i] = otcByColumn[i] || odd);
          }

          if (isDisplaySmall) {
            if (betType === BetType.straight) {

              if (sport === 24 && newOutcomes) {
                let cricketMainOutcome = mainOutcomes.filter((item: any, i: any) => Object.keys(newOutcomes).indexOf(item.toString()) >= 0);
                outcome = outcome.filter(odd => odd.outcome === cricketMainOutcome[0]);
              } else {

                outcome = outcome.filter(odd => odd.outcome === mainOutcomes[0]);
              }

            } else {
              const indexParlay = sport !== 7 ? 0 : 1;
              outcome = outcome.filter(odd => odd.outcome === parlayBySport[sport][indexParlay]);
            }
          }

          return (
            <div id={`${outcomeId}`} key={outcomeId} className={oddTableClass}>
              { event.live === 1 || !isDisplaySmall ?
                <OutcomesSportDesktop
                  otc={otc}
                  sport={sport}
                  event={event}
                  outcome={outcome}
                  outcomeId={outcomeId}
                  outcomeList={outcomeList}
                  oddClassList={oddClassList}
                /> :
                <OutcomesSportPrematchMobile
                  otc={otc}
                  sport={sport}
                  event={event}
                  outcome={outcome}
                  oddClassList={oddClassList}
                  isDisplaySmall={isDisplaySmall}
                />
              }
            </div>
          );

      })
    }
   </div>
  );

};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    user: state.user,
    betType: state.live.betType,
    parlayBySport: state.live.parlayBySport,
    userMainOutcomes: ownProps.event.live === 0 ? state.prematch.userMainOutcomes : state.live.userMainOutcomes,
    events: ownProps.event.live === 0 ? state.prematch && state.prematch.leagues : state.eventsFlatten && state.eventsFlatten.byId,
    reducedOutcomes: ownProps.event.live === 0 ? prematchOddsWithOdds(state)[ownProps.event.id] : mainOddsWithOdds(state)[ownProps.event.id] && mainOddsWithOdds(state)[ownProps.event.id][state.user.bookmakerId[ownProps.event.sport]],
    outcomes: ownProps.event.live === 0 ? prematchOddsWithOdds(state)[ownProps.event.id] : state.live.outcomes[ownProps.event.id] && state.live.outcomes[ownProps.event.id][state.user.bookmakerId[ownProps.event.sport]]
  })
)(OutcomesSportNew);
