import * as React from 'react';

let color = '#333';

const Corner = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    fill: '#000'
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps} >
      <line x1="50" y1="10" x2="50" y2="240" strokeWidth="8" stroke="#E0E0E0" strokeLinecap="round"/>
      <polygon points="50,10 200,80 50,140" strokeWidth="2" stroke="#cc4b4c" fill="#cc4b4c"/>
    </svg>
  );
};

export default Corner;
