import { IceHockeyTeam, IHockeyFieldAnimationState, t1, t2 } from  '../constants';
import { deepAssign } from '../../../../helpers';

export const t1ExtrasStart: IHockeyFieldAnimationState = deepAssign({}, t1, {

shotIcon: {
  x: 100,
  y: -10,
  opacity: 0
},
subActionText: {
  x: -15,
  y: 52
}

});
export const t1ExtrasEnd: IHockeyFieldAnimationState = deepAssign({}, t1, {
  actionText: {
    x: 85
  },
  extras: {
    x: 95,
    y: 25,
    opacity: 1
  },
   brick: {
    x: 89,
    y: 32,
    rotate: -270
  },
  shotIcon: {
    x: 100,
    y: 13,
    opacity: 1
  },
  subActionText: {
    x: 57,
    y: 52
  }
});

export const t2ExtrasStart: IHockeyFieldAnimationState = deepAssign({}, t2, {

  shotIcon: {
    x: 50,
    y: -10,
    opacity: 0
  },
  subActionText: {
    x: 200,
    y: 52
  }

});
export const t2ExtrasEnd: IHockeyFieldAnimationState = deepAssign({}, t2, {
  actionText: {
    x: 115
  },
   extras: {
    x: 70,
    y: 25,
    opacity: 1
  },
    brick: {
    x: 114,
    y: 32,
    rotate: 450
  },
  shotIcon: {
    x: 50,
    y: 13,
    opacity: 1
  },
   subActionText: {
    x: 122,
    y: 52
  }

});

export function extras(nextTeam: IceHockeyTeam) {
  return [{
    start: nextTeam === IceHockeyTeam.Team1 ? t1ExtrasStart : t2ExtrasStart,
    end: nextTeam === IceHockeyTeam.Team1 ? t1ExtrasEnd : t2ExtrasEnd
  }];
}
