import { BasketTeam, IBasketFieldAnimationState, t1, t2 } from  '../constants';
import { deepAssign } from '../../../../helpers';

export const t1ExtrasStart: IBasketFieldAnimationState = t1;
export const t1ExtrasEnd: IBasketFieldAnimationState = deepAssign({}, t1, {
  actionText: {
    x: 46,
    y: 30
  },
  extras: {
    x: 52,
    y: 19,
    opacity: 1
  }
});

export const t2ExtrasStart: IBasketFieldAnimationState = t2;
export const t2ExtrasEnd: IBasketFieldAnimationState = deepAssign({}, t2, {
  actionText: {
    x: 48,
    y: 30
  },
   extras: {
    x: 28,
    y: 19,
    opacity: 1
  }
});

export function extras(nextTeam: BasketTeam) {
  return [{
    start: nextTeam === BasketTeam.Team1 ? t1ExtrasStart : t2ExtrasStart,
    end: nextTeam === BasketTeam.Team1 ? t1ExtrasEnd : t2ExtrasEnd
  }];
}
