import * as React from 'react';
import { connect } from 'react-redux';
import Price from '../../../components/form/price';
import { addToBetSlip } from '../../../actions/bet-slip';

const eventCN = 'live-event';

class Odd extends React.Component<any, any> {
  state = {
    last: 0,
    update: 0
  };

  timeOut: any;

  componentWillReceiveProps(nextProps: any) {
    const { odd, row } = this.props;
    const newOdd = nextProps.odd;

    if (odd && newOdd && odd.price && newOdd.price && odd.price !== newOdd.price) {
      this.setState({
        update: newOdd.price - odd.price
      });

      clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => this.setState({
        last: this.state.update,
        update: 0
      }), 3500);
    }
  }

  componentWillUnmount() {
    clearTimeout(this.timeOut);

  }

  addToBetSlip = (e: React.MouseEvent<any>) => {
    const { dispatch, odd } = this.props;
    if (odd && odd.suspended === false) {

      // console.log('row is ', row);
      // console.log('add to bet slip');
      // console.log(odd);
      dispatch(addToBetSlip(odd));
      e.nativeEvent.stopImmediatePropagation();
    }
  }

  render() {
    const {
      className = '', odd,
      asian = false, isInBetSlip,
      view,
      isGrid, event,
      isSingleEvent,
      isMobileOn,
      isThreeColumns
    } = this.props;

    // console.log(odd);

    const { last, update } = this.state;
    // console.log(this.state.ticker);
    const classList = className.split(' ');
    const updClassList = [`i`];

    if (update > 0) {
      updClassList.push('i-bet-up');
      if (!isInBetSlip) {
        classList.push('green');
      }
    }
    else if (update < 0) {
      updClassList.push('i-bet-down');
      if (!isInBetSlip) {
        classList.push('red');
      }
    }
    else {
      updClassList.push('i-bet-none');
      updClassList.push(`i-bet-${last >= 0 ? 'up' : 'down'}`);
    }

    if (isInBetSlip) {
      classList.push(`${className}--added`);
    }

    if (asian) {
      return (
        <div id={odd.id} onClick={this.addToBetSlip} className={classList.join(' ')}>
          <i className={updClassList.join(' ')} />
          { odd.id && <Price className="asian__price">{ odd }</Price> }
        </div>
      );
    }

    const getLineDisplay = (odd: any) => {

      const { home, away } = event;

      let teamName = '';

      try {

      // If this is a line market we need to remove the team name from odd display
      // e.g. homeTeam +1 => +1; So we need to grab the line
        teamName = odd.display.split(' ').filter((it: any) => it !== odd.line).join(' ');
        // console.log(teamName);
      }
      catch (err) {
        // console.log(err);
        teamName = odd.display;
      }

      if (teamName === home || teamName === away) {
        return `${odd.line}`;
      }

      return odd.display.replace(/[^\d.-]/g, '');

    };

    const isOTB = !odd.display || odd.suspended || odd.status === 'TemporarilySuspended';

    if (isThreeColumns) {
      // console.log(classList);
      return (
        <span id={odd.id}>
          <i className={updClassList.join(' ')} />
            { !isOTB ?
              <Price className={`${eventCN}__price`}>{ odd }</Price> :
              'OTB'
            }
        </span>
      );
    }

    const isSimpleOdd = !isSingleEvent && view !== 3;
    if (view === 2 || isSimpleOdd) {
      return (
        <div id={odd.id} onClick={this.addToBetSlip} className={classList.join(' ')}>
          <span className={`${eventCN}__odd-display no-mobile`}>{ !isOTB ? odd.display : 'OTB' }</span>
          <span className={`${eventCN}__bet`}>
            <i className={updClassList.join(' ')} />
            { !isOTB && <Price className={`${eventCN}__price`}>{ odd }</Price> }
          </span>
        </div>
      );

    }

    const shouldOddDisplay = view === 3 || !isMobileOn || !isGrid;

    return (
      <div id={odd.id} onClick={this.addToBetSlip} className={classList.join(' ')}>
          {isGrid && odd.line !== '' && <span className={`${eventCN}__odd-display mobile`}>{getLineDisplay(odd)}</span> }
          {!isGrid && view === 3 && <span className={`${eventCN}__odd-display mobile`}>{ !isOTB ? odd.display : 'OTB' }</span>}
          { shouldOddDisplay && <span className={`${eventCN}__odd-display no-mobile`}>{ !isOTB ? odd.display : 'OTB' }</span>}
        <span className={`${eventCN}__bet`}>
          <i className={updClassList.join(' ')} />
          { !isOTB && <Price className={`${eventCN}__price`}>{ odd }</Price> }
        </span>
      </div>
    );

  }

}

export default connect(
  (state: any, ownProps: any) => {

    return {
      isInBetSlip: state.betSlip.odds.some((odd: any) => odd.id === ownProps.odd.id),
      viewPreferences: state.settings.viewPreferences,
      view: state.settings.view,
      isMobileOn: state.uiToolbar.isMobileOn,
      line: state.live.asianLine
    };
  })(Odd);
