import * as React from 'react';
import { connect } from 'react-redux';
import ThreeColumnOdd from './three-column-odd';

function ThreeColumnMarket({ event, otc, outcomes, isInBetSlip }: any) {

  const keys = Object.keys(outcomes[otc.id]);
  const values = keys.map(id => (
    Object.assign({}, outcomes[otc.id][id], {
      id,
      outcome: otc.id,
      event: event.id
    })
  )).filter(odd => !odd.status);

  if (event.sport !== 11 || !values[0]) {
    return null;
  }

  let rows: any = {};

  values.forEach(it => {
    let line = it.line === '' ?
      it.display :
      it.line;

    if (!rows[line]) {
      rows[line] = [];
    }

    rows[line] = [...rows[line], it];

  });

  let rowKeys = Object.keys(rows).filter(it => it !== 'Draw');

  if (otc.id === 1900101) {

    rowKeys = rowKeys.map(item => item).sort((a, b) => {
      if (a < b) {
        return -1;
      }

      if (a > b) {
        return 1;
      }

      return 0;
    });
  }

  if (Object.keys(rows).filter(it => it === 'Draw')[0]) {
    rowKeys = [...rowKeys, 'Draw'];
  }

  const teamLeft = event.inverted ? event.away : event.home;
  const teamRight = event.inverted ? event.home : event.away;

  return (
    <table className="box-threecolumns" style={{width: '100%', fontFamily: 'Ubuntu-Medium'}}>
      <thead>
        <tr style={{background: '#f0f0f0', fontFamily: 'Ubuntu-Light'}} >
          <td style={{background: 'transparent'}} className="row-title box-column-2"></td>
          <td className="box-column-2">{teamLeft}</td>
          <td className="box-column-2">{teamRight}</td>
        </tr>
      </thead>
      <tbody>
      {
        rowKeys
        .map((it: any) => {
          return (
            <tr key={it.id}>
              <td className="row-title box-column-2">
                <span>
                  <p style={{fontFamily: 'Ubuntu-Light'}} className="box-row">
                    {it}
                  </p>
                </span>
              </td>
              {
                rows[it].sort((a: any, b: any) => a.column - b.column).map((odd: any, i: any) => {
                    return (

                      <ThreeColumnOdd
                        key={i}
                        odd={odd}
                        isInBetSlip={isInBetSlip}
                        rowsLength={rows[it].length}
                      />

                    );
                  }
                )
              }
            </tr>
          );
        })
      }
      </tbody>
    </table>
  );
}

export default connect(
  (state: any, ownProps: any) => {
    return {
      isInBetSlip: state.betSlip.odds
    };
  })(ThreeColumnMarket);
