import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from './rule-part';
import RuleItem from './rule-item';

const General = ({ data, initialisationResultCode }: any) => {
  const { title, placement, specificity, wager, timing, settlement, abandoned, time, quarters, minimum, odd, allbetsfinal, ingamescore } = data;
  if (initialisationResultCode === 0) return <div></div>;

  return (
    <RulePart title={title} model="general">
      <RuleItem title={placement.title}>
        <span>
          <p>{placement.text.item1}</p>
          <p>{placement.text.item2}</p>
        </span>
      </RuleItem>
      <RuleItem title={specificity.title}>
        {specificity.text}
      </RuleItem>
      <RuleItem title={wager.title}>
        {wager.text}
      </RuleItem>
      <RuleItem title={timing.title}>
        <ol>
          <li>
            {timing.text.item1}
          </li>
          <li>
            {timing.text.item2}
          </li>
          <li>
            {timing.text.item3}
          </li>
          <li>
            {timing.text.item4}
          </li>
        </ol>
      </RuleItem>
      <RuleItem title={settlement.title}>
        {settlement.text}
      </RuleItem>
      <RuleItem title={abandoned.title}>
        {abandoned.text}
      </RuleItem>
      <RuleItem title={time.title}>
        {time.text}
      </RuleItem>
      <RuleItem title={quarters.title}>
        {quarters.text}
      </RuleItem>
      <RuleItem title={minimum.title}>
        {minimum.text}
      </RuleItem>
      <RuleItem title={odd.title}>
        {odd.text}
      </RuleItem>
      <RuleItem title={allbetsfinal.title}>
        {allbetsfinal.text}
      </RuleItem>
      <RuleItem title={ingamescore.title}>
        {ingamescore.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({
    data: state.languageData.rules.general,
    initialisationResultCode: state.user && state.user.response && state.user.response.initialisation_result_code
   })
)(General);
