import * as React from 'react';
import GamecastIcon from '../../../../svgs/gamecast-icon';
import ClockComponent from './../../../../../components/misc/clock-component';
import { prematchTimeConverter } from './../../live-helpers';
import ClockIcon from './../../../../../components/svgs/clock-icon';
import LiveStreamButton from './../../../../misc/live-stream-button';

class Clock extends React.Component<any, any> {

  state = {
    showText : false
  };

  showText() {
    if (!this.props.event.hasGameCast) return;
    this.setState({ showText: this.state.showText ? false : true });
  }

  hideText() {
    if (!this.props.event.hasGameCast) return;
    this.setState({showText: false });
  }

  render() {

    const { betType, sport, event, subscribedEvent, eventGamecast, score, clock } = this.props;

    const activeClass = subscribedEvent && subscribedEvent.id === event.id ? 'league-card-gamecast active-cast' : 'league-card-gamecast';

    if (!event || event.sport === 11 || event.sport === 35) return null;

    if (event.live === 0) {
      return (
        <div style={{marginTop: '8px', maxWidth: '50px', maxHeight: '50px'}} className="game-clock">
          <span><ClockIcon/></span>
            { prematchTimeConverter(event.time).split(' ')[3].length < 5 && <span>0</span> }
            <span>{`${prematchTimeConverter(event.time).split(' ')[3]}`}</span>
        </div>
      );
    }

    let liveStreamStyles: any = {
      marginTop: `${sport === 1 ? '10' : '15'}px`
    };
    if (sport === 37) {
      // liveStreamStyles.position = 'absolute';
      liveStreamStyles.bottom = '10px';
    }

    return (

      <div style={{top: '0px'}} className={`game-clock ${betType === 1 && sport === 1 ? 'parlay' : ''}`}>
        { sport !== 7 &&
          <ClockComponent clock={clock} sport={sport} score={score} isMobile={false}/>
        }
        { (sport === 1 || sport === 24) &&
          <LiveStreamButton
            event={event}
            liveStreamStyles={liveStreamStyles}
            handleOnClick={eventGamecast}
          />
        }
        { clock &&
          <div>

            <div id={event.id} className={activeClass} onClick={eventGamecast} onMouseOver={this.showText.bind(this)} onMouseOut={this.hideText.bind(this)}>
              <GamecastIcon />
              {
                this.state.showText &&
                <div className={`gamecast-tooltip ${sport === 1 ? 'soccer' : ''}`}>Get gamecast</div>
              }
            </div>
            { (sport !== 1 && sport !== 24 && sport !== 7 && sport !== 17) &&
              <LiveStreamButton
                event={event}
                handleOnClick={eventGamecast}
                liveStreamStyles={{...liveStreamStyles}}
              />
            }
          </div>
        }
      </div>
    );
    }
  }

export default Clock;
