import { CHANGE_TICKETS_SELECTED_DATE, CHANGE_TICKETS_SELECTED_TYPE, TICKETS_DATA_SUCCESS } from '../actions/tickets';

export const ticketsInitialState: any = {
  selectedDate: 7,
  selectedType: 0,
  data: []
};

export const tickets = (state = ticketsInitialState, action: any) => {
  switch (action.type) {
    case CHANGE_TICKETS_SELECTED_DATE:
      return Object.assign({}, state, {
        selectedDate: action.payload
      });
    case CHANGE_TICKETS_SELECTED_TYPE:
      return Object.assign({}, state, {
        selectedType: action.payload
      });
    case TICKETS_DATA_SUCCESS:
      return Object.assign({}, state, {
        data: action.payload
      });
  }

  return state;
};
