import * as React from 'react';
import { getMainOutcomesBySport } from './../../../constants';
import { betLayout } from './../../misc/layout-helpers';
import { mapOutcomeTitle } from '../bet-slip/helpers';
import { range } from '../../../helpers';

class NoMarkets extends React.Component<any, any> {

  state: any = {
    lastOutcomes: []
  };

  componentDidMount() {

    this.setState({ lastOutcomes: this.props.legitOutcomes });
  }

  componentWillReceiveProps(newProps: any) {

    if (this.props.legitOutcomes && newProps.legitOutcomes && newProps.legitOutcomes.length !== 0 && newProps.legitOutcomes.length !== this.props.legitOutcomes.length) {
      // console.log('length change');
      this.setState({ lastOutcomes: this.props.legitOutcomes });

    }
  }

  render() {

    const { outcomes, outcomeList, view, event } = this.props;
    const noOutcomesCN = view === 3 ? 'live-event-no-content-eur' : 'live-event-no-content';

    const {
      home,
      away,
      sport,
      inverted
    } = event;

    // console.log(outcomes.length, 'ol');
    // console.log(noOutcomes.length, 'nO');
    // console.log(legitOutcomes.length, 'lO');

    const mainOutcomes = outcomeList.filter((it: any) => {
      return getMainOutcomesBySport(event.sport).indexOf(it.id) >= 0;
    });

    if (!this.state.lastOutcomes || Object.keys(this.state.lastOutcomes).length === 0) {
      return (
        <div className={noOutcomesCN}>
          {
            mainOutcomes && mainOutcomes.map((outcome: any) =>
              <div key={outcome.id} className="box">
                <table className="league-title">
                  <tbody>
                    <tr>
                      <td id={`se-otc_${outcome.id}`}>
                        {outcome.title}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div className="live-events">
                  <div className="live-event">

                    <div className="live-event__content">
                      <div className="live-event__outcome">
                        {
                          ['OTB', 'OTB'].map((item, key) =>
                            <div key={key} className="live-event__odd suspended">
                              <div className="live-event__odd-display" style={{textAlign: 'center', fontSize: '1.15em'}}>
                                <span>OTB</span>
                              </div>
                            </div>
                          )
                        }
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )
          }
        </div>
      );
    }

    return (
      <div className={noOutcomesCN}>
        {
          this.state.lastOutcomes && this.state.lastOutcomes
          .map((outcome: any, i: number) => (
            <div key={i}>
              <div key={outcome.id} className="box">
                <table className="league-title">
                  <tbody>
                    <tr>
                      <td id={`se-otc_${outcome.id}`}>
                        {
                          mapOutcomeTitle(home, away, inverted, outcomes, outcome)
                        }
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div className="live-events">
                <div className="live-event">
                  <div className="live-event__content">
                    { getOddsData(event, outcomes, outcome) }
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))
      }
    </div>
    );
  }
}

export default NoMarkets;

function getOddsData(event: any, outcomes: any, otc: any) {

  // console.log(outcomes);

  if (!outcomes || !otc) return <div></div>;

  const keys = outcomes[otc.id] && Object.keys(outcomes[otc.id]);
  const values = keys.map(id => (
    Object.assign({}, outcomes[otc.id][id], {
      id,
      outcome: otc.id,
      event: event.id
    })
  )).sort((a, b) => a.order - b.order).filter(odd => !odd.status);

  let arrFinal = [];

  const itemOrder = values.map(item => item.order);

  if (itemOrder.indexOf(-171718) !== 0 && itemOrder.indexOf(-171717) !== 0) {

    const maxColumns = Math.max.apply(null, values.map(item => item.column)) + 1;
    const valuesByColumn = range(maxColumns).map((x, j) => values.filter(item => item.column === j)).filter(arr => arr.length > 0);
    const maxRows = Math.max.apply(null, valuesByColumn.map(arr => arr.length));

    for (let i = 0; i < maxRows; i++) {
      arrFinal[i] = [];
      for (let j = 0; j < valuesByColumn.length; j++) {
        arrFinal[i][j] = valuesByColumn[j][i];
      }

  }

  } else {

      const vals = values.map(item => item).sort((a, b) => {
        if (a.display < b.display) {
          return -1;
        }

        if (a.display > b.display) {
          return 1;
        }

        return 0;
      });

      const columns = 3;
      const rows = Math.ceil(vals.length / columns);

      for (let i = 0; i < rows; i++) {
        arrFinal[i] = [];
        for (let j = 0; j < columns; j++) {
          arrFinal[i][j] = vals[j + columns * i];
        }
      }

  }

  const outcomeClass = ['live-event__outcome'];
  const betFormat = ['one-column', 'two-column', 'three-column'];
  let isOneColumn = false;
  let isGrid = false;

  betFormat.map(format => {
    if (betLayout[event.sport]) {
      if (betLayout[event.sport][format] && betLayout[event.sport][format].indexOf(otc.id) >= 0) {
        isOneColumn = format === 'one-column' ? true : false;
        isGrid = !isOneColumn ? true : false;
        outcomeClass.push(`${format}-bet`);
      }
    }
  });

  return arrFinal

    .map((arr, c) => (
      <div key={c} className={outcomeClass.join(' ') }>
        {
          arr.map((odd, i) => {
            const oddClassList = ['live-event__odd'];
            oddClassList.push('suspended');

            if (!odd) {
              // oddClassList.push('empty-block');
              return <div key={i} className={oddClassList.join(' ')} />;
            }

            odd = Object.assign(odd, {
              suspended: true
            });

            // console.log(odd);
            return (
              <div id={odd.id} className="live-event__odd suspended">
                <div className="live-event__odd-display">
                  <span>OTB</span>
                </div>
              </div>
            );
          })
        }
      </div>
    ));
}
