import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from './rule-part';
import RuleItem from './rule-item';

const Parlay = ({ data }: any) => {
  const { title, losing, push, correlated, parlayingMoneylines, parlayingSpreads, proposition } = data;
  return (
    <RulePart title={title} model="parlay">
      <RuleItem title={losing.title}>
        {losing.text}
      </RuleItem>
      <RuleItem title={push.title}>
        {push.text}
      </RuleItem>
      <RuleItem title={correlated.title}>
        {correlated.text}
      </RuleItem>
      <RuleItem title={parlayingMoneylines.title}>
        {parlayingMoneylines.text}
      </RuleItem>
      <RuleItem title={parlayingSpreads.title}>
        {parlayingSpreads.text}
      </RuleItem>
      <RuleItem title={proposition.title}>
        {proposition.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.parlay })
)(Parlay);
