import { IBaseBallFieldAnimationState, BaseBallAction, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextAction: BaseBallAction) {
  let pitchSpotX, pitchSpotY;

  if (nextAction === BaseBallAction.Single) {
    pitchSpotX = 108;
    pitchSpotY = 50;
  } else if (nextAction === BaseBallAction.Double) {
    pitchSpotX = 100.5;
    pitchSpotY = 42;
  } else if (nextAction === BaseBallAction.Triple) {
    pitchSpotX = 93;
    pitchSpotY = 50;
  } else if (nextAction === BaseBallAction.RunScored) {
    pitchSpotX = 100.5;
    pitchSpotY = 59;
  } else if (nextAction === BaseBallAction.Foul) {
    pitchSpotX = 118;
    pitchSpotY = 50;

  }

  return {
    pitchSpot : {
      x: pitchSpotX,
      y: pitchSpotY,
      opacity: 0
    }
  };
}

function beggining() {
  return {
    pitcherEnd: {
      display: 'none'
    },
    pitcherTwo: {
      display: 'block'
    },
    batterTwo: {
      display: 'block'
    }
  };
}

function finish() {
  return {
    actionText: {
      y: 18
    },
    pitchSpot: {
      opacity: 1
    },
    diamond: {
      opacity: 1
    },
    pitcherStart : {
      display: 'none'
    },
    pitcherEnd: {
      display: 'block'
    },
    batterStart: {
      display: 'none'
    },
    batterEnd: {
      display: 'block'
    }
  };
}

const singleStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Single), beggining(), {});
const singleEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Single), finish(), {});

const doubleStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Double), beggining(), {});
const doubleEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Double), finish(), {});

const tripleStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Triple), beggining(), {});
const tripleEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Triple), finish(), {});

const scoredStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.RunScored), beggining(), {});
const scoredEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.RunScored), finish(), {});

const foulStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Foul), beggining(), {});
const foulEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, nt, start, next(BaseBallAction.Foul), finish(), {});

export function hit(start: any, state: IBaseBallFieldAnimationState, nextAction: BaseBallAction): (any|void) {

  if (nextAction === BaseBallAction.Single ) {
    return {
      start : singleStart(start),
      end : singleEnd(start)
    };
  }

  if (nextAction === BaseBallAction.Double) {
     return {
      start : doubleStart(start),
      end : doubleEnd(start)
    };
  }

  if (nextAction === BaseBallAction.Triple) {
    return {
      start : tripleStart(start),
      end : tripleEnd(start)
    };
  }

  if (nextAction === BaseBallAction.RunScored) {
    return {
      start : scoredStart(start),
      end : scoredEnd(start)
    };
  }

  if (nextAction === BaseBallAction.Foul) {
     return {
      start : foulStart(start),
      end : foulEnd(start)
    };
  }

}
