import * as React from 'react';

const ClockComponent = ({ clock, sport, score, isMobile }: any) => {

  const { timerTicking, min, sec, lastUpdate }: any = clock || { min: 0, sec: 0 };
  const isClock = sport === 6 || sport === 1 || sport === 2 || sport === 8 || sport === 37 ? true : false;
  const isInning = sport === 7 || sport === 10 ? true : false;
  const period = score !== undefined ? score.period : '';

  if (sport === 24 || sport === 9 || sport === 17) {
    return <div></div>;
  }

  const clockDisplay = (hasClock: any, min: any, sec: any, hasInning: any, period: any, sport: any) => {

    if (min === undefined) {
      return '-';
    }

    if (sport === 37) {
      return min && min !== 0 && sec && sec !== 0 && `${min}:${sec}`;
    }

    if (sport === 1) {
      return min !== 0 && sec !== 0 ? `${min}:${sec}` : '0:00';
    }

    if (hasClock) {
      return min !== 0 && sec !== 0 && `${period}-${min}:${sec}`;
    }

    if (hasInning) {
      return period === '' || !period ? `Top 1st` : `${period.slice(0, 3)} ${period.slice(period.length - 3, period.length)}`;
    }

    return '0:00';

  };

  const tickingClock = clockDisplay(isClock, min, sec, isInning, period, sport);

  if (!isMobile) {
    return (
      <div className="clock">{ tickingClock }</div>
    );
  }

  return (
    <span className="clock">{ tickingClock }</span>
  );
};

export default ClockComponent;
