import * as React from 'react';
import { betLayout } from './layout-helpers';

const MobileBetGrid = ({ event, cols, otcId, outcomeId }: any) => {

  if (!event || !betLayout[event.sport]) {
    return <div> </div>;
  }

  const { home, away } = event;

  let ids = Object.keys(outcomeId)
    .map(odd => outcomeId[odd])
    .sort((a, b) => a.column - b.column).filter(odd => odd.display !== '');

  // Helper function to display column title on mobile bet layout
  const columnTitle = (event: any, outcome: any) => {

    // If isTitleTrim === true, then market is a line market like Oleg Kutuzov -1.5 || Alexey Ermilov +1.5 ;
    // and we will need to remove the line (e.g. Oleg Kutuzov -1.5 => Oleg Kutuzov) to display title name;
    const isTitleTrim = betLayout[event.sport]['title-trim'].indexOf(otcId) >= 0;

    const getTitleName = (s: string) => {
      if (s === home) return home;
      if (s === away) return away;

      // if 'Draw' , 'Under', 'Over'
      return s;
    };

    if (!isTitleTrim) {
      return getTitleName(outcome.display);
    }

    // If our outcome display has line ;
    // we neet to remove line from string
    let hasLine = false;

    try {
      // we have to check line here and not on team's name just in case
      // team's name has a + or a - sign on it's name
      hasLine = outcome.line[0] === '+' || outcome.line[0] === '-';
    }
    catch (err) {
      // console.log('has no line', outcome.display);
      hasLine = false;
    }

    if (isTitleTrim && hasLine) {
      // remove the line value from outcome display
      let teamName = outcome.display
        .split(' ')
        .filter((it: any) => it !== outcome.line)
        .join(' ');

      return getTitleName(teamName);
    }

    return outcome.display;

  };

  if (betLayout[event.sport]['row-swap'].indexOf(otcId) >= 0) {

    let titles: any = [];
    let filterTitles: any = [];
    ids.map(title => {

      const lastSpace = title.display.lastIndexOf(' ');
      const hasSign = title.display.indexOf('+') >= 0 || title.display.indexOf('-') >= 0;

      let repl = title.display;
      let column;

      if (event.sport === 24 && otcId === 638) {
        repl = repl.replace(/[0-9]/g, '');
        repl = repl.replace('.', '');
      }

      if (event.sport === 2 && otcId === 231) {

        console.log(repl);
        if (repl.match(home) !== null || repl.match(away) !== null) {
          repl = repl.match(home) !== null ?
          repl.match(home)[0] :
          repl.match(away) !== null && repl.match(away)[0];
        }

      }

      const uniqueTitles = titles.push({
        display: hasSign ? repl.slice(0, lastSpace) : repl,
        column
      });

      filterTitles = titles.filter((item: any, pos: any, array: any) => {
        return array.map((mapItem: any) => {
          return mapItem['display'];
        }).indexOf(item['display']) === pos;
      });

    });

    return (
      <div className="two-columns-bet-header mobile">
        {
          filterTitles.map((outcome: any, i: any) => <span key={i} className="teams">{columnTitle(event, outcome)}</span>)
        }
      </div>
    );

  }

  return (
    <div className="two-columns-bet-header mobile">
      {
        ids.map((outcome, i) => <span key={i} className="teams">{columnTitle(event, outcome)}</span>)
      }
    </div>
  );

};

export default MobileBetGrid;
