import * as React from 'react';
import { connect } from 'react-redux';

import { Sports } from '../../../constants';

import Title from '../../title';
import { reqSchedule, schedule, changeDate, changeSport, changeFiltering } from '../../../actions/schedule';

import Time, { TimeFormat } from '../../../components/form/time';
import Table from '../../../components/form/table';

import { TextField, Select } from '../../../components/form';

import Scroller from '../../../components/misc/scroller';

import ButtonList from './button-list';

class Schedule extends React.Component<any, any> {
  componentDidMount() {
    this.props.dispatch(reqSchedule());
  }

  componentWillUnmount() {
    const { dispatch } = this.props;
    dispatch(schedule([]));
  }

  handleSelectDate = (key: any) => (e: any) => {
    const { dispatch } = this.props;
    dispatch(changeDate(key));
  }

  handleSportChange = (item: any) => {
    const { dispatch } = this.props;
    dispatch(changeSport(item.value));
  }

  handleFiltering = (text: any) => {
    const { dispatch } = this.props;
    dispatch(changeFiltering(text));
  }

  scheduleContent = (items: any) => {
    let { sports } = this.props;
    return (
      items.map((item: any) => (
        <tr key={item.event}>
          <td><Time format={TimeFormat.Time }>{ item.time }</Time></td>
          <td>{ sports[Sports[item.sport]] }</td>
          <td>{ item.description }</td>
        </tr>
      ))
    );
  }

  render() {
    let { title, timeZone, languageData, scheduleData, selectedDate, selectedSport, filterText, sports } = this.props;
    const offset = timeZone.offset || new Date().getTimezoneOffset();

    const formattedDates = scheduleData.map((item: any) => new Date(item.time));
    formattedDates.forEach((d: any) => d.setMinutes(d.getMinutes() - offset));

    const distinctDates = formattedDates.reduce((a: any, b: any, index: any) => {
      const date = new Date(b.getUTCFullYear(), b.getUTCMonth(), b.getUTCDate());
      if (a.map((item: any) => item.time).indexOf(date.getTime()) === -1) {
        a.push({
          index,
          time: date.getTime()
        });
      }
      return a;
    }, []);

    if (!selectedDate && distinctDates.length) {
      selectedDate = distinctDates[0].time;
    }

    let events = scheduleData
      .filter((item: any) => {
        let date = new Date(item.time);
        date.setMinutes(date.getMinutes() - offset);
        date = new Date(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate());

        return date.getTime() === selectedDate;
      })
      .filter((item: any) => !selectedSport || item.sport === selectedSport)
      .filter((item: any) => new RegExp(filterText, 'i').test(item.description));

    const sportsOptions = scheduleData
      .map((item: any) => ({
        value: item.sport,
        text: sports[Sports[item.sport]]
      }))
      .reduce((a: any, b: any) => {
        if (a.map((item: any) => item.value).indexOf(b.value) === -1) {
          a.push(b);
        }
        return a;
      }, [{
        value: null,
        text: languageData.all
      }]);

    return (
      <Title title={title}>
        <div>
          <div className="schedule-filter">
            <div className="schedule-filter__item">
              <Select label={languageData.sport} dataSource={sportsOptions} pk="value" model="text" onChange={this.handleSportChange} />
            </div>
            <div className="schedule-filter__item">
              <TextField label={languageData.filter} onChange={this.handleFiltering} value={filterText} />
            </div>
          </div>
          <div>
            <ButtonList items={distinctDates} scheduleData={scheduleData} selectedDate={selectedDate} onSelect={this.handleSelectDate} />
          </div>
          <div>
            <Table columns={[languageData.time, languageData.sport, languageData.eventDescription]}>
              <Scroller content={this.scheduleContent(events)} />
              {/* <Scroller>
                {
                  events.map((item: any) => (
                    <tr key={item.event}>
                      <td><Time format={TimeFormat.Time}>{ item.time }</Time></td>
                      <td>{ sports[Sports[item.sport]] }</td>
                      <td>{ item.description }</td>
                    </tr>
                  ))
                }
              </Scroller> */}
            </Table>
          </div>
        </div>
      </Title>
    );
  }
}

export default connect(
  (state: any) => ({
    title: state.languageData.menu.schedule,
    sports: state.languageData.sports,
    languageData: state.languageData.schedule,
    timeZone: state.settings.timeZone,
    scheduleData: state.schedule.data,
    selectedDate: state.schedule.selectedDate,
    selectedSport: state.schedule.selectedSport,
    filterText: state.schedule.filterText
  })
)(Schedule);
