import { SoccerAction, SoccerTeam, ISoccerFieldAnimationState, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1PossessionStart: ISoccerFieldAnimationState = t1;
const t1PossessionEnd: ISoccerFieldAnimationState = deepAssign({}, t1, {
  polygon: {
    translateX: -48
  },
  actionText: {
    x: 51
  }
});

const t2PossessionStart: ISoccerFieldAnimationState = t2;
const t2PossessionEnd: ISoccerFieldAnimationState = deepAssign({}, t2, {
  polygon: {
    translateX: -48
  },
  actionText: {
    x: 54
  }
});

export function possession(state: ISoccerFieldAnimationState, currentTeam: SoccerTeam, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  const ret = {
    start: nextTeam === SoccerTeam.Team1 ? t1PossessionStart : t2PossessionStart,
    end: nextTeam === SoccerTeam.Team1 ? t1PossessionEnd : t2PossessionEnd
  };

  if (nextTeam === currentTeam && (nextAction === SoccerAction.Attack || nextAction === SoccerAction.DangerousAttack)) {
    ret.start = state;
  }

  return [ret];
}
