import * as React from 'react';
import { TennisAction, actionTextTennis } from './constants';

let cancellations: any = [];

class Score extends React.Component<any, any> {
  state = {
    title: actionTextTennis(this.props.action),
    leftText: '',
    rightText: '',
    isAnimating: false
  };

  animate = () => {
    const { action, data } = this.props;

    if (action !== TennisAction.ShowScore) {
      return;
    }

    const [sets, aces, breakPoints, firstServe, secondServe] = data;
    const lastSet = sets[sets.length - 1];

    const duration = 2500;
    let i = 0;

    this.setState({
      title: `Set ${sets.length}`,
      leftText: lastSet[0],
      rightText: lastSet[1]
    });

    cancellations.push(setTimeout(() => this.setState({
      title: 'Aces',
      leftText: aces[0],
      rightText: aces[1]
    }), ++i * duration));

    cancellations.push(setTimeout(() => this.setState({
      title: 'Break Points',
      leftText: breakPoints[0],
      rightText: breakPoints[1]
    }), ++i * duration));

    cancellations.push(setTimeout(() => this.setState({
      title: '1st Serve',
      leftText: `${firstServe[0]}%`,
      rightText: `${firstServe[1]}%`
    }), ++i * duration));

    cancellations.push(setTimeout(() => this.setState({
      title: '2nd Serve',
      leftText: `${secondServe[0]}%`,
      rightText: `${secondServe[1]}%`
    }), ++i * duration));

  }

  cancel() {
    cancellations.forEach((item: any) => clearTimeout(item));
    cancellations = [];
  }

  componentWillReceiveProps(nextProps: any) {
    if (nextProps.action === 1120 && !this.state.isAnimating) {
      requestAnimationFrame(this.animate);
      this.setState({isAnimating: true });
    }
    if (nextProps.action !== 1120 && this.state.isAnimating) {
      this.cancel();
      this.setState({isAnimating: false });
    }
  }

  componentWillUnmount() {
    this.cancel();
  }

  render() {
    const { action, w, h, textColor } = this.props;
    let { title, leftText, rightText } = this.state;

    title = action !== TennisAction.ShowScore ? actionTextTennis(this.props.action) : title;

    return (
      <g>
        <g>
          <rect width={w(20)} height="6" x={w(29)} y={h(10) - 3} fill="#333" />
          <text strokeWidth="0" fill={textColor} style={{ fontSize: '3px' }} x={w(39)} y={h(10) + 1} textAnchor="middle">{ title }</text>
        </g>
        {
          action === TennisAction.ShowScore &&
            <g strokeWidth="0" fill={textColor} style={{ fontSize: '3.5px' }}>
              <text x={w(39) - 10} y={h(18) + 1} textAnchor="end">{ leftText }</text>
              <text x={w(39) + 10} y={h(18) + 1} textAnchor="start">{ rightText }</text>
            </g>
        }

      </g>
    );
  }
}

export default Score;
