import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { connect } from 'react-redux';

import { Sports } from '../../../../constants';
import { actionTextSoccer, noTeamAction } from './constants';
import { actionTextHockey } from '../../../gamecast/ice-hockey-rink/constants';
import { actionTextBaseBall } from '../../../gamecast/baseball-field/constants';
import { actionTextBasket } from '../../../gamecast/basketball-court/constants';
import { actionTextTennis } from '../../../gamecast/tennis-court/constants';
let actionStripe: any = [];
let noTeam;

class ActionTicker extends React.Component<any, any> {

  state = {
    tickerActions : 0,
    showMore: false
  };

  getSport = (props: any): (any|void) => {

    const fakeGamecast = {
      team: 1,
      action: 1999,
      scoreTeam1: 0,
      scoreTeam2: 0,
      matchData: ''
    };

    const gamecast = props.gamecast === undefined ? fakeGamecast : props.gamecast;
    const { sport } = props.event;
    const scoreTeam1 = props.score === undefined ? 0 : props.score[0];
    const scoreTeam2 = props.score === undefined ? 0 : props.score[1];

    const { action, matchData } = props;
    let action_;

    switch (sport) {
      case Sports.soccer:
        action_ = actionTextSoccer(action);
        break;
      case Sports.basketball:
        action_ = actionTextBasket(action, matchData);
        break;
      case Sports.iceHockey:
        action_ = actionTextHockey(action, matchData);
        break;
      case Sports.baseball:
        action_ = actionTextBaseBall(action);
        break;
      case Sports.tennis:
        action_ = actionTextTennis(action);
        break;
      default:
        action_ = actionTextTennis(action);
    }

    return {
      action: action_,
      score: scoreTeam1 + '-' + scoreTeam2
    };
  }

  getTeam = (props: any) => {

    let team = props === undefined ? 1 : props.team;
    const home = props.event === undefined ? '' : props.event.inverted ? props.event.away : props.event.home;
    const away = props.event === undefined ? '' : props.event.inverted ? props.event.home : props.event.away;
    let arr = this.getSport(props);

    return team === 1 ? arr['team'] = home :
      arr['team'] = away;
  }

  componentDidMount() {

    const fakeGamecast = {
      team: 1,
      action: 1999,
      scoreTeam1: 0,
      scoreTeam2: 0,
      matchData: ''
    };

    const gamecast = this.props.gamecast === undefined ? fakeGamecast : this.props.gamecast;

    const { action } = this.props;
    let arr = this.getSport(this.props);

    if (action !== undefined || action !== null ) {
      arr['team'] = this.getTeam(this.props);
      noTeam = noTeamAction.indexOf(arr['action']) >= 0 && (arr['team'] = '', arr['score'] = '');
      actionStripe.unshift({team: arr['team'], action: arr['action'], scoreboard: arr['score']});
    }

  }

  componentWillReceiveProps(newProps: any) {
    if (!this.props.gamecast) return;

    const { id } = this.props.event;
    const { action, team, gamecast, score } = this.props;
    const scoreTeam1 = score === undefined ? 0 : score[0];
    const scoreTeam2 = score === undefined ? 0 : score[1];

    if (actionStripe.length !== null) {
      this.setState({ tickerActions: actionStripe.length });
    }

    let arr = this.getSport(newProps);

    if ( newProps.event.id !== id) {
      actionStripe = [];
    }

    let isAction, isTeam, isScore1, isScore2;

    isAction = newProps.action !== action;

    isTeam = newProps.team !== team || (newProps.event.sport === 1 && team !== 3);

    isScore1 = newProps.score && newProps.score[0] !== scoreTeam1 && newProps.event.sport !== 7;
    isScore2 = newProps.score && newProps.score[1] !== scoreTeam2 && newProps.event.sport !== 7;

    if (isAction || isTeam || isScore1 || isScore2) {
      const newObjAction = newProps.action;
      // console.log(newProps.gamecast);
      if (newObjAction || newObjAction !== null) {
        arr['team'] = this.getTeam(newProps);
        noTeam = noTeamAction.indexOf(arr['action']) >= 0 && (arr['team'] = '', arr['score'] = '');
        actionStripe.unshift({ team: arr['team'], action: arr['action'], scoreboard: arr['score'] });
        // console.log(actionStripe[0]);
      }
    }

  }

  componentWillUnmount() {
    actionStripe = [];
  }

  showMore = () => {
    let fNode: any = findDOMNode(this);
    const div = fNode.querySelector('#show-more');
    if (div.className === 'show-actions active') {
      fNode.scrollTop = 0;
    }

    return !this.state.showMore ? this.setState({showMore: true}) : this.setState({showMore: false});
  }

  render() {

    const componentClasses = ['text-description', 'ticker-table'];
    const buttonClass = ['show-actions'];

    const { isPreloader, isUnabled, event, gamecast } = this.props;
    const { sport } = event;

    if (this.state.showMore) {
      componentClasses.push('active');
      buttonClass.push('active');
    }

    if (sport === 1) {
      buttonClass.push('soccer');
    }

    if (sport === 37 || sport === 24) {
      return <div></div>;
    }

    actionStripe = actionStripe.filter((item: any) => item.action !== '');
    if (actionStripe.length <= 0) return <span></span>;

    return (
      <div className={componentClasses.join(' ')}>

        <table >
            <tbody id="ticker-actions">
              {
                actionStripe.map((item: any, i: any) => (
                  <tr style={{color : '#fb0'}} key={i} className="ticker-row">
                    <td key={item.name}>{isPreloader ? '' : item.team}</td>
                    <td key={item.action} className="action-text" >
                    {
                      isPreloader && !isUnabled ? 'Loading...' : isUnabled ? 'Gamecast disabled' : item.action
                    }
                    </td>
                    {
                      <td key={item.scoreboard}>{!isUnabled && item.scoreboard}</td>
                    }
                    {
                      this.state.tickerActions >= 2 && i === 0 &&
                        <td id="show-more"
                          className={buttonClass.join(' ')}
                          onClick={this.showMore}
                          style={this.props.view !== 3 ? { top: `${110}px`} : { display: 'block', width: '40px'}}>
                          <i className="i i-select"></i>
                        </td>
                    }
                  </tr>
                ))
              }
            </tbody>
        </table>

      </div>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => ({
    gamecast: state.live.eventGamecast,
    view: state.settings.view
  })
)(ActionTicker);
