import * as React from 'react';

let color = '#EEEEEE';

const ClockIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 50 50',
    strokeWidth: 3,
    fill: '#E53935',
    stroke: color
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps} style={{ width: '15px' }}>
      <circle cx="25" cy="25" r="23" />
      <line x1="25" y1="25" x2="25" y2="10" strokeLinecap="round"/>
      <line x1="25" y1="25" x2="35" y2="35" strokeLinecap="round"/>
    </svg>
  );
};

export default ClockIcon;
