export function actionTextBaseBall(action: BaseBallAction) {

  switch (action) {
    case BaseBallAction.StartInnings:
      return 'Start Innings';
    case BaseBallAction.Strike:
      return 'Strike';
    case BaseBallAction.Ball:
      return 'Ball';
    case BaseBallAction.Single:
      return 'Single';
    case BaseBallAction.Double:
      return 'Double';
    case BaseBallAction.Triple:
      return 'Triple';
    case BaseBallAction.RunScored:
      return 'Run Scored';
    case BaseBallAction.Foul:
      return 'Foul';
    case BaseBallAction.HomeRun:
      return 'Home Run';
    case BaseBallAction.Steal:
      return 'Steal';
    case BaseBallAction.StrikeOut:
      return 'Strike Out';
    case BaseBallAction.Out:
      return 'Out';
    case BaseBallAction.DoublePlay:
      return 'Double Play';
    case BaseBallAction.TriplePlay:
      return 'Triple Play';
    case BaseBallAction.CaughtStealing:
      return 'Caught Stealing';
    case BaseBallAction.Walk:
      return 'Walk';
    case BaseBallAction.IntentionalWalk:
      return 'Intentional Walk';
    case BaseBallAction.HitByPitch:
      return 'Hit By Pitch';
    case BaseBallAction.SacrificeBunt:
      return 'Sacrifice Bunt';
    case BaseBallAction.SacrificeFly:
      return 'Sacrifice Fly';
    case BaseBallAction.BallInPlay:
      return 'In Play';
    case BaseBallAction.NewBatter:
      return 'New Batter';
    case BaseBallAction.NewPitcher:
      return 'New Pitcher';
    case BaseBallAction.EndOfHalfInnings:
      return 'End of Half Innings';
    case BaseBallAction.EndGame:
      return 'End of Game';

  }

  return '';
  }
