import * as React from 'react';

const BasketballIcon = (props?: any) => {
  let [bg, clip] = ['basketball-ball-bg', 'basketball-ball-clip'];

  const svgProps: any = {
    viewBox: '0 0 250 250',
    strokeWidth: 2,
    fill: 'transparent'
  };

  if (props.width) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
    bg += '-gc';
  }

  return (
    <svg {...svgProps}>
      <defs>
        <clipPath id={clip}>
          <circle cx="125" cy="125" r="124" />
        </clipPath>
      </defs>
      <circle fill="#ffa500" cx="125" cy="125" r="124" />
      <path clipPath={`url(#${clip})`} strokeWidth="4" stroke="#333" d="M 60 18 A 180 195 0 0 1 225 200 M 35 220 A 160 205 0 0 1 175 10 M 133 300 A 500 700 0 0 0 125 118 A 50 53 0 0 1 250 95 M 1 92 A 120 120 0 0 1 43 53 A 300 350 0 0 0 120 0" />
    </svg>
  );
};

export default BasketballIcon;
