import { IFlattenOutcomes, IOddFlatten, IUpdateOutcome, IDeleteOutcome, IClientState } from './interfaces';

class OutcomesDataHandlerClient {

  static createOutcome(state: IFlattenOutcomes, odd: IOddFlatten, clientParams?: IClientState) {
    // console.log(odd);

    let { id, sport, event, main, asian, oddIsLive }: any = odd;
    // isFromLive is from a client on the live events section so we only care obout mainOddsBySport

    // console.log('createOutcome');
    // console.log(odd);
    // console.log(clientParams);

    if (clientParams.isFromLive) {
      // console.log('ADD');
      // console.log(odd);
      return {
        ...state,
        mainOddsBySport: {
          ...state.mainOddsBySport,
          [sport]: {
            ...state.mainOddsBySport[sport],
            [id]: odd
          }
        }
      };
    }

    if (clientParams.msgType === 'ASIAN') {
      return {
        ...state,
        asianOddsBySport: {
          ...state.asianOddsBySport,
          [sport]: {
            ...state.asianOddsBySport[sport],
            [id]: odd
          }
        }
      };
    }

    return {
      ...state,
      allOddsByEvent: {
        ...state.allOddsByEvent,
        [id]: odd
      }
    };

  }

  static updateOutcome(state: IFlattenOutcomes, updatePayload: IUpdateOutcome, clientParams?: IClientState) {

    const { event, oddId, main, asian, updateObj, sport, oddIsLive }: any = updatePayload;

    if (oddIsLive === 0) return state;

    if (!updateObj) return state;

    // console.log('updateOutcome');
    // console.log(updatePayload);
    // console.log(clientParams);

    if (clientParams.isFromLive) {

      return {
        ...state,
        mainOddsBySport: {
          ...state.mainOddsBySport,
          [sport]: {
            ...state.mainOddsBySport[sport],
            [oddId]: {
              ...state.mainOddsBySport[sport][oddId],
              price: updateObj.price || state.mainOddsBySport[sport][oddId].price,
              american: updateObj.american || state.mainOddsBySport[sport][oddId].american,
              marketPrice: updateObj.marketPrice || state.mainOddsBySport[sport][oddId].marketPrice
            }

          }
        }
      };
    }

    if (clientParams.msgType === 'ASIAN') {
      // console.log('update asian odds');
      // console.log(updatePayload);
      // console.log(state.asianOddsBySport);
      return {
        ...state,
        asianOddsBySport: {
          ...state.asianOddsBySport,
          [sport]: {
            ...state.asianOddsBySport[sport],
            [oddId]: {
              ...state.asianOddsBySport[sport][oddId],
              price: updateObj.price || state.asianOddsBySport[sport][oddId].price,
              american: updateObj.american || state.asianOddsBySport[sport][oddId].american,
              marketPrice: updateObj.marketPrice || state.asianOddsBySport[sport][oddId].marketPrice
            }

          }
        }
      };
    }

    // console.log('UPDATE');
    // console.log(updatePayload);

    return {
      ...state,
      allOddsByEvent: {
        ...state.allOddsByEvent,
        [oddId]: {
          ...state.allOddsByEvent[oddId],
          price: updateObj.price || state.allOddsByEvent[oddId].price,
          american: updateObj.american || state.allOddsByEvent[oddId].american,
          marketPrice: updateObj.marketPrice || state.allOddsByEvent[oddId].marketPrice
        }
      }
    };

  }

  static deleteOutcome(state: IFlattenOutcomes, deletePayload: IDeleteOutcome, clientParams?: IClientState) {

    const { oddId, event, sport, main, asian, oddIsLive }: any = deletePayload;

    // console.log('deleteOutcome');
    // console.log(deletePayload);
    // console.log(clientParams);

    if (clientParams.isFromLive) {

      let { [oddId]: deleteMainOdd, ...restStateMainOddsBySport } = state.mainOddsBySport[sport];
      // console.log(event);

      // console.log('DELETE');
      // console.log(deletePayload);
      return {
        ...state,
        mainOddsBySport: {
          ...state.mainOddsBySport,
          [sport]: restStateMainOddsBySport
        }
      };
    }

    if (clientParams.msgType === 'ASIAN') {
      let { [oddId]: deleteAsianOdd, ...restStateAsianOddsBySport } = state.asianOddsBySport[sport];
      // console.log(state.asianOddsBySport);
      return {
        ...state,
        asianOddsBySport: {
          ...state.asianOddsBySport,
          [sport]: restStateAsianOddsBySport
        }
      };

    }

    let { [oddId]: deleteEventOdd, ...restStateAllOddsByEvent } = state.allOddsByEvent;
    // console.log('DELETE');
    // console.log(deletePayload);
    return {
      ...state,
      allOddsByEvent: restStateAllOddsByEvent
    };

  }

}

export default OutcomesDataHandlerClient;
