import * as React from 'react';
import AsianOdds from '../asian-odds';
import { headers, columns } from '../asian-helpers';

const SingleLine = ({ line, event, className, rows, sport, isMobileOn, headerName, outcomes, score }: any) => {

  const headerClass = ['odds-header'];
  const wrapperClass = ['line-wrapper'];
  className = `${className} line-view-desktop`;

  wrapperClass.push('line-wrapper-desktop');

  return (
    <div className={wrapperClass.join(' ')}>
      <div className="odds-wrapper single-line">

        <div className="single-line-headers">
          <div className={headerClass.join(' ')}>
            { columns('left', 0, isMobileOn, line, sport, headerName ) }
          </div>
          <div className={headerClass.join(' ')}>
            { columns('right', 1, isMobileOn, line, sport, headerName ) }
          </div>
        </div>

        { rows === 2 && headers(true, sport, score) }
        <div className="first-row-cont">

          <div className="left-cont">

            <AsianOdds
              line={line}
              event={event}
              isLeft={true}
              outcomes={outcomes}
              isSimple={true}
            />
          </div>

          <div className="left-cont">

            <AsianOdds
              line={line}
              event={event}
              isLeft={false}
              outcomes={outcomes}
              isSimple={true}
            />

          </div>

        </div>
        { rows === 2 && sport !== 10 && headers(false, sport, score) }
        <div className="first-row-cont">

          <div className="left-cont">

            {
              rows === 2 &&
              <AsianOdds
                line={line}
                event={event}
                isLeft={true}
                outcomes={outcomes}
                isSimple={false}
              />
            }
          </div>
          <div className="left-cont">

            {
              rows === 2 && sport !== 10 &&
              <AsianOdds
                line={line}
                event={event}
                isLeft={false}
                outcomes={outcomes}
                isSimple={false}
              />
            }
          </div>

        </div>

      </div>

    </div>
  );

};

export default SingleLine;
