import { SoccerAction, SoccerTeam, ISoccerFieldAnimationState, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1AttackStart: ISoccerFieldAnimationState = t1;
const t1AttackEnd: ISoccerFieldAnimationState = deepAssign({}, t1, {
  polygon: {
    blue: 150,
    translateX: -25
  },
  actionText: {
    x: 74
  }
});

const t2AttackStart: ISoccerFieldAnimationState = t2;
const t2AttackEnd: ISoccerFieldAnimationState = deepAssign({}, t2, {
  polygon: {
    blue: 150,
    translateX: -25
  },
  actionText: {
    x: 31
  }
});

export function attack(state: ISoccerFieldAnimationState, currentTeam: SoccerTeam, nextTeam: SoccerTeam, nextAction: SoccerAction) {
  const ret = {
    start: nextTeam === SoccerTeam.Team1 ? t1AttackStart : t2AttackStart,
    end: nextTeam === SoccerTeam.Team1 ? t1AttackEnd : t2AttackEnd
  };

  if (nextTeam === currentTeam && (nextAction === SoccerAction.Possession || nextAction === SoccerAction.DangerousAttack)) {
    ret.start = state;
  }

  return [ret];
}
