import * as React from 'react';
import { connect } from 'react-redux';

import * as Sports from './sports';
import { toggleRuleStatus } from '../../../actions/rules';

const RulePart = ({ title, model, dispatch, status, children }: any) => {
  const open = status[model];
  let className = 'rule';

  if (open) {
    className += ' rule--opened';
  }

  let item = (
    <ol className="rule__item">
      {children}
    </ol>
  );

  let sportsKey: any = Sports;
  if (Array.isArray(children) && Object.keys(Sports).map((key: any) => sportsKey[key]).indexOf(children[0].type) !== -1) {
    item = (
      <div className="rule__item">
        {children}
      </div>
    );
  }

  return (
    <article className={className}>
      <h2 onClick={e => dispatch(toggleRuleStatus({ [model]: !open }))}>{title}</h2>
      {item}
    </article>
  );
};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, { status: state.rules.status })
)(RulePart);
