import * as React from 'react';
import { connect } from 'react-redux';

import Card from '../../../svgs/card';
import Corner from '../../../svgs/corner';
import SoccerIcon from '../../../svgs/soccer-icon';

class ScoreboardNew extends React.Component<any, any> {

  getIcon(icon: string, sport: number) {
    if (sport === 1) {
      switch (icon) {
        case 'yellow':
        case 'red':
          return <Card />;
        case 'corner':
          return <Corner />;
        case 'penalty':
          return <SoccerIcon />;
        default:
          return '';
      }
    }
    return '';
  }

  render() {

    const { event, gamecast, clock, score } = this.props;
    // console.log(event);
    // console.log(score);
    const noShow = gamecast === undefined ||
    gamecast.scoreboard === undefined ||
    event.sport === 24 || event.sport === 11 || event.sport === 35;

    if (noShow) {

      if (event.sport === 24) return null;
      const scoreTeam1 = (event.sport === 11 || event.sport === 35) ? '' : '0';
      const scoreTeam2 = (event.sport === 11 || event.sport === 35) ? '' : '0';

      return (
        <div className="scoreboard-new" style={{zIndex: 3}}>
          <div className="scores-table">
            <div className="team home">
              <span className="team-name">{event.inverted ? event.away : event.home}</span>
              <span className="score">{scoreTeam1}</span>
            </div>
            <div className="team away">
              <span className="score">{scoreTeam2}</span>
              <span className="team-name">{event.inverted ? event.home : event.away}</span>
            </div>
          </div>
        </div>
      );
    }

    const { scoreboard } = gamecast;
    const { sport } = event;

    let scoreTeam1 = score !== undefined ? score.scoreTeam1 : '0';
    let scoreTeam2 = score !== undefined ? score.scoreTeam2 : '0';

    const { min, sec }: any = clock || { min: 0, sec: 0 };

    if (scoreboard.additional[0] && sport === 1) {
      if (scoreboard.additional[0].title !== 'corner') {
        scoreboard.additional = scoreboard.additional.reverse();
      }
    }

    return (
      <div className="scoreboard-new" style={{zIndex: 3}}>
        <table className="clock-table">
          <tbody>
            { sport === 8 && <tr style={{fontSize: '10px'}}><td><span>{score.period}</span></td></tr> }
            <tr><td>
              { (sport !== 24 && sport !== 11) &&
                <span className="game-clock">
                  { min !== 0 && sec !== 0 && `${min}:${sec}` }
                </span>
              }
            </td></tr>
          </tbody>
        </table>

        <div className="scores-table">
          <div className="team home">
            <span className="team-name">{event.inverted ? event.away : event.home}</span>
            <span className="score">{scoreTeam1}</span>
          </div>
          <div className="team away">
            <span className="score">{scoreTeam2}</span>
            <span className="team-name">{event.inverted ? event.home : event.away}</span>
          </div>
        </div>

        <div className="score-additional">
          <table className="table-additional">
            <tbody>
              <tr>
              {
                scoreboard.additional.map((item: any, i: number) => <td key={i} style={{width: '35px'}} className={`home-add icon ${item.title}`}><a title={item.title}>{this.getIcon(item.title, sport)}<span>{item.t1}</span></a></td> )

              }
              <td className="period"></td>
              {
                scoreboard.additional.reverse().map((item: any, i: number) => <td key={i} style={{width: '35px'}} className={`away-add icon ${item.title}`}><a title={item.title}>{this.getIcon(item.title, sport)}<span>{item.t2}</span></a></td> )
              }
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
    }
  }

export default connect(
  (state: any, ownProps: any) => ({
    scores: state.live.scores,
    event: ownProps.event,
    score: state.live.scores[ownProps.event.id],
    gamecast: state.live.eventGamecast,
    clock: state.live.clocks[ownProps.event.id]
  })
)(ScoreboardNew);
