import * as React from 'react';
import { connect } from 'react-redux';
import Title from '../../../title';
import Link from '../../../form/link';
import { loading, finished } from '../../../../actions/loading';
import SelectedSportBar from './live-events-toolbar/selected-sport-bar';

class NoEventsLink extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      initialState: true,
      timmer: null,
      counter: 0
    };
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(loading());
    let  timmer = setInterval(() => {

      // Do not reload - request events again if on maintenance
      if (this.props.maintenance) {
        dispatch(finished());
        return;
      }

      this.setState({ counter: this.state.counter + 1 });
      if (this.state.counter === 10 ) {
        this.setState({ initialState: false });
        clearInterval(this.state.timmer);
        dispatch(finished());
        console.log('NoEventsLink will reload in 1500ms');
        setTimeout(() => window.location.reload(), 1500);
      }

    }, 1000);
    this.setState({  timmer });

  }

  componentWillUnmount() {
    clearInterval(this.state.timmer);
    this.props.dispatch(finished());
  }

  render() {
    const { sport, title, view, isPrematch, maintenance } = this.props;

    if (maintenance) return null;

    const msg = () => {
      if (!this.state.initialState) {
        return (
          <span className="no-events">
            There are no events for this sport at this moment. Please check our
            <Link to={ '/schedule' }><span className="sched-link"> schedule</span></Link>.
          </span>
        );
      }
      return (
        <span className="no-events">
          Loading Events. Please wait...
        </span>
      );
    };

    return (
      <Title key={sport} title={title}>
        <div>
          {view === 3 && !isPrematch && <SelectedSportBar sport={sport} /> }
          <div className="section-wrapper">
            { msg() }
          </div>
        </div>
      </Title>
    );
  }
}

export default connect(
  (state: any) => {

    const pathState = state.router.location.pathname;
    const path = pathState.slice(pathState.lastIndexOf('/') + 1, pathState.length);

    const userPathLocation = ['live', 'prematch', 'schedule']
      .filter(it => pathState.includes(it));

    const maintenanceKey = userPathLocation && userPathLocation[0];

    return {
      path,
      maintenanceAll: state.maintenance,
      maintenanceKey: maintenanceKey || '',
      maintenance: maintenanceKey && state.maintenance[maintenanceKey].maintenance
    };
  }
)(NoEventsLink);
