import * as React from 'react';
import Button from '../button';

export interface IDialogButtonsProps {
  children?: any;
}

const DialogButtons = ({ children }: IDialogButtonsProps) => {
  const className = 'md-dialog__buttons';

  if (process.env.NODE_ENV !== 'production') {
    if (Array.isArray(children)) {
      if (children.some(c => c.type !== Button)) {
        // throw new TypeError('All the children of a DialogButtons component must be of type Button');
      }
    }
    else if (React.isValidElement(children)) {
      if (children.type !== Button) {
        throw new TypeError('The child of a DialogButtons component must be of type Button');
      }
    }
    else {
      throw new TypeError('The child of a DialogButtons component must be of type Button');
    }
  }

  return (
    <footer className={className}>
      {children}
    </footer>
  );
};

export default DialogButtons;
