import * as React from 'react';
import { connect } from 'react-redux';

import { groupEventsByLeague, detectmob } from '../live-helpers';

import Wrapper from '../wrapper';
import Title from '../../../title';
import LeagueNew from './live-events-league-card/league-new';
import SelectedSportBar from './live-events-toolbar/selected-sport-bar';

class LiveEuropean extends React.Component<any, any> {

  constructor(props: any) {
    super(props);
  }

  render() {

    let {
      sport,
      event,
      outcomeList,
      title,
      userMainOutcomes,
      betSlipOpen,
      betConfirmationOpen,
      hasVideoOnFullScreen,
      settingsOpen,
      leaguesInPlay
    } = this.props;

    let { isMobile, isTablet, subscribedEvent, getGamecast, eventsBySport } = this.props;

    const leaguesWrapper = !isMobile ? 'leagues-wrapper' : 'leagues-wrapper mob';
    let data;

    const isDisplaySmall = isMobile && !isTablet;

    // 'Hidding' events when betslip or betconfirmation are open to prevent iFrame height bug on iOS
    const hiddeEvents = (!betSlipOpen && !betConfirmationOpen && !settingsOpen) || !detectmob();

    if (eventsBySport && hiddeEvents) {

      data = leaguesInPlay.map((league: any) => eventsBySport.filter((ev: any) => ev.leagueid === league)).map((eventsByLeague: any, i: any) => {
        if (!eventsByLeague[0]) return null;
        return (
          <LeagueNew
            key={i}
            events={eventsByLeague}
            outcomeList={outcomeList}
            getGamecast={getGamecast}
            isDisplaySmall={isDisplaySmall}
            subscribedEvent={subscribedEvent}
            userMainOutcomes={userMainOutcomes}
          />
        );
      });
    }

    return (
      <Title key={sport} title={title}>
        <div>
          <SelectedSportBar sport={sport}/>
          { /* <img src="./../images/img_0.png" /> */}
          <div className="section-wrapper">
            <div className={leaguesWrapper} style={hasVideoOnFullScreen ? {display: 'none'} : {}}>
            {data}
            </div>
            { !isDisplaySmall && event && sport !== 29 ?
              <Wrapper
                sport={sport}
                isMobile={isMobile}
                isSingleEvent={false}
                event={subscribedEvent}
              /> : <div></div>
            }
          </div>
        </div>
      </Title>
    );

  }
}

export default connect(
  (state: any, ownProps: any) => {

    return {
      betSlipOpen: state.betSlip.open,
      userMainOutcomes: state.live.userMainOutcomes,
      betConfirmationOpen: state.betConfirmation.open,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
    };

  })(LiveEuropean);
