import * as React from 'react';

const Switch = ({ on = false, onToggle }: any) => (
  <div className="md-switch" onClick={onToggle}>
    <div className={`md-switch__inner${on ? ' md-switch__inner--on' : ''}`} />
    <div className={`md-switch__button${on ? ' md-switch__button--on' : ''}`} />
  </div>
);

export default Switch;
