import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Basketball = ({ data }: any) => {
  const { title, quarters, fourth } = data;
  return (
    <RulePart title={title} model="basketball">
      <RuleItem title={quarters.title}>
        {quarters.text}
      </RuleItem>
      <RuleItem title={fourth.title}>
        {fourth.text}
      </RuleItem>
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.basketball })
)(Basketball);
