import { ISoccerFieldAnimationState, SoccerTeam, BallDirection, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextTeam: SoccerTeam) {
  return {
    actionText: {
      x: nextTeam === SoccerTeam.Team1 ? 45 : 60,
      y: nextTeam === SoccerTeam.Team1 ? 50 : 27
    },
    ball: {
      x: nextTeam === SoccerTeam.Team1 ? 5.5 : 99.5,
      y: nextTeam === SoccerTeam.Team1 ? 27 : 41,
      direction: BallDirection.Goal
    }
  };
}

const t1GoalKick: ISoccerFieldAnimationState = deepAssign({}, t1, next(SoccerTeam.Team1));
const t2GoalKick: ISoccerFieldAnimationState = deepAssign({}, t2, next(SoccerTeam.Team2));

export function goalKick(state: ISoccerFieldAnimationState, nextTeam: SoccerTeam) {
  let start: ISoccerFieldAnimationState;
  let end: ISoccerFieldAnimationState = nextTeam === SoccerTeam.Team1 ? t1GoalKick : t2GoalKick;

  start = deepAssign({}, end, {
    actionText: state.actionText,
    ball: {
      x: state.ball.x,
      y: state.ball.y,
      direction: BallDirection.None
    }
  });

  return [{
    start,
    end
  }];
}
