import * as React from 'react';

export interface ICheckBoxProps {
  model: string;
  text: string;
  value?: boolean;
  enabled?: boolean;
  onChange?: (e: React.ChangeEvent<any>) => void;
}

export default function CheckBox({ text, value = false, enabled = true, onChange = f => f }: ICheckBoxProps) {
  const className = 'md-checkbox';
  const handleChange = (e: any) => onChange(e.target.checked);

  return (
    <div className="md-container">
      <label className={className + '__label'}>
        <input type="checkbox" className={className + '__input'} checked={value} disabled={!enabled} onChange={ handleChange } />
        <i className={className + '__square'}></i><span className={className + '__text'}>{text}</span>
      </label>
    </div>
  );
}
