import * as React from 'react';
import GamecastIcon from '../../../../svgs/gamecast-icon';

const Button = ({ name, handleClick, handleMouseLeave, iconClassName, showTooltip }: any) => {

    let Component: any = () => <div></div>;

    switch (name) {
      case 'gamecast':
        Component = GamecastIcon;
        break;
    }

    return (
        <li className={name} onClick={handleClick} onMouseOver={showTooltip} style={{height: '50px'}}>
          <Component onMouseLeave={handleMouseLeave} className={iconClassName} />
        </li>
    );

};

export default Button;
