import * as React from 'react';
import { connect } from 'react-redux';
import { homeAwayToLink } from '../../../../../helpers';
import { GetSportName } from '../../../../../constants';
import Link from '../../../../../components/form/link';
// import { clockDisplay } from '../../live-helpers';

import ClockComponent from './../../../../../components/misc/clock-component';
import MarketCounter from './../../../../../components/misc/market-counter';

const EventLink = ({ event, score, clock, isMobileOn, line }: any) => {

  const { home, away, sport } = event;
  const { min, sec }: any = clock || {};
  const sportName = GetSportName(sport);

  const to = `/live/${sportName}/${homeAwayToLink(home, away)}`;

  const scoreTeam1 = score !== undefined ? score.scoreTeam1 : 0;
  const scoreTeam2 = score !== undefined ? score.scoreTeam2 : 0;

  const isClock = sport === 6 || sport === 1 || sport === 2 || sport === 8 ? true : false;
  const isInning = sport === 7 || sport === 10 ? true : false;
  const period = score !== undefined ? score.period : '';

  if (!isMobileOn) {
    const linkCont = ['event-link-cont'];

    if (line === 1) {
      linkCont.push('single-line');
    }

    return (
      <Link to={to} title="click to view more odds">
        <div className={linkCont.join(' ')}>

        { sport !== 7 ?
          <ClockComponent clock={clock} sport={sport} score={score} isMobile={true}/> :
          <div className="tennis-score-container">
          {
            score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
              if (item[0] !== '') {
                return (
                  <div className="tennis-score-box" key={i}>
                  <div>{item[0]} - {item[1]}</div>
                  </div>
                );
              }
              return '';
            })
          }
          </div>
        }

          <div className="teams">
            <div className="team">
              {event.inverted ? away : home}
            </div>
            <div className="score">
              {scoreTeam1}
            </div>
          </div>

          <div className="teams" >
            <div className="team">
              {event.inverted ? home : away}
            </div>
            <div className="score">
              {scoreTeam2}
            </div>
          </div>

        </div>
      </Link>
    );
  }

  return (
      <Link to={to} title="click to view more odds">
        <div className="event-link" style={sport !== 7 ? {paddingBottom: '10px'} : {}}>
          <div className="teams-cont">
            <div className="team home">
              <span className="teams">{event.inverted ? away : home}</span>
              <span className="teams">{event.inverted ? home : away}</span>
            </div>
            <div className="team-cont">
              <span className="team-score">{scoreTeam1}</span>
              <span className="team-score">{scoreTeam2}</span>
            </div>
            <MarketCounter
              event={event}
              to={to}
              isSmallDisplay={'asian'}
            />
          </div>

        { sport !== 7 ?
          <ClockComponent clock={clock} sport={sport} score={score} isMobile={true}/> :
          <div className="tennis-score-container">
          {
            score && score.scoreByPeriod && score.scoreByPeriod.map((item: any, i: any) => {
              if (item[0] !== '') {
                return (
                  <div className="tennis-score-box" key={i}>
                  <div>{item[0]} - {item[1]}</div>
                  </div>
                );
              }
              return '';
            })
          }
          </div>
        }
        </div>
      </Link>
  );
};

export default connect(
  (state: any, initialOwnProps: any) => Object.assign({}, initialOwnProps, {
    path: state.router.location.pathname,
    score: state.live.scores[initialOwnProps.event.id],
    clock: state.live.clocks[initialOwnProps.event.id],
    numberMarkets: state.live.numberMarkets,
    isMobileOn: state.uiToolbar.isMobileOn,
    line: state.live.asianLine
  })
)(EventLink);
