import * as React from 'react';
import { connect } from 'react-redux';
import Badge from '../../misc/badge';
import { toggleBetSlip } from '../../../actions/bet-slip';

import { detectmob } from '../live/live-helpers';

const Button = ({ dispatch, open, qt, title, view }: any) => {
  const className = view !== 3 && view !== 1 ? 'bet-slip__button' : 'bet-slip-new__button';
  const classList = [className];

  if (open && qt) {
    classList.push(`${className}--active`);
  }

  const toggle = (e: any) => {
    dispatch(toggleBetSlip());
    e.nativeEvent.stopImmediatePropagation();
  };

  if ((view === 3 || view === 1) && open && detectmob()) {
    return <div></div>;
  }
  return (
    <div className={classList.join(' ')} onClick={toggle}>
      { (view !== 3 && view !== 1) && <i className="i i-coin" /> }
      <span className={`${className}-title`}>{ title }</span>
      { (view !== 3 && view !== 1) ?
        <Badge>{ qt }</Badge> :
        <p className="tickets">{ qt }</p>
      }
    </div>
  );
};

export default connect(
  (state: any) => ({
    open: state.betSlip.open,
    qt: Object.keys(state.betSlip.odds).length,
    title: state.languageData.menu.betSlip,
    view: state.settings.view
  })
)(Button);
