import * as React from 'react';

import { BetType, ViewType } from '../../../../constants';
import Title from '../../../title';

import Wrapper from '../wrapper';
import Button from '../../../form/button';
import SingleEventLeagueHeader from './live-events-toolbar/single-event-league-header';
import SingleEventOdds from './../single-event-odds';

import { detectmob } from '../live-helpers';

class SingleEventEuropean extends React.Component<any, any> {

  state = {
    scrollingLock : false,
    scrollHeight: 0

  };

  goTop = () => {
    window.scroll(0, 0);
  }

  render() {

    let { event, title, outcomeList, outcomes, betType, parlayBySport, view, uiToolbar, isMobile, betConfirmationOpen, betSlipOpen, settingsOpen, betSlipOdds, hasVideoOnFullScreen } = this.props;

    const { scrollingLock } = this.state;
    let data;
    const bigContClass = ['md-container'];
    const classList = ['single-event'];
    const gameCastClass = ['gamecast'];

    if (scrollingLock) {
      gameCastClass.push('sticky');
    }

    if (view === ViewType.european || view === ViewType.asian) {
      gameCastClass.push('classic');
      bigContClass.push('big-cont');
    }

    if (!uiToolbar.gamecast) {
      bigContClass.push('close-cast');
    }

    const noOutcomes = outcomes && outcomeList.filter((outcome: any) => outcomes[outcome.id] && outcomes[outcome.id][Object.keys(outcomes[outcome.id])[0]]).filter((outcome: any) => betType === BetType.straight || parlayBySport[event.sport].indexOf(outcome.id) !== -1);

    const gamecastTop = (!uiToolbar.gamecast && !isMobile) || isMobile;

    let backToTopStyles = hasVideoOnFullScreen ? {
      display: 'none'
    } : {};

    const mobileContainerStyles: any = {};

    if (hasVideoOnFullScreen) {
      bigContClass.push('full-screen-video');
      mobileContainerStyles.background = '#212121';
    }

    const mobileContainerClass = ['mobile-gc-container'];

    if (this.props.hasNativeFullScreen && gamecastTop) {
      mobileContainerClass.pop();
    }

    if (event) {
      const { home, sep, away, inverted, sport } = event;
      title += ` - ${home} ${sep} ${away}`;

      data = (
        <div className="noSportsList">
          <SingleEventLeagueHeader event={event} />
          <div className="sv-container" >
            <div className={bigContClass.join(' ')}>
            { gamecastTop &&
              <div
                style={mobileContainerStyles}
                className={ uiToolbar.gamecast ? mobileContainerClass.join(' ') : `${bigContClass.join(' ')} score`}
              >
                <div className="gamecast">
                  <Wrapper
                    sport={sport}
                    event={event}
                    isSingleEvent={true}
                  />
                </div>
              </div>
            }
            { ((!betSlipOpen && !betConfirmationOpen && !settingsOpen) || !detectmob()) &&
              <SingleEventOdds
                hasVideoOnFullScreen={hasVideoOnFullScreen}
                parlayBySport={parlayBySport}
                outcomeList ={outcomeList}
                outcomes={outcomes}
                classList={classList}
                betType={betType}
                event={event}
                view={view}
              />
            }
              <div style={backToTopStyles} className={`mobile back-to-top ${betSlipOdds && betSlipOdds[0] && !betSlipOpen ? 'hasBetslip' : ''}`}>
                <Button onClick={this.goTop}>Back to Top</Button>
              </div>

            </div>
            { uiToolbar.gamecast && !isMobile ?
              <section id="gc-container" className="gc-container">
                <div className={gameCastClass.join(' ')} >
                <Wrapper
                  event={event}
                  isMobile={isMobile}
                  isSingleEvent={true}/>
                </div>
              </section> : <div></div>
            }
          </div>
        </div>
      );
    }

    return (
        <div>
          <Title title={title}>
            { data }
          </Title>
        </div>
    );
  }
}

export default SingleEventEuropean;
