import * as React from 'react';

import { CricketAction, CricketTeam, actionTextCricket } from './constants';
import { getAnimations } from './actions';
import { Animation } from '../helpers';
import CricketBat from '../../svgs/cricket-bat';
import CricketIcon from '../../svgs/cricket-icon';
import BowlingBall from './bowling-ball';

export class CricketGround extends React.Component<any, any> {

  state = {
    svgHeight: 'auto',
    animation: getAnimations(null, this.props.team, this.props.action)[0].start,
    action: this.props.action
  };

  get deliveries() {
    if (this.props !== undefined) {
      const { matchData } = this.props;
      return matchData && matchData
      .split('#')
      .map((s: any) => s.split(':'));
    }
    return '';
  }

  render() {
    const { action, width = 200, height = 86, team, team1, team2, scoreTeam1 } = this.props;
    const { svgHeight, animation } = this.state;
    const { actionBox, ball } = animation;
    const [skyColor, groundColor, white, postColor, fieldColor, textBoxColor, actionBoxColor] = ['#8ED1C3', '#C69C6D', '#E6E6E6', '#FBAE17', '#5F9237', '#333333', '#212121'];
    let className = 'gc-cricket';

    const w = (p: any) => width * p / 200;
    const h = (p: any) => height * p / 86;
    // console.log(action);
    return (

      <div ref="gc" className={className} style={{ height: svgHeight }}>
        <svg className={`${className}__court`} viewBox={`-1 -1 ${width + 2} ${height + 2}`} fill="transparent" stroke="transparent" strokeWidth="0.3">
          <rect x="0" y="0"
                width={w(200)}
                height={h(86)}
                fill={skyColor}
                stroke={skyColor} />

          <path d={`M ${w(0)} ${h(35)} V ${w(86)} H ${w(200)} V ${h(35)}z`}
                fill={fieldColor}
                stroke={fieldColor}/>

          <path d={`M ${w(20)} ${h(49)} H ${w(180)} L ${w(160)} ${h(37)} H ${w(40)} L ${w(20)} ${h(49)}z`}
                fill={groundColor}
                stroke={groundColor}/>

          <path d={`M ${w(50)} ${h(49)}, ${w(60)} ${h(37)}, ${w(62)} ${h(37)}, ${w(52)} ${h(49)}z`}
                fill={white}
                stroke={white}
                strokeWidth="0.25"/>

          <path d={`M ${w(150)} ${h(49)}, ${w(140)} ${h(37)}, ${w(138)} ${h(37)}, ${w(148)} ${h(49)}z`}
                fill={white}
                stroke={white} strokeWidth="0.25"/>

          <line x1={`${w(38)}`} y1={`${w(46)}`}
                x2={`${w(38)}`} y2={`${w(36)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(40)}`} y1={`${w(44)}`} x2={`${w(40)}`} y2={`${w(34)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(42)}`} y1={`${w(42)}`}
                x2={`${w(42)}`} y2={`${w(32)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(38)}`} y1={`${w(36)}`}
                x2={`${w(42)}`} y2={`${w(32)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(164)}`} y1={`${w(46)}`}
                x2={`${w(164)}`} y2={`${w(36)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(162)}`} y1={`${w(44)}`}
                x2={`${w(162)}`} y2={`${w(34)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(160)}`} y1={`${w(42)}`}
                x2={`${w(160)}`} y2={`${w(32)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <line x1={`${w(164)}`} y1={`${w(36)}`}
                x2={`${w(160)}`} y2={`${w(32)}`}
                fill={postColor}
                stroke={postColor}
                strokeWidth="1"/>

          <rect x="0" y="66"
                width={w(200)} height={h(20)}
                fill={textBoxColor}
                stroke={textBoxColor}
                opacity="0.65"/>

          { this.deliveries &&
            <text x={w(80)} y={w(15)}
                  fill={textBoxColor}
                  style={{ fontSize: '7px' }}>
              { this.deliveries[1] !== undefined ? this.deliveries[1][0] : ''} over - ball { this.deliveries[1] !== undefined ? this.deliveries[1][1] : ''}
            </text>
          }

          <text x={w(5)} y={w(78)}
                fill={white}
                style={{ fontSize: '7px' }}>
            Last 6 deliveries
          </text>

          { this.deliveries && this.deliveries[0].length > 1 &&
                  this.deliveries[0].map((item: any, i: any) =>
                  <g key={i}>
                  { item !== '0' ?
                  <text x={w(70 + (i * 23))} y={w(78)}
                        fill={white}
                        style={{ fontSize: '7px' }}>
                        {item}
                  </text> :
                  <circle cx={w(70 + (i * 24))}
                              cy={w(76)}
                              r="2"
                              fill={white}
                              stroke={white} />
                  }
                  { i <= 4 &&
                  <line x1={w(85 + (i * 23))} y1={w(72)}
                        x2={w(85 + (i * 23))} y2={w(80)}
                        fill={white}
                        stroke={white}
                        strokeWidth="0.75"/>
                  }
                  </g>
                  )
          }
          <text x={w(5)} y={w(15)}
                fill={textBoxColor}
                style={{ fontSize: '7px' }}>
            {team1}
          </text>

          <text x={team === 2 ? w(5) : w(195)} y={w(25)}
                textAnchor={team === 2 ? 'start' : 'end'}
                fill={textBoxColor}
                style={{ fontSize: '7px' }}>
            {scoreTeam1}
          </text>

          <text x={w(195)} y={w(15)}
                fill={textBoxColor}
                textAnchor="end"
                style={{ fontSize: '7px' }}>
            {team2}
          </text>

          <g transform={ team === 2 ? `scale(-1, 1)` : `scale(1, 1)`}>
            <CricketBat x={ team === 2 ? w(-60) : w(140)} y={h(25)} width="20" height="20"/>

          </g>
          <g opacity={ball.opacity}>
            <BowlingBall x={ ball.x} y={ball.y} width={width} height={height} scale="0.75"/>
          </g>

          <g transform={ team === 1 ? `scale(-1, 1)` : `scale(1, 1)`}>
            <CricketIcon x={ team === 1 ? w(-60) : w(140)} y={h(25)} width="5" height="5"/>

          </g>

          <rect x={actionBox.x} y={actionBox.y}
                width={w(80)} height={h(40)}
                fill={actionBoxColor}
                stroke={postColor}
                strokeWidth="1" />

          <text x={actionBox.x + 40} y={actionBox.y + 15}
                fill={postColor}
                textAnchor="middle"
                style={{ fontSize: '7px' }}

          >
            {actionTextCricket(action)}
            <tspan x={actionBox.x + 40} y={actionBox.y + 28}>
              {action === CricketAction.EndOfOver &&
              `Over ${this.deliveries[0][1]} Ball ${this.deliveries[1][1]}`
              }
            </tspan>
          </text>
      {/*
          <image xlinkHref="https://www.esanttests.com/bowling.png" x="19" y="10" width="150" height="56" opacity="0.7"/>
      */}
        </svg>
      </div>
    );
  }
}
