import { IBaseBallFieldAnimationState, BaseBallAction } from '../constants';
import { pitch } from './pitch-swing/pitch';
import { strike } from './strike';
import { ball } from './ball';
import { homeRun } from './home-run';
import { hit } from './hit';

function next() {

  return pitch({
    pitcherStart : {
      display: 'block'
    }
  });
}

const pitchMe = next();

export function throwBall(state: IBaseBallFieldAnimationState, nextAction: BaseBallAction): (any|void) {
  const pitchYou = pitchMe;

  switch (nextAction) {

    case BaseBallAction.Strike:
      return [
        pitchYou,
        strike(pitchYou.end)
      ];

    case BaseBallAction.Ball:
      return [
        pitchYou,
        ball(pitchYou.end)
      ];

    case BaseBallAction.HomeRun:
      return [
        pitchYou,
        homeRun(pitchYou.end)
      ];

    case BaseBallAction.SacrificeBunt:
      return [
        pitchYou,
        homeRun(pitchYou.end)
      ];

    case BaseBallAction.SacrificeFly:
      return [
        pitchYou,
        homeRun(pitchYou.end)
      ];

    case BaseBallAction.BallInPlay:
      return [
        pitchYou,
        homeRun(pitchYou.end)
      ];

    case BaseBallAction.Foul:
      return [
        pitchYou,
        hit(pitchYou.end, state, nextAction)
      ];

    case BaseBallAction.Single:
      return [
        pitchYou,
        hit(pitchYou.end, state, nextAction)
      ];

    case BaseBallAction.Double:
      return [
        pitchYou,
        hit(pitchYou.end, state, nextAction)
      ];

     case BaseBallAction.Triple:
      return [
        pitchYou,
        hit(pitchYou.end, state, nextAction)
      ];

  }
}
