import * as React from 'react';
import config from './../../../../config';

import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Container from '@material-ui/core/Container';

import { Sports, sportHasGamecast } from './../../../constants';
import {
  FootBallFieldNew,
  FootBallField,
  BasketballCourt,
  BaseBallField,
  IceHockeyRink,
  SoccerField,
  TennisCourt
} from './../../gamecast';
import CricketLottie from './../../gamecast/cricket-lottie';
import FootballLottie from './../../gamecast/football-lottie';
import BasketBallLottie from './../../gamecast/basketball-lottie';

import { SoccerActions } from './../../gamecast/soccer-field/constants';
import { TennisAction } from './../../gamecast/tennis-court/constants';
import { BaseBallAction } from './../../gamecast/baseball-field/constants';
import { FootBallAction } from './../../gamecast/football-field/constants';
import { CricketAction } from './../../gamecast/cricket-lottie/constants';
import { IceHockeyAction } from './../../gamecast/ice-hockey-rink/constants';
import { BasketballActionMock } from './../../gamecast/basketball-court/constants';
import { BasketballActions } from '../../gamecast/basketball-lottie/constants';

// import { gamecastMock } from './mock-data';

// const gamecastMock: any = [{
//   scoreboard: { total: [] },
//   sequence: 0
// }];

class GamecastMock extends React.Component<any, any> {

  constructor(props: any) {
    super(props);

    this.state = {
      sport: 6,
      action: 0
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleActionChange = this.handleActionChange.bind(this);
  }

  handleChange(e: any) {
    this.setState({ sport: e.target.value });
  }

  handleActionChange(e: any) {
    this.setState({ action: e.target.value });
  }

  render() {

    let hasGamecast = sportHasGamecast;
    // 211
    // let mockData = gamecastMock[460];

    // console.log(gamecastMock.length);

    let event = {
      action: this.state.action,
      sport: this.state.sport,
      home: 'San Francisco 49ers',
      away: 'Kansas City Chiefs',
      team: 1
    };

    let { sport, action } = event;
    // console.log(mockData);
    // console.log(mockData.scoreboard.total);
    // console.log(mockData.sequence, 'sequece');

    let sports = Object.values(Sports)
      .filter((item: any) => typeof item === 'number')
      .filter((item: any) => hasGamecast.indexOf(item) >= 0);

    let Component: any = () => <div></div>;
    let Actions: any = {};

    let getActions = (actions: any) => {
      return Object.values(actions)
        .filter((item: any) => typeof item === 'number');
    };

    // Actions = BasketballActionMock;
    // Component = BasketBallLottie;

    switch (sport) {
      case Sports.baseball:
        Component = BaseBallField;
        Actions = BaseBallAction;
        break;
      case Sports.basketball:
        Component = BasketBallLottie;
        Actions = BasketballActionMock;
        break;
      case Sports.iceHockey:
        Component = IceHockeyRink;
        Actions = IceHockeyAction;
        break;
      case Sports.soccer:
        Component = SoccerField;
        Actions = SoccerActions;
        break;
      case Sports.tennis:
        Component = TennisCourt;
        Actions = TennisAction;
        break;
      case Sports.americanFootball:

        let FootballGamecast: any = config.footballGamecast === 'modern' ?
        FootBallFieldNew : FootBallField;

        if (config.footballGamecastAnimated) {
          FootballGamecast = FootballLottie;
        }

        Component = FootballGamecast;
        Actions = FootBallAction;
        break;
      case Sports.cricket:
        Component = CricketLottie;
        Actions = CricketAction;
        break;
    }

    let ActionDescription = Actions[0] && getActions(Actions);

    return (
      <Container maxWidth="sm">
        <div style={{ marginTop: '70px' }}>
          <FormControl style={{width: '200px'}}>
            <InputLabel id="sport">Select a Sport</InputLabel>
            <Select
              value={sport}
              onChange={this.handleChange}
              id="demo-simple-select"
              labelId="demo-simple-select-label"
            >
              {
                sports.map((item: any, i: any) =>
                  <MenuItem
                    key={i}
                    value={item}>
                      {Sports[item]}
                  </MenuItem>
                )
              }
            </Select>
          </FormControl>
          <FormControl style={{width: '200px'}}>
            <InputLabel id="action">Select an Action</InputLabel>
            <Select
              value={action}
              onChange={this.handleActionChange}
              id="demo-simple-select-action"
              labelId="demo-simple-select-label"
            >
              {
                ActionDescription &&
                ActionDescription.map((item: any, i: any) =>
                  <MenuItem
                    key={i}
                    value={item}>
                    {Actions[item]}
                  </MenuItem>
                )
              }
            </Select>
          </FormControl>

        </div>
        <div className="gamecast__animation">
          <div className="gc-wrap-lottie" style={{top: '25px'}}>
            <Component
              path={'/gamecast-mock'}
              event={event}
              action={event.action}
              team1={event.home}
              team2={event.away}
              team={event.team}
              extraData={{'extra': 0}}
              matchData={'###'}
            />
          </div>
        </div>

      </Container>
    );
  }

}

export default GamecastMock;
