import { createSelector } from 'reselect';
import { LIVE_EVENTS_FLATTEN, LIVE_EVENTS_ASIAN } from '../actions/events-flatten';

import OutcomesDataHandlerClient from './../../data-handlers/outcomes/client';
import { IFlattenOutcomes, IDeleteOutcome, IOddFlatten, IClientState } from './../../data-handlers/outcomes/interfaces';

import {
  GET_MAIN_OUTCOMES, UPDATE_MAIN_OUTCOME, DELETE_MAIN_OUTCOME, ADD_MAIN_OUTCOME,
  GET_EV_OUTCOMES, UPDATE_EV_OUTCOME, DELETE_EV_OUTCOME, ADD_EV_OUTCOME,
  GET_ASIAN_OUTCOMES,
  UPDATE_ASIAN_OUTCOME,
  DELETE_ASIAN_OUTCOME,
  ADD_ASIAN_OUTCOME,
  CLEAR_EV_OUTCOMES
} from '../actions/outcomes';

import { UPDATE_PREMATCH_OUTCOMES } from './../actions/prematch';

import { SUBSCRIBE_SPORT_DATA } from './../actions/live';
// let unrecCounter = 0;
// setInterval(() => {
//   console.log('unrecoginzed updates', unrecCounter);
// }, 300000);

export const outcomesFlattenInitialState: IFlattenOutcomes = {
  allPrematchOddsByEvent: {},
  mainPrematchOddsBySport: {},
  allOddsByEvent: {},
  mainOddsBySport: {},
  asianOddsBySport: {}
};

export const outcomesFlatten = (state = outcomesFlattenInitialState, action: any): any => {

  let msg = action.type;
  let msgType = msg.split('_')[1];
  let otcGroup = ['MAIN', 'EV', 'ASIAN'].indexOf(msgType);

  let oddsType = msgType === 'MAIN' ? state.mainOddsBySport : state.asianOddsBySport;
  let sport = Object.keys(oddsType)[0] || '0';
  let clientParams: IClientState = { isFromLive: true, msgType };

  if (msg === SUBSCRIBE_SPORT_DATA) {
    if (action.payload.section === 'asian') {
      // console.log(action.payload.outcomes);
      return {
        ...state,
        asianOddsBySport: action.payload.outcomes
      };
    }
    return {
      ...state,
      mainOddsBySport: action.payload.outcomes
    };
  }

  if (msg === UPDATE_MAIN_OUTCOME || msg === UPDATE_EV_OUTCOME || msg === UPDATE_ASIAN_OUTCOME) {

    // console.log(msgType, 'updatemsgtype');
    clientParams.isFromLive = msgType === 'MAIN' ? true : false;
    // console.log(clientParams.isFromLive, 'clientParams');
    // let sport = Object.keys(state.mainOddsBySport)[0];

    let oddId = Object.keys(action.payload)[0];
    let updateObj = action.payload[oddId];

    let updateMainObj = {
      sport,
      oddId,
      updateObj,
      oddIsLive: 1,
      main: msgType === 'MAIN' ? true : false,
      asian: msgType === 'ASIAN' ? true : false,
      event: updateObj.eventId
    };

    // if (!state.mainOddsBySport[sport][oddId] && !state.allOddsByEvent[oddId]) {
    //   console.log('unrecognized oddId trying to update');
    //   console.log(updateMainObj);
    //   unrecCounter ++;
    // }

    return OutcomesDataHandlerClient.updateOutcome(state, updateMainObj, clientParams);
  }

  if (msg === DELETE_MAIN_OUTCOME || msg === DELETE_EV_OUTCOME || msg === DELETE_ASIAN_OUTCOME) {
    // console.log(state.mainOddsBySport);
    // console.log('deleting outcome');
    // console.log(action.payload);

    clientParams.isFromLive = msgType === 'MAIN' ? true : false;
    let deleteMainObj: IDeleteOutcome = {
      oddId: Object.keys(action.payload)[0],
      asian: msgType === 'ASIAN' ? true : false,
      main: msgType === 'MAIN' ? true : false,
      oddIsLive: 1,
      event: 0,
      sport
    };

    // if (msg === DELETE_MAIN_OUTCOME) {
    //   console.log('reduced delete main');
    //   // console.log(deleteMainObj);
    //   console.log('reduced delete main for ', deleteMainObj.oddId);
    //   console.log(OutcomesDataHandlerClient.deleteOutcome(state, deleteMainObj, clientParams));
    // }

    return OutcomesDataHandlerClient.deleteOutcome(state, deleteMainObj, clientParams);
  }

  if (msg === ADD_MAIN_OUTCOME || msg === ADD_EV_OUTCOME || msg === ADD_ASIAN_OUTCOME) {
    // console.log('adding odd');
    // console.log(state.mainOddsBySport);
    // console.log(action.payload[Object.keys(action.payload)[0]]);
    // console.log(action.payload[Object.keys(action.payload)[0]]);
    // console.log(Object.keys(action.payload)[0]);
    clientParams.isFromLive = msgType === 'MAIN' ? true : false;
    let addMainObj: IOddFlatten = action.payload[Object.keys(action.payload)[0]];

    return OutcomesDataHandlerClient.createOutcome(state, addMainObj, clientParams);
  }

  let flattenOutcomes;

  switch (action.type) {
    case CLEAR_EV_OUTCOMES:
      // console.log('clearing event outcomes-..');
      return {
        ...state,
        allOddsByEvent: {},
        allPrematchOddsByEvent: {}
      };
    case UPDATE_PREMATCH_OUTCOMES:

      return {
        ...state,
        allPrematchOddsByEvent: action.payload
      };
    case LIVE_EVENTS_ASIAN:
      // console.log('LIVE_EVENTS_ASIAN');
      // console.log(action.payload.flattenOutcomes);
      flattenOutcomes = action.payload.flattenOutcomes;
      return {
        ...state,
        asianOddsBySport: flattenOutcomes.asianOddsBySport

      };
    case LIVE_EVENTS_FLATTEN:
      flattenOutcomes = action.payload.flattenOutcomes;
      return {
        ...state,
        mainOddsBySport: flattenOutcomes.mainOddsBySport
      };
    case GET_ASIAN_OUTCOMES:
      // console.log('GET_ASIAN_OUTCOMES');
      // console.log(action.payload);
      return {
          ...state,
          asianOddsBySport: action.payload
        };

    case GET_MAIN_OUTCOMES:

      return {
          ...state,
          mainOddsBySport: action.payload
        };

    case GET_EV_OUTCOMES:
        // console.log('getting event outcomes');

        return {
          ...state,
          allOddsByEvent: action.payload
        };

    default:
      return state;
  }
};

// Selectors

const mainPrematchOddsSelector = (state: any) => state.prematch.reducedOutcomes;
const allOddsByEventSelector = (state: any) => state.outcomesFlatten.allOddsByEvent;
const mainOddsBySportSelector = (state: any) => state.outcomesFlatten.mainOddsBySport;
const asianOddsBySporSelector = (state: any) => state.outcomesFlatten.asianOddsBySport;
const allPrematchOddsByEventSelector = (state: any) => state.outcomesFlatten.allPrematchOddsByEvent;

const paramsSelector = (state: any, props: any) => props.match.params;
const viewSelector = (state: any) => state.settings.view;
const routingSelector = (state: any) => state.router;
const userSelector = (state: any) => state.user;

export const eventOddsWithOdds = createSelector(
  [
    allPrematchOddsByEventSelector,
    allOddsByEventSelector,
    routingSelector,
    paramsSelector,
    userSelector
  ],
  (prematchEvOutcomes: any, eventOutcomes: any, router: any, params: any, user: any) => {

    const path = router.location.pathname;
    const isPrematch = params.gameid !== undefined || path.split('/').indexOf('prematch') >= 0;
    let derivedEventOdds: any = {};

    const evOutcomes = isPrematch ? prematchEvOutcomes : eventOutcomes;

    Object.keys(evOutcomes).forEach(otcId => {
      let otc = evOutcomes[otcId];
      let { bookmakerId, outcomeId, sport, id } = otc;
      let userBookmaker = user.bookmakerId[sport];

      if (!derivedEventOdds[outcomeId]) {
        derivedEventOdds[outcomeId] = {};
      }

      derivedEventOdds = bookmakerId && (userBookmaker === bookmakerId.toString() || userBookmaker === bookmakerId) ? {
        ...derivedEventOdds,
        [outcomeId] : {
          ...derivedEventOdds[outcomeId],
          [id]: otc
        }
      } : { ...derivedEventOdds };

    });

    return derivedEventOdds;

  }
);

export const prematchOddsWithOdds = createSelector(
  [mainPrematchOddsSelector, userSelector],
  (mainOutcomes: any, user: any) => {
    // console.log(view, 'this is view');

    let outcomes = mainOutcomes;
    let derivedMainOdds: any = {};

    if (!Object.keys(outcomes)[0]) return {};

    let sport_ = Object.keys(outcomes)[0];
    // console.log(outcomes);
    Object.keys(outcomes).forEach(otcId => {

      let otc = outcomes[otcId];
      let { event, bookmakerId, outcomeId, id, sport } = otc;
      let userBookmaker = user.bookmakerId[sport];

      if (!derivedMainOdds[event]) {
        derivedMainOdds[event] = {};
      }

      if (!derivedMainOdds[event][outcomeId]) {
        derivedMainOdds[event][outcomeId] = {};
      }

      derivedMainOdds = bookmakerId && (userBookmaker === bookmakerId.toString() || userBookmaker === bookmakerId) ? {
        ...derivedMainOdds,
        [event]: {
          ...derivedMainOdds[event],
          [outcomeId]: {
            ...derivedMainOdds[event][outcomeId],
            [id]: otc
          }

        }
      } : { ...derivedMainOdds };

    });

    return derivedMainOdds;
  }
);

export const mainOddsWithOdds = createSelector(
  [asianOddsBySporSelector, mainOddsBySportSelector, userSelector, viewSelector],
  (asianOutcomes: any, mainOutcomes: any, user: any, view: any) => {
    // console.log(view, 'this is view');

    let outcomes = view === 1 ? asianOutcomes : mainOutcomes;
    let derivedMainOdds: any = {};

    if (!Object.keys(outcomes)[0]) return {};

    let sport_ = Object.keys(outcomes)[0];

    Object.keys(outcomes[sport_]).forEach(otcId => {

      let otc = outcomes[sport_][otcId];
      let { event, bookmakerId, outcomeId, id, sport } = otc;
      let userBookmaker = user.bookmakerId[sport];
      // console.log(otc);

      if (!derivedMainOdds[event]) {
        derivedMainOdds[event] = {};
      }

      if (!derivedMainOdds[event][userBookmaker]) {
        derivedMainOdds[event][userBookmaker] = {};
      }

      if (!derivedMainOdds[event][userBookmaker][outcomeId]) {
        derivedMainOdds[event][userBookmaker][outcomeId] = {};
      }

      derivedMainOdds = bookmakerId && (userBookmaker === bookmakerId.toString() || userBookmaker === bookmakerId) ? {
        ...derivedMainOdds,
        [event]: {
          ...derivedMainOdds[event],
          [userBookmaker]: {
            ...derivedMainOdds[event][userBookmaker],
            [outcomeId]: {
              ...derivedMainOdds[event][userBookmaker][outcomeId],
              [id]: otc
            }
          }
        }
      } : { ...derivedMainOdds };

    });

    return derivedMainOdds;
  }
);
