import * as React from 'react';
import { Sports } from './../../../constants';

import Link from './../../form/link';

import BaseballIcon from './.././../svgs/baseball-icon';
import BasketballIcon from './../../svgs/basketball-icon';
import IceHockeyIcon from './../../svgs/ice-hockey-icon';
import SoccerIcon from './../../svgs/soccer-icon';
import TennisIcon from './../../svgs/tennis-icon';
import CricketIcon from './../../svgs/cricket-icon';
import AmericanFootballIcon from './../../svgs/american-football-icon';
import RugbyIcon from './../../svgs/rugby-icon';
import EsportsIcon from './../../svgs/esports-icon';
import GolfIcon from './../../svgs/golf-icon';
import TableTennisIcon from './../../svgs/table-tennis-icon';
import SnookerIcon from './../../svgs/snooker-icon';
import DartsIcon from './../../svgs/darts-icon';
import VolleyIcon from './../../svgs/volley-icon';
import CurlingIcon from './../../svgs/curling-icon';
import BadmintonIcon from './../../svgs/badminton-icon';
import BoxIcon from './../../svgs/box-icon';
import HandBallIcon from './../../svgs/handball-icon';
import AustralianIcon from './../../svgs/australian-icon';
import PesapalloIcon from './../../svgs/pesapallo-icon';

const icons: any = {
  [Sports.volleyball]: <VolleyIcon />,
  [Sports.darts]: <DartsIcon />,
  [Sports.snooker]: <SnookerIcon />,
  [Sports.tableTennis]: <TableTennisIcon />,
  [Sports.americanFootball]: <AmericanFootballIcon />,
  [Sports.baseball]: <BaseballIcon />,
  [Sports.basketball]: <BasketballIcon />,
  [Sports.iceHockey]: <IceHockeyIcon />,
  [Sports.soccer]: <SoccerIcon />,
  [Sports.tennis]: <TennisIcon />,
  [Sports.cricket]: <CricketIcon />,
  [Sports.rugbyUnion]: <RugbyIcon />,
  [Sports.rugbyLeague]: <RugbyIcon />,
  [Sports.eSports]: <EsportsIcon />,
  [Sports.australianRules]: <CricketIcon />,
  [Sports.badminton]: <BadmintonIcon />,
  [Sports.beachSoccer]: <SoccerIcon />,
  [Sports.floorball]: <SoccerIcon />,
  [Sports.futsal]: <SoccerIcon  />,
  [Sports.handball]: <CricketIcon />,
  [Sports.pesapallo]: <CricketIcon />,
  [Sports.golf]: <GolfIcon />,
  [Sports.curling]: <CurlingIcon />,
  [Sports.boxing]: <BoxIcon />,
  [Sports.handball]: <HandBallIcon />,
  [Sports.australianRules]: <AustralianIcon />,
  [Sports.pesapallo]: <PesapalloIcon />
};

export const sortAlpha = (arr: any, prop: any) => {
  if (!arr) return [];
  return arr.sort((a: any, b: any) => {
    return a[prop] < b[prop] ? -1 : a[prop] > b[prop] ? 1 : 0;
  });
};

const SportsItem = ( {
    sport,
    activeSport,
    activeCountry,
    getActiveCountry,
    getActiveSport,
    getActiveLeague,
    sports,
    prematchLeagues,
    prematchEvents,
    headerHeight
  }: any ) => {

  if (!prematchLeagues[0]) return <div></div>;

  const to = `/prematch/${sports[Sports[sport.id]]}`;

  const spLeagues = sport.leagues && sortAlpha(sport.leagues, 'country');

  const countryLeagues = (item: any) => {
    return prematchEvents
      .filter((sport_: any) => sport_.id === sport.id)
      .map((it: any) => it.leagues.filter((league_: any) => league_.country === item.country)
      .map((lg: any) => {
        const lo = lg.leagues_by_country.sort((a: any, b: any) => {
          if (a < b) return -1;
          if (a > b) return 1;
          return 0;
        });
        return lo;
      }));
  };

  return (
    <div className="prematch-sports-item" key={sport.id}>
      <Link to={to}>
        <div onClick={() => getActiveSport(sport.id)} key={sport.id} className="sports-title">
          <div className="icon">{ icons[sport.id] }</div>
          <p className="sport-qt" style={{color: `${prematchLeagues[0].sport === sport.id ? '#ef8800' : ''}`}}>{sports[Sports[sport.id]]} <span>+{sport.qt}</span></p>
          <p className="sport-button">{activeSport === sport.id ? '[ - ]' : '[ + ]'}</p>
        </div>
      </Link>
      <div className={`country-menu ${activeSport === sport.id ? 'active' : ''}`}>
      {
        sport.leagues &&
        spLeagues.map((item: any, i: any) =>
          <div className="league-menu" key={i}>
            <p className="country-qt" onClick={() => getActiveCountry(sport.id, item.country)} >{item.country}&nbsp;<span>&nbsp;+{item.qt_by_country}</span></p>
            { activeCountry === item.country &&
              <ul className="league-list">
                {
                  countryLeagues(item).map((lg_: any, j: any) =>
                    lg_[0].map((_item: any, o: any) => <li className="league-item" onClick={() => getActiveLeague(_item, sport.id)} key={o}>{_item}</li>)
                  )
                }
              </ul>
            }
          </div>
        )
      }
      </div>
    </div>
  );
};

export default SportsItem;
