export const REMOVE_EVENT_FLATTEN = 'REMOVE_EVENT_FLATTEN';
export const LIVE_EVENTS_FLATTEN = 'LIVE_EVENTS_FLATTEN';
export const LIVE_EVENTS_ASIAN = 'LIVE_EVENTS_ASIAN';
export const NEW_EVENT_FLATTEN = 'NEW_EVENT_FLATTEN';

export const MARKET_COUNT_UPDATE = 'MARKET_COUNT_UPDATE';
export const VIDEO_URL_UPDATE = 'VIDEO_URL_UPDATE';

export const HAS_GAMECAST_UPDATE = 'HAS_GAMECAST_UPDATE';

export const hasGamecastUpdate = (payload: any) => ({
  type: HAS_GAMECAST_UPDATE,
  payload
});

export const updateVideoUlr = (payload: any) => ({
  type: VIDEO_URL_UPDATE,
  payload
});

export const liveEventsAsian = (payload: any) => ({
  type: LIVE_EVENTS_ASIAN,
  payload
});

export const liveEventsFlatten = (payload: any) => ({
  type: LIVE_EVENTS_FLATTEN,
  payload
});

export const newEventFlatten = (payload: any) => ({
  type: NEW_EVENT_FLATTEN,
  payload
});

export const removeEventFlatten = (payload: any) => ({
  type: REMOVE_EVENT_FLATTEN,
  payload
});

export const updateMarketCount = (payload: any) => ({
  type: MARKET_COUNT_UPDATE,
  payload
});
