// This helper function allows us to accomodate big team names on our gamecast.
// Gamecast is svg based and its not easy to set a word wrap value if our strings are dynamic (sent by server)
// The function converts the team's name on an array of words that will be iterated to print the word
// on a word-wrapped format adding breaklines if the combination of words is bigger than the value set on @maximumWordLenght variable
// Each array item is a word or a combination of words not greater than the value set on @maximumWordLenght variable
// e.g.
//      ""NJ Wings" ---> ["NJ Wings"]
//      "Esc Alemana Paillaco Women" ---> ["Esc Alemana", "Paillaco Women"]
//      "Blyskavky VinnytsiaOdessa Women" ---> ["Blyskavky", "VinnytsiaOdessa", "Women"]
//      "Interkhim-Sdyusshor Odessa Women" ---> ["Interkhim-Sdyusshor", "Odessa Women"]
// Test of this program are available on the jest folder

// Sum array items length
function sum(arr: Array<any>): number {
  if (arr.length === 0) return 0;
  return arr.reduce((a, b) => a + b, 0);
}

// Insert item at given position
function insertItem (array: any, action: any) {
  let newArray = array.slice();
  newArray.splice(action.index, 0, action.item);
  return newArray;
}

// function to remove item at given position
function removeItem(array: any, action: any) {
  return [
    ...array.slice(0, action.index),
    ...array.slice(action.index + 1)
  ];

}

export default class TeamSplitter {

  private maximumWordLenght: number;
  private teamNameArray: string[];
  private teamLengthArray: Array<any>;
  private mutableLength: Array<any>;
  private mutableTeam: string[];
  public resultArr: string[];

  constructor(public teamName: string) {

    this.maximumWordLenght = 15;

    this.resultArr = [];
    this.teamNameArray = teamName.split(' ');
    this.teamLengthArray = this.teamNameArray.map(it => it.length);

    this.mutableLength = this.teamLengthArray;
    this.mutableTeam = this.teamNameArray;

    // Inital Word is Less than 16 chars, no need to run the program
    if (this.teamName.length <= this.maximumWordLenght + 1) {
      this.resultArr = [...this.resultArr, this.teamName];
      return;
    }

    // this.checkLastStringLength();
    this.checkIfItemsFit();

  }

  checkIfItemsFit() {

    // If all items on the array fit
    if (sum(this.mutableLength) <= this.maximumWordLenght ) {

      this.resultArr = insertItem(this.resultArr, { index: 0, item: this.mutableTeam.join(' ') } );

    } else {

      // Check if it's the last available item, just push it
      if (!this.mutableLength[1]) {

        this.resultArr = insertItem(this.resultArr, { index: 0, item: this.mutableTeam.join(' ') });
        return;
      }

      // Check if last word has more chars than allowed and if it's the case, push it
      let lastLengthItem: number = this.mutableLength[this.mutableLength.length - 1];
      let lastNameItem: string = this.mutableTeam[this.mutableTeam.length - 1];

      if (lastLengthItem >= this.maximumWordLenght) {
        this.resultArr = insertItem(this.resultArr, {index: 0, item: lastNameItem });

        this.removeLastItemArray();

        return;
      }

      // Iterate throug the available words checking for matches
      let mutableLength_ = this.mutableLength;

      // remove first item

      let matchFound = false;

      while (mutableLength_[0] && !matchFound) {

        mutableLength_ = removeItem(mutableLength_, { index: 0 });

        if (sum(mutableLength_) <= this.maximumWordLenght) {

          matchFound = true;

          let newMutableLenght = this.mutableLength
            .filter((item: number) => ( mutableLength_.indexOf(item) < 0 ));

          let wordToPush = this.mutableTeam
            .filter((item: string, index: number) => ( index >= newMutableLenght.length ));

          let wordsLeft = this.mutableTeam
            .filter((item: string, index: number) => ( index < newMutableLenght.length ));

          this.resultArr = insertItem(this.resultArr, {index: 0, item: wordToPush.join(' ')});

          // Reset the words array and the length array with lefting words
          this.mutableTeam = wordsLeft;
          this.mutableLength = this.mutableTeam
            .map((item: string) => (
              item.length
            ));
        }

      }

      this.checkIfItemsFit();

    }

  }

  removeLastItemArray() {

    this.mutableTeam = removeItem(this.mutableTeam, { index: this.mutableTeam.length - 1 });
    this.mutableLength = this.mutableTeam.map((it: string) => (
      it.length
    ));

    if (!this.mutableTeam[0]) return;
    this.checkIfItemsFit();
  }

}
