import * as React from 'react';

function polygon(color: string, ...points: number[][]) {
  return <polygon points={points.map(point => `${point[0]} ${point[1]}`).join(', ')} fill={color} />;
}

export default class Flag extends React.Component<any, any> {
  shouldComponentUpdate() {
    return false;
  }

  render() {
    const size = 7.5;
    const [green, orange] = ['#bbff66', '#ff6611'];

    return (
      <svg viewBox="0 0 75 100" x="20" y="5" strokeWidth="0" width="12.5" height="18" fill="transparent">
        <defs>
          <pattern id="bg-offside-flag" patternUnits="userSpaceOnUse" width={size * 2} height={size * 2}>
            <g strokeWidth="0" fill="#ff6611">
              { polygon(green, [0, 0], [0, size], [size, 0]) }
              { polygon(orange, [0, size], [size, 0], [size * 2, size]) }
              { polygon(green, [size, 0], [size * 2, size], [size * 2, 0]) }
              { polygon(green, [0, size], [size, size * 2], [0, size * 2]) }
              { polygon(orange, [0, size], [size, size * 2], [size * 2, size]) }
              { polygon(green, [size, size * 2], [size * 2, size * 2], [size * 2, size]) }
            </g>
          </pattern>
        </defs>
        <rect width="72" height="50" fill="url(#bg-offside-flag)" />
        <rect x="70" width="3" height="100" fill="#333" />
      </svg>
    );
  }
}
