import * as React from 'react';
import { connect } from 'react-redux';

class CSRF extends React.Component<any, any> {
  render() {
    let { csrf_token } = this.props;
    return <div className="csrf_token" id="csrf_token" hidden={true}>{JSON.stringify(csrf_token)}</div>;
  }
}
export default connect(
  (state: any) => ({ csrf_token: typeof state.user === 'undefined' ? 'NOT_YET_SET' : state.user.csrf_token })
)(CSRF);
