import * as React from 'react';
import AsianOdds from './asian-odds';
import { headers, columns } from './asian-helpers';

const MobileLayout = ({ line, event, className, rows, sport, isMobileOn, headerName, outcomes, score }: any) => {

  const headerClass = ['odds-header'];
  const wrapperClass = ['line-wrapper'];

  if (!isMobileOn) {
    wrapperClass.push('line-wrapper-desktop');
  }

  return (
    <div className={wrapperClass.join(' ')}>

    <div className="odds-wrapper">
      { rows === 2 && headers(true, sport, score) }
      <div className={headerClass.join(' ')}>
        { columns('left', 0, isMobileOn, line, sport, headerName ) }
      </div>
      <AsianOdds
        line={line}
        event={event}
        isLeft={true}
        outcomes={outcomes}
        isSimple={true}
      />
    </div>

    <div className="odds-wrapper">
      { line !== 2 &&
        <div className={headerClass.join(' ')}>
          { columns('right', 1, isMobileOn, line, sport, headerName ) }
        </div>
      }

      <AsianOdds
        line={line}
        event={event}
        isLeft={false}
        outcomes={outcomes}
        isSimple={true}
      />
    </div>

  { line !== 0 &&
  <div className="odds-wrapper">
    { rows === 2 && sport !== 10 && headers(false, sport, score) }
    { rows === 2 && sport !== 10 &&
      <div className={headerClass.join(' ')}>
      { columns('left', 0, isMobileOn, line, sport, headerName ) }
      </div>
    }
    {
      rows === 2 &&
      <AsianOdds
        line={line}
        event={event}
        isLeft={true}
        outcomes={outcomes}
        isSimple={false}
      />
    }
    </div>
  }

  { line !== 0 &&
    <div className="odds-wrapper">
      { rows === 2 && line !== 2 &&
        <div className={headerClass.join(' ')}>
        { columns('right', 0, isMobileOn, line, sport, headerName ) }
        </div>
      }
      {
        rows === 2 && sport !== 10 &&
        <AsianOdds
          line={line}
          event={event}
          isLeft={false}
          outcomes={outcomes}
          isSimple={false}
        />
      }
    </div>
  }

  </div>
  );
};

export default MobileLayout;
