import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { connect } from 'react-redux';
import config from './../../../../config';

import { push } from 'connected-react-router';

import { showSportsListOn, gameCastOn } from '../../../actions/ui-toolbar';
import { dashedToCamelCase, camelCaseToDashed, homeAwayToLink } from '../../../helpers';
import { groupEventsByLeague, detectmob } from './live-helpers';
import { reqLiveEvents, openSport, subscribeEvent, unsubscribeEvent, getCurrentEvent, toggleVideoTab } from '../../../actions/live';
import { redirectToNotLoggedInPage } from '../../../actions/user';
import { createRoute } from '../../../helpers';
import { openSettings, redirectSettings } from '../../../actions/settings';
import { Sports, ViewType, GetSportName } from '../../../constants';

import Title from '../../title';
import AsianLine from './asian/line';
import ClassicLeague from './league';
import LiveEuropean from './european';
import AsianHeader from './asian/header';
import AsianLeague from './asian/league';
import Navbar from './ui-toolbar/navbar';
import TypeSwitcher from './type-switcher';

import NoEventsLink from './european/no-events-link';

import { getOutcomes_, eventsBySport_, leaguesInPlay_, getEvent } from './../../../reducers/events-flatten';

class Live extends React.Component<any, any> {

  constructor(props: any) {
    super(props);

    const { sport, eventsBySport } = this.props;

    let subscribedEvent = sport && eventsBySport && eventsBySport[0];
    let eventId = subscribedEvent && subscribedEvent.id;

    this.state = {
      eventId,
      hidecast: true,
      isTablet: false,
      isMobile: false,
      subscribedEvent,
      isMobileDevice: false
    };
  }

  redirect() {
    const { redirect, dispatch, redirectToSettings, eventsLoaded } = this.props;

    if (!eventsLoaded) return;

    if (redirectToSettings) {

      // It is possible that 'redirect' has not been initialised yet.  So only continue if it is initialised
      if (redirect) {
        console.log('redirectToSettings detected - redirect is', redirect);
        dispatch(redirectSettings(false));
        // this.props.params.settings = false;
        dispatch(openSettings(createRoute('/live/' + camelCaseToDashed(redirect))));
      }
    }
    else if (redirect) {
      dispatch(openSport(camelCaseToDashed(redirect)));
    }
  }

  checkData(prevProps: any = {}, prevState: any = {}) {
    const { eventsLoaded, event, dispatch, user, view } = this.props;
    let isAsian = view === 1;

    if (!eventsLoaded) {

      dispatch(reqLiveEvents(isAsian));

      // console.log(`request live events`);
    } else if (event && this.state.eventId !== prevState.eventId) {
      // console.log(`subscribe me to ${event.sport} gamecast switch`);
      let section = isAsian ? 'asian' : 'live';

      dispatch(subscribeEvent(this.state.eventId, event.sport, user.bookmakerId[event.sport], section));
      dispatch(getCurrentEvent(this.state.eventId));

    }

  }

  componentDidMount() {
    const { dispatch, view, event, user, location } = this.props;
    let isAsian = view === 1;
    // this.checkData();

    if (view === 3) {
      dispatch(showSportsListOn());
    }
    // console.log(location.search === '?settings');
    dispatch(redirectSettings(location.search === '?settings'));

    this.redirect();
    if (view === 3) {
      dispatch(showSportsListOn());
    }

    window.addEventListener('resize', this.getWidth);

    this.setState({ isMobile : window.innerWidth < 760 || detectmob() ? true : false });
    this.setState({ isTablet : window.innerWidth > 765 && detectmob()  ? true : false });
    this.setState({ isMobileDevice : detectmob() });

    if (view === 3) {
      dispatch(showSportsListOn());
    }

    if (event) {
      // console.log(`subscribe me to ${event.sport} mounting`);
      let section = isAsian ? 'asian' : 'live';
      dispatch(subscribeEvent(this.state.eventId, event.sport, user.bookmakerId[event.sport], section));
      dispatch(getCurrentEvent(this.state.eventId));

    }

    dispatch(gameCastOn());

  }

  componentDidUpdate(prevProps: any, prevState: any) {

    if (prevProps.closedConnection || this.props.closedConnection) return;

    this.checkData(prevProps, prevState);
    this.redirect();
  }

  componentWillUnmount() {
    const { dispatch, event, sport } = this.props;

    if (event) {
      dispatch(unsubscribeEvent(this.state.eventId, sport));
    }

    window.removeEventListener('resize', this.getWidth);
  }

  componentWillReceiveProps(newProps: any) {
    const { view, sport, dispatch, closedConnection } = this.props;

    Object.keys(this.props)
      .forEach(prop => {

        if (this.props[prop] !== newProps[prop]) {
          // console.log(prop);
          // console.log(newProps[prop]);
        }
      });

    const that = this;

    if (closedConnection) return;

    if (sport !== newProps.sport) {
      // console.log(`subscribe me to ${newProps.sport}`);
      dispatch(toggleVideoTab(false));

      if (sport && newProps.sport ) {
        dispatch(unsubscribeEvent(this.state.eventId, sport));
      }

      if (newProps.eventsBySport &&  newProps.eventsBySport[0]) {
        this.setState({ eventId: newProps.eventsBySport && newProps.eventsBySport[0].id });
        setTimeout(function() {
          that.setState({subscribedEvent: newProps.eventsBySport && newProps.eventsBySport.filter((evs: any) => evs.id === that.state.eventId)[0] });
          // dispatch(subscribeEvent(that.state.eventId, event.sport, '103'));
        }, 100);
      }

    }

  }

  getWidth = () => {
    this.setState({
      isMobile: window.innerWidth < 760 || detectmob() ? true : false
    });
    // If user resizes window and video component is hidden, stop stream
    let findDOMNode_: any = findDOMNode(this);
    const gamecastWrapper = findDOMNode_.querySelector('.wrapper-cont.live');

    if (gamecastWrapper && window.getComputedStyle(gamecastWrapper).display === 'none') {
      this.props.dispatch(toggleVideoTab(false));
    }
  }

  getGamecast = (e: any, id: any) => {

    const that = this;
    const { dispatch } = this.props;

    // Gamecast and video component can have 'display: none' on medium and small devices;
    // function should not run (stream should not play) if Live Stream button is clicked in this case.

    let findDOMNode_: any = findDOMNode(this);
    // We get gamecast/video wrapper DOM node
    const gamecastWrapper = findDOMNode_.querySelector('.wrapper-cont.live');
    // Check if wrapper has display: 'none'
    if (gamecastWrapper && window.getComputedStyle(gamecastWrapper).display === 'none') return;

    let isStreamButton = e.currentTarget.className.includes('stream');
    dispatch(toggleVideoTab(isStreamButton));

    if (this.state.eventId === id) {
      return;
    }

    dispatch(unsubscribeEvent(this.state.eventId, this.props.sport, '0', 'gamecast'));
    console.log(`unsubscribing from gamecast`);

    this.setState({ eventId: id });

    setTimeout(function() {
      that.setState({
        subscribedEvent: that.props.eventsBySport.filter((evs: any) => evs.id === that.state.eventId)[0]
      });
      that.props.dispatch(getCurrentEvent(that.state.subscribedEvent.id));
    }, 500);

  }

  render() {

    let {
      user,
      view,
      game,
      title,
      sport,
      event,
      asianLine,
      isMobileOn,
      outcomeList,
      killedSession,
      eventsBySport,
      parlayBySport
    } = this.props;

    if (killedSession || user && user.response && user.response.initialisation_result_code === 0) {
     this.props.dispatch(redirectToNotLoggedInPage());
    }

    if (!parlayBySport && config.hasParlay) {
      // console.log('check your env file. You need to copy parlay_data.json file to the path at PARLAY_META_FILE_LOCATION=');
    }

    if (eventsBySport !== undefined) {
      const urlFromID = eventsBySport.map((item: any) => {
        if (item.id === parseInt(game)) {
          const sport = Sports[item.sport];
          const url = `${sport}/${homeAwayToLink(item.home, item.away)}`;
          window.location.href = `${url}`;
          return;
        }
      });
    }

    if (eventsBySport === undefined) {
      return <NoEventsLink isPrematch={false} sport={sport} title={title} view={view} dispatch={this.props.dispatch}/>;
    }

    if (view === ViewType.european) {
      // console.log(events);
      // console.log(eventsBySport);
      return  (
        <LiveEuropean
          event={event}
          sport={sport}
          title={title}
          isMobileOn={isMobileOn}
          outcomeList={outcomeList}
          eventsBySport={eventsBySport}
          isTablet={this.state.isTablet}
          isMobile={this.state.isMobile}
          getGamecast={this.getGamecast}
          dispatch={this.props.dispatch}
          leaguesInPlay={this.props.leaguesInPlay}
          subscribedEvent={this.state.subscribedEvent}/>
     );

    } else {

      let data;
      const classList = ['live'];
      const classContent = ['md-container live-content'];

      if (view === ViewType.northAmerican) {
        classList.push('north-american');

        if (detectmob()) {
          classList.push('northAm-mobile');
        }

      }

      const asian = view === ViewType.asian;

      if (eventsBySport) {
        const League: any = asian ? AsianLeague : ClassicLeague;
        data = groupEventsByLeague(eventsBySport, sport, view).map(eventsByLeague => (
          <League key={eventsByLeague[0].league} events={eventsByLeague} view={view}/>
        ));
      }

      return (

        <div>
          {!asian &&
            <Navbar {...this.state} />
          }
          <Title key={sport} title={title}>
            <div className={classContent.join(' ')}>
              { asian ? <AsianLine /> : config.hasParlay && parlayBySport && <TypeSwitcher /> }
              <div className={classList.join(' ')}>
                { asian ?
                  <AsianHeader
                    key="2"
                    sport={sport}
                    line={asianLine}
                    events={eventsBySport}
                    isMobileOn={isMobileOn}>{ data }
                  </AsianHeader> : data
                }
              </div>
            </div>
          </Title>

        </div>
      );
    }
  }
}

export default connect(
  (state: any, ownProps: any) => {

    const { asianLine } = state.live;

    // const currentRoute = ownProps.routes[ownProps.routes.length - 1];
    const killedSession = state.user.killedSession;

    let subscribedSport = state.live.subscribedSport;

    const closedConnection = state.live.closedConnectionLive;

    let events = state.eventsFlatten && state.eventsFlatten.bySport && state.eventsFlatten.bySport[subscribedSport];
    // console.log(state.eventsFlatten.bySport[subscribedSport]);

    const eventsLoaded = events && events.length > 0;
    const openMarkets = state.live.openMarkets;

    const view = state.settings.view;

    if (!eventsLoaded || !openMarkets || closedConnection) {
      return {
        view,
        eventsLoaded,
        killedSession,
        language: state.languageData.betSlip
      };
    }

    // Get the first event of sport with highest number of events
    let event = getEvent(state);

    const menuTitle = state.languageData.menu;
    const title = !state.live.filterStreaming ? menuTitle.inPlay : menuTitle.streaming;
    const redirectToSettings = state.settings.redirectToSettings;

    if (!events || !event) {
      return {
        eventsLoaded,
        killedSession,
        language: state.languageData.betSlip
      };
    }

    // Open settings dialogue

    if (ownProps.location.search === '?settings' && !ownProps.match.params.sport) {
      console.log('redirect is', !ownProps.match.params.sport ? Sports[event.sport] : ownProps.match.params.sport);
      return {
        title,
        eventsLoaded,
        killedSession,
        language: state.languageData.betSlip,
        redirectToSettings,
        redirect: !ownProps.match.params.sport ? Sports[event.sport] : ownProps.match.params.sport
      };
    }

    if (!ownProps.match.params.sport) {
      console.log('stopping here');
      return {
        title,
        eventsLoaded,
        killedSession,
        redirect: Sports[event.sport],
        language: state.languageData.betSlip
      };
    }

    const ccSport = dashedToCamelCase(ownProps.match.params.sport);
    const game = ownProps.match.params.sport;

    let sportsCC_: any = ccSport;

    const eventsBySport = eventsBySport_(state, ownProps);

    // If there is no event for the sport contained on the url , jump to the next sport with events
    const eventsBySportCC = state.eventsFlatten.bySport[Sports[sportsCC_]] && eventsBySport;

    if (!eventsBySportCC) {
      return {
        title,
        eventsLoaded,
        killedSession,
        redirect: Sports[event.sport],
        language: state.languageData.betSlip
      };
    }

    if (eventsBySport.length <= 0) {
      return {
        eventsLoaded,
        killedSession,
        language: state.languageData.betSlip
      };
    }

    // console.log('eventsBySport');
    // console.log(eventsBySport);
    event = eventsBySport[0];

    let ccSport_: any = ccSport;
    let leaguesInPlay = leaguesInPlay_(state, ownProps);
    // let leaguesInPlay = state.eventsFlatten.leagueIdsBySport[Sports[ccSport_]].filter((league: any) => eventsBySport.map((ev: any) => ev.leagueid).indexOf(league) >= 0);

    // console.log(state.outcomesFlatten);
    // console.log('here');
    // // console.log(event);
    // console.log(event && getOutcomes_(state, event));
    return {
      title: `${title} - ${state.languageData.sports[ccSport]}`,
      sport: Sports[ccSport_],
      asianLine,
      eventsLoaded,
      event,
      game,
      betSlipOdds: state.betSlip.odds,
      view: state.settings.view,
      loading: state.loading,
      uiToolbar: state.uiToolbar,
      isMobileOn: state.uiToolbar.isMobileOn,
      outcomes: state.live.outcomes,
      outcomeList: event ? getOutcomes_(state, event) : [],
      user: state.user,
      numberMarkets: state.live.numberMarkets,
      redirectToSettings,
      killedSession,
      eventsBySport,
      leaguesInPlay,
      subscribedSport: state.live.subscribedSport,
      parlayBySport: state.live.parlayBySport,
      closedConnection,
      filterStreaming: state.live.filterStreaming

    };
  }
)(Live);
