import { deepAssign } from '../../../helpers';

export const enum CricketAction {
  None = 0,
  Runs = 168,
  Wicket = 174,
  Bowled = 175,
  LBW = 176,
  // RunOut = 1177,
  // Caught = 1178,
  Stumped = 179,
  // RetiredHurt = 1180,
  // HitWicket = 1181,
  // HandledBall = 1182,
  Bowling = 206,
  EndOfOver = 207,
  MatchEnded = 212,
  InningsBreak = 245

}

export const enum CricketTeam {
  Team1 = 1,
  Team2 = 2
}

export interface ICricketGroundAnimationState {
  actionBox: {
    x: number;
    y: number;
  };
  ball: {
    opacity: number;
    x: number;
    y: number
  };
}

export const nt: ICricketGroundAnimationState = {
  actionBox: {
    x: 60,
    y: - 50
  },
  ball: {
    opacity : 0,
    x: 45,
    y: 25
  }
};

export const t1: ICricketGroundAnimationState = deepAssign({}, nt, {
  actionBox: {
    x: 60,
    y: -50
  },
  ball: {
    opacity : 0,
    x: 45,
    y: 25
  }
});

export const t2: ICricketGroundAnimationState = deepAssign({}, nt, {
  actionBox: {
    x: 60,
    y: -50
  },
  ball: {
    opacity : 0,
    x: 45,
    y: 25
  }
});

export function actionTextCricket(action: CricketAction, data?: any) {

  switch (action) {
    case CricketAction.None:
      return 'Upcoming Game';
    case CricketAction.Wicket:
      return 'Wicket';
    case CricketAction.Bowled:
      return 'Bowled';
    case CricketAction.Bowled:
      return 'LBW';
    case CricketAction.Bowled:
      return 'Stumped';
    case CricketAction.Bowling:
      return 'Bowling';
    case CricketAction.EndOfOver:
      return 'End Of Over';
    case CricketAction.MatchEnded:
      return 'Match Ended';
    case CricketAction.MatchEnded:
      return 'Innings Break';
  }

  return '';
}
