import * as React from 'react';
import RadioButton from './radio-button';

export interface IRadioButtonListProps {
  model: string;
  value?: string;
  title: string;
  name: string;
  inline?: boolean;
  onChange?: (e: any) => void;
  children?: any;
}

export default function RadioButtonList({ title, value = null, name, inline = true, children, onChange = f => f }: IRadioButtonListProps) {
  const className = 'md-radio';
  const handleChange = (e: any) => onChange(e.target.value);

  return (
    <div className="md-container">
      <div className={ className + '__title' }>
        <label>{title}</label>
      </div>
      <div className={ inline && className + '--inline' }>
        {
          React.Children.map(children, (el, i) => {
            if (process.env.NODE_ENV !== 'production') {
              if (!((el as React.ReactElement<any>).type === RadioButton)) {
                throw new TypeError('Os filhos de um RadioButtonList devem ser do tipo RadioButton');
              }
            }
            return React.cloneElement(el as any, {
              key: i,
              name: name,
              checked: value,
              onChange: handleChange
            });
          })
        }
      </div>
    </div>
  );
}
