import { ISoccerFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const extrasStart: ISoccerFieldAnimationState = nt;
const extrasEnd: ISoccerFieldAnimationState = deepAssign({}, nt, {
  actionText: {
    y: 17
  },
  extras: {
    rotate: 0
  }
});

export function extras() {
  return [{
    start: extrasStart,
    end: extrasEnd
  }];
}
