import * as React from 'react';

export interface ITextFieldProps {
  label: string;
  model?: string;
  type?: string;
  onChange?: (e: React.ChangeEvent<any>) => void;
  onFocus?: (e: React.FocusEvent<any>) => void;
  onClick?: (e: React.MouseEvent<any>) => void;
  onKeyDown?: (e: React.KeyboardEvent<any>) => void;
  value?: string;
  enabled?: boolean;
  readOnly?: boolean;
  required?: boolean;
  pattern?: string;
  step?: string;
  autoFocus?: boolean;
  rows?: number;
  min?: number;
  max?: number;
  placeholder?: string;
}

export default function TextField({ label, type = 'text', onChange, onFocus, onKeyDown, onClick, value = '', enabled = true, placeholder = '', readOnly = false, required = false, pattern = undefined, step = undefined, autoFocus = false, rows = 0, min = undefined, max = undefined }: ITextFieldProps) {
  let className = rows === 0 ? 'md-input' : 'md-textarea';
  if (!!value) {
    className += ' has-value';
  }

  const handleInvalid = (e: any) => e.target.scrollIntoView(false);
  const handleChange = (e: any) => {
    if (onChange) {
      onChange(e.target.value);
    }
  };

  const handleEvent = (ev: any) => (e: any) => {
    if (ev) {
      ev(e);
    }
  };

  const props = {
    autoFocus,
    type,
    className,
    value,
    onInvalid: handleInvalid,
    onChange: handleChange,
    onFocus: handleEvent(onFocus),
    onClick: handleEvent(onClick),
    onKeyDown: handleEvent(onKeyDown),
    disabled: !enabled,
    required,
    pattern,
    step,
    autoComplete: 'off',
    readOnly,
    min,
    max
  };

  return (
    <div className="md-container">
      {
        rows === 0 ?
          <input key="input" {...props} /> :
          <textarea key="textarea" {...props}></textarea>
      }
      <label key="label" className="md-input__label">{label}</label>
      <i key="i" className="md-bar"></i>
      <span className="md-input__placeholder">{placeholder}</span>
    </div>
  );
}
