import * as React from 'react';
import Lottie from 'react-lottie';
import { connect } from 'react-redux';
import { BasketballActions, hasLottieAnimation } from './constants';
import { getAnimations } from './../../../actions/animations';

class BasketBallLottie extends React.Component<any, any> {

  public defaultOptions: any;

  constructor(props: any) {
    super(props);

    this.state = {
      mockIndex: 0,
      shouldLottieRun: false,
      previousAction: null,
      action: this.props.action,
      defaultOptions: {
        loop: false,
        autoplay: true,
        animationData: '',
        rendererSettings: {
          preserveAspectRatio: 'xMidYMid slice'
        }
      },
      lottieAnimImagePath: './../images/'
    };

  }

  componentDidMount() {
    const { path } = this.props;

    let that = this;

    // Request animations only if they are not already on store :)
    if (!Object.keys(this.props.animations['basketball'])[0]) {
      console.log('no data');
      this.props.dispatch(getAnimations('basketball'));
    }

    // For some reason, we have to change image's paths on json file
    // if gamecast is on SingleEvent section

    const pathSplit = path ? path.split('/') : [];

    // Get path to see if this is single or live events section

    this.setState({
      lottieAnimImagePath: path && pathSplit[pathSplit.length - 1].includes('-') ? './../../images/' : './../images/'
    });

    if (!hasLottieAnimation(this.state.action)) return;

    let basketballAction = BasketballActions[this.state.action];

    if (!basketballAction) return;

    basketballAction = basketballAction.toLowerCase();

    let animationData_ = this.props.animations['basketball'][basketballAction];

    if (!animationData_) return;

    this.setState({shouldLottieRun: true});

    setTimeout(() => {
      that.setState({shouldLottieRun: false });
    }, 8000);

    this.setState({
      defaultOptions: {
        ...this.state.defaultOptions,
        animationData: animationData_
      }
    });
  }

    // Not worry about not using prevProps, it is a funciton's parameter
    componentDidUpdate(prevProps: any, prevState: any) {

      if (!Object.keys(this.props.animations['basketball'])[0]) {
        console.log('animations file might not have been loaded properly');
        return;
      }

      if (prevState.action !== this.state.action) {

        let basketballAction = BasketballActions[this.state.action];
        console.log(basketballAction);
        console.log(hasLottieAnimation(this.state.action));
        if (!basketballAction || !hasLottieAnimation(this.state.action)) return;

        basketballAction = basketballAction.toLowerCase();
        console.log(basketballAction);

        let animationData_ = this.props.animations['basketball'][basketballAction];
        console.log(animationData_);
        console.log(this.props.animations['basketball']);
        // Prevent UI from crashing if there is no JSON animations file
        if (!animationData_) return;
        // let animationData_ = require(`./actions/${cricketAction}.json`);

        this.setState({shouldLottieRun: true});

        let that = this;
        setTimeout(() => {
          that.setState({shouldLottieRun: false });
        }, 5000);

        this.setState({
          defaultOptions: {
            ...this.state.defaultOptions,
            animationData: animationData_
          }
        });
      }
    }

  // We use this static function to avoid componentWillReceiveProps (deprecated method)

  static getDerivedStateFromProps(nextProps: any, prevState: any) {

    if (nextProps.action === 8888) return null;

    return nextProps.action !== prevState.action ?
      {
        previousAction: prevState.action,
        action: nextProps.action
      } :
      null;
  }

  render() {
    // let { mockIndex } = this.state;

    let { extraData, team, matchData, action } = this.props;

    // let actionText_ = actionTextFootBall(action);

    let actionText_ = '';

    console.log(action);
    console.log(actionText_);
    return (
      <div style={{ width: '100%'}}>
        <Lottie options={this.state.defaultOptions} />
      </div>
    );
    // return (
    //   <>
    //     <div style={{ width: '100%'}}>
    //       { hasLottieAnimation(this.state.action) && this.state.shouldLottieRun ?
    //         <>
    //           <Lottie options={this.state.defaultOptions} />
    //           <div
    //             style={{ background: '#212121', color: '#fff', textAlign: 'center', padding: '10px', marginTop: '-5px'}}
    //             className="actions"
    //           > {actionText_}</div>
    //         </> :
    //         <>
    //           <FootBallField
    //             view={3}
    //             path={this.props.path}
    //             team={team}
    //             event={this.props.event}
    //             score={this.props.score}
    //             action={this.props.action}
    //             loaded={this.props.loaded}
    //             team1={this.props.event.home}
    //             team2={this.props.event.away}
    //             timeouts={this.props.timeouts}
    //             extraData={extraData}
    //             matchData={matchData}
    //           />
    //           {/* <img style={{ width: '100%', height: '100%' }} src={`${this.state.lottieAnimImagePath}/football/idle-bg.png`} /> */}
    //         </>
    //       }
    //     </div>
    //   </>
    // );
  }

}

export default connect(
  (state: any) => ({
    animations: state.animations
  })
)(BasketBallLottie);
