import * as React from 'react';
import config from './../../../../config';
import { FootBallAction, FootBallTeam, actionTextFootBall } from './constants';
import { getAnimations } from './actions';
import { Animation } from '../helpers';
import { FootballActions, hasLottieAnimation } from './../football-lottie/constants';

import PassIncomplete from './pass-incomplete';
import TouchDown from './touch-down';
import PuntIcon from './punt-icon';
import RushIcon from './rush-icon';
import PassIcon from './pass-icon';
import Penalty from './penalty';
import FGIcon from './fg-icon';

import { getYardage } from './../../../../server/ws/helpers';

export class FootBallField extends React.Component<any, any> {
  animation = new Animation(this);
  promise: Promise<any|void> = Promise.resolve();

  state = {
    svgHeight: 'auto',
    animation: getAnimations(null, this.props.team, this.props.action)[0].start,
    action: this.props.action
  };

  handleResize = () => {
    const gc = (this.refs['gc'] as HTMLDivElement);

    if (!gc) {
      return;
    }

    let { width = 105, height = 68 } = this.props;
    [width, height] = [width, height].map(x => x + 2);

    this.setState({
      svgHeight: (gc.clientWidth * height / width) + 'px'
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    requestAnimationFrame(this.handleResize);
  }

  componentWillReceiveProps(newProps: any) {

    if (newProps.team !== this.props.team || newProps.action !== this.props.action || !this.props.loaded) {

      if ((newProps.action !== 1401 || this.props.action !== 1401) && (newProps.action !== 1473 || this.props.action !== 1473) && (newProps.action !== 1474 || this.props.action !== 1474)) {
        this.promise = getAnimations(this.state.animation, this.props.team, this.props.action, newProps.team, newProps.action)
          .reduce((all: any, next: any) => (
            all.then(() => Promise.all(Object.keys(next.start).map(key => {
              this.setState({
                action: newProps.action
              });
              return this.animation.start(key, next.start[key], next.end[key], 750);
            })))
          ), this.promise);
      }
    }

  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
    this.animation.cancel();
  }

  get team() {
    const { team, team1, team2, event } = this.props;
    if (!event) return;
    return team === FootBallTeam.Team1 ? event.inverted ? team2 : team1 : team === FootBallTeam.Team2 ? event.inverted ? team1 : team2 : '';
  }

  drawLines(w: any, h: any, strokeColor: any) {
    const arr = [];

    for (let i = 10; i < (105 - 8.5); i += 8.5) {
      arr.push(i);
    }

    return arr.map((item, i) => {
        return (
          <g key={i}>
            { i > 0 && i < 10 &&
            <g>
              <g transform="scale(-1, -1)">
                <text x={w(item - (107.25))} y={h(-10)} strokeWidth="0" fill={strokeColor} textAnchor="start" style={{ fontSize: '3px' }}>{i <= 5 ? i : i - ((i - 5) * 2)} 0</text>
              </g>
              { i !== 5 &&
                <g>
                  { i >= 6 ?
                  <g>
                    <g transform={`translate(${w(item) + 2.25}, 1.25)`}>
                      <polygon points="1,7 0,6.25 0,7.75" fill={strokeColor}/>
                    </g>
                    <g transform={`translate(${w(item) + 2.25}, 45)`}>
                      <polygon  points="1,7 0,6.25 0,7.75" fill={strokeColor}/>
                    </g>
                  </g> :
                  <g>
                    <g transform={`translate(${w(item) - 2.25}, 1.25)`}>
                      <polygon points="-1.5,7 -0.5,6.25 -0.5,7.75" fill={strokeColor}/>
                    </g>
                    <g transform={`translate(${w(item) - 2.25}, 45)`}>
                      <polygon points="-1.5,7 -0.5,6.25 -0.5,7.75" fill={strokeColor}/>
                    </g>
                  </g>
                  }
                </g>

              }
              <text x={w(item - 2.25)} y={h(50)} strokeWidth="0" fill={strokeColor} textAnchor="start" style={{ fontSize: '3px' }}>{i <= 5 ? i : i - ((i - 5) * 2)} 0</text>
            </g>

            }

            <line  x1={w(item)} y1={h(-2)} x2={w(item)} y2={h(70)} stroke={strokeColor} strokeWidth="0.5"/>
          </g>
        );
    });

  }

  drawYards(w: any, h: any, strokeColor: any) {

    const arr = [];

    for (let i = 9; i < 85; i += 0.76) {
      arr.push(i);
    }

    return arr.map((item, i) => {
      return (
        <g key={i}>
          <line x1={item} y1={h(- 2)} x2={item} y2={h(0)} stroke={strokeColor} strokeWidth="0.25"/>
          <line x1={item} y1={h(68)} x2={item} y2={h(70)} stroke={strokeColor} strokeWidth="0.25"/>
        </g>
      );
    });
  }

  getTimeouts() {

    const { width = 105, height = 68, action, timeouts } = this.props;
    // console.log(`get ${action}`);
    // console.log(this.props.matchData);
    let mockTimeout = timeouts;
    if (!timeouts) return <g></g>;

    // console.log(this.props);
    const t1TO = mockTimeout.split(',')[0] && parseInt(mockTimeout.split(',')[0]);
    const t2TO = mockTimeout.split(',')[1] && parseInt(mockTimeout.split(',')[1]);

    return [1, 6, 11, 4, 9, 14].map((it, i) => {
     return (
      <g key={i}>
        { i <= 2 ?
          <rect x={it} y={(height - 10) + 2} opacity={t1TO >= i + 1 ? 1 : 0} strokeWidth="0" fill={'red'} width={3} height={1} /> :
          <rect x={ width - it } y={(height - 10) + 2} opacity={t2TO >= i - 3 ? 1 : 0} strokeWidth="0" fill={'red'} width={3} height={1} />
        }
      </g>
     );
   });
  }

  getLinesPoints() {
    // Width 70
    // starts at 10
    // ends at 80
    let { extraData, team, matchData } = this.props;
    if (!Object.keys(extraData)[1] || matchData === '###' || matchData === '####' || !extraData.redLine || !extraData.yellowLine) return;
    if (extraData.redLine === extraData.yellowLine) return;

    // If Team === 2 and yellow line < 10 ; yellow line === 10 to go 'and Goal'
    // If Team === 2 and red line < 10 ; red line === 10
    // If Team === 1 and yellow line > 91 ; yellow line === 91 to go 'and Goal'
    // If Team === 1 and red line > 91 ; red line === 91

    if (team === 2 && extraData.yellowLine <= 10) {
      return {
        toGo: extraData.toGo,
        yellowLine: 10,
        redLine: extraData.redLine < 10 ? 10 : extraData.redLine
      };
    }

    if (team === 1 && extraData.yellowLine >= 91) {
      return {
        toGo: extraData.toGo,
        yellowLine: 94,
        redLine: extraData.redLine > 91 ? 91 : extraData.redLine + 3
      };
    }

    return {
      toGo: extraData.toGo,
      yellowLine: team === 1 ? extraData.yellowLine + 3 : extraData.yellowLine,
      redLine: team === 1 ? extraData.redLine + 3 : extraData.redLine
    };

  }

  render() {
    const { width = 105, height = 68, action, matchData } = this.props;
    const { svgHeight, animation } = this.state;
    const { actionText } = animation;
    const [mainColor, secondaryColor, strokeColor] = ['#006837', '#1CB55C', '#ffffff'];

    const getPrefix = (num: any) => {
      switch (num) {
        case '1':
          return '1st';
        case '2':
          return '2nd';
        case '3':
          return '3rd';
        case '4':
          return '4th';
        default:
          return '';
      }
    };

    const toGo = () => {

      let isGoal = false;

      if (!this.props.matchData) return '';

      if (this.props.extraData) {
        let { team, extraData } = this.props;
        isGoal = team === 2 && extraData.yellowLine <= 10 || team === 1 && extraData.yellowLine >= 91;
      }

      let down = getPrefix(this.props.matchData.split('#')[1]);
      let _toGo: any = parseFloat(this.props.matchData.split('#')[0]) * 100;
      if (!down || !_toGo) return '';
      _toGo = isGoal ? 'Goal' : Math.floor(_toGo);

      return this.props.matchData && `${down} and ${_toGo}`;
    };

    let className = 'gc-football';

    const w = (p: any) => width * p / 105;
    const h = (p: any) => height * p / 68;

    let _actionText = actionTextFootBall(action);

    if (action === 1459 || action === 1460) {

      try {
        let matchData_ = matchData.includes('^') ? matchData.split('^')[0] : matchData;
        let yards = matchData_.split('##')[matchData_.split('##').length - 1];
        yards = Math.floor(parseFloat(yards) * 100);
        _actionText = `${_actionText} for ${yards} yards`;

      }

      catch (err) {
        console.log(err);
        _actionText = ``;
      }

    }

    const widthIdx = _actionText.length >= 15 ? 2 : 1;
    const textBoxWidth = 30 + (_actionText.length * widthIdx);
    this.getLinesPoints();
    // console.log(actionTextFootBall(action));

    return (
      <div ref="gc" className={className}>
        <svg className={`${className}__court`} viewBox={`-1 -1 ${width + 2} ${height + 2}`} fill="transparent">

          <rect x="-1" y="-1" strokeWidth="0" fill={mainColor} width={width + 2} height={(height - 10) + 2} />
          <rect x="10" y="-1" strokeWidth="0" fill={secondaryColor} width={width - 20} height={(height - 10) + 2} />
            { this.drawLines(w, h, strokeColor) }
            { this.drawYards(w, h, strokeColor) }
            {
            Object.keys(this.props.extraData)[1] && this.props.matchData !== '###' && this.getLinesPoints() &&
              <g>
                <rect x={ this.props.team === 1 ? this.getLinesPoints().redLine : this.getLinesPoints().yellowLine } y="1" strokeWidth="0" fill={'#313131'} opacity="0.35" width={this.getLinesPoints().toGo} height={(height - 10) + 2} />
                <rect x={ this.getLinesPoints().yellowLine } y="1" strokeWidth="0" fill={'yellow'} width="1" height={(height - 10) + 2} />
                <rect x={ this.getLinesPoints().redLine } y="1" strokeWidth="0" fill={'#da1820'}  width="1" height={(height - 10) + 2} />
              </g>
            }
           { action !== 1401 &&
           <g>
            <rect fill={'yellow'} x={(width * 0.5) - (textBoxWidth * 0.5)} y={actionText.y} width={textBoxWidth} height="13" rx="1" ry="1" />
            <text fontFamily="Ubuntu-Bold" x={width * 0.5} y={actionText.y + 8} strokeWidth="0" fill={'#313131'} textAnchor="middle" style={{ fontSize: '6px' }}>{_actionText}</text>
          </g>
          }
          <g className="timeOuts">
            <rect x="-1" y={(height - 10) + 1} strokeWidth="0" fill={'#313131'} width={width + 2} height={10} />
            {this.getTimeouts()}

          </g>
          { action === 1459 &&
            <g> <PassIcon x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /> </g>
          }
          { (action === 1403 || action === 1404) &&
            <g> <FGIcon x={(width * 0.5) - 15} y={(height * 0.5) - 15} width="30" height="30" /> </g>
          }
          {
            action === 1402 &&
            <g><TouchDown x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /></g>
          }
          { (action === 1422 || action === 1425 || action === 1426 || action === 1475 || action === 1483) &&
            <g><Penalty x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /></g>
          }
          { action === 1461 &&
            <g> <PassIncomplete x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /> </g>
          }
          { action === 1460 &&
            <g> <RushIcon x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /> </g>
          }
          {
            action === 1465 &&
            <g> <PuntIcon x={(width * 0.5) - 12.5} y={(height * 0.5) - 12.5} width="25" height="25" /> </g>
          }
          <text fontFamily="Ubuntu-Medium" x={width * 0.5} y={(height - 10) + 7} strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
          {
             (_actionText !== '' && action !== 1401 && action !== 451 && action !== 468 && action !== 1469 && action !== 1470 && action !== 1452 && action !== 1453) && `${this.team} ${config.footballGamecastAnimated ? _actionText : ''}`
          }
          {/* { _actionText !== '' &&
            hasLottieAnimation(action) ? `${this.team} ${_actionText}` : (action !== 1401 && action !== 451) && `${this.team} ${_actionText}`
          } */}

          </text>

          <g className="yards-to-go">
            <rect x="-1" y="-1" strokeWidth="0" fill={'#313131'} width={width + 2} height={5} />
            {
              (action === 1402 || action === 1406 || action === 1403) &&
              <text fontFamily="Ubuntu-Light" x={width * 0.5} y="2.5" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
                {this.props.matchData && this.props.matchData.replace(/[^a-zA-Z ]/g, '')}
              </text>
            }
            { (action !== 1402 && action !== 1406) && (this.team === this.props.team1 || this.team === this.props.team2) &&
              <text fontFamily="Ubuntu-Light" x={width * 0.5} y="2.5" strokeWidth="0" fill={'#fff'} textAnchor="middle" style={{ fontSize: '4px' }}>
                { toGo() !== '' && this.team} {toGo()}
              </text>
            }
          </g>
        </svg>
      </div>
    );
  }
}
