/** This function search the best available row if user's placed outcome has gone suspended.
/ ** It returns the closest row among the given available outcomes on the same market
/ **  @currentOutcome : outcome placed by user and whose row is no longer available
/ **  @updateOutcomes : available outcome with the same market as currentOutcome
*/

const getBestRow = (updateOutcomes: any, currentOutcome: any): any => {

  let sortable = [];

  // We get an array of availableOutcomesLines
  for (let id_ in updateOutcomes) {

    // Get only the outcomes from the same column as the currentOutcome

    if (updateOutcomes[id_].column === currentOutcome.column) {

      // Filter outcomes with no display value or suspended

      if (updateOutcomes[id_].display !== '' && !updateOutcomes[id_].suspended) {

        sortable.push({ id: updateOutcomes[id_].id, row: updateOutcomes[id_].row});
      }
    }
  }

  // Get the closest line

  let closestLine = sortable.sort((a, b) => {
    return Math.abs(a.row - currentOutcome.row) - Math.abs(b.row - currentOutcome.row);
  });

  // console.log(currentOutcome.row);
  // console.log(closestLine);

  return closestLine;

};

export default getBestRow;
