import * as React from 'react';
import { getAsianHeaderBySport, getAsian1stLineHeaderBySport, AsianLine } from './../../../../../constants';

export const headers = (isTop: any, sport: any, score: any) => getAsian1stLineHeaderBySport(sport, score).map((h, i) => {
  if (i === 0 && isTop || i === 1 && !isTop) {
     return <div key={i} className="asian__header center" >{h}</div>;
  }
  return '';
});

export const columns = (str: any, j: any, isMobileOn: any, line: any, sport: any, headerName: any) => {

  const headerClass_ = ['header-asian'];

  headerClass_.push(str);

  if (!isMobileOn && line === 1) {
    headerClass_.push('header-single-line');
  }

  return (
    <div key={j} className={headerClass_.join(' ')}>

     {
       getAsianHeaderBySport(sport, line).map((text, i) => {
        let rows;
        if (sport !== 1) {
          rows = str === 'left' ? 2 : 3;
        } else {
          rows = str === 'left' ? 3 : 4;
        }

        const shouldPrint = str === 'left' ? i <= rows : i >= rows;

        if (shouldPrint)  {

          if (text === 'Game Handicap' || text === 'HDP' || text === 'O/U') {
            text = text === 'Game Handicap' || text === 'HDP' ? 'Hdp' : 'O/U';
            return <div className={`${headerName.join(' ')} handicap`} key={i}>{text}</div>;
          }
          if (text === 'Total Goals' || text === 'Total Points') {
            text = text === 'Total Goals' ? 'TGoals' : 'TPts';
            return <div className={`${headerName.join(' ')} handicap`} key={i}>{text}</div>;
          }

          if (text === 'Games') {
            return <div className={`${headerName.join(' ')} games`} key={i}>Gms</div>;
          }

          return <div className={`${headerName.join(' ')} odd`} key={i}>{text}</div>;
        }

        return '';
      })
     }
    </div>
  );
};
