import { loading, finished } from '../../actions/loading';
import { setCSRFHeader } from '../../actions/user';

export default class Base {
  constructor(protected dispatch: any) {

  }

  private status = (resp: Response): Promise<Response> => (
    new Promise((resolve, reject) => {
      if (resp.status >= 200 && resp.status < 300) {
        return resolve(resp);
      }
      return reject(resp);
    })
  )

  api = (url: string, init?: any): Promise<any> => {

    let shouldCallFinishDispatch = false;
    if (url !== '/api/user' && url !== '/api/user/ping' && url !== '/api/user/image-upload' && url !== '/api/user/check-on-idle') {
      this.dispatch(loading());
      shouldCallFinishDispatch = true;
    }

    if (!url.includes('en?isInCache=false')) {

      let csrf_inner_html = 'CSRF_NOT_FOUND';
      if (document && (document as any).getElementById('csrf_token')) {
        csrf_inner_html = document && (document as any).getElementById('csrf_token').innerHTML;
        if (init && init.headers) {
          init.headers['CCSTK'] = csrf_inner_html.replace(/"/g, '');
        }
      }

    }

    let baseURL = '';
    if (global && (global as any).baseURL) {
      baseURL = (global as any).baseURL;
    }

    return fetch(baseURL + url, init)
      .then(this.status)
      .then(r => {
        let possibleHeader = r.headers.get('ccstk');
        if (possibleHeader) {
          this.dispatch(setCSRFHeader(possibleHeader));
        }
        return r.json();
      })
      .then(d => {
        if (shouldCallFinishDispatch)
          this.dispatch(finished());
        return d;
      })
      .catch(err => {
        if (shouldCallFinishDispatch)
          this.dispatch(finished());
        throw err;
      });
  }
}
