import * as React from 'react';

let color = '#333';

const SwitchLayoutIcon = (props?: any) => {
  const svgProps: any = {
    viewBox: '0 0 250 250',
    strokeWidth: 5,
    fill: color,
    stroke: color
  };

  if (props) {
    Object.keys(props).forEach(key => svgProps[key] = props[key]);
  }

  return (
    <svg {...svgProps} >
      <rect x="70" y="40" width="170" height="120" strokeWidth="8" stroke="#fff"/>
      <rect x="85" y="55" width="40" height="90" fill="#fff" />
      <rect x="135" y="55" width="40" height="90" fill="#fff" />
      <rect x="185" y="55" width="40" height="90" fill="#fff" />
      <rect x="5" y="90" width="170" height="120" strokeWidth="8" stroke="#fff"/>
      <rect x="20" y="105" width="140" height="90" fill="#fff" />
    </svg>
  );
};

export default SwitchLayoutIcon;
