import * as React from 'react';
import { prematchTimeConverter } from './../views/live/live-helpers';
import Sticky from './../views/live/sticky-component';

const PrematchEventHeader = ({ event, wrapperCont }: any) => {
  return (
      <Sticky
        className={wrapperCont.join(' ')}
        enter={130}
        exit={120}>
        <div className="prematch-event-header">
          <div className="date">{prematchTimeConverter(event.startdate)}</div>
          <div className="event">{event.inverted ? event.away : event.home}<span className="separator"> {event.sep} </span>{event.inverted ? event.home : event.away}</div>
        </div>
      </Sticky>
  );
};

export default PrematchEventHeader;
