import * as React from 'react';
import Price from './../../../../../components/form/price';

const EventsDesktopOdd = ({ sportsList, odd, addToBetSlip, oddDisplay, isHandicap, updClassList, classList }: any) => {

  const isOTB = !oddDisplay || odd.suspended || odd.status === 'TemporarilySuspended';

  if (sportsList) {
    return (
      <td id={odd.id} className="odd-button" onClick={addToBetSlip}>
        <div className={classList.join(' ')}>
          { oddDisplay === 'Draw' || isHandicap ?
            <span className="odd-dislplay">{ !isOTB ? oddDisplay : '' } </span> : ''
          }
          {/*
            isHandicap &&
            <span>{odd.display}</span>
          */}

          <span >
            <i className={updClassList.join(' ')} />

            { !isOTB ? <Price>{ odd }</Price> : 'OTB'}
          </span>
        </div>
      </td>
    );
  } else {
    return (
      <td id={odd.id} onClick={addToBetSlip}>
        <div className={classList.join(' ')}>
          <span>
            <i className={updClassList.join(' ')} />

            { !isOTB ? <Price>{ odd }</Price> : 'OTB'}
          </span>
        </div>
        <span >{ !isOTB ? oddDisplay : '' }</span>
      </td>
    );
  }
};

export default EventsDesktopOdd;
