import { REMOVE_EVENT } from '../actions/live';
import {
  USER_UPDATE_BET_SLIP,
  TOGGLE_BET_SLIP,
  ADD_TO_BET_SLIP,
  REMOVE_FROM_BET_SLIP,
  UPDATE_BET,
  BET_SLIP_ERROR,
  UPDATE_PRICE_CHANGE,
  LIMITS
} from '../actions/bet-slip';

import {
  CLOSED_CONNECTION_LIVE,
  CLOSED_CONNECTION_PREMATCH
} from '../actions/live';
import oddsCard from '../components/views/live/asian/mobile/odds-card';

export const betSlipInitialState: any = {
  open: false,
  parlay: { id: 'multi', risk: 0, toWin: 0 },
  odds: [],
  limits: [],
  error: ''
};

export const betSlip = (state = betSlipInitialState, action: any) => {

  switch (action.type) {
    case UPDATE_PRICE_CHANGE:

      let oddsUpdated = state.odds.map((odd: any) => {
        if (odd.uuid === action.payload.uuid) {
          odd = action.payload;
        }
        return odd;
      });

      return {
        ...state,
        odds: oddsUpdated
      };

    case CLOSED_CONNECTION_LIVE:
      if (action.payload !== true) return state;
      return {
        ...state,
        odds: state.odds.filter((odd: any) => odd.oddIsLive === 0)
      };
    case CLOSED_CONNECTION_PREMATCH:
      if (action.payload !== true) return state;
      return {
        ...state,
        odds: state.odds.filter((odd: any) => odd.oddIsLive === 1)
      };
    case USER_UPDATE_BET_SLIP:
      return {
        ...state,
        odds: action.payload
      };
    case TOGGLE_BET_SLIP:
      return Object.assign({}, state, {
        open: typeof action.payload === 'boolean' ? action.payload : !state.open
      });
    case ADD_TO_BET_SLIP:

      const oddToAdd = state.odds.filter((o: any) => o.id === action.payload.id)[0];
      if (oddToAdd) {
        return state;
      }

      return Object.assign({}, state, {
        open: true,
        odds: [...state.odds, action.payload]
      });
    case REMOVE_FROM_BET_SLIP:

      if (action.payload.uuid) {
        return Object.assign({}, state, {
          odds: state.odds.filter((o: any) => o.uuid !== action.payload.uuid),
          parlay: state.odds.length === 1 ? { id: 'multi', risk: 0, toWin: 0 } : state.parlay
        });
      }
      return Object.assign({}, state, {
        odds: state.odds.filter((o: any) => o.id !== action.payload.id),
        parlay: state.odds.length === 1 ? { id: 'multi', risk: 0, toWin: 0 } : state.parlay
      });
    case UPDATE_BET:
      if (action.payload.id === 'multi') {
        return Object.assign({}, state, {
          parlay: Object.assign({}, state.parlay, action.payload)
        });
      }

      const idToFind = action.oldOdd ? action.oldOdd.id : action.payload.id;
      return Object.assign({}, state, {
        odds: state.odds.map((odd: any) => {
          if (odd.id === idToFind) {
            return Object.assign({}, odd, action.payload);
          }
          return odd;
        })
      });
    case REMOVE_EVENT:
      if (state.odds.some((odd: any) => odd.event === action.payload)) {
        return Object.assign({}, state, {
          odds: state.odds.filter((odd: any) => odd.event !== action.payload)
        });
      }
      break;
    case BET_SLIP_ERROR:
      return Object.assign({}, state, {
        error: action.payload
      });
    case LIMITS:

      if (action.payload[0].id === 'multi') {
        return Object.assign({}, state, {
          parlay: includeLimits(action.payload, action.pct)(state.parlay)
        });
      }

      return Object.assign({}, state, {
        odds: state.odds.map(includeLimits(action.payload, action.pct))
      });
  }

  return state;
};

// ---
function includeLimits(limits: any, pct: any) {
  return (odd: any) => {
    let limit = limits.filter((l: any) => l.id === odd.id)[0];

    if (limit) {
      let { max, min, reduction, perLineBlock, priceBlock } = limit;

      if (pct > 0.00)
        max = Math.floor((max * pct) + 0.5) - (reduction || 0);
      if (max < 0.01)
        max = 0.00;

      min = Math.min(min, max) / 100;
      max /= 100;

      limit = { min, max, perLineBlock, priceBlock };
    }
    else {
      limit = { min: 0, max: 0 };
    }

    return Object.assign({}, odd, { limit });
  };
}
