import { IBasketFieldAnimationState, BasketTeam } from '../../constants';
import { deepAssign } from '../../../../../helpers';

const t1ScoredEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
  ball : {
    x: 88.5,
    y: 24,
    scale: 0.1
  },
  scoreText : {
    y: 15
  }
});

const t2ScoredEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
  ball : {
    x: 5.5,
    y: 24,
    scale: 0.1
  },
  scoreText : {
    y: 15
  }
});

export function scored(start: any, nextTeam: BasketTeam) {
  return {
    start,
    end: nextTeam === BasketTeam.Team1 ? t1ScoredEnd(start) : t2ScoredEnd(start)
  };
}
