import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../config';

import { isMobileOn, isMobileOff } from '../../actions/ui-toolbar';
import { openMenu, closeMenu } from '../../actions/menu';
import { changeBetType } from '../../actions/live';
import { openSettings } from '../../actions/settings';
import { detectmob } from '../views/live/live-helpers';

import Menu from './menu';
import Link from '../form/link';
import BetSlip from '../views/bet-slip';
import SwitchNew from './../misc/switch-new';

class Header extends React.Component<any, any> {
  state = {
    previousPage: ''
  };

  handleWindowClick = (e: any) => {
    const { dispatch, isMenuOpened } = this.props;
    const { classList } = e.target;

    if (!isMenuOpened) {
      if (classList && (classList.contains('menu-button') || classList.contains('i-menu') || classList.contains('i-back'))) {
        dispatch(openMenu());
      }
    }
    else {
      if (classList && !classList.contains('menu__left')) {
        dispatch(closeMenu());
      }
    }
  }

  componentWillReceiveProps(nextProps: any) {
    if (nextProps.pathState !== this.props.pathState) {
      this.setState({
        previousPage: this.props.pathState
      });
      this.getWidth();
    }
  }

  componentDidMount() {
    const { dispatch } = this.props;

    this.getWidth();
    window.addEventListener('resize', this.getWidth);
    window.addEventListener('click', this.handleWindowClick);
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.handleWindowClick);
    window.removeEventListener('resize', this.getWidth);
  }

  opSets = (props: any) => {
    return this.props.dispatch(openSettings(this.props.pathState));
  }

  getWidth = () => {
    const { dispatch } = this.props;
    return window.innerWidth < 760 || detectmob() ? dispatch(isMobileOn()) : dispatch(isMobileOff());
  }

  changeBet = () => {
    const { dispatch } = this.props;
    return this.props.dispatch(changeBetType(+!this.props.betType));
  }

  render() {
    const {
      view,
      path,
      betType,
      isMobileOn,
      isPrematch,
      parlayBySport,
      hasVideoOnFullScreen
    } = this.props;
    const className = view !== 3 && view !== 1 ? 'header' : 'header european';
    const topClassName = `${className}__top`;

    // To mimic a full-screen video view on mobile devices
    let headerStyles = hasVideoOnFullScreen ? {
      display: 'none'
    } : {};

    return (
      <header className={className} style={headerStyles}>
        <div className={topClassName}>
          <span className="menu-button">
            <i className="i i-menu"></i>
          </span>
          <Menu />
          <BetSlip />
          {/*
            config.hasPrematchLink && isMobileOn && (view === 3)  &&
            <PrematchLink
              linkClassName={''}
              linkTypeClassName={'prematch-link-mobile'}
            />
          */}
          {
            (config.hasParlay && parlayBySport) && isMobileOn && (view === 3)  &&
              <SwitchNew
                className_={'mobile'}
                checked_={betType === 1}
                leftText={'Straight'}
                rightText={'Parlays'}
                onToggle={() => this.changeBet()}
              />
          }
          { config.hasPrematchLink && isMobileOn && (view === 3)  &&
            <div className="bet-type-mobile">
              <Link
                activeClassName=""
                to={`${isPrematch ? '/' : '/prematch'}`}
              >
                <SwitchNew
                  className_={'mobile'}
                  checked_={isPrematch}
                  leftText={config.switchLiveText}
                  rightText={config.switchPrematchText}
                  onToggle={() => console.log('{}')}
                />
              </Link>
            </div>
          }
          {/* (config.hasParlay && parlayBySport) && isMobileOn && (view === 3)  &&
            <div className="bet-type-mobile">
              <TypeSwitcher />
            </div>
          */}
          <ul className="menu__right">
            <li title={this.props.settings} onClick={this.opSets} className="menu__item menu__link menu__link--ontop">
              <i className="i i-settings"></i>
            </li>
          </ul>
        </div>
      </header>
    );
  }
}

export default connect(
  (state: any, ownProps: { subPage: boolean }) => {
    const pathState = state.router.location.pathname;
    const path = pathState.slice(pathState.lastIndexOf('/') + 1, pathState.length);
    //
    return {
      path,
      pathState,
      subPage: ownProps.subPage,
      view: state.settings.view,
      betType: state.live.betType,
      isMenuOpened: state.menu.open,
      language: state.languageData.betSlip,
      currentEvent: state.live.currentEvent,
      isMobileOn: state.uiToolbar.isMobileOn,
      parlayBySport: state.live.parlayBySport,
      settings: state.languageData.menu.settings,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen,
      isPrematch: pathState.split('/').indexOf('prematch') > 0
    };
  }
)(Header);
