import * as React from 'react';
import { connect } from 'react-redux';

import RulePart from '../rule-part';
import RuleItem from '../rule-item';

const Nascar = ({ data }: any) => {

  const { title } = data;

  return (
    <RulePart title={title} model="nascar">
      {
        Object.keys(data)
          .filter(it => it !== 'title')
          .map(it => {
            return (
              <RuleItem key={it} title={data[it].title}>
                { data[it].text }
              </RuleItem>
            );
          })
      }
    </RulePart>
  );
};

export default connect(
  (state: any) => ({ data: state.languageData.rules.sportSpecific.nascar })
)(Nascar);
