document.addEventListener('click', onRippleEffect);

function onRippleEffect(e: MouseEvent) {
  if (e.button !== 0) {
    return;
  }

  const buttons = [].slice.call(document.querySelectorAll('.with-ripple'));
  let i = buttons.indexOf(e.target);

  if (i < 0) {
    i = buttons.indexOf((e.target as Node).parentElement);

    if (i < 0) {
      return;
    }
  }

  const btn = buttons[i] as HTMLButtonElement;
  if (btn.disabled) {
    return;
  }

  const ripple = document.createElement('div');
  ripple.id = 'ripple';
  ripple.addEventListener('transitionend', removeRipple);

  btn.appendChild(ripple);

  requestAnimationFrame(function rippleEffect() {
    const { left, top, width, height } = btn.getBoundingClientRect();
    const size = `${Math.max(width, height) * 2.25}px`;

    const bgColor = getComputedStyle(btn).backgroundColor.match(/\d{1,3}/g);

    if (bgColor) {
      const [r, g, b] = bgColor.map(Number).slice(0, 3);
      const isDark = (r * 0.2126 + g * 0.7152 + b * 0.0722) < 128;

      ripple.style.width = ripple.style.height = size;
      ripple.style.left = (e.clientX - left - (ripple.offsetWidth / 2)) + 'px';
      ripple.style.top = (e.clientY - top - (ripple.offsetHeight / 2)) + 'px';

      ripple.classList.add(isDark ? 'light' : 'dark');
      ripple.classList.add('transition');
    }
  });

  function removeRipple(ev: Event) {
    ripple.parentNode.removeChild(ripple);
  }
}
