import * as React from 'react';
import { connect } from 'react-redux';
import { getMainOutcomesBySport, getPlaceholderByOutcome, BetType } from '../../../constants';
import { mainOddsWithOdds, prematchOddsWithOdds } from './../../../reducers/outcomes-flatten';

import Odd from './odd';

let className = 'live-event';

const OutcomesSport = ({ event, betType, parlayBySport, user, reducedOutcomes }: any) => {

  const { home, away, sport } = event;

  if (!user.bookmakerId) return <div></div>;

  // let bookmakerId = user.bookmakerId[sport];
  let newOutcomes = reducedOutcomes;

  return (
    <div className={`${className}__content`}>
    {
      getMainOutcomesBySport(sport)
        .filter(outcomeId => betType === BetType.straight || parlayBySport[sport].indexOf(outcomeId) !== -1)
        .map(outcomeId => {
          const oddClassList = [`${className}__odd`];
          const otc = newOutcomes && newOutcomes[outcomeId];
          let outcome: any[] = getPlaceholderByOutcome(outcomeId, home, away);

          if (otc) {
            const otcByColumn = (
              Object
                .keys(otc)
                .reduce((arr, id) => {
                  arr[otc[id].column] = Object.assign({}, otc[id], {
                    id,
                    outcome: outcomeId,
                    event: event.id
                  });

                  return arr;
                }, [])
                .filter(Boolean)
            );

            outcome.forEach((odd, i) => outcome[i] = otcByColumn[i] || odd);
            }

          return (
            <div className={`${className}__outcome`} key={outcomeId}>
              {
                outcome
                  .map((odd, i): (any|void) => {
                    if (!otc || (!odd.hasOwnProperty('suspended') || odd.suspended)) {
                      oddClassList.push('suspended');
                    }

                    return (
                      <Odd
                        index={i * 7}
                        key={odd.id || i}
                        className={oddClassList.join(' ')}
                        odd={odd}
                        ticker={false}
                        sport={sport}
                        isGrid={false}
                        isSingleEvent={false}
                      />
                    );

                })
              }

            </div>

          );
        })
    }
  </div>
  );

};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    user: state.user,
    betType: state.live.betType,
    parlayBySport: state.live.parlayBySport,
    reducedOutcomes: ownProps.event.live === 0 ? prematchOddsWithOdds(state)[ownProps.event.id] : mainOddsWithOdds(state)[ownProps.event.id] && mainOddsWithOdds(state)[ownProps.event.id][state.user.bookmakerId[ownProps.event.sport]]
  })
)(OutcomesSport);
