import { IBaseBallFieldAnimationState, BaseBallAction, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

function next(nextAction: BaseBallAction) {
  let pitchSpotX, pitchSpotY, walk;

  walk = nextAction === BaseBallAction.Walk ? (pitchSpotX = 108, pitchSpotY = 50) : (pitchSpotX = 100.5, pitchSpotY = 59);

  return {
    pitchSpot : {
      x: pitchSpotX,
      y: pitchSpotY,
      opacity: 0
    }
  };
}

function finish() {
  return {
    actionText: {
      y: 18
    },
    pitchSpot: {
      opacity: 1
    },
    diamond: {
      opacity: 1
    }
  };
}

const walkStart: IBaseBallFieldAnimationState = deepAssign({}, nt, next(BaseBallAction.Walk), {});
const walkEnd: IBaseBallFieldAnimationState = deepAssign({}, nt, next(BaseBallAction.Walk), finish(), {});

const scoredStart: IBaseBallFieldAnimationState = deepAssign({}, nt, next(BaseBallAction.RunScored), {});
const scoredEnd: IBaseBallFieldAnimationState = deepAssign({}, nt, next(BaseBallAction.RunScored), finish(), {});

export function walk(state: IBaseBallFieldAnimationState, nextAction: BaseBallAction) {
  let start: IBaseBallFieldAnimationState;
  let end: IBaseBallFieldAnimationState;

  if (nextAction === BaseBallAction.Walk || nextAction === BaseBallAction.IntentionalWalk || nextAction === BaseBallAction.HitByPitch) {
    start = walkStart;
    end = walkEnd;
  }

  if (nextAction === BaseBallAction.RunScored) {
    start = scoredStart;
    end = scoredEnd;
  }

  return [{
    start,
    end
  }];
}
