import * as React from 'react';
import LeagueCard from './league-card';
import { connect } from 'react-redux';
import PrematchDateHeader from './../../../../misc/prematch-date-header';

class LeagueNew extends React.Component<any, any> {

  render() {
    const { /*view,*/ events, getGamecast, outcomeList, subscribedEvent, isDisplaySmall, betType, parlayBySport, isPrematch, userMainOutcomes } = this.props;
    const { sport } = events[0];
    const { bookmakerId } = this.props.user;

    if (!outcomeList) return <div></div>;

    let outcomeLists: any;
    if (userMainOutcomes[sport] !== undefined) {
      // console.log(outcomeList);
      outcomeLists = outcomeList.filter((odd: any, i: any) => userMainOutcomes[sport].indexOf(odd.id) !== - 1 );
      // console.log(userMainOutcomes[sport]);
      outcomeLists = userMainOutcomes[sport].map((item: any) => {
        for (let j = 0; j < outcomeLists.length; j++) {
          if (item === outcomeLists[j].id) {
            return outcomeLists[j];
          }
        }
      }).filter((item: any) => item !== '' && item !== undefined);

    } else {
      outcomeLists = outcomeList.filter((outcome: any, i: any) => i <= 2);
    }
    // console.log(outcomeLists);

    const titlesClass = isDisplaySmall ? '' : 'odd-table-titles-desktop';

    // log on console if there are odds not registered on outcomelist
    let outcomeList_ = outcomeList.map((item: any) => item.id);
    if (userMainOutcomes && userMainOutcomes[sport]) {
      userMainOutcomes[sport].forEach((item: any) => {
        if (outcomeList_.indexOf(item) < 0) {
          console.log(`marketId ${item} is being sent by backend but it's not visible on UI because it's title is not registered on outcomeList for sport ${sport}.`);
          // console.log(outcomeList);
        }
      });
    }

    if (betType === 1) {
      outcomeLists = outcomeLists
        .filter((odd: any, i: any) => parlayBySport[sport].indexOf(odd.id) !== - 1)
        .filter((odd: any, i: any) => i <= 2);
    }

    const style_ = isPrematch ? { borderBottom: 'solid 1px #313131' } : {};
    const styleBox = isPrematch && isDisplaySmall ? {padding: '5px 0px'} : {};

    // Some outcome titles on cricket have variables inside
    // we trim title before printing
    const trimTitle = (title: string) => {
      if (!title.includes('{')) return title;
      let idx = title.indexOf('{');
      return title.substring(0, idx);
    };

    return (
      <div className="box live" style={styleBox}>
        <table className="league-title" >
          {
            events &&
            events[0] &&
            events[0].sport === 35 ?
            <tbody className={titlesClass}>
              <tr>
                <th>{ events[0].league.toUpperCase() }</th>
              </tr>
            </tbody> :
            <tbody className={titlesClass}>
              <tr>
                <th>{ events[0].league.toUpperCase() }</th>
                {
                  (!isDisplaySmall && events[0].sport !== 35) &&
                  outcomeLists.map((item: any, i: any)  => (
                      <th key={i} className="odd-title">{trimTitle(item.title)}</th>
                  ))
                }
                {
                  !isDisplaySmall &&
                  outcomeLists.length <= 2 &&
                  <th></th>
                }
                <th></th>
              </tr>
            </tbody>
          }
        </table>
        <div className="inplay-events" style={style_}>
          {
            events.sort((a: any, b: any) => a.startdate - b.startdate).map((ev: any, i: any) => {

              // If ev is prematch...
              if (ev && ev.live === 0) {
                return (
                  <div key={i}>
                    {
                      ev &&
                      ev.live === 0 &&
                        <PrematchDateHeader events={events} ev={ev} i={i} view={`european`}/>
                    }
                    <LeagueCard
                      event={ev}

                      isDisplaySmall={isDisplaySmall}
                      getGamecast={getGamecast}
                      subscribedEvent={subscribedEvent}
                      outcomeList={outcomeList}
                      bookmakerId={this.props.user && ev.sport && this.props.user.bookmakerId[ev.sport]}
                    />
                  </div>
                );
              }

              return (
                <LeagueCard
                  key={i}
                  event={ev}
                  getGamecast={getGamecast}
                  outcomeList={outcomeList}
                  isDisplaySmall={isDisplaySmall}
                  subscribedEvent={subscribedEvent}
                  bookmakerId={this.props.user && ev.sport && this.props.user.bookmakerId[ev.sport]}
                />
              );
            })
          }
        </div>
      </div>
    );
  }
}

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    betType: state.live.betType,
    parlayBySport: state.live.parlayBySport,
    user: state.user
  })
)(LeagueNew);
