import * as React from 'react';
import { connect } from 'react-redux';

import { homeAwayToLink } from '../../../helpers';

import GameClock from './game-clock';
import Link from '../../../components/form/link';
import Time, { TimeFormat } from '../../../components/form/time';
import MarketCounter from './../../../components/misc/market-counter';

import { GetSportName } from '../../../constants';

const className = 'live-event';

const LinkEvent = ({ event, path, score }: any) => {
  const { home, away, sep } = event;
  const { time } = event;
  // const to = `/live/${path.split('/')[path.split('/').length - 1]}/${homeAwayToLink(home, away)}`;
  const { scoreTeam1, scoreTeam2, period }: any = score || {};
  const sportName = GetSportName(event.sport);
  const isPrematch = event.live === 0;

  const to = isPrematch ? `/prematch/${sportName}/${event.id}/${homeAwayToLink(home, away)}` :
  `/live/${sportName}/${homeAwayToLink(home, away)}`;

  const teamNames = event.inverted ? [away, home] : [home, away];
  const teamScores = event.inverted ? [scoreTeam1, scoreTeam2] : [scoreTeam1, scoreTeam2];

  const ClockComp: any = () => isPrematch ? <span></span> :
  <GameClock period={period} event={event} className={`${className}__game-clock`}/>;

  const teamMisc = [
    <ClockComp />,
    <Time className={`${className}__time`} format={TimeFormat.Time}>{ time * 1000 }</Time>,
    <MarketCounter event={event} to={to} isSmallDisplay={true}/>
  ];

  const makeScores = (classStr: string, arr: any) => {

    const isScores = classStr === `scores`;
    const isMisc = classStr === `clocks`;
    const liClass = isScores ? `scores-classic ${sportName}` : '';

    const styleTeams = {
      flexGrow: 1
    };

    let styleTeamsA = {
      listStyle: 'none', margin: '0', paddingLeft: '0'
    };

    if (isScores) {
      styleTeamsA.paddingLeft = '0';
    }
    if (isMisc) {
      styleTeamsA.paddingLeft = '15px';
    }

    return (
      <div className={classStr} style={!isScores && !isMisc ? styleTeams : {}}>
        <ul style={styleTeamsA}>
          {
            arr.map((item: any, i: number) => {
              let margVal = i === 0 || isScores || isMisc ? 2 : 8;

              if (item === undefined) {
                return '';
              }
              return (
                <li key={i} style={{margin: `${margVal}px 0`}}
                    className={liClass}>{item}</li>
              );
            })
          }
        </ul>
      </div>
    );
  };

  return (
    <Link className={`${className}__link`} to={to} title="click to view more odds">
      <div style={{ display: 'flex', width: '100%' }}>
        {makeScores('teams', teamNames)}
        {!isPrematch && makeScores(`scores`, teamScores) }
        { makeScores('clocks', teamMisc) }
      </div>

    </Link>
  );
};

export default connect(
  (state: any, initialOwnProps: any) => Object.assign({}, initialOwnProps, {
    path: state.router.location.pathname,
    score: state.live.scores[initialOwnProps.event.id],
    clock: state.live.clocks[initialOwnProps.event.id]
  })
)(LinkEvent);
