import { IHockeyFieldAnimationState, IceHockeyTeam, t1, t2 } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1GateStart: IHockeyFieldAnimationState = t1;
const t1GateEnd: IHockeyFieldAnimationState = deepAssign({}, t1, {
  gate: {
    y: 0
  }
});

const t2GateStart: IHockeyFieldAnimationState = t2;
const t2GateEnd: IHockeyFieldAnimationState = deepAssign({}, t2, {
  gate: {
    y: 0,
    scaleX: -1
  }
});

export function hockeyGate(nextTeam: IceHockeyTeam) {
  return {
    start: nextTeam === IceHockeyTeam.Team1 ? t1GateStart : t2GateStart,
    end: nextTeam === IceHockeyTeam.Team1 ? t1GateEnd : t2GateEnd
  };
}
