import * as React from 'react';
import { connect } from 'react-redux';

import { OddsFormat } from '../../constants';

const Price = ({ className, odd, oddsFormat }: any) => (
  <span className={className}>
    { odd && oddsFormat === OddsFormat.american ? odd.american.slice(0, 1) + Math.round(odd.american.slice(1)) : odd && odd.price.toFixed(2) }
  </span>
);

export default connect(
  (state: any, ownProps: any) => ({
    className: ownProps.className,
    odd: ownProps.children,
    oddsFormat: state.settings.oddsFormat
  })
)(Price);
