import { IBaseBallFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const strikeStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, nt, {
  pitcherStart: {
    visibility: 'block'
  },
  pitcherTwo: {
    display: 'block'
  },
  batterTwo: {
    display: 'block'
  },
  ball: {
      scale: 0
  }
});

const strikeEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, nt, {
  actionText: {
    y: 18
  },
  pitchSpot: {
    x: 100,
    opacity: 1
  },
  pitcherStart: {
    display: 'none'
  },
  pitcherEnd: {
    display: 'block'
  },
  batterStart: {
    display: 'none'
  },
  batterEnd: {
    display: 'block'
  },
  ball: {
    x: 100,
    y: 51,
    scale: 2
  }

});

export function strike(start: any) {
  return {
    start: strikeStart(start),
    end: strikeEnd(start)
  };
}
