import * as React from 'react';
import { connect } from 'react-redux';

import { getRules } from '../../actions/settings';

import Title from '../title';
import General from './rules/general';
import OfferTypes from './rules/offer-types';
import Parlay from './rules/parlay';
import Sports from './rules/sports-specific';

class Rules extends React.Component<any, any> {
  static getInitialData(dispatch: any, props: any, cookies: any) {
    return dispatch(getRules(cookies));
  }

  loadRules = () => {
    const { dispatch, rulesLoaded } = this.props;

    if (!rulesLoaded) {
      dispatch(getRules());
    }
  }

  componentDidMount() {
    requestAnimationFrame(this.loadRules);
  }

  render() {
    const { title, rulesLoaded } = this.props;
    let children;
    console.log('rules has loaded');
    if (rulesLoaded) {
      children = (
        <div className="md-container">
          <General />
          <OfferTypes />
          <Parlay />
          <Sports />
        </div>
      );
    }

    return (
      <Title title={title}>
        {children}
      </Title>
    );
  }
}

export default connect(
  (state: any) => ({ title: state.languageData.menu.rules, rulesLoaded: !!state.languageData.rules })
)(Rules);
