import { IBaseBallFieldAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const strikeStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, nt, {
  pitchSpot: {
    x: 135,
    opacity: 0
  },
   pitcherEnd: {
    display: 'none'
  },
  pitcherTwo: {
    display: 'block'
  },
  ball: {
      scale: 0
  }

});

const strikeEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, nt, {
  actionText: {
    y: 18
  },
  pitchSpot: {
    x: 135,
    opacity: 1
  },
   pitcherStart : {
    display: 'none'
  },
  pitcherEnd: {
    display: 'block'
  },
   ball: {
    x: 135,
    y: 51,
    scale: 2
  }

});

export function ball(start: any) {
  return {
    start: strikeStart(start),
    end: strikeEnd(start)
  };
}
