import * as React from 'react';
import { connect } from 'react-redux';

import HorizontalScrollableContainer from '../misc/horizontal-scrollable-container';
import SportsList from './sports-list';
import SportsItem from './sports-item';

// import { detectmob } from '../views/live/ui-toolbar/constants';

const DocumentTitle = require('react-document-title');

const Title = ({ title = 'Home', sportsList, uiView, hasVideoOnFullScreen }: any) => {

  const isClassicAmerican = uiView !== 3 && uiView !== 1;
  const classList = isClassicAmerican ? 'sports' : 'sports-european';
  const contList = isClassicAmerican ? 'sports__container' : 'sports-european__container-european';

  let hideSportsBar = title.split('/')
    .filter((item: any) => item === 'prematch' || item === 'rules' || item === 'schedule' || item === 'tickets')[0];

  const titleStyles = hasVideoOnFullScreen ? {
    display: 'none'
  } : {};

  return (
    <DocumentTitle title={ `${title} | Sports Betting` }>
      { sportsList && !hideSportsBar ?
        <div className={classList} style={titleStyles}>
          <HorizontalScrollableContainer style={contList}>
            <SportsList />
          </HorizontalScrollableContainer>
          <div className="mobList">
            <SportsList />
          </div>
        </div> :
        <div></div>
      }
    </DocumentTitle>
  );
};

export default connect(
  (state: any) => ({
    uiView: state.settings.view,
    title: state.router.location.pathname,
    sportsList: state.uiToolbar.show_sports_list,
    hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
  })

)(Title);
