import { IBaseBallFieldAnimationState } from '../constants';
import { deepAssign } from '../../../../helpers';

const strikeStart = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, {
  pitcherEnd: {
    display: 'none'
  },
  pitcherTwo: {
    display: 'block'
  }
});

const strikeEnd = (start: any): IBaseBallFieldAnimationState => deepAssign({}, start, {
  actionText: {
    y: 18
  },
   pitcherStart : {
    display: 'none'
  },
  pitcherTwo: {
    display: 'none'
  },
  pitcherEnd: {
    display: 'block'
  },
  batterStart: {
    display: 'none'
  },
  batterEnd: {
    display: 'block'
  },
   extras: {
    opacity: 1
  }

});

export function homeRun(start: any) {
  return {
    start: strikeStart(start),
    end: strikeEnd(start)
  };
}
