import * as React from 'react';

interface IRuleItemProps {
  title: string;
  children?: any;
}

const RuleItem = ({ title, children }: IRuleItemProps) => {
  let data = children;

  if (typeof children === 'string') {
    data = <p>{data}</p>;
  }

  return (
    <li>
      <h3>{title}</h3>
      {data}
    </li>
  );
};

export default RuleItem;
