import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../../config';
import * as VideoReact from 'video-react';
import HLSSource from './HLSSource';
import NoVideo from './no-video';
import AFK from './AFK';

import TwitchPlayer from './TwitchPlayer';

import {
  killedSession,
  lastInteraction,
  nativeFullScreen,
  toggleFakeFullScreen,
  redirectToNotLoggedInPage
} from './../../../actions/user';

class VideoPlayer extends React.Component<any, any> {

  private player: any = '';
  private streamInterval: any = null;

  constructor(props: any) {
    super(props);

    this.state = {
      streamDuration: 0,
      streamInterval: null,
      isVideoPlaying: false,
      idleStreamCountdown: null,
      mockInteractionInterval: null,
      idleStreamCounter: config.videoAfkPopUpEndSessionInterval / 1000
    };

    this.toggleModal = this.toggleModal.bind(this);
    this.keepWatching = this.keepWatching.bind(this);
    this.userInteraction = this.userInteraction.bind(this);
    this.toggleFakeFullScreen = this.toggleFakeFullScreen.bind(this);
    this.startMockInteractionInterval = this.startMockInteractionInterval.bind(this);
  }

  componentDidMount() {
    window.addEventListener('click', this.userInteraction);

    // subscribe player's state change
    console.log(typeof this.player.subscribeToStateChange);
    if (typeof this.player.subscribeToStateChange !== 'function') return;

    this.player.subscribeToStateChange(this.handleStateChange.bind(this));

  }

  handleStateChange(state: any) {
    // copy player state to this component's state
    this.setState({
      player: state
    });
  }

  userInteraction() {

    // Reset streamDuration in idle counter
    this.setState({ streamDuration: 0 });
    if (this.state.player && this.state.player.isModalOpen) {
      this.toggleModal();
    }
  }

  startMockInteractionInterval() {

    let that = this;
    console.log('start mock interaction interval');
    let mockInteractionInterval = setInterval(() => {
      let time = Date.now();
      that.props.dispatch(lastInteraction(time));
    }, 45000);

    this.setState({ mockInteractionInterval });

  }

  componentDidUpdate(prevProps: any, prevState: any) {

    if (prevState.player) {
      if (this.props.hasNativeFullScreen !== this.state.player.isFullscreen) {
        this.props.dispatch(nativeFullScreen(this.state.player.isFullscreen));
      }
    }

  }

  componentWillReceiveProps(newProps: any) {

    if (!newProps.event || !this.props.event || !this.player) return;

    const { hasStarted, paused } = this.player.getState().player;

    let videoUrlHasChanged = (newProps.event.id === this.props.event.id) && (newProps.event.videoUrl !== this.props.event.videoUrl);

    if (videoUrlHasChanged) {
      console.log('change the video stream');
      console.log(this.player);

      // this.player.load();
      // resetVideoStream();
    }

    // Start stream idle interval
    if (!this.state.isVideoPlaying && (hasStarted && !paused)) {
      this.setState({ isVideoPlaying: true });
      console.log('start interval');
      this.watchingStreamInterval();
    }

    // Stop stream idle interval
    if (this.state.isVideoPlaying && paused) {
      console.log('stop interval');
      this.setState({ isVideoPlaying: false });
      clearInterval(this.state.streamInterval);
      clearInterval(this.state.mockInteractionInterval);
    }

  }

  componentWillUnmount() {
    if (this.state.streamInterval !== null) {
      console.log('stop interval::');
      clearInterval(this.state.streamInterval);
    }

    if (this.state.mockInteractionInterval) {
      console.log('stop mock interaction interval::');
      clearInterval(this.state.mockInteractionInterval);
    }

    window.removeEventListener('click', this.userInteraction);
  }

  watchingStreamInterval() {

    let that = this;
    that.startMockInteractionInterval();

    let streamInterval = setInterval(() => {

      that.setState({ streamDuration: that.state.streamDuration + 1});

      // Show AFK Pop Up
      if (that.state.streamDuration * 1000 >= config.videoAfkPopUpInterval && (that.state.player && !that.state.player.isModalOpen)) {
        console.log('send idle on stream');

        this.player.handleModalToggle();

        let idleStreamCountdown = setInterval(() => {
          that.setState({ idleStreamCounter: that.state.idleStreamCounter - 1 });

          // Kill Session on AFK
          if (that.state.idleStreamCounter <= 0) {

            if (that.props.hasVideoOnFullScreen) {
              that.toggleFakeFullScreen();
            }

            that.props.wsm.socket.disconnect();
            that.props.dispatch(killedSession(true));
            that.props.dispatch(redirectToNotLoggedInPage());
            clearInterval(idleStreamCountdown);
          }
        }, 1000);
        that.setState({ idleStreamCountdown });

      }
      console.log(that.state.streamDuration, 'stream duration');
      console.log(config.videoAfkPopUpInterval, 'config.afkPopUpInterval');
    }, 1000);

    this.setState({  streamInterval  });
  }

  toggleModal() {
    this.player.handleModalToggle();
    this.keepWatching();
  }

  keepWatching() {
    console.log('keep watching');
    this.setState({idleStreamCounter: config.videoAfkPopUpEndSessionInterval / 1000});
    clearInterval(this.state.idleStreamCountdown);
  }

  toggleFakeFullScreen() {
    this.props.dispatch(toggleFakeFullScreen());
    console.log('toggleFakeFullScreen');
  }

  render() {
    const { event, user, hasVideoOnFullScreen } = this.props;
    let balance = user && typeof user.balance_real === 'number' ?
    user.balance_real : Number(user.balance_real);

    let isModernBrowser = user && user.isModernBrowser;

    if (!isModernBrowser || balance <= 0) {
      return (
        <NoVideo
          balance={balance}
          isModernBrowser={ isModernBrowser }
        />
      );
    }

    const vr: any = VideoReact;

    const Player = vr[Object.keys(VideoReact)[0]];

    let src_ = this.props.sources && this.props.sources[0] && this.props.sources[0].src;

    let modalChildren = [
      <AFK
        key="afk"
        buttonText="Keep Watching"
        modalText={`Are You Still Watching Our Stream? Otherwise Your Session Will Expire in ${this.state.idleStreamCounter} seconds.`}
        handleClick={this.toggleModal}
      />
    ];

    const outerClass = ['video-wrapper-outer'];
    const innerClass = ['video-wrapper-inner'];

    if (hasVideoOnFullScreen) {
      [outerClass, innerClass].forEach((item: any) => {
        item.push('full-screen-video');
      });
    }

    const getChannel = src_ && src_.split('/')[src_.split('/').length - 1];
    let isTwitch = typeof getChannel === 'string' && event.videoSource === 2 || event.videoSource === '2';

    return (
      <div className={outerClass.join(' ')}>
        <div className={innerClass.join(' ')}>
          { !isTwitch ?
            <Player
              ref={(player: any) => { this.player = player; }}
              hasModal={this.state.player && this.state.player.isModalOpen}
              modalChildren={modalChildren}
            >
              <HLSSource
                isVideoChild
                src={src_}
              />
            </Player> :
            <div className="embed-responsive embed-responsive-16by9">
              <div id="tw-player" className="embed-responsive-item">
                <TwitchPlayer
                  width="100%"
                  height="100%"
                  layout="video"
                  targetID="tw-playe"
                  autoplay={false}
                  channel={getChannel.replace('?', '')}
                />
              </div>
            </div>
          }
        </div>
      </div>
    );
  }
}

export default connect(
  (state: any) => {
    return {
      user: state.user,
      wsm: state.wsm,
      idleOnStream: state.user.isIdleOnStream,
      hasNativeFullScreen: state.user.hasNativeFullScreen,
      hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
    };
  })(VideoPlayer);
