import { IBasketFieldAnimationState, BasketTeam, BasketballAction } from '../constants';
import { shoot } from './shoot';
import { scored } from './shoot/scored';
import { missed } from './shoot/missed';

function next(nextTeam: BasketTeam) {
  const isTeam1 = nextTeam === BasketTeam.Team1;

  return shoot({
    actionText: {
      x: isTeam1 ? 64 : 30
    },
    shootSpot: {
      x: isTeam1 ? 75 : 19,
      y: isTeam1 ? 15 : 35
    },
    twoPointPath: {
      x: isTeam1 ? 94 : 0
    },
    ball: {
      x: isTeam1 ? 75 : 19,
      y: isTeam1 ? 15 : 35
    }
  }, nextTeam);
}

const t1TwoPointShoot = next(BasketTeam.Team1);
const t2TwoPointShoot = next(BasketTeam.Team2);

export function twoPoints(state: IBasketFieldAnimationState, nextTeam: BasketTeam, nextAction: BasketballAction): (any|void) {
  const tpShoot = nextTeam === BasketTeam.Team1 ? t1TwoPointShoot : t2TwoPointShoot;

  switch (nextAction) {
    case BasketballAction.TwoPointShootScored:
      return [
        tpShoot,
        scored(tpShoot.end, nextTeam)
      ];
    case BasketballAction.TwoPointShootMissed:
      return [
        tpShoot,
        missed(tpShoot.end, nextTeam)
      ];
  }
}
