import {
  CHANGE_LAST_SUBSCRIPTION_MESSAGE
} from '../actions/user';

export const subscriptionCacheInitialState: any = {
  subscriptionMsg: ''
};

export const subscriptionCache = (state = subscriptionCacheInitialState, action: any): any => {
  switch (action.type) {
    case CHANGE_LAST_SUBSCRIPTION_MESSAGE:
      console.log(`Update subscriptionCache.subscriptionMsg to: `, action.payload);
      return Object.assign({}, state, {
        subscriptionMsg: action.payload
      });
  }
  return state;
};
