export function actionTextBasket(action: BasketballAction, data?: any) {
  let addText;

  if (action !== BasketballAction.EndOfHalf) {
    // console.log(data, data);
    switch (data) {
    case '1':
      addText = 'First';
      break;
    case '2':
      addText = 'Second';
      break;
    case '3':
      addText = 'Third';
      break;
    case '4':
      addText = 'Fourth';
      break;
    case '1:1':
      addText = '1 of 1';
      break;
    case '1:2':
      addText = '1 of 2';
      break;
    case '2:2':
      addText = '2 of 2';
      break;
    case '1:3':
      addText = '1 of 3';
      break;
    case '2:3':
      addText = '2 of 3';
      break;
    case '3:3':
      addText = '3 of 3';
      break;
    default:
      addText = '';
    }
  }

  switch (action) {
      case BasketballAction.None:
      return 'Upcoming Game';
    case BasketballAction.Possession:
      return 'In Possession';
    case BasketballAction.TimeOut:
      return 'Time Out';
    case BasketballAction.Foul:
      return 'Foul';
    case BasketballAction.FreeThrow:
      return 'Free Throw ' + addText;
    case BasketballAction.FreeThrowScored:
      return 'Free Throw Scored';
    case BasketballAction.FreeThrowMissed:
      return 'Free Throw Missed';
    case BasketballAction.TwoPointShootScored:
      return 'Two Point Shot Scored';
    case BasketballAction.TwoPointShootMissed:
      return 'Two Point Shot Missed';
    case BasketballAction.ThreePointShootScored:
      return 'Three Point Shot Scored';
    case BasketballAction.ThreePointShootMissed:
      return 'Three Point Shot Missed';
    case BasketballAction.Quarter1:
      return 'First Quarter';
    case BasketballAction.Quarter2:
      return 'Second Quarter';
    case BasketballAction.Quarter3:
      return 'Third Quarter';
    case BasketballAction.Quarter4:
      return 'Fourth Quarter';
    case BasketballAction.Half1:
      return 'First Half';
    case BasketballAction.Half2:
      return 'Second Half';
    case BasketballAction.EndOfQuarter:
      return 'End of ' + addText + ' Quarter';
    case BasketballAction.EndOfHalf:
      return 'End of Half';
    case BasketballAction.EndOfMatch:
      return 'End of Match';
    case BasketballAction.OverTime:
      return 'Over Time';
  }

  return '';
}
