import * as React from 'react';

import { Animation } from '../helpers';
import { TennisAction } from './constants';

import Ball from '../../svgs/tennis-icon';

const timeouts: any = [];
class Balls extends React.Component<any, any> {
  animation = new Animation(this);

  state = {
    animation: {
      ball1: {
        opacity: 0
      },
      ball2: {
        opacity: 0
      },
      ball3: {
        opacity: 0
      },
      ball4: {
        opacity: 0
      },
      ball5: {
        opacity: 0
      }
    }
  };

  animate = () => {
    for (let i = 1; i <= 5; i++) {
      timeouts.push(setTimeout(() => this.animation.start(`ball${i}`, { opacity: 0 }, { opacity: 1 }, 500), 200 * i));
      timeouts.push(setTimeout(() => this.animation.start(`ball${i}`, { opacity: 1 }, { opacity: 0 }, 500), 1000 + (200 * i)));
    }
    timeouts.push(setTimeout(this.animate, 2500));
  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    this.animation.cancel();
    timeouts.forEach((timeout: any) => clearTimeout(timeout));
    timeouts.length = 0;
  }

  render() {
    const { x, y, scaleX, scaleY, translateX, translateY, action } = this.props;
    const { animation } = this.state;
    const { ball1, ball2, ball3, ball4, ball5 } = animation;

    switch (action) {
      case TennisAction.Serving:
      case TennisAction.Fault:
      case TennisAction.LetFirstServe:
      case TennisAction.LetSecondServe:
        return (
          <svg x={x} y={y}>
            <g transform={`scale(${scaleX} ${scaleY}) translate(${translateX} ${translateY})`}>
              <Ball width="2.5" height="2.5" opacity={ball1.opacity} />
              <Ball width="2.5" height="2.5" x="2.2" y="2.6" opacity={ball2.opacity} />
              <Ball width="2.5" height="2.5" x="5.4" y="4.1" opacity={ball3.opacity} />
              <Ball width="2.5" height="2.5" x="8.7" y="5.2" opacity={ball4.opacity} />
              <Ball width="2.5" height="2.5" x="12.2" y="5.7" opacity={ball5.opacity} />
            </g>
          </svg>
        );
    }

    return <svg></svg>;
  }
}

export default Balls;
