// Polyfill for old IEs
Object.assign = require('object-assign');

import './ripple';
import config from '../config';

import * as React from 'react';
import { render as renderDOM, hydrate } from 'react-dom';

// import WebSocketManager from './web-socket-manager';

import SocketIOManager from './socket-io-manager';

import configureStore from '../common/store/configure-store';
import { makeSession, sessionPing } from '../common/actions/user';

// We use the server generated initial state on the client
const { initialState } = (window as any);
document.documentElement.lang = initialState.settings.language;

const iframeSizer = require('./iframe-sizer');
iframeSizer.init();

const socket = new SocketIOManager();

// const wsm = new WebSocketManager();
initialState.wsm = socket;

const { store, history } = configureStore(initialState);
// wsm.configureWebSocket(store);
socket.configureSocketManager(store);

if (!initialState.user.userid_internal) {
  requestAnimationFrame(() => store.dispatch(makeSession()));
  setInterval(function() {

    if (window.location.pathname.includes('expired')) return;

    store.dispatch(sessionPing());
  }, config.sessionPingIntervalMS);

}

const root = document.querySelector('main');

let render = () => {
    // We're requiring "Root" manually here because we want to get a fresh Root everytime we save a file
    const req = require('../common/components/root');
    const Root = req.hasOwnProperty('default') ? req.default : req;

    hydrate(
      <Root store={store} history={history}/>,
      root
    );
};

// We're simply creating a wrapper here to polyfill Intl when needed (IE10 and Safari < 9)
let renderApp = render;
render = () => {
  if (typeof Intl !== 'undefined') {
    renderApp();
  }
  else {
    (require as any).ensure(['./intl-polyfill'], (require: any) => {
      require('./intl-polyfill');
      renderApp();
    }, 'intl');
  }
};

// When in development, we're enabling Webpack's Hot Module Reload
if (process.env.NODE_ENV !== 'production' && module.hot) {
    // (window as any).Perf = require('react-addons-perf');
    let renderHot = render;

    const renderError = (error: any) => {
      renderDOM(
        <pre style={{
          position: 'fixed',
          top: 0,
          left: 0,
          margin: 0,
          width: '100vw',
          height: '100vh',
          backgroundColor: 'red',
          color: 'white'
        }}>{ error }</pre>,
        root
      );
    };

    render = () => {
      try {
        renderHot();
      }
      catch (err) {
        renderError(err);
      }
    };

    module.hot.accept('../common/components/root', () => {
      requestAnimationFrame(render);
    });
}

render();
