import { currencyCodes } from './currencyCodes';

export const currencySymbol = (code: any, amount: any) => {

  if (typeof code !== 'string') {
    return undefined;
  }

  code = code.toUpperCase();

  if (!currencyCodes.hasOwnProperty(code)) {
      return undefined;
  }

  // if (code === 'EUR') {
  //  return amount + currencyCodes[code];
  // }

  return currencyCodes[code] + amount;
};
