import * as React from 'react';
import SportsItem from './sports-item';
import { Sports } from './../../../constants';

import config from './../../../../config';
import { sortAlpha } from './sports-item';

import Link from './../../form/link';
import SwitchNew from './../../misc/switch-new';
import TypeSwitcher from './../live/type-switcher';

import { changeBetType } from '../../../actions/live';

let divElement: any;
let sportsContainer: any;

class SidebarSportsMenu extends React.Component<any, any> {

  constructor(props: any) {
    super(props);
    this.state = {
      headerHeight: 0,
      windowHeight: window.innerHeight,
      sportsHeight: 0,
      hasOverflow: false
    };
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    if (this.props.isDisplaySmall) {
      this.setState({ headerHeight: divElement.clientHeight });
      this.props.setRef(divElement.clientHeight);
    } else {
      this.setState({ sportsHeight: sportsContainer.clientHeight });
    }
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentDidUpdate() {

    if (this.props.isDisplaySmall) {
      if (divElement.clientHeight !== this.state.headerHeight) {
        this.setState({ headerHeight: divElement.clientHeight });
        this.props.setRef(divElement.clientHeight);
      }
    } else {
      if (sportsContainer.clientHeight !== this.state.sportsHeight) {
        this.setState({ sportsHeight: sportsContainer.clientHeight });
        this.setState({hasOverflow: sportsContainer.clientHeight > this.state.windowHeight });
      }
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {

    if (!this.props.isDisplaySmall) {
      this.setState({ windowHeight: window.innerHeight });
      this.setState({hasOverflow: sportsContainer.clientHeight > this.state.windowHeight });
    }
  }

  changeBet = () => {
    const { dispatch } = this.props;
    return dispatch(changeBetType(+!this.props.betType));
  }

  render() {
    const { view, betType, parlayBySport, isOpen, openHeader, activeLeague, prematchEvents, prematchLeagues, activeSport, activeCountry, getActiveSport, getActiveCountry, getActiveLeague, sports, isDisplaySmall } = this.props;

    const sportsTitle = isDisplaySmall && activeSport ? ` || ${sports[Sports[activeSport]]} || ${activeCountry !== -1 ? activeCountry : ''} || ${activeLeague}` : ' ';

    const isOverflow = !isDisplaySmall && this.state.hasOverflow;
    // console.log(`pass ${isOverflow}`);
    let alphaSortSports = prematchEvents && prematchEvents.filter((it: any) => it.home === undefined).map((item: any) => ({ ...item, name: sports[Sports[item.id]] }));
    alphaSortSports = sortAlpha(alphaSortSports, 'name');

    return(
      <div className={`sports-item-wrapper ${isOverflow ? 'scroll' : '' } `}>
      { !isDisplaySmall ?
        <div className="fake-background"></div> : <span></span>
      }
      <div style={{height: !isOpen && divElement && isDisplaySmall && `${divElement.clientHeight}px`}} className={`side-bar-menu ${isOpen ? 'active' : ''}`}>

        <div onClick={() => openHeader()} className="sports-header" ref={ (divElement_) => divElement = divElement_} >SPORTS<span style={{fontFamily: 'Ubuntu-Light'}}>{sportsTitle}</span><i className={`i i-select side-bar-button ${isOpen ? 'active' : ''}`} style={{float: 'right'}}></i></div>
        { ((config.hasParlay && parlayBySport) || config.hasPrematchLink) && !isOpen &&
          <div className={view === 3 ? `parlay-tablet` : `parlay-tablet classic`}>
              <div className="bet-type">
                { config.hasParlay && parlayBySport &&
                  <div className="bet-type-select">
                    <SwitchNew
                      className_={'prematch'}
                      checked_={betType === 1}
                      leftText={'Straight'}
                      rightText={'Parlays'}
                      onToggle={() => this.changeBet()}
                    />
                  </div>
                }
                {
                  config.hasPrematchLink &&
                  <div style={{top: '5px'}} className="prematch-link-desktop-cont prematch">
                    <Link
                      activeClassName=""
                      to={`/`}
                    >
                      <SwitchNew
                        className_={'prematch'}
                        checked_={true}
                        leftText={config.switchLiveText}
                        rightText={config.switchPrematchText}
                        onToggle={() => console.log('{}')}
                      />
                    </Link>
                  </div>

                }
              </div>
          </div>
        }

        <div ref={ (sportsContainer_) => sportsContainer = sportsContainer_} className="sports-item-container" style={{top: `${isDisplaySmall && divElement ? (divElement.clientHeight + 40) : 40}px`}}>
          {
            prematchEvents &&
            alphaSortSports.map((sport: any, i: any) =>
              <SportsItem
                key={i}
                sport={sport}
                sports={sports}
                activeSport={activeSport}
                activeCountry={activeCountry}
                getActiveSport={getActiveSport}
                prematchEvents={prematchEvents}
                prematchLeagues={prematchLeagues}
                getActiveLeague={getActiveLeague}
                getActiveCountry={getActiveCountry}
                headerHeight={this.state.headerHeight}
              />
            )
          }
        </div>

    </div>

    </div>
    );
  }
}

export default SidebarSportsMenu;
