import * as React from 'react';
import { detectmob } from './live-helpers';
import Time, { TimeFormat } from '../../../components/form/time';

import LinkEvent from './link-event';
import OutcomesSport from './outcomes-bysport';

const className = 'live-event';

const Event = ({ event, ticker, view }: any) => {
  const { time } = event;

  if (view === null) {
    return <div></div>;
  }

  return (
    <div className={className}>
      <div className={`${className}__title`}>
        <LinkEvent event={event} />
      </div>
      <OutcomesSport event={event} ticker={ticker}/>
    </div>
  );
};

export default Event;
