import * as React from 'react';
import { groupEventsByLeague} from './../../live-helpers';
import League from './league';
class AsianMobile extends React.Component<any, any> {

  render() {
    const { events, sport } = this.props;

    let data;

    if (!events) {
      return <div></div>;
    }

    let view = 1;
    if (events) {
      data = groupEventsByLeague(events, sport, view).map(eventsByLeague => (
        <League key={eventsByLeague[0].league} events={eventsByLeague} view={view}/>
      ));
    }

    return (
      <div>
        {data}
      </div>
    );
  }

}

export default AsianMobile;
