import { IBasketFieldAnimationState, BasketTeam, t1, t2 } from '../../constants';
import { deepAssign } from '../../../../../helpers';

const t1ShootStart = (start: any): IBasketFieldAnimationState => deepAssign({}, t1, start, {
  ball: {
    scale: 1
  }
});

const t2ShootStart = (start: any): IBasketFieldAnimationState => deepAssign({}, t2, start, {
  ball: {
    scale: 1
  }
});

export const t1ShootEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
   ball: {
    x: 86,
    y: 25,
    scale: 2.5
  }
});

export const t2ShootEnd = (start: any): IBasketFieldAnimationState => deepAssign({}, start, {
  ball: {
    x: 8,
    y: 25,
    scale: 2.5
  }
});

export function shoot(shootStart: any, nextTeam: BasketTeam) {
  const start = nextTeam === BasketTeam.Team1 ? t1ShootStart(shootStart) : t2ShootStart(shootStart);
  const end = nextTeam === BasketTeam.Team1 ? t1ShootEnd(start) : t2ShootEnd(start);

  return {
    start,
    end
  };
}
