import { ViewType } from './common/constants';
import * as appRootPath from 'app-root-path';

function buildConfig() {

  // Copy over environment variables (some are set by .env in the root)
  let environment: any = Object.assign({}, process.env);

  // Allow overrides via the command line arguments
  process.argv.forEach(argument => {

    let equalSignIndex = argument.indexOf('=');

    if (equalSignIndex >= 0) {
      let [key, val] = argument.split('=');
      environment[key] = val;
    }

  });

  if (!environment['VERSION']) {
    console.warn('Warning - Application version not found!');
  }

  let allowAnimations = true;
  if (environment['ALLOW_ANIMATIONS'] !== 'true' && environment['ALLOW_ANIMATIONS'] !== true)
    allowAnimations = false;

  let openSettingsDialog = false;
  if (environment['OPEN_SETTINGS_DIALOG'] === 'true' || environment['OPEN_SETTINGS_DIALOG'] === true)
    openSettingsDialog = true;

  let redirectPrematch = false;
  if (environment['REDIRECT_PREMATCH'] === 'true' || environment['REDIRECT_PREMATCH'] === true) {
    redirectPrematch = true;
    openSettingsDialog = false;
  }

  let isOriginator = true;
  if (environment['IS_ORIGINATOR'] !== 'true' && environment['IS_ORIGINATOR'] !== true)
    isOriginator = false;

  let hasParlay = false;
  if (environment['HAS_PARLAY'] === 'true' || environment['HAS_PARLAY'] === true)
    hasParlay = true;

  let hasLegacyLink = true;
  if (environment['HAS_LEGACY_LINK'] !== 'true' && environment['HAS_LEGACY_LINK'] !== true)
    hasLegacyLink = false;

  let hasPrematchLink = false;
  if (environment['HAS_PREMATCH_LINK'] === 'true' || environment['HAS_PREMATCH_LINK'] === true)
    hasPrematchLink = true;

  let hasTicketsLink = false;
  if (environment['HAS_TICKETS_LINK'] === 'true' || environment['HAS_TICKETS_LINK'] === true)
    hasTicketsLink = true;

  let hasMaxViewHeight = true;
  if (environment['HAS_MAX_VIEW_HEIGHT'] === 'false' || environment['HAS_MAX_VIEW_HEIGHT'] === false)
    hasMaxViewHeight = false;

  let hasVideoStream = false;
  if (environment['HAS_VIDEO_STREAM'] === 'true' || environment['HAS_VIDEO_STREAM'] === true)
    hasVideoStream = true;

  let footballGamecastAnimated = false;
  if (environment['FOOTBALL_GAMECAST_ANIMATED'] === 'true' || environment['FOOTBALL_GAMECAST_ANIMATED'] === true)
    footballGamecastAnimated = true;

  let hasAutoStreamPlay = false;
  if (environment['HAS_AUTO_STREAM_PLAY'] === 'true' || environment['HAS_AUTO_STREAM_PLAY'] === true)
    hasAutoStreamPlay = true;

  let hasWs = false;
  if (environment['HAS_WEBSOCKETS_CONNECTION'] === 'true' || environment['HAS_WEBSOCKETS_CONNECTION'] === true)
    hasWs = true;

  let maintenanceCheckerInterval = 60000;
  if (!isNaN(+environment['MAINTENANCE_CHECKER_INTERVAL']))
    maintenanceCheckerInterval = +environment['MAINTENANCE_CHECKER_INTERVAL'];

  return {
    version: environment['VERSION'],
    host: {
      name: environment['HOST_NAME'],
      port: environment['PORT'],
      httpServerPort: environment['HTTP_SERVER_PORT'],
      httpsServerPort: environment['HTTPS_SERVER_PORT'],
      trail: environment['HOST_TRAIL']
    },
    certPath: {
      crt: environment['CERT'],
      key: environment['CERT_KEY'],
      ca: environment['CERT_CA'] ? [environment['CERT_CA']] : []
    },
    hasWebSocketsConnection: hasWs,
    websockets: {
      ip: environment['SOCKET_ADDRESS']
    },
    php: environment['PHP'],
    frontEndID: parseInt(environment['FRONT_END_ID']),
    viewType: ViewType.european,
    betAPIEndpoint: environment['BET_ENDPOINT'],
    betSalt: environment['BET_HASH_SALT'],
    getTicketsAPIEndpoint: environment['GET_TICKETS_API_ENDPOINT'],
    authenticateAPIEndpoint: environment['AUTHENTICATE_API_ENDPOINT'],
    parlayMetaFileLocation: environment['PARLAY_META_FILE_LOCATION'],
    sessionInitAPIEndpoint: environment['SESSION_INIT_API_ENDPOINT'],
    sessionInitAPISalt: environment['SESSION_INIT_API_SALT'],
    sessionInitAPIGroup: environment['SESSION_INIT_API_GROUP'],
    sessionCSRFSalt: environment['SESSION_CSRF_SALT'],
    ticketHistoryAPIEndpoint: environment['TICKET_HISTORY_API_ENDPOINT'],
    sessionInitTSTolerance: environment['SESSION_INIT_TS_TOLERANCE'],
    redisSessionTTL: environment['REDIS_SESSION_TTL'],
    sessionPingIntervalMS: environment['SESSION_PING_INTERVAL_MS'],
    redisServerConnectionString: environment['REDIS_SERVER_CONNECTION_STRING'],
    theme: environment['THEME'] || false,
    betScreenshotDirectory: environment['BET_SCREENSHOT_DIRECTORY'] || appRootPath + '/logs/bet-screenshots/',
    sessionMonitorLogDirectory: environment['SESSION_MONITOR_LOG_DIRECTORY'] || appRootPath + '/logs/session-monitor-log/',
    sessionStartLogDirectory: environment['SESSION_START_LOG_DIRECTORY'] || appRootPath + '/logs/session-start-log/',
    placedBetsLogDirectory: environment['PLACED_BETS_LOG_DIRECTORY'] || appRootPath + '/logs/placed-bets-log/',
    webSocketsStartLogDirectory: environment['WEBSOCKETS_START_LOG_DIRECTORY'] || appRootPath + '/logs/websockets-start-log/',
    webSocketsNonlegitLogDirectory: environment['WEBSOCKETS_NONLEGIT_LOG_DIRECTORY'] || appRootPath + '/logs/websockets-nonlegit-log/',
    backEndConnectionLogDirectory: environment['BACKEND_CONNECTION_LOG_DIRECTORY'] || appRootPath + '/logs/backend-connection-log/',
    allowAnimations: allowAnimations,
    gameClockOffset: parseInt(environment['GAME_CLOCK_OFFSET']) || 0,
    eventDebugLog: environment['EVENT_DEBUG_LOG'] || '',
    openSettingsDialog: openSettingsDialog,
    maxAnimationQueueDifference: parseInt(environment['MAX_ANIMATION_QUEUE_DIFFERENCE']) || 15,
    isOriginator: isOriginator,
    originatorValue: environment['ORIGINATOR_VALUE'] || 'sports-betting-ui',
    legacyURLTemplate: environment['LEGACY_URL_TEMPLATE'],
    legacyUISalt: environment['LEGACY_UI_SALT'],
    hasLegacyLink: hasLegacyLink,
    hasPrematchLink: hasPrematchLink,
    userMainOutcomes: environment['USER_MAIN_OUTCOMES'],
    redirectPrematch,
    mockBookmaker: environment['MOCK_BOOKMAKER'] || 103,
    trackerServerAddress: environment['TRACKER_SERVER_ADDRESS'] || '0',
    trackerAppKey: environment['TRACKER_APP_KEY'] || '0',
    idleTimmer: environment['IDLE_TIMMER'] || 60000,
    onIdleExpire: environment['ON_IDLE_EXPIRE'] || 60000,
    setTimezone: environment['SET_TIMEZONE'] || '',
    hasTicketsLink,
    hasMaxViewHeight,
    hasParlay,
    backendHttpEndpoint: environment['BACKEND_HTTP_ENDPOINT'] || '',
    scheduleHttpEndpoint: environment['SCHEDULE_HTTP_ENDPOINT'] || '',
    liveOddsWebsocketUrl: environment['LIVE_ODDS_WEBSOCKET_URL'] || '',
    liveOddsUsername: environment['LIVE_ODDS_USERNAME'] || '',
    liveOddsPassword: environment['LIVE_ODDS_PASSWORD'] || '',
    prematchOddsWebsocketUrl: environment['PREMATCH_ODDS_WEBSOCKET_URL'] || '',
    prematchOddsUsername: environment['PREMATCH_ODDS_USERNAME'] || '',
    prematchOddsPassword: environment['PREMATCH_ODDS_PASSWORD'] || '',
    fileContainingGamecastWatchlist: environment['FILE_CONTAINING_GAMECAST_WATCHLIST'] || '',
    gamecastWatchlistLogFile: environment['GAMECAST_WATCHLIST_LOG_FILE'] || '',
    lottieAnimationsDirectory: environment['LOTTIE_ANIMATIONS_DIRECTORY'] || appRootPath + '/animations/lottie-animations/',
    liveAPIKey: environment['LIVE_API_KEY'] || '',
    prematchAPIKey: environment['PREMATCH_API_KEY'] || '',
    rabbitServerUrl: environment['RABBIT_SERVER_URL'] || 'amqp://127.0.0.1',
    customLeaguesDirectory: environment['CUSTOM_LEAGUES_DIRECTORY'] || appRootPath + '/custom_leagues/',
    hasVideoStream,
    videoAfkPopUpInterval: environment['VIDEO_AFK_POPUP_INTERVAL'] || 3600000,
    videoAfkPopUpEndSessionInterval: environment['VIDEO_AFK_POPUP_END_SESSION_INTERVAL'] || 10000,
    footballGamecast: environment['FOOTBALL_GAMECAST'] || 'modern',
    footballGamecastAnimated,
    hasAutoStreamPlay,
    switchLiveText: typeof environment['SWITCH_LIVE_TEXT'] === 'string' && environment['SWITCH_LIVE_TEXT'] !== '' ? environment['SWITCH_LIVE_TEXT'] : 'Live Wagering',
    switchPrematchText: typeof environment['SWITCH_PREMATCH_TEXT'] === 'string' && environment['SWITCH_PREMATCH_TEXT'] !== '' ? environment['SWITCH_PREMATCH_TEXT'] : 'Prematch Wagering',
    backendPingUrl: environment['BACKEND_PING_URL'] || '',
    maintenanceCheckerUrl: environment['MAINTENANCE_CHECKER_URL'] || '',
    maintenanceCheckerInterval
  };
}

let config: any = null;

// If this is client-side, the config will have been provided in the window object
// because environment variables aren't accessible in webpack.
if (typeof window !== 'undefined') {

  let window_: any = window;

  config = window_['config'];
}
else {
  config = buildConfig();
}

export default config;
