import { GET_PREMATCH, PREMATCH_LEAGUES, NEW_PREMATCH_EVENT, REMOVE_EVENT_PREMATCH, UPDATE_PREMATCH } from '../actions/prematch';
import { CLOSED_CONNECTION_PREMATCH } from '../actions/live';
import { UPDATE_PREMATCH_OUTCOME, ADD_PREMATCH_OUTCOME, DELETE_PREMATCH_OUTCOME } from '../actions/outcomes';

export const prematchInitialState: any = {
  events: [],
  leagues: [],
  outcomes: {},
  outcomeList: {},
  reducedOutcomes: {},
  userMainOutcomes: {},
  closedConnectionPrematch: true
};

export const prematch = (state = prematchInitialState, action: any): any => {

  let events;
  let leagues: any;

  switch (action.type) {
    case UPDATE_PREMATCH_OUTCOME:
      // console.log('update reducer');
      // console.log(state);
      // console.log(action.payload);
      let updatedOtcId = Object.keys(action.payload)[0];

      if (updatedOtcId && state.reducedOutcomes[updatedOtcId]) {
        let updateObj = action.payload[updatedOtcId];

        return {
          ...state,
          reducedOutcomes: {
            ...state.reducedOutcomes,
            [updatedOtcId]: {
              ...state.reducedOutcomes[updatedOtcId],
              price: updateObj.price || state.reducedOutcomes[updatedOtcId].price,
              american: updateObj.american || state.reducedOutcomes[updatedOtcId].american,
              marketPrice: updateObj.marketPrice || state.reducedOutcomes[updatedOtcId].marketPrice
            }
          }
        };
      }

      return state;

    case DELETE_PREMATCH_OUTCOME:
      // console.log('delete reducer');
      // console.log(state);
      // console.log(action.payload);
      let deletedOtcId = Object.keys(action.payload)[0];

      if (deletedOtcId && state.reducedOutcomes[deletedOtcId]) {
        let { [deletedOtcId]: deletePrematchOdd, ...restStatereducedOutcomes } = state.reducedOutcomes[deletedOtcId];

        return {
          ...state,
          reducedOutcomes: {
            ...state.reducedOutcomes,
            [deletedOtcId]: restStatereducedOutcomes
          }
        };

      }

      return state;
    case ADD_PREMATCH_OUTCOME:
        // console.log('add reducer');
        // console.log(state);
        // console.log(action.payload);
        if (state.leagues[0]) return state;
        let addOtcId = Object.keys(action.payload)[0];
        // console.log(addOtcId);

        // Check if the event for this outcome is part of the state

        let isEvent = state.leagues((ev: any) => ev.id === action.payload[addOtcId].event)[0];

        if (isEvent) {
          return {
            ...state,
            reducedOutcomes: {
              ...state.reducedOutcomes,
              [addOtcId]: action.payload[addOtcId]
            }
          };
        }
        return state;
    case CLOSED_CONNECTION_PREMATCH:

      if (!action.payload) {
        return {
          ...state,
          closedConnectionPrematch: action.payload
        };
      }

      return {
        ...state,
        events: [],
        leagues: [],
        outcomes: {},
        outcomeList: {},
        reducedOutcomes: {},
        userMainOutcomes: {},
        closedConnectionPrematch: action.payload
      };

    case UPDATE_PREMATCH:
      events = action.payload.events;
      // leagues = action.payload.leagues;
      return Object.assign({}, state, {
        events
        // leagues
      });
    case GET_PREMATCH:
      events = action.payload;
      return Object.assign({}, state, action.payload);
    case PREMATCH_LEAGUES:
      leagues = action.payload;
      return Object.assign({}, state, action.payload);
    case NEW_PREMATCH_EVENT:
      console.log('new event');

      if (state.closedConnectionPrematch) return state;

      leagues = state.leagues.slice();
      if (state.leagues[0] && action.payload.sport === state.leagues[0].sport)  {
        console.log('new event from same sport');
        leagues.push(action.payload);
      }

      let updateEventNumber = (stateEvents: any) => {
        console.log(action.payload);
        // console.log(stateEvents);
        // console.log(leagues);
        // Check if there is a sport for that event;

        let findIndexSport = stateEvents
          .findIndex((sport: any) => sport.id === action.payload.sport);

        if (findIndexSport >= 0) {
          // If there is a sport add one to the counter
          stateEvents[findIndexSport].qt = stateEvents[findIndexSport].qt + 1;
          // console.log('yes there is a sports');
          // console.log(stateEvents[findIndexSport]['leagues']);
          let findIndexCountry = stateEvents[findIndexSport]['leagues']
            .findIndex((leagues: any) => leagues.country === action.payload.countryname);

          if (findIndexCountry >= 0) {
            // console.log('yes there is a country');
            // console.log(stateEvents[findIndexSport]['leagues'][findIndexCountry]);
            stateEvents[findIndexSport]['leagues'][findIndexCountry]['qt_by_country'] = stateEvents[findIndexSport]['leagues'][findIndexCountry]['qt_by_country'] + 1;

            if (stateEvents[findIndexSport]['leagues'][findIndexCountry]['leagues_by_country'].indexOf(action.payload.league) < 0) {
              // console.log('no there is no league');
              stateEvents[findIndexSport]['leagues'][findIndexCountry]['leagues_by_country'] = [...stateEvents[findIndexSport]['leagues'][findIndexCountry]['leagues_by_country'], action.payload.league];
            }
          } else {
            // No country yet registered
            let leagueItem = {
              country: action.payload.countryname,
              qt_by_country: 1,
              leagues_by_country: [action.payload.league]
            };

            stateEvents[findIndexSport]['leagues'] = [...stateEvents[findIndexSport]['leagues'], leagueItem];

          }

        } else {
          // console.log('no sport registered');
          // No sport yet registered
          let sportItem = {
            id: action.payload.sport,
            qt: 1,
            leagues: [
              {
                country: action.payload.countryname,
                qt_by_country: 1,
                leagues_by_country: [action.payload.league]
              }
            ]
          };
          stateEvents = [...stateEvents, sportItem];

        }

        return stateEvents;
      };
      // return state;

      return {
        ...state,
        events: updateEventNumber(state.events),
        leagues
      };
    case REMOVE_EVENT_PREMATCH:
      leagues = state.leagues.slice();

      // console.log(action.payload, 'deleting prematch');
      action.payload.forEach((ev: any) => {
        const event = leagues.filter((ev_: any) => ev_.id === ev.id)[0];
        if (event) {
          leagues.splice(leagues.indexOf(event), 1);
        }
      });
      // console.log(leagues.length);
      return Object.assign({}, state, {
        leagues
      });

  }

  return state;
};
