import { ICricketGroundAnimationState, nt } from  '../constants';
import { deepAssign } from '../../../../helpers';

const generalStart: ICricketGroundAnimationState = nt;
const generalEnd: ICricketGroundAnimationState = deepAssign({}, nt, {
  actionBox: {
    y: 12
  }
});

export function general() {
  return [{
    start: generalStart,
    end: generalEnd
  }];
}
