import * as uuid from 'uuid/v4';
import User from '../middleware/api/user';
import { mapOutcomeTitle } from '../components/views/bet-slip/helpers';
import { getTickets } from './tickets';
import { userUpdateBetSlip } from './bet-slip';

import { BetType } from '../constants';

import * as html2canvas from 'html2canvas';

export const OPEN_BET_CONFIRMATION = 'OPEN_BET_CONFIRMATION';
export const CLOSE_BET_CONFIRMATION = 'CLOSE_BET_CONFIRMATION';
export const ADD_CONFIRMATION_ODDS = 'ADD_CONFIRMATION_ODDS';
export const FREEZE_CONFIRMATION_ODDS = 'FREEZE_CONFIRMATION_ODDS';
export const UNFREEZE_UPDATE_CONFIRMATION_ODDS = 'UNFREEZE_UPDATE_CONFIRMATION_ODDS';
export const UPDATE_STATUS_CONFIRMATION_ODDS = 'UPDATE_STATUS_CONFIRMATION_ODDS';
export const CONFIRM_BETS = 'CONFIRM_BETS';
export const UPLOAD_CONFIRMATION_SCREENSHOT = 'UPLOAD_CONFIRMATION_SCREENSHOT';
export const REMOVE_FROM_BET_CONFIRMATION = 'REMOVE_FROM_BET_CONFIRMATION';

export const USER_UPDATE_BET_CONFIRMATION = 'USER_UPDATE_BET_CONFIRMATION';

export const userUpdateBetConfirmation = (bets: any) => (dispatch: any, getState: any) => {

  let bets_ = bets.map((item: any) => {
    if (!item.uuid) {
      return {
        ...item,
        uuid: uuid()
      };
    }
    return item;
  });
  dispatch(userUpdateBetSlip(bets_));
  dispatch(userUpdateConfirmation(bets_));

};

const userUpdateConfirmation = (bets: any) => ({
  type: USER_UPDATE_BET_CONFIRMATION,
  payload: bets
});

export const openBetConfirmation = () => ({
  type: OPEN_BET_CONFIRMATION
});

export const closeBetConfirmation = () => ({
  type: CLOSE_BET_CONFIRMATION
});

export const addConfirmOdds = (odds: any, parlay: any) => ({
    type: ADD_CONFIRMATION_ODDS,
    payload: odds,
    parlay
});

export const addConfirmationOdds = (odds: any, parlay: any) => (dispatch: any, getState: any) => {

  let odds_ = odds.map((item: any) => {
    if (!item.uuid) {
      return {
        ...item,
        uuid: uuid()
      };
    }
    return item;
  });
  dispatch(userUpdateBetSlip(odds_));
  dispatch(addConfirmOdds(odds_, parlay));

};

export const updateStatusConfirmationOdds = (odds: any) => ({
  type: UPDATE_STATUS_CONFIRMATION_ODDS,
  payload: odds
});

const freezeConfirmationOdds = () => ({
  type: FREEZE_CONFIRMATION_ODDS
});

export const unfreezeUpdateConfirmationOdds = () => ({
  type: UNFREEZE_UPDATE_CONFIRMATION_ODDS
});

export const removeBet = (odd: any) => ({
  type: REMOVE_FROM_BET_CONFIRMATION,
  payload: odd
});

export let fakeArray: any;

export const confirmBets = (odds: any, parlay: any) => (dispatch: any, getState: any) => {
  dispatch(freezeConfirmationOdds());

  const user_interface = new User(dispatch);

  const { live, router, user, prematch, eventsFlatten } = getState();
  const { betType, events, outcomeList } = live;
  const { byId } = eventsFlatten;
  const { pathname } = router.location;

  const bets = odds.map((odd: any) => {
    const event = odd.oddIsLive === 0 ? prematch.leagues.filter((ev: any) => ev.id === odd.event)[0] : byId[odd.event];
    const { home, away, inverted, sport } = event;
    const outcomeList_ = odd.oddIsLive === 0 ? prematch.outcomeList : live.outcomeList;

    return Object.assign({}, odd, {
      event,
      outcomestring: mapOutcomeTitle(home, away, inverted, null, outcomeList_[sport][odd.oddIsLive][odd.outcome], odd)
    });
  });

  user_interface.confirmBets({ betType, parlay, bets, user })
    .then((data: any) => {
      dispatch(updateStatusConfirmationOdds(data));

      const updated = getState().betConfirmation.odds;
      fakeArray = updated;

      data.map((item: any, i: any) => {
        if (item.id === updated[i].id) {
          updated[i].ticketNo = item.ticketNo;
        }
      });

      if (betType === BetType.parlay && updated[0].status === 'ok') {
          // updated.forEach(bet => dispatch(removeBet(bet)));
      }
      else {
        updated.forEach((bet: any) => {
          if (bet.status === 'ok') {
          // updated.forEach(bet => dispatch(removeBet(bet)));
          }
        });
      }

      if (pathname.indexOf('tickets') >= 0) {
        dispatch(getTickets(365));
      }
    })
    .catch((err: any) => console.error(err));
};

export const uploadConfirmationScreenshot = (ticketNumber: any) => (dispatch: any, getState: any) => {
    const user_interface = new User(dispatch);
    let purpose = 'FULLSCREEN';

    // if (!html2canvas) return;

    setTimeout(() => {
      try {
        let html2canvas_: any = html2canvas;

        html2canvas_(document.body).then( (canvas: any) => {

          try {
            user_interface.imageUpload(purpose, ticketNumber, canvas.toDataURL('image/jpeg', 0.2)).catch((_uploadError: any) => {
              console.log('html2canvas uploadError error: ', _uploadError);
            });
          }
          catch (_inner) {
            console.log('html2canvas inner(2) error: ', _inner);
          }
        }).catch((_err: any) => {
          console.log('html2canvas inner(1) error: ', _err);
        });
      }
      catch (_err) {
        console.log('html2canvas outer error: ', _err);
      }
    }, 50);
};
