import * as React from 'react';
import { connect } from 'react-redux';
import Link from './../../components/form/link';

const MarketCounter = ( { to, event, isSmallDisplay, view, user }: any) => {

  const { sport } = event;
  const numberMarketsClass = ['market-counter'];
  const bookmakerId = user.bookmakerId[sport];

  if (sport !== 7) {
    numberMarketsClass.push('no-tennis');
  }

  if (sport === 1) {
    numberMarketsClass.push('soccer');
  }

  if (isSmallDisplay === 'asian') {
    numberMarketsClass.push('asian-market-counter');
  }

  if (view === 3) {
    numberMarketsClass.push('european-market-counter');
  }

  const marketCounter = () => {
    const style_ = event.live === 0 && isSmallDisplay ? {number: { fontSize: '11px', top: '-4px', left: '5px', borderBottom: '0px solid transparent'}, arrow: {top: '1px', left: '4px', fontSize: '12px'}} : {number: {}, arrow: {}};
    return (
      <span className={numberMarketsClass.join(' ')} style={style_.number}>
        <span>{event.numberMarkets[bookmakerId] ? event.numberMarkets[bookmakerId] : 0}</span>
        <i className="i i-select" style={style_.arrow}></i>
      </span>
    );
  };

  if (isSmallDisplay === 'asian') {
    return marketCounter();
  }

  if (isSmallDisplay) {
    numberMarketsClass.push('small-display');
    return (
      marketCounter()
    );
  }

  return (
    <Link to={to} title="click to view more odds" className={'market-counter-link'}>
      { marketCounter() }
    </Link>
  );

};

export default connect(
  (state: any, ownProps: any) => {
    return {
      view: state.settings.view,
      user: state.user
    };

  }
)(MarketCounter);
