import * as React from 'react';
import { connect } from 'react-redux';

import Slider from './slider';
import Button from './button';

const BetSlip = ({ qt, view, open }: any) => {

  let slipClass = '';

  if (view === 3 || view === 1) {
    slipClass = qt > 0 ? 'bet-slip-new active' : 'bet-slip-new';
    if (open) {
      slipClass = slipClass + ' closed';
    }

  } else {
    slipClass = 'bet-slip';
  }

  return (
    <div className={slipClass}>
      <Slider />
      <Button />
    </div>
  );

};

export default connect(
  (state: any) => ({
    qt: Object.keys(state.betSlip.odds).length,
    view: state.settings.view,
    open: state.betSlip.open
  })
)(BetSlip);
