import * as React from 'react';
import EventsOdd from './../../events-odd/';
import { getPlaceholderByOutcome } from './../../../../../../../common/constants';

const OutcomesSportPrematchMobile = ({ event, outcome, otc, oddClassList, sport, isDisplaySmall }: any) => {

  return (
    <div className="odd-table prematch" style={{display: 'flex'}}>
    {
      outcome.map((odd: any, i: any) => {
        if (!otc || (!odd.hasOwnProperty('suspended') || odd.suspended)) {
          oddClassList.push('suspended');
        }

        const oddHeader = getPlaceholderByOutcome(odd.outcome, '1', '2');

        return (
          <div className="odds-prematch" key={odd.id || i} >
            <div className="odds-prematch-header">{oddHeader[i] === 'Draw' ? 'x' : oddHeader[i]}</div>
            <div className="odds-prematch-odds">
              <EventsOdd
                odd={odd}
                sport={sport}
                event={event}
                isDisplaySmall={isDisplaySmall}
                className={oddClassList.join(' ')}
              />
            </div>
          </div>
        );

      })

    }
  </div>
  );
};

export default OutcomesSportPrematchMobile;
