import {
  LIVE_EVENTS, NEW_EVENT,
  REMOVE_EVENT, UPDATE_OUTCOMES,
  UPDATE_OUTCOME_TITLE, UPDATE_EVENT_OUTCOMES,
  UPDATE_EVENT_GAMECAST, UPDATE_EVENT_SCORE,
  UPDATE_EVENT_CLOCK, CHANGE_BET_TYPE,
  CHANGE_ASIAN_LINE, GET_CURRENT_EVENT,
  IS_REPLACING, CLOSED_CONNECTION_LIVE,
  EVENT_UPDATE, CHECK_GAME_CLOCKS,
  UPDATE_BETSLIP_OUTCOMES,
  DELETE_OUTCOME,
  ADD_OUTCOME,
  UPDATE_SUBSCRIBED_SPORT,
  LIVE_CLOCKS,
  OPEN_MARKETS_UPDATE,
  EMPTY_GAMECAST_QUEUE,
  SUBSCRIBE_SPORT_DATA,
  TOGGLE_VIDEO_TAB,
  FILTER_STREAMING
} from '../actions/live';

import { LIVE_EVENTS_FLATTEN, REMOVE_EVENT_FLATTEN, LIVE_EVENTS_ASIAN } from './../actions/events-flatten';

import { IClocks, IEventFinal } from './../../data-handlers/events/interfaces';

import { BetType, AsianLines } from '../constants';
import { sportUsesReverseTickTimer } from '../helpers';
import config from '../../config';

import OutcomesFeedHandler from './outcomes-feed-handler';

import { range } from '../helpers';

export interface IInitialLiveState {
  betType: number;
  events: any[];
  outcomes: any;
  outcomeList: any;
  eventOutcomes: any;
  betslipOutcomes: Object;
  betslipUpdateOutcomes: Object;
  eventGamecast: Object;
  gamecastQueueAnimations: any[];
  scores: Object;
  clocks: IClocks;
  parlayBySport: Object;
  asianLine: number;
  currentEvent: number;
  isReplacing: boolean;
  closedConnectionLive: boolean;
  numberMarkets: any[];
  userMainOutcomes: Object;
  subscribedSport: any;
  openMarkets: any[];
  isVideoTab: boolean;
  filterStreaming: boolean;
}

export const liveInitialState: IInitialLiveState = {
  betType: BetType.straight,
  events: [],
  outcomes: {},
  outcomeList: {},
  eventOutcomes: {},
  betslipOutcomes: {},
  betslipUpdateOutcomes: {},
  eventGamecast: {},
  gamecastQueueAnimations: [],
  scores: {},
  clocks: {},
  parlayBySport: {},
  asianLine: AsianLines[0].value,
  currentEvent: 0,
  isReplacing: false,
  closedConnectionLive: true,
  numberMarkets: [],
  userMainOutcomes: {},
  subscribedSport: '',
  openMarkets: [],
  isVideoTab: false,
  filterStreaming: false
};

export const live = (state = liveInitialState, action: any): any => {
  let events: IEventFinal[];
  let revisedClocks: IClocks;
  let outcomesFeedHandler = new OutcomesFeedHandler(state);

  switch (action.type) {
    case FILTER_STREAMING:
      return {
        ...state,
        filterStreaming: action.payload,
        isVideoTab: false
      };
    case TOGGLE_VIDEO_TAB:
      return {
        ...state,
        isVideoTab: action.payload
      };
    case OPEN_MARKETS_UPDATE:
      return {
        ...state,
        openMarkets: action.payload
      };

    case SUBSCRIBE_SPORT_DATA:
      // console.log('update clocks and scores here');
      // console.log(action.payload);
      // return state;
      return {
        ...state,
        clocks: action.payload.clocks,
        scores: action.payload.scores
      };

    case LIVE_CLOCKS:
      return {
        ...state,
        clocks: action.payload
      };

    case UPDATE_SUBSCRIBED_SPORT:

      return {
        ...state,
        subscribedSport: action.payload

      };

    case LIVE_EVENTS_ASIAN:
      return {
        ...state,
        subscribedSport: action.payload.subscribedSport,
        outcomeList: action.payload.outcomeList,
        openMarkets: action.payload.openMarkets,
        outcomes: action.payload.outcomes,
        clocks: action.payload.clocks,
        scores: action.payload.scores

      };
    case LIVE_EVENTS_FLATTEN:

      return {
        ...state,
        subscribedSport: action.payload.subscribedSport,
        outcomeList: action.payload.outcomeList,
        openMarkets: action.payload.openMarkets,
        outcomes: action.payload.outcomes,
        clocks: action.payload.clocks,
        scores: action.payload.scores

      };

    case CHANGE_BET_TYPE:
      return Object.assign({}, state, {
        betType: action.payload
      });
    case NEW_EVENT:
      events = state.events.slice();
      events.push(action.payload);

      return Object.assign({}, state, {
        events
      });
    case REMOVE_EVENT_FLATTEN:
      // If event has finished. We need to remove that events clock. Event remove logic ./events-flatten.ts
      let eventId = action.payload;
      revisedClocks = {};

      for (let evID in state.clocks) {
        const evIdNumber = parseInt(evID);

        if (evIdNumber !== eventId) {
          revisedClocks[evID] = state.clocks[evID];
        }

      }
      return {
        ...state,
        clocks: revisedClocks
      };

    case UPDATE_OUTCOMES:
      return Object.assign({}, state, {
        outcomes: Object.assign({}, state.outcomes, action.payload)
      });
    case UPDATE_OUTCOME_TITLE:
      return Object.assign({}, state, {
        outcomeList: Object.assign({}, state.outcomeList, {
          [action.sport]: Object.assign({}, state.outcomeList[action.sport], action.payload)
        })
      });
    case UPDATE_EVENT_OUTCOMES:
      return Object.assign({}, state, {
        eventOutcomes: action.payload
      });
    case UPDATE_BETSLIP_OUTCOMES:

      const oddIds = Object.keys(action.payload);

      if (action.payload[oddIds[0]]) {
        let event_ = action.payload[oddIds[0]].event;
        let outcome = action.payload[oddIds[0]].outcome;

        // const updatedOutcomes = action.payload[`${event_}_${outcome}`];
        // console.log(action.payload);
        // console.log('actionPayload');
        let updatedOutcomes = action.payload;
        getOddsData(updatedOutcomes)
        .forEach((item: any, i_: number) => {
          item
            .forEach((otc: any) => {
              if (otc) {
                // add row value to all our available replacement outcomes
                otc.row = i_;
              }
            });
          // console.log(item);
        });

        // return {
        //   ...state,
        //   betslipOutcomes: action.payload,
        //   betslipUpdateOutcomes: {
        //     ...state.betslipUpdateOutcomes,
        //     [`${event_}_${outcome}`]: action.payload
        //   }

        // }

        return Object.assign({}, state, {
          betslipOutcomes: updatedOutcomes,
          betslipUpdateOutcomes: Object.assign({}, state.betslipUpdateOutcomes, {
            [`${event_}_${outcome}`]: updatedOutcomes
          })
        });

      }
      return state;
    case EMPTY_GAMECAST_QUEUE:
      console.log('empty');
      return {
        ...state,
        gamecastQueueAnimations: []
      };
    case UPDATE_EVENT_GAMECAST:

    let getQueueAnimations = () => {

      if (JSON.stringify(action.payload) === '{}') return state.gamecastQueueAnimations;

      if (JSON.stringify(action.payload) !== JSON.stringify(state.gamecastQueueAnimations[state.gamecastQueueAnimations.length - 1])) {
        return [...state.gamecastQueueAnimations, action.payload];
      }

      return state.gamecastQueueAnimations;
    };

    return {
      ...state,
      gamecastQueueAnimations: getQueueAnimations(),
      eventGamecast: action.payload

    };

    case CHANGE_ASIAN_LINE:
      return Object.assign({}, state, {
        asianLine: action.payload
      });
    case UPDATE_EVENT_SCORE:
      return Object.assign({}, state, {
        scores: Object.assign({}, state.scores, {
          [action.event]: action.payload
        })
      });
    case UPDATE_EVENT_CLOCK:
      return Object.assign({}, state, {
        clocks: Object.assign({}, state.clocks, {
          [action.event]: action.payload
        })
      });
    case GET_CURRENT_EVENT:
      return Object.assign({}, state, {
        currentEvent: action.payload
      });
    case IS_REPLACING:
      return Object.assign({}, state, {
        isReplacing: state.isReplacing ? false : true
      });
    case CLOSED_CONNECTION_LIVE:
      if (!action.payload) {
        return {
          ...state,
          closedConnectionLive: action.payload
        };
      }

      return {
        ...state,
        clocks: {},
        scores: {},
        outcomes: {},
        openMarkets: [],
        eventOutcomes: {},
        closedConnectionLive: action.payload
      };

    case EVENT_UPDATE:
      return Object.assign({}, state, {
        events: action.payload
      });

    case CHECK_GAME_CLOCKS:
      revisedClocks = {};
      // events = state.events.slice();

      events = action.payload.events;

      if (!events) return state;

      let clocks: IClocks = action.payload.clocks;
      // Each item is an object, keyed by eventID
      for (let eventID in clocks) {
        // if this event is no longer in the master events array it means that the event is gone
        // so we should remove this clock too.
        const eventIdNumber = parseInt(eventID);

        if (!events[eventIdNumber]) {
          let { [eventIdNumber]: deleteEvent, ...restStateById } = revisedClocks;
          revisedClocks = restStateById;
        }

        let _clock = Object.assign({}, clocks[eventID]);

        // mins = minutes sent by bend
        // min = calculated min
        if (_clock.timerTicking && _clock.timerTicking === 1 && _clock.lastUpdate && _clock.lastUpdate > 0) {
          const OFFSET = config.gameClockOffset;
          const now = (new Date()).getTime() / 1000.0;
          const diff = now - (_clock.lastUpdate - OFFSET);

          let sdiff = 0;
          let mdiff = 0;

          sdiff = Math.floor(diff % 60);
          mdiff = Math.floor(diff / 60);

          let finalMins = parseInt(_clock.mins) + mdiff;
          let finalSecs = parseInt(_clock.secs) + sdiff;

          if (sportUsesReverseTickTimer(_clock.sportId)) {
            finalMins = parseInt(_clock.mins) - mdiff;
            finalSecs = parseInt(_clock.secs) - sdiff;

            while (finalSecs < 0) {
              finalMins --;
              finalSecs += 60;
            }

          } else {

            while (finalSecs > 59) {
              finalMins++;
              finalSecs -= 59;
            }

          }

          if (finalMins < 0) {
            finalMins = Math.abs(finalMins);
          }

          _clock.min = (finalMins < 10 ? '0' : '') + finalMins;
          _clock.sec =  (finalSecs < 10 ? '0' : '') + finalSecs;

          revisedClocks[eventID] = _clock;
        }
        // Clock is not ticking -- so just re-assign the clock as is without modifying it.
        else {

          revisedClocks[eventID] = _clock;
        }
      }
      return Object.assign({}, state, {
        clocks: Object.assign({}, revisedClocks)
      });

      case ADD_OUTCOME:

        let addObject = action.payload;
        let bookmakerId__ = Object.keys(addObject)[0];
        // let eventId__ = addObject[bookmakerId__].event;

        return outcomesFeedHandler.addEventOutcome(addObject);

      case DELETE_OUTCOME:

        let deleteObject = action.payload;
        // let ev_ = Object.keys(deleteObject)[0];
        return outcomesFeedHandler.deleteEventOutcome(deleteObject);

  }

  return state;
};

function getOddsData(outcomes: any): any {

  if (outcomes) {
    let values = Object.keys(outcomes).map(item => {
      return outcomes[item];
    }).sort((a: any, b: any) => a.order - b.order).filter((odd: any) => !odd.status);

    let arrFinal = [];

    const itemOrder = values.map((item: any) => item.order);

    if (itemOrder.indexOf(-171718) !== 0 && itemOrder.indexOf(-171717) !== 0) {

      const maxColumns = Math.max.apply(null, values.map((item: any) => item.column)) + 1;
      const valuesByColumn = range(maxColumns).map((x, j) => values.filter((item: any) => item.column === j)).filter(arr => arr.length > 0);
      const maxRows = Math.max.apply(null, valuesByColumn.map(arr => arr.length));

      for (let i = 0; i < maxRows; i++) {
        arrFinal[i] = [];
        for (let j = 0; j < valuesByColumn.length; j++) {
          arrFinal[i][j] = valuesByColumn[j][i];
        }

    }

    } else {

        const vals = values.map((item: any) => item).sort((a: any, b: any) => {
          if (a.display < b.display) {
            return -1;
          }

          if (a.display > b.display) {
            return 1;
          }

          return 0;
        });

        const columns = 3;
        const rows = Math.ceil(vals.length / columns);

        for (let i = 0; i < rows; i++) {
          arrFinal[i] = [];
          for (let j = 0; j < columns; j++) {
            arrFinal[i][j] = vals[j + columns * i];
          }
        }

    }
    // console.log(arrFinal);
    return arrFinal;
  }
  return [];
}
