import { ICricketGroundAnimationState, nt } from '../constants';
import { deepAssign } from '../../../../helpers';

const t1ShotStart = (start: any): ICricketGroundAnimationState => deepAssign({}, start, {
  ball: {
    // opacity: 1,
    x: 108,
    y: 48
  }

});

const t1ShotEnd = (start: any): ICricketGroundAnimationState => deepAssign({}, start, {
  ball: {
    // opacity: 1,
    x: 145,
    y: 38
  }

});

export function shots(start: any) {

  return {
    start: t1ShotStart(start),
    end: t1ShotEnd(start)
  };
}
