import * as React from 'react';
import { connect } from 'react-redux';
import config from './../../../config';

import { ViewType } from '../../constants';

const Content = ({
  user,
  uiView,
  subPage,
  children,
  sportsList,
  title,
  hasVideoOnFullScreen
  }:
  {
    user: any,
    uiView: any;
    subPage: boolean,
    children?: any,
    sportsList: boolean,
    title?: any,
    hasVideoOnFullScreen: boolean
  }) => {

  const className = 'content';
  const classList = [className];
  const wrapperClassList = [`${className}__wrapper`];

  if (config.hasMaxViewHeight) {
    // console.log('HAS_MAX_VIEW_HEIGHT true');
    classList.push('hasMaxViewHeight');
  }

  let isMain = title.split('/').indexOf('live') >= 0;
  if (!isMain) {
    classList.push('subpage');
  }

  if (uiView === ViewType.northAmerican) {
    wrapperClassList.push('content__north-american');
  }

  if (uiView === ViewType.european || uiView === ViewType.asian) {
    classList.push('european');
    wrapperClassList.push('content__european');
  }

  if (!sportsList || title.split('/').indexOf('rules') >= 0 || title.split('/').indexOf('tickets') >= 0) {
    classList.push('no-sports-list');
  }

  let contentStyle: any = {};

  if (title.split('/').indexOf('prematch') > 0) {
    contentStyle['marginTop'] = '3.5rem';
  }

  if (Object.keys(user.bookmakerId).length <= 0) {
    return (
      <section className={classList.join(' ')} style={contentStyle}>
        <div className={wrapperClassList.join(' ')}>
          ùser object not arrived
        </div>
      </section>
    );
  }

  if (hasVideoOnFullScreen) {
    contentStyle = {
      ...contentStyle,
      marginTop: '0px'
    };

    wrapperClassList.push('full-screen-video');
  }

  return (
    <section className={classList.join(' ')} style={contentStyle}>
      <div className={wrapperClassList.join(' ')}>
        { children }
      </div>
    </section>
  );
};

export default connect(
  (state: any, ownProps: any) => ({
    uiView: state.settings.view,
    user: state.user,
    title: state.router.location.pathname,
    sportsList: state.uiToolbar.show_sports_list,
    hasVideoOnFullScreen: state.user.hasVideoOnFullScreen
   })
)(Content);
