import * as React from 'react';
import SwitchLayoutIcon from '../../../svgs/switchlayout-icon';
import GamecastIcon from '../../../svgs/gamecast-icon';

const Button = ({ name, handleClick, iconClassName }: any) => {

    let Component: any = () => <div></div>;

    // const { name, handleClick, handleMouseLeave, iconClassName, showTooltip } = this.props;

    switch (name) {
      case 'layout':
        Component = SwitchLayoutIcon;
        break;
      case 'gamecast':
        Component = GamecastIcon;
        break;
    }

    return (
        <li className={name} onClick={handleClick} style={{height: '50px'}}>
          <Component className={iconClassName}/>
        </li>
    );

};

export default Button;
