import * as React from 'react';
import EventsOdd from './../../events-odd/';
import CricketMarketHeader from './../../../../../misc/cricket-market-header';

const OutcomesSportDesktop = ({ sport, outcomeId, outcome, oddClassList, otc, outcomeList, event }: any) => {
  let outcomeFake = [
    { american: '+197', asian: true, bet: '1', bookmakerId: '103', column: 0, display: '', event: 23638, id: '1516736160527', line: '', main: true, marketPrice: 3, oddIsLive: 1, order: 0, outcome: 1, price: 2.9702970297, replacers: [''], sport: '1', status_: 'Closed', suspended: true },
    { american: '+197', asian: true, bet: '1', bookmakerId: '103', column: 0, display: '', event: 23638, id: '1516736160523', line: '', main: true, marketPrice: 3, oddIsLive: 1, order: 0, outcome: 1, price: 2.9702970297, replacers: [''], sport: '1', status_: 'Closed', suspended: true }
  ];

  return (
    <table className="odd-table">
    {/* sport === 24 && <CricketMarketHeader outcomeId={outcomeId} outcomeList={outcomeList} /> */ }
      <tbody>
        {
          outcome[0] ? outcome.map((odd: any, i: any) => {
            if (!otc || (!odd.hasOwnProperty('suspended') || odd.suspended)) {
              oddClassList.push('suspended');
            }
            return (
              <tr key={odd.id || i}>

                <EventsOdd
                  className={oddClassList.join(' ')}
                  odd={odd}
                  sport={sport}
                  event={event} />
              </tr>
            );

          }) :
          outcome.length <= 0 &&
          outcomeFake.map((odd, i) => {
            oddClassList.push('suspended');
            return (
              <tr key={odd.id || i}>
                <EventsOdd
                  className={oddClassList.join(' ')}
                  odd={odd}
                  sport={sport}
                  event={event} />
              </tr>
            );
          })
        }

      </tbody>
    </table>
  );

};

export default OutcomesSportDesktop;
