import { IInitialLiveState } from './live';

class OutcomesFeedHandler {

  public _state: IInitialLiveState;

  constructor(state: IInitialLiveState) {
    this._state = state;
  }

  updateEventOdd(updateObject: any) {

    let ev = Object.keys(updateObject)[0];
    let bookmakerId = Object.keys(updateObject[ev])[0];
    let marketId = Object.keys(updateObject[ev][bookmakerId])[0];
    let outcomeId = Object.keys(updateObject[ev][bookmakerId][marketId])[0];
    let main = updateObject[ev][bookmakerId][marketId][outcomeId].main;

    let outcomes_: any = this._state.eventOutcomes[bookmakerId];

    // Updates eventOutcomes for event subscribed clients - clientsBySubscription
    if (outcomes_ && outcomes_[marketId] && outcomes_[marketId][outcomeId]) {
      let updateObject_: any = {};
      updateObject_[bookmakerId] = updateObject[ev][bookmakerId];

      this._state = {
        ...this._state,
        eventOutcomes: {
          ...this._state.eventOutcomes,
          [bookmakerId]: {
            ...this._state.eventOutcomes[bookmakerId],
            [marketId]: {
              ...this._state.eventOutcomes[bookmakerId][marketId],
              [outcomeId]: Object.assign(this._state.eventOutcomes[bookmakerId][marketId][outcomeId], updateObject_[bookmakerId][marketId][outcomeId])
            }
          }
        }
      };
    }

    if (main) {

      let lstOutcomes = this._state.outcomes[ev];
      if (lstOutcomes && lstOutcomes[bookmakerId] && lstOutcomes[bookmakerId][marketId] && lstOutcomes[bookmakerId][marketId][outcomeId]) {

        this._state = {
          ...this._state,
          outcomes: {
            ...this._state.outcomes,
            [ev]: {
              ...this._state.outcomes[ev],
              [bookmakerId]: {
                ...this._state.outcomes[ev][bookmakerId],
                [marketId]: {
                  ...this._state.outcomes[ev][bookmakerId][marketId],
                  [outcomeId]: Object.assign(this._state.outcomes[ev][bookmakerId][marketId][outcomeId], updateObject[ev][bookmakerId][marketId][outcomeId])
                }
              }
            }
          }
        };
      }
    }

    return this._state;
  }

  addEventOutcome(updateObject: any) {

    let addObject = updateObject;

    let bookmakerId = Object.keys(addObject)[0];
    let eventId = addObject[bookmakerId].event;
    let marketId = addObject[bookmakerId].outcome;
    let outcomeId = addObject[bookmakerId].id;
    let main = addObject[bookmakerId].main;

    if (this._state.eventOutcomes[eventId] && this._state.eventOutcomes[eventId][bookmakerId] && this._state.eventOutcomes[eventId][bookmakerId][marketId]) {
      // State already has market with tat Id. So just add this outcome.
      let newOutcome = {
        [outcomeId]: addObject[bookmakerId]
      };

      this._state = {
        ...this._state,
        eventOutcomes: {
          ...this._state.eventOutcomes,
          [bookmakerId]: {
            ...this._state.eventOutcomes[bookmakerId],
            [marketId]: {
              ...this._state.eventOutcomes[bookmakerId][marketId],
              [outcomeId]: Object.assign({}, newOutcome[outcomeId])
            }
          }
        }
      };

    } else if (this._state.eventOutcomes[eventId] && this._state.eventOutcomes[eventId][bookmakerId]) {
      // State doesnt have a market with that Id. So lets create it first and add outcome.

      let newMarketId: any = {
        [marketId]: {
          // [outcomeId__]: addObject[bookmakerId__]
        }
      };

      newMarketId[marketId][outcomeId] = addObject[bookmakerId];

      this._state = {
        ...this._state,
        eventOutcomes: {
          ...this._state.eventOutcomes,
          [bookmakerId]: {
            ...this._state.eventOutcomes[bookmakerId],
            [marketId]: Object.assign({}, newMarketId[marketId])
          }
        }
      };
    }

    if (main) {
      if (this._state.outcomes[eventId] && this._state.outcomes[eventId][bookmakerId] && this._state.outcomes[eventId][bookmakerId][marketId]) {

        // State already has market with tat Id. So just add this outcome.
        // console.log('State already has market with tat Id. So just add this outcome.');

        let newOutcome = {
          [outcomeId]: addObject[bookmakerId]
        };

        this._state = {
          ...this._state,
          outcomes: {
            ...this._state.outcomes,
            [eventId]: {
              ...this._state.outcomes[eventId],
              [bookmakerId]: {
                ...this._state.outcomes[eventId][bookmakerId],
                [marketId]: {
                  ...this._state.outcomes[eventId][bookmakerId][marketId],
                  [outcomeId]: Object.assign({}, newOutcome[outcomeId])
                }
              }
            }
          }
        };

      } else if (this._state.outcomes[eventId] && this._state.outcomes[eventId][bookmakerId]) {
        // State doesnt have market with that Id. So we create it.
        // console.log('State doesnt have market with tat Id. So we create it.')

        let newOutcome: any = {};
        newOutcome[marketId] = {};
        newOutcome[marketId][outcomeId] = addObject[bookmakerId];

        if (!this._state.outcomes[eventId]) {

          console.log('no such event was found');
          return this._state;
        }

        this._state = {
          ...this._state,
          outcomes: {
            ...this._state.outcomes,
            [eventId]: {
              ...this._state.outcomes[eventId],
              [bookmakerId]: {
                ...this._state.outcomes[eventId][bookmakerId],
                [marketId]: Object.assign({}, newOutcome[marketId])
              }
            }
          }
        };
      }

    }
    return this._state;

  }

  deleteEventOutcome(updateObject: any) {
    let deleteOutcome = updateObject;

    let ev = Object.keys(deleteOutcome)[0];
    let bookmakerId = Object.keys(deleteOutcome[ev])[0];
    let marketId = Object.keys(deleteOutcome[ev][bookmakerId])[0];
    let outcomeId = deleteOutcome[ev][bookmakerId][marketId].id;
    let main = deleteOutcome[ev][bookmakerId][marketId].main;

    let _eventOutcomes = this._state.eventOutcomes[bookmakerId];

    if (_eventOutcomes && _eventOutcomes[marketId] && _eventOutcomes[marketId][outcomeId]) {

      const {
        [outcomeId]: outcomeIdValue,
        ...noOutcome
      } = this._state.eventOutcomes[bookmakerId][marketId];

      this._state = {
        ...this._state,
        eventOutcomes: {
          ...this._state.eventOutcomes,
          [bookmakerId]: {
            ...this._state.eventOutcomes[bookmakerId],
            [marketId]: noOutcome
          }
        }
      };
    }

    if (main) {
      if (this._state.outcomes && this._state.outcomes[ev] && this._state.outcomes[ev][bookmakerId] && this._state.outcomes[ev][bookmakerId][marketId] && this._state.outcomes[ev][bookmakerId][marketId][outcomeId]) {

        const {
          [outcomeId]: outcomeIdValue,
          ...newOutcomesState
        } = this._state.outcomes[ev][bookmakerId][marketId];

        this._state = {
          ...this._state,
          outcomes: {
            ...this._state.outcomes,
            [ev]: {
              ...this._state.outcomes[ev],
              [bookmakerId]: {
                ...this._state.outcomes[ev][bookmakerId],
                [marketId]: newOutcomesState
              }
            }
          }
        };
      }
    }

    return this._state;

  }

}

export default OutcomesFeedHandler;
