import thunk from 'redux-thunk';
import { routerMiddleware } from 'connected-react-router';
import { createStore, applyMiddleware, compose } from 'redux';
import { createBrowserHistory, createMemoryHistory } from 'history';

import rootReducer from '../reducers/index';

let batchedSubscribe = require('redux-batched-subscribe');

let _ = require('lodash');
let debouncer: any;
debouncer = _.debounce((notify: any) => notify());

export const isServer = function () {
  return !(
    typeof window !== 'undefined' &&
    window.document &&
    window.document.createElement
  );
};

export default function configureStore(initialState?: any, url?: any) {

  const history = isServer()
    ? createMemoryHistory({ initialEntries: [url] })
    : createBrowserHistory();

  const enhancers = [];

  if (process.env.NODE_ENV === 'development' && !isServer()) {

    let window_: any = window;

    const devToolsExtension = window_.devToolsExtension;

    if (typeof devToolsExtension === 'function') {
      enhancers.push(devToolsExtension());
    }
  }

  const middleware = [thunk, routerMiddleware(history)];
  const composedEnhancers = compose(
    applyMiddleware(...middleware),
    batchedSubscribe.batchedSubscribe(debouncer),
    ...enhancers
  );

  const store = createStore(
    rootReducer(history),
    initialState,
    composedEnhancers
  );

  if (module.hot) {
    module.hot.accept('../reducers', () => {
      // const nextRootReducer = require('../reducers').default;
      store.replaceReducer(rootReducer(history));
    });
  }

  return {
    store,
    history
  };
}
