import * as React from 'react';
import { connect } from 'react-redux';
import config from '../../../config';

import { Dialog, DialogContent, DialogButtons } from '../form/dialog';
import { Button, Select } from '../form';
import Label from '../form/label';
import ThemeSwitcher from '../misc/theme-switcher';

import { changeLanguage, getTimeZones, changeTimeZone, changeOddsFormat, changeView, closeSettings, changeViewPreferences } from '../../actions/settings';

import { showSportsListOn, showSportsListOff } from '../../actions/ui-toolbar';

import { Languages, OddsFormat, ViewType } from '../../constants';

import Switch from '../misc/switch';

import * as moment from 'moment-timezone';
// const moment = require('./moment-timezone');

interface ISettingsProps {
  title: string;
  languageData: any;
  language: string;
  timeZone: string;
  timeZones: any[];
  oddsFormat: number;
  view: number;
  path: string;
  open: boolean;
  dispatch: (action: {} | Promise<any>) => void;
  viewPreferences: any;
}

class Settings extends React.Component<ISettingsProps, any> {
  update = () => {
    const { open, timeZones, dispatch } = this.props;

    if (open && !timeZones.length) {
      dispatch(getTimeZones());
    }
  }

  get odds() {
    const { american, decimal/*, fractional*/ } = this.props.languageData.oddsFormat;

    return [
      { value: OddsFormat.american, text: american },
      { value: OddsFormat.decimal, text: decimal }/*,
      { value: OddsFormat.fractional, text: fractional }*/
    ];
  }

  get views() {
    return [
      { value: ViewType.classic, text: 'Classic', hide: false },
      { value: ViewType.asian, text: 'Asian', hide: false },
      { value: ViewType.northAmerican, text: 'North American', hide: true },
      { value: ViewType.european, text: 'European', hide: false }
    ];
  }

  get views_allowed() {
    return [
      { value: ViewType.classic, text: 'Classic', hide: false },
      { value: ViewType.asian, text: 'Asian', hide: false },
      { value: ViewType.european, text: 'European', hide: false }
    ];
  }

  componentDidMount() {
    requestAnimationFrame(this.update);

    // Here
    // this.props.dispatch(changeTimeZone('Pacific/Guam'));
  }

  componentDidUpdate() {
    requestAnimationFrame(this.update);
  }

  render() {
    const { title, languageData, language, timeZone, timeZones, oddsFormat, view, viewPreferences, path, open, dispatch } = this.props;
    const lang = Languages.filter(item => item.value === language)[0];

    const handleLanguageChange = (item: any) => dispatch(changeLanguage(item.value));
    const handleTimeZoneChange = (item: any) => {
      const itemIdx = item.indexOf(')') + 2;
      item = item.slice(itemIdx, item.length);
      dispatch(changeTimeZone(item));
    };
    const handleOddsFormatChange = (item: any) => dispatch(changeOddsFormat(item.value));
    const handleViewChange = (item: any) => {
      // 2018-04-18:  Only allow EUROPEAN and ASIAN views for now

      let val = item.value;
      if (item.value !== 1 && item.value !== 3 && item.value !== 0)
        val = 3;
      dispatch(changeView(val));

      if (val !== 3) {
        dispatch(showSportsListOn());
      } else {
        dispatch(showSportsListOff());
      }

    };

    const oddsPreference = 3;
    const oddsFormatShown = oddsFormat === null ? oddsPreference : oddsFormat;

    if (oddsFormat === null) {
      handleOddsFormatChange(oddsPreference);
    }

    let content = <DialogContent />;

    if (timeZones.length) {

      let viewPreferencesOptions = null;

      if (this.views[view].value === ViewType.classic) {
        viewPreferencesOptions = (
          <div className="md-container">
            <Label>Show Full Bet Description</Label><br/>
            <span>Off</span>
            <Switch
              on={viewPreferences.doubleLine}
              onToggle={() => dispatch(changeViewPreferences({
                doubleLine: !viewPreferences.doubleLine
              }))}
            />
            <span>On</span>
          </div>
        );
      }

      const browserTime = moment.tz.guess();
      console.log('browserTime from settings render', browserTime);
      console.log('timeZone from settings render', timeZone);

      const sortedZones = timeZones.map(zone => {
        return {
          timeZone: zone,
          offset: moment.tz.zone(zone).utcOffset(Date.now())
        };
      })
      .sort((a, b) => a.offset - b.offset)
      .map(zone => {
        const offset = zone.offset;
        const isBehind = offset < 0 || (offset / 60) === 0;

        let toUTC: any = offset / 60;
        if ((toUTC) % 1 !== 0) {
          const hrs = isBehind ? Math.floor(toUTC) + 1 : Math.floor(toUTC);
          toUTC = `${hrs}:${60 * Math.abs((toUTC) % 1)}`;
        }

        return `(GMT${!isBehind ? '+' : ''}${toUTC}) ${zone.timeZone}`;
      });

      content = (
        <DialogContent>
          <small className="md-label">Version {config.version}</small>
          <Select label={languageData.language} defaultSelected={lang} dataSource={Languages} pk="value" model="text" onChange={handleLanguageChange} />
          <Select label={languageData.timeZone} defaultSelected={timeZone === undefined ? browserTime : timeZone} dataSource={sortedZones} onChange={handleTimeZoneChange} />
          <Select label={languageData.oddsFormat.title} defaultSelected={this.odds[oddsFormatShown]} dataSource={this.odds} pk="value" model="text" onChange={handleOddsFormatChange} />
          <Select label="View" defaultSelected={this.views[view]} dataSource={this.views_allowed} pk="value" model="text" onChange={handleViewChange} />
          {viewPreferencesOptions}
          <ThemeSwitcher />
        </DialogContent>
      );
    }

    return (
      <Dialog title={title} open={open}>
        { content }
        <DialogButtons>
          <Button onClick={() => dispatch(closeSettings(path))}>OK</Button>
        </DialogButtons>
      </Dialog>
    );
  }
}

export default connect(
  (state: any) => ({
    title: state.languageData.menu.settings,
    languageData: state.languageData.settings,
    language: state.settings.language,
    timeZone: state.settings.timeZone.name,
    timeZones: state.settings.timeZones,
    oddsFormat: state.settings.oddsFormat,
    view: state.settings.view,
    path: state.router.location.pathname,
    open: state.router.location.search === '?settings',
    viewPreferences: state.settings.viewPreferences
  })
)(Settings);
