import * as React from 'react';
import OddsCard from './odds-card';
import EventLink from './event-link';
import { connect } from 'react-redux';

const EventCard = ({ event, isMobileOn }: any) => {

  return (
    <div>
      { isMobileOn &&
        <EventLink event={event} />
      }
      <OddsCard event={event}/>
    </div>
  );
};

export default connect(
  (state: any, ownProps: any) => Object.assign({}, ownProps, {
    isMobileOn: state.uiToolbar.isMobileOn
  })
)(EventCard);
